import classNames from 'classnames';
import React, { Children, ComponentPropsWithoutRef, FC, ReactElement, useEffect, useId, useState } from 'react';
import { TimelineEventProps } from '../shared/timeline-event/timeline-event';

import { BooleanProp } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { TimelineEventVertical } from '../shared/timeline-event-vertical';

import './timeline-vertical.scss';

export interface TimelineVerticalProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Hide the labels
   **/
  hideLabels: BooleanProp;
  /**
   * Timeline events
   **/
  children: ReactElement<TimelineEventProps> | Array<ReactElement<TimelineEventProps>>;
}

export const TimelineVertical: FC<TimelineVerticalProps> = ({ hideLabels, ...props }) => {
  const generateId = useId();
  const [events, setEvents] = useState<Array<Array<ReactElement<TimelineEventProps>>>>([]);

  useEffect(() => {
    const sortedEvents = Children.toArray(props.children) as Array<ReactElement<TimelineEventProps>>;

    const groupedEvents = sortedEvents.reduce(
      (grouped, event) => {
        const label = event.props.label ?? '';
        grouped[label] = grouped[label] || [];
        grouped[label].push(event);
        return grouped;
      },
      {} as Record<string, ReactElement<TimelineEventProps>[]>
    );

    const groupedEventsArray = Object.values(groupedEvents);

    setEvents(groupedEventsArray);
  }, [props.children]);

  return (
    <div {...props} className={classNames('timeline-vertical')}>
      {events.map((group, groupIndex) => {
        const keyGroup = `timeline-group-${groupIndex}-${generateId}`;

        const groupClassNames = classNames('timeline-group', { 'has-label': !stringToBoolean(hideLabels) });

        return (
          <div key={keyGroup} className={groupClassNames}>
            {!stringToBoolean(hideLabels) && (
              <div className="timeline-label-wrapper">
                <div className="timeline-label">{group[0].props.label}</div>
              </div>
            )}
            <div className="timeline-items">
              {group.map((event, eventIndex) => {
                const keyEvent = `timeline-event-${groupIndex}-${eventIndex}-${generateId}`;
                return (
                  <TimelineEventVertical key={keyEvent} hasIcon={event.props.hasIcon} icon={event.props.icon}>
                    {event.props.children}
                  </TimelineEventVertical>
                );
              })}
            </div>
          </div>
        );
      })}
    </div>
  );
};
