import classNames from 'classnames';
import React, { FC, KeyboardEvent, MouseEvent, useEffect, useMemo, useRef, useState } from 'react';
import { useDatePickerStateContext } from '../contexts/use-date-picker-state-context';
import { getDateString } from '../../../../utils/get-date-string';
import { isDateInSameMonthOfYear } from '../utils/is-date-in-same-month';
import { isSameDay } from '../utils/is-same-day';
import { parseInputDate } from '../utils/parse-input-date';

export const DialogBody: FC = () => {
  const ctx = useDatePickerStateContext();

  const ref = useRef(null);

  const [isCalendarActive, setIsCalendarActive] = useState(false);

  const parseDateFromTarget = (target: HTMLElement): Date | undefined => {
    const dateString = target.getAttribute('data-date');
    return parseInputDate(dateString);
  };

  const focusAndSelectTargetDate = (targetDate: Date | undefined) => {
    if (targetDate && isDateInSameMonthOfYear(ctx.focusedDate, targetDate)) {
      ctx.setFocusedDate(targetDate);
      ctx.setSelectedDate(targetDate);
    }
  };

  const onClickHandler = (evt: MouseEvent<HTMLTableCellElement>) => {
    focusAndSelectTargetDate(parseDateFromTarget(evt.target as HTMLElement));
  };

  const onKeydownHandler = (evt: KeyboardEvent<HTMLTableCellElement>) => {
    const { shiftKey } = evt;
    const { target } = evt;

    switch (evt.code) {
      case 'ArrowUp':
        ctx.moveToPreviousWeek();
        break;
      case 'ArrowDown':
        ctx.moveToNextWeek();
        break;
      case 'ArrowLeft':
        ctx.moveToPreviousDay();
        break;
      case 'ArrowRight':
        ctx.moveToNextDay();
        break;
      case 'PageUp':
        if (shiftKey) {
          ctx.moveToPreviousYear();
        } else {
          ctx.moveToPreviousMonth();
        }
        break;
      case 'PageDown':
        if (shiftKey) {
          ctx.moveToNextYear();
        } else {
          ctx.moveToNextMonth();
        }
        break;
      case 'Home':
        ctx.moveToFirstDayOfWeek();
        break;
      case 'End':
        ctx.moveToLastDayOfWeek();
        break;
      case 'Enter':
      case 'Space':
        focusAndSelectTargetDate(parseDateFromTarget(target as HTMLElement));
        break;
      default:
        break;
    }
  };

  const onFocusHandler = () => {
    setIsCalendarActive(true);
  };

  const onBlurHandler = () => {
    setIsCalendarActive(false);
  };

  const buildDay = (currentDay: Date, weekNumber: number, dayNumber: number) => {
    currentDay.setDate(currentDay.getDate() + 1);

    const isInCurrentMonth = currentDay.getMonth() === ctx.focusedDate.getMonth();
    const isFocusedDate = isSameDay(currentDay, ctx.focusedDate);
    const isSelectedDate = isSameDay(currentDay, ctx.selectedDate);
    const isToday = isSameDay(currentDay, new Date());

    const stringDate = getDateString(currentDay);

    const tdDayClasses = classNames(
      'day w-[44px] h-[44px] border-2 rounded-4 outline-0 flex items-center justify-center text-s-regular text-[var(--color-neutral-900)] cursor-default',
      //  DEFAULT
      {
        'border-transparent': !isSelectedDate && !isFocusedDate
      },
      //  DAY IN CURRENT MONTH
      {
        'hover:bg-[var(--color-primary-100)] hover:text-[var(--color-primary-600)]': isInCurrentMonth
      },
      //  TODAY DEFAULT
      {
        'text-s-bold text-[var(--color-primary-600)]': isToday && !isSelectedDate
      },
      //  SELECTED
      {
        'bg-[var(--color-primary-600)] text-white': isSelectedDate
      },
      //  FOCUS WITH CALENDAR GRID NOT FOCUSED
      {
        'border-[var(--color-primary-400)]': !isSelectedDate && isFocusedDate && !isCalendarActive && isInCurrentMonth
      },
      //  FOCUS
      {
        'outline outline-2 -outline-offset-2 outline-[var(--color-focus)]': isCalendarActive && isFocusedDate && isInCurrentMonth
      }
    );

    const dayClasses = classNames('pointer-events-none select-none', { 'opacity-20': !isInCurrentMonth });

    const trTdKey = `tr-td-key-${weekNumber}-${dayNumber}`;

    const ariaLabelForDay = (() => {
      const labels = ctx.getLabels();

      let dayLabel = currentDay.toLocaleDateString(undefined, { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' });

      if (isToday && labels.todayDayAriaLabel) {
        dayLabel = `${labels.todayDayAriaLabel}, ${dayLabel}`;
      }

      if (isFocusedDate && labels.focusedDayAriaLabel) {
        dayLabel = `${dayLabel}, ${labels.focusedDayAriaLabel}`;
      }

      if (isSelectedDate && labels.selectedDayAriaLabel) {
        dayLabel = `${dayLabel}, ${labels.selectedDayAriaLabel}`;
      }

      return dayLabel;
    })();

    return (
      <div
        key={trTdKey}
        className={tdDayClasses}
        role="gridcell"
        data-date={stringDate}
        aria-label={ariaLabelForDay}
        aria-selected={isFocusedDate}
        tabIndex={isInCurrentMonth && isFocusedDate ? 0 : -1}
        onKeyDown={onKeydownHandler}
        onClick={onClickHandler}
        onFocus={onFocusHandler}
        onBlur={onBlurHandler}
      >
        <div aria-hidden className={dayClasses}>
          {currentDay.getDate()}
        </div>
      </div>
    );
  };

  const buildWeek = (weekNumber: number) => {
    const firstDayOfMonth = new Date(ctx.focusedDate.getFullYear(), ctx.focusedDate.getMonth(), 1);

    const dayOfWeek = firstDayOfMonth.getDay() + 1;

    firstDayOfMonth.setDate(firstDayOfMonth.getDate() - dayOfWeek);
    firstDayOfMonth.setDate(firstDayOfMonth.getDate() + weekNumber * 7);

    return Array.from(Array(7).keys()).map((dayNumber) => {
      return buildDay(firstDayOfMonth, weekNumber, dayNumber);
    });
  };

  const memoGrid = useMemo(() => {
    return Array.from(Array(6).keys()).map((trCount) => {
      const trKey = `tr-key-${trCount}`;
      return (
        <div role="row" className="grid grid-flow-col gap-[2px]" key={trKey}>
          {buildWeek(trCount)}
        </div>
      );
    });
  }, [ctx.focusedDate, ctx.selectedDate, isCalendarActive]);

  const focusDay = () => {
    if (ref.current) {
      const elem = Array.from((ref.current as HTMLElement).querySelectorAll('.day[tabIndex="0"]')).shift();

      if (elem) {
        (elem as HTMLElement).focus();
      }
    }
  };

  //  INITIAL FOCUS ON SELECTED/FOCUSED DAY
  useEffect(() => focusDay(), []);

  //  WHENEVER CALENDAR GRID IS ACTIVE, FOCUS THE DAY AGAIN
  useEffect(() => {
    if (isCalendarActive) {
      focusDay();
    }
  }, [isCalendarActive]);

  //  WHENEVER FOCUSED DATE CHANGES, FOCUS IT
  useEffect(() => focusDay(), [ctx.focusedDate]);

  return (
    <div className="my-16">
      <div role="grid" className="w-full">
        <div role="rowheader" className="grid grid-flow-col gap-[2px]">
          {ctx.getAbbrWeekDays().map((d) => {
            return (
              <div
                role="gridcell"
                key={d}
                className="w-[44px] h-32 flex items-center justify-center bg-[var(--color-neutral-100)] text-s-regular"
                aria-hidden
              >
                {d}
              </div>
            );
          })}
        </div>
        <div ref={ref} role="rowgroup">
          {memoGrid}
        </div>
      </div>
    </div>
  );
};
