import classNames from 'classnames';
import React, { FC } from 'react';
import { Anchor } from '../../../atoms/anchor';

import { AgoraIllustrateBookmark } from '../../../../assets/agora-illustrate-bookmark';
import { CardFooterProps } from '../models';
import { ContactsFooterProps } from '../models/ContactsFooterProps';
import { PublicFooterContacts } from './public-footer-contacts';

const PublicFooterHighlightCard: FC<{ card?: CardFooterProps; contacts?: ContactsFooterProps[]; hasCardLinks: boolean }> = ({
  card,
  contacts,
  hasCardLinks
}) => {
  const cardClassName = classNames(
    'grid gap-x-32 xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-8 xs:col-span-4 md:col-span-8 xl:col-span-8 xl:row-span-1 bg-[var(--color-primary-100)] pb-32 relative',
    {
      'pb-64': !contacts
    }
  );

  const cardHelpClassName = classNames('z-[1]', {
    'xs:col-span-4 md:col-span-6 xl:col-span-6': hasCardLinks,
    'xs:col-span-4 md:col-span-8 xl:col-span-6': !hasCardLinks
  });

  const contactsClassName = classNames('grid gap-x-32 xl:row-span-1 bg-[var(--color-primary-100)]', {
    'xs:grid-cols-4 md:grid-cols-4 xl:grid-cols-8 xs:col-span-4 md:col-span-4 xl:col-span-8 xs:pb-32 md:pb-64 xl:pb-64':
      card && hasCardLinks && contacts,
    'xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-8 xs:col-span-4 md:col-span-8 xl:col-span-8 xs:pb-32 md:pb-64 xl:pb-64':
      card && !hasCardLinks && contacts,
    'xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-8 xs:col-span-4 md:col-span-8 xl:col-span-8 xs:pb-32 md:pb-32 xl:pb-64 ':
      !card && !hasCardLinks && contacts
  });
  return (
    <>
      {card && (
        <div className={cardClassName}>
          {hasCardLinks && (
            <div
              aria-hidden
              className="xl:hidden absolute left-0 xs:w-[calc(100%+32px)] md:w-[calc(100%+64px)] xl:w-[calc(100%+64px)] h-full bg-[var(--color-primary-100)] z-0"
            />
          )}
          <div className={cardHelpClassName}>
            <AgoraIllustrateBookmark aria-hidden className="mb-32" />
            {card?.title && <h2 className="xs:text-m-bold md:text-l-bold text-[var(--color-primary-900)]">{card.title}</h2>}
            {card?.description && <p className="text-m-regular text-[var(--color-primary-900)] mt-16">{card.description}</p>}
          </div>
          {card.links && (
            <ul className="xs:col-span-4 md:col-span-8 xl:col-span-6 mt-32 inline-flex gap-x-32 xs:flex-col md:flex-row xl:flex-row">
              {card.links
                .filter((_i, index) => index < 3)
                .map((link, index) => {
                  const key = `link-${index}`;
                  return (
                    <li key={key} className="py-8">
                      <Anchor
                        {...link}
                        className="xs:flex xs:justify-between"
                        appearance="link"
                        variant="primary"
                        hasIcon
                        trailingIcon="agora-line-arrow-right-circle"
                        trailingIconHover="agora-solid-arrow-right-circle"
                      />
                    </li>
                  );
                })}
            </ul>
          )}
        </div>
      )}
      {contacts && (
        <div className={contactsClassName}>
          {contacts && <PublicFooterContacts contacts={contacts} contactsOnly={!card && !hasCardLinks} hasCardLinks={hasCardLinks} />}
        </div>
      )}
    </>
  );
};

export { PublicFooterHighlightCard };
