import classNames from 'classnames';
import React, { Children, cloneElement, ComponentPropsWithoutRef, FC, isValidElement, ReactElement } from 'react';
import { AvatarProps } from '../../atoms/avatar';
import './avatar-group.scss';
import { VisibleCountAvatarGroup } from './visible-count-avatar-group';

export interface AvatarGroupProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * List of avatar components to use as children of the group.
   */
  children: ReactElement<AvatarProps> | Array<ReactElement<AvatarProps>>;
  /**
   * Number of avatars that will be visible in the group. Once it exceeds that number a separate icon will show how many more avatars there are left.
   * Clicking on the icon with the number will preview the other avatars as well.
   */
  visibleCount?: number;
  /**
   * Dark mode
   */
  darkMode?: boolean;
  /**
   * Accessible text for the dropdown containing the remaining avatars.
   */
  visibleCountAriaLabel?: string;
}

const AvatarGroup: FC<AvatarGroupProps> = ({
  className,
  children,
  visibleCount,
  darkMode,
  visibleCountAriaLabel,
  ...props
}): ReactElement => {
  const avatarGroupClasses = classNames('avatar-group', className, {
    'dark-mode': darkMode
  });

  const avatarCount = Children.toArray(children).length;
  const totalVisibleAvatars = visibleCount ?? avatarCount;

  const renderAvatarListItem = (child: ReactElement<AvatarProps>) => {
    return (
      <div role="listitem">
        {cloneElement<AvatarProps>(child, {
          darkMode
        } as AvatarProps)}
      </div>
    );
  };

  return (
    <div>
      <div {...props} className={avatarGroupClasses} role="list">
        {Children.map(children, (child, index) => {
          if (index < totalVisibleAvatars) {
            return isValidElement(child) ? renderAvatarListItem(child) : null;
          }

          return null;
        })}
        {avatarCount > totalVisibleAvatars && (
          <div role="listitem">
            <VisibleCountAvatarGroup
              visibleCount={totalVisibleAvatars}
              avatarCount={avatarCount}
              visibleCountAriaLabel={visibleCountAriaLabel}
            >
              {children}
            </VisibleCountAvatarGroup>
          </div>
        )}
      </div>
    </div>
  );
};

export { AvatarGroup };
