import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction } from 'react';
import { BooleanProp } from '../../../models';
import classNames from 'classnames';
import { Button } from '../../atoms/button';

export interface SearchPageProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Current page.
   */
  current?: BooleanProp;

  /**
   * Page number.
   */
  pageNumber?: number;
}

export const InnerSearchPage: ForwardRefRenderFunction<HTMLDivElement, SearchPageProps> = (
  { className, current, pageNumber, ...props },
  ref
) => {
  const containerClasses = classNames('search-page', className, {
    current
  });

  return (
    <div {...props} ref={ref}>
      <Button
        className={containerClasses}
        appearance="link"
        aria-label={pageNumber?.toString()}
        {...(current ? { 'aria-current': 'page' } : {})}
      >
        {pageNumber}
      </Button>
    </div>
  );
};

export const SearchPage = forwardRef<HTMLDivElement, SearchPageProps>(InnerSearchPage);
