import React, {
  Children,
  cloneElement,
  ComponentPropsWithoutRef,
  FC,
  isValidElement,
  KeyboardEvent,
  ReactElement,
  useId,
  useRef,
  useState
} from 'react';
import { AvatarProps } from '../../atoms/avatar';
import { Button } from '../../atoms/button';
import { Dropdown, DropdownOption, DropdownRef, DropdownSection } from '../dropdown';

export interface VisibleCountAvatarGroupProps extends ComponentPropsWithoutRef<'div'> {
  children: ReactElement<AvatarProps> | Array<ReactElement<AvatarProps>>;
  visibleCount: number;
  avatarCount: number;
  visibleCountAriaLabel?: string;
}

export const VisibleCountAvatarGroup: FC<VisibleCountAvatarGroupProps> = ({
  avatarCount,
  visibleCount,
  visibleCountAriaLabel,
  children
}) => {
  const generatedId = useId();

  const countValue = visibleCount >= 0 ? `+${avatarCount - visibleCount}` : `+${avatarCount}`;

  const avatarCountList = Children.map(children, (avatar, index) => {
    if (index >= visibleCount && isValidElement(avatar)) {
      return cloneElement(avatar);
    }

    return null;
  });

  const dropdownRef = useRef<DropdownRef | null>(null);

  const [isDropdownVisible, setIsDropdownVisible] = useState(false);

  const onHide = () => {
    setIsDropdownVisible(false);
  };

  const onShow = () => {
    setIsDropdownVisible(true);
  };

  const handleKeyUp = (evt: KeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (dropdownRef.current) {
      if (code === 'Escape') {
        dropdownRef.current.hide();
      }

      if (code === 'Enter' || code === 'NumpadEnter' || code === 'Space') {
        if (dropdownRef.current.visibility) {
          dropdownRef.current.hide();
        } else {
          dropdownRef.current.show();
        }
      }

      if (code === 'Tab' && dropdownRef.current.visibility) {
        dropdownRef.current.first();
      }
    }
  };

  const handleKeyDownCapture = (evt: KeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (code === 'Tab') {
      //  PREVENT TAB INDEX CHANGE FOCUS
      evt.preventDefault();
      evt.stopPropagation();
    }
  };

  const onMouseEnterHandler = () => {
    dropdownRef.current?.show();
  };

  const onMouseLeaveHandler = () => {
    dropdownRef.current?.hide();
  };

  return (
    <div className="avatar-visible-count-wrapper" onMouseEnter={onMouseEnterHandler} onMouseLeave={onMouseLeaveHandler}>
      <Button
        onKeyDownCapture={handleKeyDownCapture}
        onKeyUp={handleKeyUp}
        role="combobox"
        aria-expanded={isDropdownVisible}
        aria-labelledby={`avatar-group-${generatedId}-label`}
        aria-controls={`${generatedId}-dropdown-avatar-group-container`}
        className="avatar-visible-count"
      >
        <div id={`avatar-group-${generatedId}-label`} aria-hidden>
          {countValue}
        </div>
      </Button>

      <div className="absolute -bottom-8 left-0 w-full min-h-[8px]">
        <Dropdown className="min-w-[224px]" ref={dropdownRef} onHide={onHide} onShow={onShow} type="text">
          <DropdownSection
            name={`avatar-dropdown-${generatedId}`}
            value={`avatar-list-${generatedId}`}
            hideSectionName={true}
            aria-label={visibleCountAriaLabel}
          >
            {avatarCountList.map((avatar, index) => {
              const avatarKey = `avatar-group-${generatedId}-option-${index}`;

              return (
                <DropdownOption
                  key={avatarKey}
                  value={avatar.props.information}
                  hasIcon={avatar.props.avatarType !== 'initials'}
                  icon={avatar.props.srcPath}
                  iconPosition="leading"
                >
                  {avatar.props.information}
                </DropdownOption>
              );
            })}
          </DropdownSection>
        </Dropdown>
      </div>
    </div>
  );
};
