import classNames from 'classnames';
import React, { FC } from 'react';
import { ContactsFooterProps } from '../models/ContactsFooterProps';

const PublicFooterContacts: FC<{ contacts: ContactsFooterProps[]; contactsOnly: boolean; hasCardLinks: boolean }> = ({
  contacts,
  contactsOnly,
  hasCardLinks
}) => {
  const contactsClassName = classNames('grid gap-x-32 xs:gap-y-32 md:gap-y-64 md:content-center', {
    'xs:pt-32 md:pt-32 xl:pt-64': contactsOnly,
    'xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-6 xs:col-span-4 md:col-span-8 xl:col-span-6': !hasCardLinks,
    'xs:grid-cols-4 md:grid-cols-4 xl:grid-cols-6 xs:col-span-4 md:col-span-4 xl:col-span-6': !contactsOnly && hasCardLinks
  });
  return (
    <div className={contactsClassName}>
      {contacts
        ?.filter((_i, index) => index < 2)
        .map((contact, index) => {
          const key = `contacts-${index}`;
          return (
            <div key={key} className="xs:col-span-4 md:col-span-4 xl:col-span-3">
              <div className="text-m-bold text-[var(--color-primary-900)] mb-8">{contact.title}</div>
              <div className="text-m-regular text-[var(--color-primary-900)]">{contact.description}</div>
              {contact.phones && (
                <div>
                  {contact.phones[0] && <span className="text-l-bold text-[var(--color-primary-900)]">{contact.phones[0]}</span>}
                  {contact.phones[1] && <span className="text-m-regular text-[var(--color-primary-900)]">{` ${contact.phones[1]} `}</span>}
                  {contact.phones[2] && <span className="text-l-bold text-[var(--color-primary-900)]">{contact.phones[2]}</span>}
                </div>
              )}
            </div>
          );
        })}
    </div>
  );
};

export { PublicFooterContacts };
