import React, { FC, ReactNode } from 'react';
import { LinkableImagesProps } from '../models/LinkableImagesProps';

const FooterCaptionPartners: FC<{
  caption: string | ReactNode;
  partnersLogos: LinkableImagesProps[];
}> = ({ caption, partnersLogos }) => {
  return (
    <div className="grid items-center xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-32 py-32">
      <div className="xs:col-span-4 md:col-span-4 xl:col-span-6">
        <span className="text-m-regular text-[var(--color-neutral-500)]">{caption}</span>
      </div>
      <div className="xs:col-span-4 md:col-span-4 xl:col-span-6 md:justify-self-end md:w-[calc(100%+32px)]">
        <ul className="flex flex-wrap md:justify-end xs:gap-32 md:gap-[30px]">
          {partnersLogos
            .filter((_i, index) => index < 10)
            .map((partner, index) => {
              const key = `logo-${index}`;

              return (
                <li key={key}>
                  {partner?.link && partner?.image && (
                    <a
                      {...partner.link}
                      className="inline-block outline-none border-2 border-transparent rounded-4 opacity-50 hover:opacity-100 focus:rounded-none focus:outline focus:outline-2 focus:-outline-offset-2 focus:outline-[var(--color-focus)] focus:opacity-100"
                    >
                      <img alt={partner.image.alt} {...partner.image} />
                    </a>
                  )}
                  {!partner?.link && partner?.image && <img {...partner?.image} alt={partner?.image.alt} />}
                </li>
              );
            })}
        </ul>
      </div>
    </div>
  );
};

export { FooterCaptionPartners };
