import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, useMemo } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Icon, IconName } from '../icon';
import './avatar.scss';
import { InteractiveAvatar } from './interactive-avatar';
import { NonInteractiveAvatar } from './non-interactive-avatar';
import { Tooltip, TooltipAlignment, TooltipPosition } from '../tooltip';

/**
 * The available types of the avatar. This types will define the appearance of the avatar image.
 */
export type AvatarType = 'image' | 'icon' | 'initials';

/**
 * The available badge positions. The badge position defines the location of the badge on the avatar.
 */
export type AvatarBadgePosition = 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';

/**
 * The available variants of the badge. This variants will define the color of the avatar badge.
 */
export type AvatarBadgeVariant = 'primary' | 'secondary' | 'danger' | 'success' | 'warning' | 'neutral' | 'white';

export interface AvatarProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * The available avatar types. Defines the appearance of the avatar.
   */
  avatarType?: AvatarType;

  /**
   * Avatar source to be used. Depending on the selected avatar type it can be an external image url or an icon name that exists in AgoraDS
   * or first 2 letters of the given name.
   */
  srcPath?: IconName;

  /**
   * Avatar information. Information related to the avatar, such as user name or other.
   */
  information: string;

  /**
   * Avatar alt personalized description of the avatar. The text appears if the avatar fails to load on the page.
   */
  alt: string;

  /**
   * The flag to place a badge of different colors on the avatar.
   */
  hasBadge?: boolean;

  /**
   * The available badge positions. The badge position defines the location of the badge on the avatar.
   */
  badgePosition?: AvatarBadgePosition;

  /**
   * The variant of the badge. This variants will define the color of the avatar badge.
   */
  badgeVariant?: AvatarBadgeVariant;

  /**
   * The available tooltip positions. The tooltip position defines the location of the tooltip on the avatar.
   */
  tooltipPosition?: TooltipPosition;

  /**
   * The available tooltip alignment. The tooltip alignment defines the align of the tooltip on the avatar.
   */
  tooltipAlignment?: TooltipAlignment;

  /**
   * The flag to make the avatar interactive. When it is interactive,
   * avatar can be focused by clicking on it, or pressing SPACE or ENTER.
   */
  interactive?: BooleanProp;

  /**
   * Dark mode
   */
  darkMode?: boolean;

  /**
   * Callback to run whenever the avatar is activated.
   */
  onActivate?: () => void;
}

export const Avatar: FC<AvatarProps> = ({
  className,
  avatarType = 'icon',
  srcPath,
  hasBadge,
  information = 'info',
  badgePosition = 'top-right',
  badgeVariant = 'primary',
  tooltipPosition = 'bottom',
  tooltipAlignment = 'center',
  interactive,
  darkMode,
  onActivate,
  ...props
}) => {
  const containerClasses = classNames('agora-avatar', className, {
    interactive,
    'dark-mode': darkMode
  });

  const memoContent = useMemo(() => {
    const parsedAvatarType = srcPath ? avatarType : 'icon';

    const avatarClasses = classNames(`avatar avatar-${parsedAvatarType}`);

    const badgeClasses = classNames(className, {
      [`badge badge-${badgeVariant} badge-${badgePosition}`]: hasBadge
    });

    if (avatarType === 'initials' && srcPath && srcPath.trim().length !== 0) {
      return (
        <>
          <div className={avatarClasses}>{srcPath.slice(0, 2)}</div>
          {hasBadge && <div className={badgeClasses} />}
        </>
      );
    }

    return (
      <>
        <Icon name={srcPath} className={avatarClasses} aria-hidden />
        {hasBadge && <div className={badgeClasses} />}
      </>
    );
  }, [className, avatarType, srcPath, hasBadge, badgeVariant, badgePosition, darkMode]);

  if (stringToBoolean(interactive)) {
    return (
      <Tooltip
        alignment={tooltipAlignment}
        className="avatar-tooltip"
        description={information}
        position={tooltipPosition}
        variant={darkMode ? 'dark' : 'light'}
      >
        <InteractiveAvatar {...props} className={containerClasses} onActivate={onActivate}>
          {memoContent}
        </InteractiveAvatar>
      </Tooltip>
    );
  }

  return (
    <Tooltip
      alignment="center"
      className="avatar-tooltip"
      description={information}
      position="bottom"
      variant={darkMode ? 'dark' : 'light'}
    >
      <NonInteractiveAvatar {...props} className={containerClasses}>
        {memoContent}
      </NonInteractiveAvatar>
    </Tooltip>
  );
};
