import { createContext, useContext } from 'react';
import { DropdownType } from '../dropdown-type';
import { InternalDropdownOptionProp } from './internal-dropdown-option';
import { InternalDropdownSectionProps } from './internal-dropdown-section';

export interface InternalDropdownContextProps {
  type: DropdownType;
  sections: InternalDropdownSectionProps[];
  options: InternalDropdownOptionProp[];

  visibility?: boolean;
  show: () => void;
  hide: () => void;

  first: () => void;
  prev: () => void;
  next: () => void;
  last: () => void;

  selectOption: (props: InternalDropdownOptionProp) => void;
  selectSectionOptions: (name: string) => void;
  isAllSectionOptionsSelected: (name: string) => boolean;

  hasSectionOptionsToInteract: (name: string) => boolean;
  selectAll: () => void;
  isAllSelected: boolean;
  isAllDisabled: boolean;

  applyFilter: (filter: string) => void;
  activeFilter: string;
  resetFilter: () => void;
}

export const InternalDropdownContext = createContext<InternalDropdownContextProps>({} as InternalDropdownContextProps);

export const useInternalDropdownContext = (): InternalDropdownContextProps => {
  return useContext(InternalDropdownContext);
};
