import React, { FC, useEffect, useMemo, useState } from 'react';
import { Months } from '../../../models';
import { WeekDays } from '../../../models/week-days.interface';
import { DatePickerStateContextType, DatePickerStateProvider } from './contexts/date-picker-state.context';
import { DialogBody } from './date-picker-dropdown-body/date-picker-dropdown-body';
import { DialogFooter } from './date-picker-dropdown-footer/date-picker-dropdown-footer';
import { DialogHeader } from './date-picker-dropdown-header/date-picker-dropdown-header';

export interface DatePickerDropdownProps {
  date: Date;
  onChange: (date: Date) => void;
  onCancel: () => void;
  labels: {
    previousYearAriaLabel: string;
    previousMonthAriaLabel: string;
    nextMonthAriaLabel: string;
    nextYearAriaLabel: string;
    monthsLabels?: Months;
    weekdaysLabels?: WeekDays;
    focusedDayAriaLabel?: string;
    selectedDayAriaLabel: string;
    todayDayAriaLabel: string;
    todayLabel: string;
    todayAriaLabel?: string;
    cancelLabel: string;
    cancelAriaLabel?: string;
    okLabel: string;
    okAriaLabel?: string;
  };
}

export const DatePickerDropdown: FC<DatePickerDropdownProps> = ({ date, onChange, onCancel, labels }) => {
  const [initialized, setInitialized] = useState(false);

  const [selectedDate, setSelectedDate] = useState(date);

  const [focusedDate, setFocusedDate] = useState(date);

  const cancel = () => {
    onCancel();
  };

  useEffect(() => {
    if (initialized) {
      onChange(selectedDate);
    } else {
      setInitialized(true);
    }
  }, [selectedDate]);

  const managerMemo = useMemo(() => {
    return {
      focusedDate,
      selectedDate,
      setFocusedDate,
      setSelectedDate,
      cancel,

      labels
    } as DatePickerStateContextType;
  }, [selectedDate, focusedDate]);

  return (
    <DatePickerStateProvider value={managerMemo}>
      <div className="absolute left-0 top-[68px] shadow-bottom-high bg-white p-16 border-2 border-[var(--color-neutral-200)] rounded-4 z-dropdown">
        <DialogHeader />
        <DialogBody />
        <div className="border-1 border-[var(--color-neutral-300)]" />
        <DialogFooter />
      </div>
    </DatePickerStateProvider>
  );
};
