import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, ComponentType, FC, HTMLProps, LazyExoticComponent, Suspense, SVGProps } from 'react';
import { SVGIconProps } from '../../../models/svg-icon.interface';
import EmptyIcon from './assets/empty-icon';
import { iconList, IconName, isBundledIcon } from './assets/index';
import './icon.scss';

const iconsCache: Record<IconName, LazyExoticComponent<ComponentType<SVGIconProps>>> = {};

const loadIcon = (name: IconName): LazyExoticComponent<ComponentType<SVGIconProps>> => {
  const cachedIcon = iconsCache[name];

  if (cachedIcon) {
    return cachedIcon;
  }

  const lazyComponent = iconList[name];

  iconsCache[name] = lazyComponent;

  return lazyComponent;
};

/**
 * Allowed icon dimensions.
 */
export type IconDimensions = 'xs' | 's' | 'm' | 'l' | 'xl' | 'xxl' | 'xxxl';

type SuperIconProps = SVGProps<SVGSVGElement> & HTMLProps<HTMLImageElement>;

export interface IconProps extends SuperIconProps {
  /**
   * Predefined icon dimensions.
   */
  dimensions?: IconDimensions;
  /**
   * Icon name to be used. Can be an external image url.
   */
  name?: IconName;
  /**
   * Callback to run when image is loaded.
   */
  onIconLoad?: () => void;
  /**
   * Callback to run when image fails to load.
   */
  onIconError?: () => void;
}

export const Icon: FC<IconProps> = ({ dimensions = 'm', name = 'no-icon', onIconLoad, onIconError, ...props }) => {
  const classes = classNames('icon', props.className, {
    [`icon-${dimensions?.toLowerCase()}`]: dimensions,
    'fill-[var(--color-primary-600)]': !props.className?.match('fill-')
  });

  const onLoad = () => {
    onIconLoad?.();
  };

  const onError = () => {
    onIconError?.();
  };

  const shouldRenderImage = name !== 'no-icon' && !isBundledIcon(name);

  //  SRC OR BASE-64
  if (shouldRenderImage) {
    // Assume that it is a base64 image and let the browser do his work
    return <img {...props} src={name} alt={props.alt} className={classes} onLoad={onLoad} onError={onError} />;
  }

  const LazyComponent = loadIcon(name);

  const fullProps = {
    ...props,
    className: classes,
    title: props.title ?? props.alt
  } as ComponentPropsWithoutRef<'svg'>;

  return LazyComponent ? (
    <Suspense>
      <LazyComponent {...fullProps} />
    </Suspense>
  ) : (
    <EmptyIcon {...fullProps} />
  );
};
