import React, { FC, ReactNode, TdHTMLAttributes } from 'react';

export interface TableCellProps extends TdHTMLAttributes<HTMLTableCellElement> {
  children: ReactNode;
  /**
   * Header label.
   */
  headerLabel: ReactNode;
  /**
   * Header identification key.
   */
  headerKey?: string;
  /**
   * Value of the cell.
   */
  value?: string | number | Date;
}

const TableCell: FC<TableCellProps> = ({ children, headerKey = '', headerLabel, ...props }) => {
  return (
    <td {...props} data-header-key={headerKey} data-label={headerLabel}>
      {children}
    </td>
  );
};

export { TableCell };
