import classNames from 'classnames';
import React, { FC, KeyboardEvent, useEffect, useRef } from 'react';
import { Icon } from '../../../atoms/icon';
import { useInternalDropdownContext } from './internal-dropdown-context';
import './internal-dropdown-select-all-section.scss';

export interface InternalDropdownSelectAllSectionProps {
  label?: string;
}

export const InternalDropdownSelectAllSection: FC<InternalDropdownSelectAllSectionProps> = ({ label }) => {
  const ref = useRef<HTMLLIElement>(null);

  const dropdownContext = useInternalDropdownContext();
  const visibility = dropdownContext.visibility;

  const isAllSelected = dropdownContext.isAllSelected;
  const isAllDisabled = dropdownContext.isAllDisabled;
  const selectAll = dropdownContext.selectAll;
  const hide = dropdownContext.hide;
  const prev = dropdownContext.prev;
  const next = dropdownContext.next;
  const first = dropdownContext.first;
  const last = dropdownContext.last;

  useEffect(() => {
    if (ref.current && visibility) {
      const { height } = ref.current.getBoundingClientRect();
      ref.current.setAttribute('data-select-all-section-height', `${height}`);
    }
  }, [visibility]);

  const handleClick = () => {
    selectAll();
  };

  const onKeyDownHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    const { code } = evt;

    //  SPACE TRIGGERS SCROLLING BEHAVIOUR. ITS MUST BE HANDLED IN KEYDOWN CAPTURE TO PREVENT IT.
    if (code === 'Space') {
      evt.preventDefault();
      evt.stopPropagation();
    }
  };

  const onKeyUpHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    const code = evt.code;

    evt.preventDefault();

    switch (code) {
      case 'Escape':
        hide();
        break;
      case 'Space':
      case 'Enter':
      case 'NumpadEnter':
        selectAll();
        break;
      case 'ArrowUp':
        prev();
        break;
      case 'ArrowDown':
        next();
        break;
      case 'Home':
        first();
        break;
      case 'End':
        last();
        break;
    }
  };

  const selectAllItemClassNames = classNames('select-all', { disabled: isAllDisabled });

  const checkboxClassNames = classNames('checkbox-mark', { checked: isAllSelected });

  return (
    <ul className="agora-dropdown-select-all-section" role="none">
      <li className="agora-dropdown-select-all-section-item" ref={ref} role="none">
        <div
          className={selectAllItemClassNames}
          tabIndex={isAllDisabled ? -1 : 0}
          aria-disabled={isAllDisabled}
          onClick={handleClick}
          onKeyUp={onKeyUpHandler}
          onKeyDownCapture={onKeyDownHandler}
          role="option"
          aria-selected={isAllSelected}
          aria-label={label}
        >
          <Icon className={checkboxClassNames} name="agora-line-check" aria-hidden />
          {label && <div className="checkbox-label">{label}</div>}
          {!label && <div className="checkbox-label">&nbsp;</div>}
        </div>
      </li>
    </ul>
  );
};
