import classNames from 'classnames';
import React, {
  Children,
  cloneElement,
  ComponentPropsWithoutRef,
  FC,
  isValidElement,
  KeyboardEvent,
  ReactElement,
  useEffect,
  useId,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { TabBodyProps } from './tab-body/tab-body';
import { TabHeaderProps } from './tab-header/tab-header';
import { TabProps } from './tab/tab';
import './tabs.scss';

export interface TabsProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Children content.
   */
  children: ReactElement<TabProps> | Array<ReactElement<TabProps>>;
  /**
   * Defines if the tab should be fluid.
   */
  fullWidth?: BooleanProp;
  /**
   * Automatic activation.
   */
  automaticActivation?: BooleanProp;
}

export const Tabs: FC<TabsProps> = ({ children, automaticActivation = true, fullWidth = false, className = '', ...props }) => {
  const TabsClassName = classNames('tabs', { 'tabs-full-width': fullWidth }, className);
  const [activeTab, setActiveTab] = useState(0);
  const [tabsHeaderData, setTabsHeaderData] = useState<Array<ReactElement<TabHeaderProps>>>([]);
  const [tabsBodyData, setTabsBodyData] = useState<Array<ReactElement<TabBodyProps>>>([]);
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;

  const handleKeyDownClick = (evt: KeyboardEvent<HTMLDivElement>, index: number) => {
    let navigation = index;
    switch (evt.code) {
      case 'Space':
      case 'Enter':
        evt.preventDefault();
        setActiveTab(index);
        break;
      case 'ArrowRight':
        navigation = index === tabsHeaderData.length - 1 ? 0 : index + 1;
        break;
      case 'ArrowLeft':
        navigation = index === 0 ? tabsHeaderData.length - 1 : index - 1;
        break;
      case 'Home':
        navigation = 0;
        break;
      case 'End':
        navigation = tabsHeaderData.length - 1;
        break;
      default:
        break;
    }
    document.getElementById(`tab-${propsClone.id}-${navigation}`)?.focus();
    if (automaticActivation) {
      setActiveTab(Number(navigation));
    }
  };

  useEffect(() => {
    const header: Array<ReactElement<TabHeaderProps>> = [];
    const body: Array<ReactElement<TabBodyProps>> = [];

    let parsedChildren = children;

    if (!Array.isArray(parsedChildren)) {
      parsedChildren = [parsedChildren];
    }

    Children.forEach(parsedChildren, (element: ReactElement<TabProps>, index) => {
      if (!isValidElement(element)) return;

      if (element.props.active) {
        setActiveTab(index);
      }

      header.push(element.props?.children?.[0] as ReactElement<TabHeaderProps>);
      body.push(element.props?.children?.[1] as ReactElement<TabBodyProps>);
    });

    setTabsHeaderData(header);
    setTabsBodyData(body);
  }, [children]);

  return (
    <div className={TabsClassName}>
      <ul className="tab-list" role="tablist">
        {Children.map(tabsHeaderData, (child: ReactElement<TabHeaderProps>, index) => {
          const key = `tab-${propsClone.id}-${index}`;
          const tabListItemClassNames = classNames('tab-list-item', { 'tab-list-item-active': stringToBoolean(activeTab === index) });
          return (
            <li
              key={key}
              role="tab"
              id={`tab-li-${propsClone.id}-${index}`}
              aria-selected={stringToBoolean(activeTab === index)}
              aria-controls={key}
              className={tabListItemClassNames}
            >
              {cloneElement<TabHeaderProps>(child, {
                ...child.props,
                id: key,
                onClick: () => setActiveTab(index),
                onKeyDown: (evt) => handleKeyDownClick(evt, index),
                className: stringToBoolean(activeTab === index) ? 'tab-header-active' : '',
                tabIndex: stringToBoolean(activeTab === index) ? 0 : -1
              })}
            </li>
          );
        })}
      </ul>
      {tabsBodyData[activeTab] &&
        Children.map(tabsBodyData[activeTab], (child: ReactElement<TabBodyProps>, index) => {
          const panelId = `tabpanel-${propsClone.id}-${index}`;
          return cloneElement<TabBodyProps>(child, {
            ...child.props,
            key: panelId,
            id: panelId,
            role: 'tabpanel',
            tabIndex: 0
          });
        })}
    </div>
  );
};
