import React, { ComponentPropsWithRef, FC, ReactNode } from 'react';
import { BooleanProp } from '../../../models/boolean-type';
import { Button } from '../../atoms/button';
import { useModalContext } from './modal-context';
import './modal-dialog.scss';
import classNames from 'classnames';
import { stringToBoolean } from '../../../utils';

export interface ModalDialogProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * Close button text.
   */
  closeButtonLabel: string;
  /**
   * Children content.
   */
  children: ReactNode;
  /**
   * Render dialog with dark mode styles.
   */
  darkMode?: BooleanProp;
}

const ModalDialog: FC<ModalDialogProps> = ({ closeButtonLabel, children, darkMode = true }) => {
  const { hide } = useModalContext();

  const containerClassNames = classNames('agora-modal-dialog-container', { 'dark-mode': stringToBoolean(darkMode) });

  return (
    <div className={containerClassNames}>
      <div className="agora-modal-dialog w-full h-full flex flex-col">
        <div className="agora-popup-dialog-header h-128 flex flex-row items-start justify-end">
          <Button
            className="mt-16 mr-24"
            onClick={hide}
            appearance="link"
            variant="neutral"
            darkMode
            hasIcon
            trailingIcon="agora-line-x"
            trailingIconHover="agora-solid-x"
          >
            {closeButtonLabel}
          </Button>
        </div>
        <div className="agora-popup-dialog-body mb-32 max-h-[calc(100%-160px)] overflow-hidden">
          <div tabIndex={-1} className="max-h-full overflow-auto container mx-auto">
            {children}
          </div>
        </div>
      </div>
    </div>
  );
};

export { ModalDialog };
