import React, { FC, ReactNode, useCallback, useMemo, useRef, useState } from 'react';
import { Dialog, DialogElementRef } from '../../atoms/dialog';
import { LoaderDialogContext, LoaderDialogContextProps } from './loader-dialog-context';
import { LoaderDialog, LoaderDialogProps } from './loader-dialog';

export interface LoaderDialogProviderProps {
  /**
   * Children content.
   */
  children: ReactNode;
}

const LoaderDialogProvider: FC<LoaderDialogProviderProps> = ({ children }) => {
  const dialogRef = useRef<DialogElementRef>(null);

  const [dialogProps, setDialogProps] = useState<LoaderDialogProps>();
  const [ariaLiveMessage, setAriaLiveMessage] = useState('');

  const showLoader = useCallback(
    (currentDialogProps: LoaderDialogProps) => {
      if (currentDialogProps['aria-label']) {
        setAriaLiveMessage(currentDialogProps['aria-label']);
      } else if (currentDialogProps['aria-labelledby']) {
        const ids = currentDialogProps['aria-labelledby'].split(' ');

        const val = ids
          .map((id) => {
            return document.getElementById(id)?.innerText ?? '';
          })
          .filter((str) => !!str)
          .join(' ');

        setAriaLiveMessage(val);
      } else {
        setAriaLiveMessage(`${currentDialogProps.title} ${currentDialogProps.subtitle}`);
      }

      setDialogProps(currentDialogProps);

      dialogRef.current?.show();
    },
    [setDialogProps]
  );

  const hideLoader = useCallback((): void => {
    setAriaLiveMessage('');
    dialogRef.current?.hide();
  }, []);

  const contextValue = useMemo(
    () =>
      ({
        showLoader,
        current: dialogRef.current,
        visibility: !!dialogRef.current?.visible,
        hideLoader
      }) as LoaderDialogContextProps,
    [showLoader, hideLoader]
  );

  return (
    <LoaderDialogContext.Provider value={contextValue}>
      <div aria-live="assertive" className="sr-only">
        {ariaLiveMessage}
      </div>
      <Dialog ref={dialogRef} fullScreen dismissOnBackdropClick={false} dismissOnEscape={false} aria-busy="true">
        {!!dialogProps && <LoaderDialog {...dialogProps} />}
      </Dialog>
      {children}
    </LoaderDialogContext.Provider>
  );
};

export { LoaderDialogProvider };
