import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode, useId } from 'react';
import { HeadingLevel } from '../../../../models';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import './card-hero-sitemap.scss';

export interface CardHeroSitemapProps extends Omit<ComponentPropsWithoutRef<'div'>, 'title'> {
  /**
   * header title.
   */
  headingLevel?: HeadingLevel;
  /**
   * The title of the card
   */
  title?: ReactNode;
  /**
   * The description of the card
   */
  description?: ReactNode;
  /**
   * The list of links of the card
   */
  anchors: AnchorProps[];
  /**
   * Object anchor default
   */
  highlightedLink?: AnchorProps;
}

export const CardHeroSitemap: FC<CardHeroSitemapProps> = ({
  description,
  headingLevel: Heading = 'h2',
  title,
  anchors = [],
  highlightedLink,
  className,
  ...props
}) => {
  const generatedId = useId();
  const arrayLink = anchors.slice(0, 5);

  const CardHeroSitemapClasses = classNames('card-hero-sitemap', className);

  const headingPlacement = {
    title: !!title,
    description: !title && !!description
  };

  const buildText = (text: ReactNode, isHeading: boolean): ReactNode => {
    return isHeading ? <Heading>{text}</Heading> : text;
  };

  return (
    <div className={CardHeroSitemapClasses} {...props}>
      {(title || description) && (
        <div className="flex gap-8 flex-col  text-l-semibold">
          {title && <div className="title-container">{buildText(title, headingPlacement.title)}</div>}
          {description && <div className="description-container">{buildText(description, headingPlacement.description)}</div>}
        </div>
      )}

      {arrayLink.length > 0 && (
        <ul className="flex flex-col justify-start items-start gap-8">
          {arrayLink?.map((link, index) => {
            const key = `card-hero-sitemap-${generatedId}-description-block-${index}`;
            return (
              <li key={key}>
                <Anchor {...link} />
              </li>
            );
          })}
        </ul>
      )}

      {highlightedLink && (
        <Anchor
          appearance="link"
          {...highlightedLink}
          hasIcon={highlightedLink.hasIcon ?? true}
          trailingIcon={highlightedLink.trailingIcon ?? 'agora-line-arrow-right-circle'}
          trailingIconHover={highlightedLink.trailingIcon ?? 'agora-line-arrow-right-circle'}
          {...(!highlightedLink?.children && { className: '!block' })}
        />
      )}
    </div>
  );
};
