import { createContext, ReactNode, useContext } from 'react';
import { DialogElementRef } from '../../atoms/dialog';
import { ModalDialogProps } from './modal-dialog';

export interface ModalContextProps {
  current: DialogElementRef | null;
  visibility?: boolean;
  show: (children: ReactNode, popupProps: ModalDialogProps) => void;
  hide: () => void;
}

const ModalContext = createContext<ModalContextProps>({} as ModalContextProps);

const useModalContext = (): ModalContextProps => {
  return useContext(ModalContext);
};

export { ModalContext, useModalContext };
