import classNames from 'classnames';
import React, { CSSProperties, FC, MouseEvent } from 'react';
import { stringToBoolean } from '../../../../../utils';
import { Anchor, AnchorAppearance, AnchorProps, AnchorVariant } from '../../../../atoms/anchor';
import { Button, ButtonAppearance, ButtonProps, ButtonVariant } from '../../../../atoms/button';
import { HeaderMenuOptionsProps } from './header-menu-item-options';
import './header-menu-item-options-links.scss';

export const HeaderMenuItemOptionsLinks: FC<HeaderMenuOptionsProps & { className?: string; style?: CSSProperties }> = ({
  submenu,
  closeOtherMenuItemOpened,
  setBackdropVisible,
  onClick,
  className,
  style
}) => {
  const options = stringToBoolean(submenu.actionItem) ? (submenu.submenuItems as ButtonProps[]) : (submenu.submenuItems as AnchorProps[]);

  const containerClassName = classNames('container mx-auto', {
    'py-32': submenu.submenuType !== 'topics',
    'pt-64 pb-32': submenu.submenuType === 'topics',
    'flex flex-col items-start':
      submenu.submenuType === 'link' && (submenu.submenuAlignment === 'start' || submenu.submenuAlignment === undefined),
    'flex flex-col items-end': submenu.submenuType === 'link' && submenu.submenuAlignment === 'end'
  });

  const ulClassName = classNames({
    'grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-32': submenu.submenuType === 'language' || submenu.submenuType === 'platform',
    'grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-x-32 gap-y-16': submenu.submenuType === 'topics',
    'xs:w-full md:w-auto xl:w-auto gap-x-32 gap-y-16 flex xs:flex-col md:flex-row xl:flex-row flex-wrap': submenu.submenuType === 'link'
  });

  const liClassName = classNames({
    'h-full xs:col-span-4 md:col-span-4 xl:col-span-3 !mb-0': submenu.submenuType === 'language' || submenu.submenuType === 'platform',
    'xs:col-span-4 md:col-span-4 xl:col-span-3': submenu.submenuType === 'topics'
  });

  return (
    <div className={containerClassName}>
      {stringToBoolean(submenu.hasBackButton) && submenu.backButtonLabel && (
        <Button
          variant="neutral"
          appearance="link"
          hasIcon
          leadingIcon="agora-line-arrow-left-anchor"
          leadingIconHover="agora-solid-arrow-left-anchor"
          className="xs:mx-32 md:mx-64 xl:mx-0 mb-32"
          onClick={submenu.handleBackButton}
        >
          {submenu.backButtonLabel}
        </Button>
      )}
      <div className={className} style={style}>
        {stringToBoolean(submenu.hasSubmenuItemsSectionTitle) && submenu.submenuItemsSectionTitle && (
          <p className="text-m-semibold text-[var(--color-neutral-500)] mb-32">{submenu.submenuItemsSectionTitle}</p>
        )}
        {stringToBoolean(submenu.hasSubmenuItemsTitle) && submenu.submenuItemsTitle && (
          <h2 className="text-xl-bold text-[var(--color-primary-900)] mb-32">{submenu.submenuItemsTitle}</h2>
        )}
        <ul className={ulClassName} role="menu" aria-label={submenu.submenuAriaLabel}>
          {options.map((element, index) => {
            const key = `menu-options-nav-${index}`;
            const optionClasseName = classNames(
              'min-h-[60px]',
              {
                'agora-rounded-leading-icon full-image !py-[6px] !min-h-[44px] language': submenu.submenuType === 'language',
                'agora-rounded-leading-icon small-image platform': submenu.submenuType === 'platform',
                'link xs:w-full md:w-auto xl:w-auto xs:[&>.children-wrapper]:mr-auto md:[&>.children-wrapper]:mr-0 xl:[&>.children-wrapper]:mr-0':
                  submenu.submenuType === 'link',
                'topics xs:w-full md:w-auto xl:w-auto xs:[&>.children-wrapper]:mr-auto md:[&>.children-wrapper]:mr-0 xl:[&>.children-wrapper]:mr-0':
                  submenu.submenuType === 'topics'
              },
              element.className
            );
            const getTrailingIcon = () => {
              if (submenu.submenuType === 'language') {
                return 'agora-line-check';
              }
              return undefined;
            };

            const getTrailingIconHover = () => {
              if (submenu.submenuType === 'language') {
                return 'agora-solid-check';
              }
              return undefined;
            };

            const handleClick = (evt: MouseEvent<any>) => {
              closeOtherMenuItemOpened(evt.currentTarget as unknown as HTMLDivElement);
              setBackdropVisible(false);
              document.body.classList.remove('overflow-hidden');
              onClick?.(evt);
              element.onClick?.(evt);
            };

            const defaultVariant = submenu.submenuType === 'topics' ? 'primary' : 'neutral';
            return (
              <li key={key} className={liClassName} role="none">
                {stringToBoolean(submenu.actionItem) ? (
                  <Button
                    appearance={(element.appearance as ButtonAppearance) ?? 'link'}
                    variant={(element.variant as ButtonVariant) ?? defaultVariant}
                    hasIcon={element.hasIcon ?? true}
                    trailingIcon={element.trailingIcon ?? getTrailingIcon()}
                    trailingIconHover={element.trailingIconHover ?? getTrailingIconHover()}
                    role="menuitem"
                    {...(element as ButtonProps)}
                    className={optionClasseName}
                    onClick={handleClick}
                  />
                ) : (
                  <Anchor
                    appearance={(element.appearance as AnchorAppearance) ?? 'link'}
                    variant={(element.variant as AnchorVariant) ?? defaultVariant}
                    hasIcon={element.hasIcon ?? true}
                    trailingIcon={element.trailingIcon ?? getTrailingIcon()}
                    trailingIconHover={element.trailingIconHover ?? getTrailingIconHover()}
                    role="menuitem"
                    {...(element as AnchorProps)}
                    className={optionClasseName}
                    onClick={handleClick}
                  />
                )}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
