import React, { FC, HTMLProps } from 'react';
import { SubmenuItemProps } from '../../models';
import { HeaderMenuItemOptionsLinks } from './header-menu-item-options-links';
import { HeaderMenuItemOptionsSearch } from './header-menu-item-options-search';

export interface HeaderMenuOptionsProps extends HTMLProps<HTMLButtonElement | HTMLAnchorElement | HTMLDivElement> {
  /**
   *  Submenu options configuration
   */
  submenu: SubmenuItemProps;
  /**
   *
   * Function to close other menu items that are open
   */
  closeOtherMenuItemOpened: (currentSubmenu: HTMLDivElement) => void;

  /**
   *
   * Set state if backdrop visible or not
   */
  setBackdropVisible: (val: boolean) => void;
}

const HeaderMenuOptions: FC<HeaderMenuOptionsProps> = ({
  submenu,
  closeOtherMenuItemOpened,
  setBackdropVisible,
  onClick,
  className,
  style
}) => {
  if (
    ((!submenu.submenuItems || submenu.submenuItems.length === 0) && submenu.submenuType !== 'search') ||
    (!submenu.submenuSearchContent && submenu.submenuType === 'search')
  ) {
    return;
  }

  if (
    submenu.submenuType === 'language' ||
    submenu.submenuType === 'platform' ||
    submenu.submenuType === 'link' ||
    submenu.submenuType === 'topics'
  ) {
    return (
      <HeaderMenuItemOptionsLinks
        submenu={submenu}
        closeOtherMenuItemOpened={closeOtherMenuItemOpened}
        setBackdropVisible={setBackdropVisible}
        onClick={onClick}
        className={className}
        style={style}
      />
    );
  } else if (submenu.submenuType === 'search' && submenu.submenuSearchContent) {
    return <HeaderMenuItemOptionsSearch {...submenu.submenuSearchContent} />;
  }
};

export { HeaderMenuOptions };
