import classNames from 'classnames';
import React, { createRef, FC, KeyboardEvent, ReactElement, RefObject, useEffect, useId, useMemo, useRef } from 'react';
import { BooleanProp } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Icon } from '../../../atoms/icon/icon';
import { useInternalDropdownContext } from './internal-dropdown-context';
import { InternalDropdownOption, InternalDropdownOptionProp } from './internal-dropdown-option';
import './internal-dropdown-section.scss';

const SectionNameComponent: FC<{ id: string; value: string; name: string; hideSectionName?: BooleanProp }> = ({
  id,
  value,
  name,
  hideSectionName = false
}) => {
  const sectionNameRef = useRef<HTMLLIElement>(null);

  const dropdownContext = useInternalDropdownContext();
  const type = dropdownContext.type;
  const isAllSectionOptionsSelected = dropdownContext.isAllSectionOptionsSelected;
  const hasSectionOptionsToInteract = dropdownContext.hasSectionOptionsToInteract;
  const selectSectionOptions = dropdownContext.selectSectionOptions;
  const hide = dropdownContext.hide;
  const next = dropdownContext.next;
  const prev = dropdownContext.prev;
  const first = dropdownContext.first;
  const last = dropdownContext.last;
  const visibility = dropdownContext.visibility;
  const activeFilter = dropdownContext.activeFilter;

  useEffect(() => {
    if (sectionNameRef.current && visibility) {
      const sectionHeight = sectionNameRef.current.getBoundingClientRect().height;
      sectionNameRef.current.setAttribute('data-section-name-height', `${sectionHeight}`);
    }
  }, [visibility, activeFilter]);

  const allSelected = useMemo(() => {
    return isAllSectionOptionsSelected(value);
  }, [isAllSectionOptionsSelected, value]);

  const handleSectionClick = () => {
    if (type !== 'checkbox' || !hasSectionOptionsToInteract(value)) {
      return;
    }

    selectSectionOptions(value);
  };

  const onKeyDownHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    if (type !== 'checkbox') {
      return;
    }

    const code = evt.code;

    if (code === 'Space') {
      evt.preventDefault();
      evt.stopPropagation();
    }
  };

  const onKeyUpHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    if (type !== 'checkbox') {
      return;
    }

    evt.preventDefault();

    const code = evt.code;

    switch (code) {
      case 'Escape':
        hide();
        break;
      case 'Space':
      case 'Enter':
      case 'NumpadEnter':
        selectSectionOptions(value);
        break;
      case 'ArrowUp':
        prev();
        break;
      case 'ArrowDown':
        next();
        break;
      case 'Home':
        first();
        break;
      case 'End':
        last();
        break;
    }
  };

  const checkboxClassNames = classNames('checkbox-mark', { checked: allSelected });

  const sectionNameOptionClassNames = classNames(
    'section-name-item',
    { [type]: type },
    { disabled: !hasSectionOptionsToInteract(value) },

    { hidden: type !== 'checkbox' && stringToBoolean(hideSectionName) } // NEED SECTION NAMES WHEN ITS CHECKBOX TYPE
  );

  const sectionNameClassNames = classNames(
    'section-name',
    { hidden: type !== 'checkbox' && stringToBoolean(hideSectionName) } // NEED SECTION NAMES WHEN ITS CHECKBOX TYPE
  );

  return (
    <li className={sectionNameClassNames} ref={sectionNameRef} data-section-option-id={id}>
      <div
        className={sectionNameOptionClassNames}
        aria-label={name}
        role="option"
        aria-selected={allSelected}
        aria-disabled={!hasSectionOptionsToInteract(value)}
        onClick={handleSectionClick}
        onKeyUp={onKeyUpHandler}
        onKeyDownCapture={onKeyDownHandler}
        tabIndex={type === 'checkbox' && hasSectionOptionsToInteract(value) ? 0 : -1}
      >
        <div className="label">{name}</div>
        {type === 'checkbox' && hasSectionOptionsToInteract(value) && (
          <Icon className={checkboxClassNames} name="agora-line-check" aria-hidden />
        )}
      </div>
    </li>
  );
};

export interface InternalDropdownSectionProps {
  'className'?: string;
  'hideSectionName'?: BooleanProp;
  'name': string;
  'value': string;
  'children': ReactElement<InternalDropdownOptionProp> | ReactElement<InternalDropdownOptionProp>[];
  'aria-label'?: string;
}

export const InternalDropdownSection: FC<InternalDropdownSectionProps> = ({
  hideSectionName = false,
  className,
  name,
  value,
  'aria-label': ariaLabel
}) => {
  const generatedId = useId();
  const sectionId = value ?? generatedId;

  const dropdownContext = useInternalDropdownContext();
  const options = dropdownContext.options;
  const type = dropdownContext.type;
  const visibility = dropdownContext.visibility;

  const optionsRefMap = useMemo(() => new Map<string, RefObject<HTMLLIElement>>(), []);

  const sectionClassNames = classNames('agora-dropdown-section', className);

  const sectionOptions = useMemo(() => {
    return options
      .filter((opt) => opt.name === value)
      .map((props: InternalDropdownOptionProp) => {
        const key = `${sectionId}-${type}-${props.value}`;

        const optionClassNames = classNames(props.className, {
          'no-section-name': type !== 'checkbox' && stringToBoolean(hideSectionName)
        });

        const newRef = createRef<HTMLLIElement>();
        optionsRefMap.set(key, newRef);

        return (
          <li className="section-option" key={key} ref={newRef} data-section-option-id={key}>
            <InternalDropdownOption {...props} className={optionClassNames} />
          </li>
        );
      });
  }, [options, hideSectionName, optionsRefMap, sectionId, type, value]);

  useEffect(() => {
    if (visibility) {
      optionsRefMap.forEach((ref) => {
        if (ref.current) {
          const optionHeight = ref.current.getBoundingClientRect().height;
          ref.current.setAttribute('data-section-option-height', `${optionHeight}`);
        }
      });
    }
  }, [visibility, type, optionsRefMap]);

  return (
    <ul className={sectionClassNames} aria-label={ariaLabel}>
      <SectionNameComponent id={sectionId} value={value} name={name} hideSectionName={hideSectionName} />
      {sectionOptions}
    </ul>
  );
};
