import { useContext, useMemo } from 'react';
import { Months } from '../../../../models';
import { WeekDays } from '../../../../models/week-days.interface';
import { DatePickerStateContext } from './date-picker-state.context';

const DEFAULT_MONTHS = {
  jan: 'Janeiro',
  feb: 'Fevereiro',
  mar: 'Maro',
  apr: 'Abril',
  may: 'Maio',
  jun: 'Junho',
  jul: 'Julho',
  aug: 'Agosto',
  set: 'Setembro',
  oct: 'Outubro',
  nov: 'Novembro',
  dec: 'Dezembro'
} as Months;

const DEFAULT_WEEK_DAYS = {
  sun: 'Domingo',
  mon: 'Segunda',
  tue: 'Tera',
  wed: 'Quarta',
  thu: 'Quinta',
  fri: 'Sexta',
  sat: 'Sbado'
} as WeekDays;

const useDatePickerStateContext = () => {
  const ctx = useContext(DatePickerStateContext);

  if (!ctx) {
    throw new Error('Context must be used within a provider.');
  }

  const months = ctx.labels.monthsLabels ?? DEFAULT_MONTHS;

  const weekDays = ctx.labels.weekdaysLabels ?? DEFAULT_WEEK_DAYS;

  const mapMonths = new Map<number, string>();
  Object.values(months).forEach((value, index) => {
    mapMonths.set(index, value);
  });

  const getAbbrWeekDays = () => {
    return Object.values(weekDays).map((wd: string) => wd.substring(0, 3));
  };

  const focusedMonth = useMemo(() => {
    return mapMonths.get(ctx.focusedDate.getMonth());
  }, [ctx.focusedDate]);

  const focusedYear = useMemo(() => {
    return ctx.focusedDate.getFullYear();
  }, [ctx.focusedDate]);

  const focusToday = (): void => {
    ctx.setFocusedDate(new Date());
  };

  const moveToPreviousYear = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setFullYear(newDate.getFullYear() - 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToPreviousMonth = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setMonth(newDate.getMonth() - 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToNextMonth = () => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setMonth(newDate.getMonth() + 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToNextYear = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setFullYear(newDate.getFullYear() + 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToNextDay = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() + 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToNextWeek = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() + 7);
    ctx.setFocusedDate(newDate);
  };

  const moveToPreviousDay = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() - 1);
    ctx.setFocusedDate(newDate);
  };

  const moveToPreviousWeek = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() - 7);
    ctx.setFocusedDate(newDate);
  };

  const moveToFirstDayOfWeek = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() - newDate.getDay());
    ctx.setFocusedDate(newDate);
  };

  const moveToLastDayOfWeek = (): void => {
    const newDate = new Date(ctx.focusedDate.getTime());
    newDate.setDate(newDate.getDate() + (6 - newDate.getDay()));
    ctx.setFocusedDate(newDate);
  };

  const selectFocusedDate = (): void => {
    ctx.setSelectedDate(ctx.focusedDate);
  };

  const getLabels = () => {
    return ctx.labels;
  };

  return {
    ...ctx,
    getLabels,

    getAbbrWeekDays,

    focusedMonth,
    focusedYear,

    moveToPreviousYear,
    moveToPreviousMonth,
    moveToNextMonth,
    moveToNextYear,

    moveToNextDay,
    moveToNextWeek,
    moveToPreviousDay,
    moveToPreviousWeek,
    moveToFirstDayOfWeek,
    moveToLastDayOfWeek,
    selectFocusedDate,

    focusToday
  };
};

export { useDatePickerStateContext };
