import classNames from 'classnames';
import React, { FC, KeyboardEvent, useRef, useState } from 'react';
import { IconPosition } from '../../../../models';
import { BooleanProp } from '../../../../models/boolean-type';
import { stringToBoolean } from '../../../../utils';
import { Icon } from '../../../atoms/icon';
import { DropdownType } from '../dropdown-type';
import { useInternalDropdownContext } from './internal-dropdown-context';
import './internal-dropdown-option.scss';

const OptionComponent: FC<{ type: DropdownType; hasIcon?: BooleanProp; icon?: string; children: string }> = ({
  type,
  icon,
  hasIcon,
  children
}) => {
  if (type === 'checkbox') {
    return (
      <div className="flex gap-8 w-full">
        <Icon className="checkbox-mark" name="agora-line-check" aria-hidden />
        <div className="option-content">{children}</div>
        {hasIcon && <Icon className="option-icon" name={icon} aria-hidden />}
      </div>
    );
  }

  if (!hasIcon) {
    return <div className="option-content">{children}</div>;
  }

  return (
    <>
      <Icon className="option-icon" name={icon} aria-hidden />
      <div className="option-content">{children}</div>
    </>
  );
};

export interface InternalDropdownOptionProp {
  className?: string;
  name?: string;
  value: string;
  children: string;
  hasIcon?: BooleanProp;
  icon?: string;
  iconPosition?: IconPosition;
  disabled?: BooleanProp;
  selected?: BooleanProp;
  filtered?: BooleanProp;
}

export const InternalDropdownOption: FC<InternalDropdownOptionProp> = ({
  className,
  disabled,
  selected,
  filtered,
  hasIcon,
  icon,
  iconPosition = 'leading',
  children,
  name,
  value
}) => {
  const parsedIconName = icon?.replace('solid', 'line');
  const [iconName, setIconName] = useState(parsedIconName ?? '');

  const dropdownContext = useInternalDropdownContext();
  const selectOption = dropdownContext.selectOption;
  const options = dropdownContext.options;
  const last = dropdownContext.last;
  const first = dropdownContext.first;
  const next = dropdownContext.next;
  const prev = dropdownContext.prev;
  const type = dropdownContext.type;
  const hide = dropdownContext.hide;

  const ref = useRef<HTMLDivElement>(null);

  const optionWrapperClasses = classNames(
    'agora-dropdown-option',
    className,

    { [type]: type },
    { disabled: stringToBoolean(disabled) },
    { selected: stringToBoolean(selected) },
    { filtered: stringToBoolean(filtered) },
    {
      'leading-icon': stringToBoolean(hasIcon) && iconPosition === 'leading',
      'trailing-icon': stringToBoolean(hasIcon) && iconPosition === 'trailing'
    }
  );

  const select = () => {
    const option = options.find((opt) => {
      return opt.value === value && opt.name === name;
    });

    if (option) {
      selectOption(option);
    }
  };

  const onKeyDownCaptureHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    const { code } = evt;

    //  SPACE, UP AND DOWN TRIGGERS SCROLLING BEHAVIOUR. ITS MUST BE HANDLED IN KEYDOWN CAPTURE TO PREVENT IT.
    if (code === 'Space' || code === 'Tab') {
      evt.stopPropagation();
      evt.preventDefault();
    }
  };

  const onKeyUpHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    const { code, shiftKey } = evt;

    switch (code) {
      case 'Escape':
        hide();
        break;
      case 'Tab':
        if (shiftKey) {
          prev();
        } else {
          next();
        }
        break;
      case 'ArrowUp':
        prev();
        break;
      case 'ArrowDown':
        next();
        break;
      case 'Home':
        first();
        break;
      case 'End':
        last();
        break;
      case 'Space':
      case 'Enter':
      case 'NumpadEnter':
        select();
        break;
    }
  };

  const clickHandler = () => {
    if (disabled) {
      return;
    }

    select();
  };

  const applySolidIcon = () => {
    if (!disabled) {
      setIconName((lastIconName) => lastIconName.replace('line', 'solid'));
    }
  };

  const applyLineIcon = () => {
    if (!disabled) {
      setIconName((lastIconName) => lastIconName.replace('solid', 'line'));
    }
  };

  return (
    <div
      ref={ref}
      className={optionWrapperClasses}
      role="option"
      aria-selected={stringToBoolean(selected)}
      aria-label={children}
      onClick={clickHandler}
      onKeyUp={onKeyUpHandler}
      onKeyDownCapture={onKeyDownCaptureHandler}
      tabIndex={disabled ? -1 : 0}
      onMouseEnter={applySolidIcon}
      onMouseMove={applySolidIcon}
      onMouseLeave={applyLineIcon}
    >
      <OptionComponent type={type} hasIcon={stringToBoolean(hasIcon)} icon={iconName}>
        {children}
      </OptionComponent>
    </div>
  );
};
