import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  MouseEvent,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import './input-range.scss';

export type RangeOrientation = 'horizontal' | 'vertical';

export type RangeType = 'single' | 'double';

export interface RangeProps extends Omit<ComponentPropsWithRef<'input'>, 'value' | 'onChange'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Decides if display/hide label.
   */
  hideLabel?: BooleanProp;
  /**
   * The orientation of the range (horizontal or vertical).
   */
  orientation?: RangeOrientation;
  /**
   * The type of the range (single or double).
   */
  type?: RangeType;
  /**
   * If `true`, the range will be disabled.
   */
  disabled?: boolean;
  /**
   * The value(s) of the range.
   */
  value: number[];
  /**
   * The minimum value of the range.
   */
  min: number | string;
  /**
   * The maximum value of the range.
   */
  max: number | string;
  /**
   * Custom onChange event handler for double range type.
   * Will be called with an array of values.
   */
  onChange?: (values: number[]) => void;
}
export interface InputRangeRef {
  current: (HTMLDivElement | HTMLInputElement)[];
}

const InnerRange: ForwardRefRenderFunction<HTMLDivElement | HTMLInputElement | (HTMLInputElement | null)[], RangeProps> = (
  { label, hideLabel, type = 'single', disabled, orientation = 'horizontal', value: propValue = [0, 0], ...props },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const { onChange } = propsClone;

  const [currentVal, setCurrentVal] = useState<number[]>([propValue[0], propValue[1]]);

  const prevPropValue = useRef<number[]>(propValue);

  const innerRefs1 = useRef<HTMLInputElement>(null);
  const innerRefs2 = useRef<HTMLInputElement>(null);

  useImperativeHandle(ref, () => {
    if (type === 'single') {
      return [innerRefs1.current as HTMLInputElement];
    }
    return [innerRefs1.current as HTMLInputElement, innerRefs2.current as HTMLInputElement];
  });

  useEffect(() => {
    if (prevPropValue.current[0] !== propValue[0] || prevPropValue.current[1] !== propValue[1]) {
      const values = [...propValue];

      if (type === 'single') {
        values[1] = Number(propsClone.max);
      }

      setCurrentVal(values);

      prevPropValue.current = values;
    }
  }, [propValue]);

  const handleRangeChange = (values: number[]): void => {
    const [firstValue, secondValue] = values;

    if (type === 'single') {
      setCurrentVal(values);
      onChange?.(values);
    } else if (type === 'double') {
      if (firstValue > secondValue || secondValue < firstValue || firstValue === secondValue) {
        return;
      }

      setCurrentVal(values);
      onChange?.(values);
    }
  };

  const getBackground = () => {
    if (!document) {
      return '#FFFFFF'; //  HAMMER.... REVIEW THIS... NEXTJS WAS CRASHING DUE TO DOCUMENT SSR STUFF
    }

    const primaryColor = getComputedStyle(document.documentElement).getPropertyValue('--color-primary-600');
    const secondaryColor = getComputedStyle(document.documentElement).getPropertyValue('--color-neutral-200');
    const min = Number(propsClone.min);
    const max = Number(propsClone.max);

    if (type === 'single' && !disabled) {
      let percent = ((currentVal[0] - min) / (max - min)) * 100;
      percent = Math.min(percent, 100); // Round to 100% if the value is greater than the maximum
      return `linear-gradient(to right, ${primaryColor} ${percent}%, ${secondaryColor} ${percent}%, ${secondaryColor}  100%)`;
    }

    if (type === 'double' && !disabled) {
      let percent0 = ((currentVal[0] - min) / (max - min)) * 100;
      let percent1 = ((currentVal[1] - min) / (max - min)) * 100;
      percent0 = Math.min(percent0, 100); // Round to 100% if the value is greater than the maximum
      percent1 = Math.min(percent1, 100); // Round to 100% if the value is greater than the maximum
      return `linear-gradient(to right, ${secondaryColor} ${percent0}%, ${primaryColor} ${percent0}%, ${primaryColor} ${percent1}%, ${secondaryColor} ${percent1}%)`;
    }

    return secondaryColor;
  };

  const RangeClassName = classNames('input-range-wrapper min-h-[44px]', {
    'input-range-wrapper-disabled': disabled
  });

  const hasLabel = !hideLabel && label && label.length > 0;
  const RailClassName = classNames(
    'rail absolute w-full h-[4px] left-[0px] z-[0]',
    { 'top-[20px]': !hasLabel },
    { 'bottom-[22px]': hasLabel }
  );

  const InputClassName = classNames(
    'w-full outline-none rounded-16 appearance-none h-[0] absolute',
    { 'top-[22px]': !hasLabel },
    { 'top-[52px]': hasLabel }
  );

  const RangeStyle = {
    background: getBackground()
  };

  const handleMouseUp = () => {
    innerRefs1.current?.classList.remove('z-[2]');
    innerRefs2.current?.classList.remove('z-[2]');
  };

  const handleMouseDown = (evt: MouseEvent<HTMLInputElement>) => {
    handleMouseUp();

    const target = evt.target as HTMLInputElement;
    target.classList.add('z-[2]');
  };

  return (
    <div className={RangeClassName}>
      <div className={`input-range-${orientation}`}>
        <label hidden={stringToBoolean(hideLabel)} htmlFor={`${propsClone.id}-0`} className="text-[var(--color-primary-900)] text-m-medium">
          {label}
        </label>
        <div className={RailClassName} style={RangeStyle} />
        <input
          {...props}
          className={InputClassName}
          type="range"
          id={`${propsClone.id}-0`}
          disabled={disabled}
          onChange={(event) => handleRangeChange([Number(event.target.value), currentVal[1]])}
          value={currentVal[0]}
          ref={innerRefs1}
          aria-label={propsClone['aria-label'] ?? label}
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
        />
        <input
          {...props}
          className={InputClassName}
          hidden={type === 'single'}
          type="range"
          id={`${propsClone.id}-1`}
          disabled={disabled}
          onChange={(event) => handleRangeChange([currentVal[0], Number(event.target.value)])}
          value={currentVal[1]}
          ref={innerRefs2}
          aria-label={propsClone['aria-label'] ?? label}
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
        />
      </div>
    </div>
  );
};

export const InputRange = forwardRef<HTMLDivElement | HTMLInputElement | (HTMLInputElement | null)[], RangeProps>(InnerRange);
