import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  ReactNode,
  Ref,
  useId,
  useImperativeHandle,
  useRef
} from 'react';

import classNames from 'classnames';
import { BooleanProp } from '../../../models';
import { Button } from '../../atoms/button';
import { Tooltip, TooltipAlignment, TooltipPosition, TooltipVariant } from '../../atoms/tooltip';

export interface BackToTopProps extends ComponentPropsWithRef<'button'> {
  /**
   * The variant of the Tooltip.
   */
  tooltipVariant?: TooltipVariant;
  /**
   * Position of the tooltip to shows when the user focus or hovers the component.
   */
  tooltipPosition?: TooltipPosition;
  /**
   * Alignment of the tooltip shown whenever the user focus or hovers the component.
   */
  tooltipAlignment?: TooltipAlignment;
  /**
   * If the Tooltip has a title.
   */
  hasTooltipTitle?: BooleanProp;
  /**
   * The title of the Tooltip.
   */
  tooltipTitle?: ReactNode;

  /**
   * Tooltip description to show when user focus or hovers the component.
   */
  tooltipDescription: ReactNode;
}

export const InnerBackToTop: ForwardRefRenderFunction<HTMLButtonElement, BackToTopProps> = (
  {
    tooltipVariant = 'light',
    tooltipAlignment = 'start',
    tooltipPosition = 'right',
    hasTooltipTitle,
    tooltipTitle,
    tooltipDescription,
    ...props
  },
  ref: Ref<HTMLButtonElement | null>
) => {
  const backToTopRef = useRef<HTMLButtonElement>(null);
  const backToTopClassName = classNames('!rounded-full', props.className);
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;
  useImperativeHandle(ref, () => backToTopRef.current);

  const goToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  return (
    <Tooltip
      id={`tooltip-${propsClone.id}`}
      variant={tooltipVariant}
      alignment={tooltipAlignment}
      position={tooltipPosition}
      hasTitle={hasTooltipTitle}
      title={tooltipTitle}
      description={tooltipDescription}
    >
      <Button
        hasIcon={true}
        {...propsClone}
        ref={backToTopRef}
        onClick={goToTop}
        leadingIcon="agora-line-chevron-up"
        leadingIconHover="agora-line-chevron-up"
        className={backToTopClassName}
        iconOnly
      />
    </Tooltip>
  );
};

export const BackToTop = forwardRef<HTMLButtonElement, BackToTopProps>(InnerBackToTop);
