import classNames from 'classnames';
import React, { FC, useId } from 'react';

import { BreadcrumbProps } from '../breadcrumb';
import './breadcrumb-desktop.scss';
import { Anchor } from '../../anchor';
import { Icon } from '../../icon';

export const BreadcrumbDesktop: FC<BreadcrumbProps> = ({ items = [], darkMode, ...props }) => {
  const generatedId = useId();

  const ariaLabel = props['aria-label'] ?? 'Breadcrumb';

  const BreadcrumbClassName = classNames(props.className, 'agora-breadcrumb-desktop');
  const iconClassName = classNames('mx-[4px]', {
    'fill-white': darkMode,
    'fill-[var(--color-neutral-900)]': !darkMode
  });

  return (
    <nav className={BreadcrumbClassName} aria-label={ariaLabel}>
      <ol className="flex gap-y-8 flex-wrap items-center">
        {items.map((link, index) => {
          const key = `${generatedId}-${index}`;

          return (
            <li key={key} className="flex flex-row items-center">
              {index !== 0 && <Icon name="agora-line-chevron-right" dimensions="m" aria-hidden="true" className={iconClassName} />}
              {index === items.length - 1 || !link.url ? (
                <span aria-current="page">{link.label}</span>
              ) : (
                <Anchor darkMode={darkMode} variant="neutral" appearance="text" href={link.url} className="text-s-regular">
                  {link.label}
                </Anchor>
              )}
            </li>
          );
        })}
      </ol>
    </nav>
  );
};
