import classNames from 'classnames';
import React, { Children, ComponentPropsWithoutRef, FC, ReactElement, useEffect, useLayoutEffect, useState } from 'react';
import { BooleanProp } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { TimelineEvent, TimelineEventProps } from '../shared/timeline-event/timeline-event';
import { Button } from '../../../atoms/button/button';
import './timeline-horizontal.scss';
import { TimelineVertical } from '../timeline-vertical';
export interface TimelineHorizontalProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Label for the next button
   **/
  nextLabel: string;
  /**
   * Label for the previous button
   **/
  previousLabel: string;
  /**
   * Determines if the timeline is interactive or not
   **/
  interactive?: BooleanProp;
  /**
   * Timeline events
   **/
  children: ReactElement<TimelineEventProps> | Array<ReactElement<TimelineEventProps>>;
}

export const TimelineHorizontal: FC<TimelineHorizontalProps> = ({ children, nextLabel, previousLabel, interactive = true, ...props }) => {
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const isInteractive = stringToBoolean(interactive);
  const maxVisibleEvents = 6;
  const [events, setEvents] = useState<Array<ReactElement<TimelineEventProps>>>([]);
  const [activeEvent, setActiveEvent] = useState<number>(0);

  const [startIndex, setStartIndex] = useState<number>(0);
  const [endIndex, setEndIndex] = useState<number>(maxVisibleEvents);

  useEffect(() => {
    const eventChildren = Children.toArray(children) as Array<ReactElement<TimelineEventProps>>;
    setEvents(eventChildren);
    const activeIndex = eventChildren.findIndex((child) => child.props.active);
    setActiveEvent(activeIndex !== -1 ? activeIndex : 0);
  }, [children]);

  const handleClickEvent = (index: number) => {
    setActiveEvent(index);
  };

  const getTimelineStepClasses = (index: number, currentActiveEvent: number) => {
    return index < currentActiveEvent ? 'completed' : '';
  };

  const handleNextButtonClick = () => {
    const newEvent = Math.min(activeEvent + 1, Children.count(children) - 1);
    setActiveEvent(newEvent);

    if (newEvent >= endIndex) {
      setStartIndex(startIndex + 1);
      setEndIndex(endIndex + 1);
    }
  };

  const handlePreviousButtonClick = () => {
    const newEvent = Math.max(activeEvent - 1, 0);
    setActiveEvent(newEvent);

    if (newEvent < startIndex) {
      setStartIndex(startIndex - 1);
      setEndIndex(endIndex - 1);
    }
  };

  const selectedContentEvent = Children.map(children, (child, index) => {
    return index === activeEvent ? child.props.children : null;
  });

  const visibleEvents = events.slice(startIndex, endIndex);

  useEffect(() => {
    if (activeEvent > endIndex - 1 || activeEvent < startIndex) {
      const newStartIndex = Math.max(0, activeEvent - Math.floor(maxVisibleEvents / 2));
      const newEndIndex = newStartIndex + maxVisibleEvents;

      if (newEndIndex > events.length) {
        setStartIndex(events.length - maxVisibleEvents);
        setEndIndex(events.length);
      } else {
        setStartIndex(newStartIndex);
        setEndIndex(newEndIndex);
      }
    }
  }, [activeEvent, events, maxVisibleEvents, startIndex, endIndex]);

  useLayoutEffect(() => {
    function updateSize() {
      setIsMobile(window.innerWidth <= 768);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  const renderTimelineActions = () => {
    const isPreviousButtonDisabled = activeEvent === 0;
    const isLastButtonDisabled = activeEvent === Children.count(children) - 1;

    return (
      <div className="timeline-actions">
        <Button
          appearance="link"
          variant="primary"
          hasIcon
          leadingIcon="agora-line-arrow-left-circle"
          leadingIconHover="agora-line-arrow-left-circle"
          className="button-previous"
          onClick={handlePreviousButtonClick}
          disabled={isPreviousButtonDisabled}
        >
          {previousLabel}
        </Button>
        <Button
          appearance="link"
          variant="primary"
          hasIcon
          trailingIcon="agora-line-arrow-right-circle"
          trailingIconHover="agora-line-arrow-right-circle"
          className="button-next"
          onClick={handleNextButtonClick}
          disabled={isLastButtonDisabled}
        >
          {nextLabel}
        </Button>
      </div>
    );
  };

  const timelineClasses = classNames(
    'timeline-horizontal',
    {
      'event-static': !isInteractive
    },
    props.className
  );

  return (
    <div>
      {isMobile ? (
        <TimelineVertical {...props} hideLabels={false}>
          {children}
        </TimelineVertical>
      ) : (
        <div {...props} className={timelineClasses}>
          <div className="timeline-steps">
            {visibleEvents.map((event, index) => {
              const isFirstVisibleEvent = index + startIndex === 0;
              const isLastVisibleEvent = index + startIndex === endIndex - 1;

              const hasMoreEventsLeft = startIndex > 0;
              const hasMoreEventsRight = endIndex < events.length;

              const eventClassNames = classNames(getTimelineStepClasses(index + startIndex, activeEvent), {
                'first-event': isFirstVisibleEvent,
                'last-event': isLastVisibleEvent && index + startIndex === events.length - 1,
                'has-more-events-left': hasMoreEventsLeft,
                'has-more-events-right': hasMoreEventsRight
              });

              return (
                <TimelineEvent
                  key={event.props.id ?? index}
                  {...event.props}
                  active={index + startIndex === activeEvent}
                  onClick={() => handleClickEvent(index + startIndex)}
                  interactive={isInteractive}
                  className={eventClassNames}
                >
                  {event.props.children}
                </TimelineEvent>
              );
            })}
          </div>
          {isInteractive && events.length > maxVisibleEvents && renderTimelineActions()}
          <div className="timeline-content">{selectedContentEvent}</div>
        </div>
      )}
    </div>
  );
};
