import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode, useEffect, useRef, useState } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Button } from '../../atoms/button';
import { usePopupContext } from './popup-context';
import './popup-dialog.scss';

export type PopupDimensions = 's' | 'm' | 'l';

export interface PopupDialogProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * Available popup sizes. Can be small (S), medium (M), and large (L) dimensions.
   */
  dimensions?: PopupDimensions;
  /**
   * Title of the popup dialog.
   */
  title: ReactNode;
  /**
   * Close button alternative text.
   */
  closeAriaLabel: string;
  /**
   * Children content.
   */
  children: ReactNode;
  /**
   * Render dialog with dark mode styles.
   */
  darkMode?: BooleanProp;
}

const PopupDialog: FC<PopupDialogProps> = ({ darkMode = false, dimensions = 'm', closeAriaLabel, title, children }) => {
  const popupHeaderRef = useRef<HTMLDivElement>(null);

  const [maxHeight, setMaxHeight] = useState('');

  const { hide } = usePopupContext();

  const dialogClassNames = classNames('agora-popup-dialog max-h-full rounded-16 p-32', `agora-popup-dialog-${dimensions}`, {
    'dark-mode': stringToBoolean(darkMode)
  });

  const titleClassNames = classNames('title mt-8 w-full line-clamp-6 text-xl-bold');

  useEffect(() => {
    const elem = popupHeaderRef.current;

    if (elem) {
      const { height } = (elem as HTMLElement).getBoundingClientRect();
      setMaxHeight(`calc(100% - ${height + 16}px)`); // height + margin-bottom
    }
  }, [title]);

  return (
    <div className="absolute z-dialog  max-h-full flex justify-center my-64 md:my-128 mx-32 md:mx-auto">
      <div className={dialogClassNames}>
        <div ref={popupHeaderRef} className="agora-popup-dialog-header flex flex-row items-start gap-24 mb-16">
          <div className={titleClassNames}>{title}</div>
          <Button
            onClick={hide}
            appearance="link"
            variant="neutral"
            iconOnly
            hasIcon
            trailingIcon="agora-line-x"
            trailingIconHover="agora-solid-x"
            aria-label={closeAriaLabel}
          ></Button>
        </div>
        <div style={{ maxHeight: maxHeight ?? 'none' }} className={`agora-popup-dialog-body overflow-y-auto`}>
          {children}
        </div>
      </div>
    </div>
  );
};

export { PopupDialog };
