import { Children, ReactElement, RefObject, useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { DropdownOptionProp } from '../dropdown-option';
import { DropdownSectionProps } from '../dropdown-section';
import { DropdownType } from '../dropdown-type';
import { InternalDropdownRef } from './internal-dropdown';
import { InternalDropdownContextProps } from './internal-dropdown-context';
import { InternalDropdownOptionProp } from './internal-dropdown-option';
import { InternalDropdownSectionProps } from './internal-dropdown-section';

export const useDropdownState = (
  internalDropdownRef: RefObject<InternalDropdownRef>,
  type: DropdownType,
  onChange?: (options: DropdownOptionProp[]) => void,
  onSearchChange?: (term: string, options: DropdownOptionProp[]) => void,
  onShow?: () => void,
  onHide?: () => void,
  children?: ReactElement<DropdownSectionProps> | ReactElement<DropdownSectionProps>[]
) => {
  //  #region "OPTIONS"
  const [sections, setSections] = useState<Array<InternalDropdownSectionProps>>([]);
  const [options, setOptions] = useState<Array<InternalDropdownOptionProp>>([]);

  useEffect(() => {
    const parsedSectionProps: Array<InternalDropdownSectionProps> = [];
    const parsedOptionProps: Array<InternalDropdownOptionProp> = [];

    if (children) {
      if (!Array.isArray(children)) {
        parsedSectionProps.push({
          ...children.props,
          type
        } as InternalDropdownSectionProps);
      } else {
        Children.forEach(children, (c) => {
          parsedSectionProps.push({
            ...c.props,
            type
          } as InternalDropdownSectionProps);
        });
      }
    }

    parsedSectionProps.forEach((sec) => {
      Children.forEach(sec.children, (opt) => {
        parsedOptionProps.push({
          ...opt.props,
          name: sec.value
        });
      });
    });

    setSections(parsedSectionProps);
    setOptions(parsedOptionProps);
  }, [children, type, onChange]);

  //  #endregion "OPTIONS"

  //  #region "FILTER"
  const [activeFilter, setActiveFilter] = useState('');

  const applyFilter = (newFilter: string) => {
    setActiveFilter((lastFilter) => {
      if (lastFilter === newFilter) {
        return lastFilter;
      }

      setOptions((lastOptions) => {
        const newOptions = lastOptions.map((opt) => {
          if (newFilter === '') {
            //  CLEAR FILTER
            return { ...opt, filtered: false };
          }

          const val = opt.value.toLowerCase();
          const childrenVal = opt.children.toLowerCase();

          const notFound = !val.includes(newFilter) && !childrenVal.includes(newFilter);

          return {
            ...opt,
            filtered: notFound
          };
        });

        onSearchChange?.(
          newFilter,
          newOptions.filter((opt) => !opt.filtered)
        );

        return newOptions;
      });

      return newFilter;
    });
  };

  const resetFilter = useCallback(() => {
    if (internalDropdownRef.current) {
      internalDropdownRef.current.resetFilter();
    }
  }, [internalDropdownRef]);

  useEffect(() => {
    resetFilter();
  }, [children, resetFilter]);

  //  #endregion "FILTER"

  //  #region "NAVIGATION"

  const focusElem = (elem?: HTMLElement, fromVisibilityHidden?: boolean) => {
    if (elem) {
      elem.focus();

      if (fromVisibilityHidden) {
        elem.scrollIntoView(true);
      } else {
        elem.scrollIntoView({ behavior: 'instant', block: 'nearest', inline: 'start' });
      }
    }
  };

  const first = useCallback(
    (fromVisibilityHidden?: boolean) => {
      if (internalDropdownRef.current) {
        const inputFilter = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('.input-filter') ?? []);
        const opts = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('[role="option"]:not(.filtered)') ?? []);
        const domOptions = [...inputFilter, ...opts];

        const idx = Array.from(domOptions).findIndex((opt) => {
          return (opt as HTMLElement).tabIndex === 0;
        });

        if (idx >= 0) {
          focusElem(domOptions[idx] as HTMLElement, fromVisibilityHidden);
        }
      }
    },
    [internalDropdownRef]
  );

  const last = useCallback(() => {
    if (internalDropdownRef.current) {
      const inputFilter = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('.input-filter') ?? []);
      const opts = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('[role="option"]:not(.filtered)') ?? []);
      const domOptions = [...inputFilter, ...opts];

      if (domOptions.length > 0) {
        const idx = Array.from(domOptions).findLastIndex((opt) => {
          return (opt as HTMLElement).tabIndex === 0;
        });

        focusElem(domOptions[idx] as HTMLElement);
      }
    }
  }, [internalDropdownRef]);

  const next = useCallback((): void => {
    if (internalDropdownRef.current) {
      const current = document.activeElement;
      const inputFilter = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('.input-filter') ?? []);
      const opts = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('[role="option"]:not(.filtered)') ?? []);
      const domOptions = [...inputFilter, ...opts];

      if (domOptions.length > 0) {
        const currentIndex = Array.from(domOptions).findIndex((opt) => {
          return opt.isEqualNode(current);
        });

        const idx = Array.from(domOptions).findIndex((opt, i) => {
          return i > currentIndex && (opt as HTMLElement).tabIndex === 0;
        });

        if (idx >= 0) {
          focusElem(domOptions[idx] as HTMLElement);
        } else {
          first();
        }
      }
    }
  }, [internalDropdownRef, first]);

  const prev = useCallback((): void => {
    if (internalDropdownRef.current) {
      const current = document.activeElement;
      const inputFilter = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('.input-filter') ?? []);
      const opts = Array.from(internalDropdownRef.current.listboxElem?.querySelectorAll('[role="option"]:not(.filtered)') ?? []);
      const domOptions = [...inputFilter, ...opts];

      if (domOptions.length > 0) {
        const currentIndex = Array.from(domOptions).findIndex((opt) => {
          return opt.isEqualNode(current);
        });

        const idx = Array.from(domOptions).findLastIndex((opt, i) => {
          return i < currentIndex && (opt as HTMLElement).tabIndex === 0;
        });

        if (idx >= 0) {
          focusElem(domOptions[idx] as HTMLElement);
        } else {
          last();
        }
      }
    }
  }, [internalDropdownRef, last]);

  //  #endregion "NAVIGATION"

  //  #region "VISIBILITY"
  const [visibility, setVisibility] = useState(false);

  const show = useCallback(() => {
    setVisibility(true);
  }, []);

  const hide = useCallback(() => {
    setVisibility(false);
  }, []);

  //  Need this ref because first render will trigger the effect... we only want this event whenever show or hide is called
  const firstEffect = useRef(true);
  useEffect(() => {
    if (firstEffect.current) {
      firstEffect.current = false;
      return;
    }

    if (visibility) {
      onShow?.();
    } else {
      onHide?.();
    }
  }, [visibility, onShow, onHide]);

  //  #endregion "VISIBILITY"

  //  #region "SELECTION"
  const triggerOnChange = useCallback(
    (newOptions: InternalDropdownOptionProp[]) => {
      const selectedOptions = newOptions
        .filter((o) => o.selected)
        .map((o) => {
          return {
            selected: o.selected,
            value: o.value,
            className: o.className,
            disabled: o.disabled,
            hasIcon: o.hasIcon,
            icon: o.icon,
            iconPosition: o.iconPosition,
            children: o.children
          } as DropdownOptionProp;
        });

      onChange?.(selectedOptions);

      if (type === 'text' && selectedOptions.length > 0) {
        hide();
      }
    },
    [onChange, hide, type]
  );

  const isMultiSelection = useMemo(() => {
    return type === 'checkbox';
  }, [type]);

  const selectOption = useCallback(
    (option: InternalDropdownOptionProp): void => {
      if (option.disabled || option.filtered) {
        return;
      }

      setOptions((lastOptions) => {
        const newOptions = lastOptions.map((opt) => {
          const isProvidedOption = option.name === opt.name && option.value === opt.value;

          if (isMultiSelection) {
            return {
              ...opt,
              selected: isProvidedOption ? !opt.selected : opt.selected
            };
          }

          return {
            ...opt,
            selected: isProvidedOption ? !opt.selected : false
          };
        });

        triggerOnChange(newOptions);

        return newOptions;
      });
    },
    [setOptions, isMultiSelection, triggerOnChange]
  );

  const isAllSectionOptionsSelected = useCallback(
    (sectionName: string): boolean => {
      const availableOptions = options.filter((opt) => opt.name === sectionName && !opt.filtered && !opt.disabled);
      const hasOptionsUnselected = availableOptions.some((opt) => !opt.selected);
      return availableOptions.length > 0 && !hasOptionsUnselected;
    },
    [options]
  );

  const selectSectionOptions = useCallback(
    (sectionName: string): void => {
      setOptions((lastOptions) => {
        const allOptionsSelected = isAllSectionOptionsSelected(sectionName);

        const newOptions = lastOptions.map((opt) => {
          const isInSectionProvided = sectionName === opt.name;
          return {
            ...opt,
            selected: !opt.filtered && !opt.disabled && isInSectionProvided ? !allOptionsSelected : opt.selected
          };
        });

        triggerOnChange(newOptions);

        return newOptions;
      });
    },
    [setOptions, isAllSectionOptionsSelected, triggerOnChange]
  );

  const hasSectionOptionsToInteract = useCallback(
    (sectionName: string): boolean => {
      const availableOptions = options.filter((opt) => opt.name === sectionName && !opt.filtered && !opt.disabled);
      return availableOptions.length > 0;
    },
    [options]
  );

  const isAllSelected = useMemo(() => {
    const availableOptions = options.filter((opt) => !opt.filtered && !opt.disabled);
    const hasOptionsUnselected = availableOptions.some((opt) => !opt.selected);
    return availableOptions.length > 0 && !hasOptionsUnselected;
  }, [options]);

  const isAllDisabled = useMemo(() => {
    const availableOptions = options.filter((opt) => !opt.filtered);
    const hasOptionsNotDisabled = availableOptions.some((opt) => !opt.disabled);
    return availableOptions.length > 0 && !hasOptionsNotDisabled;
  }, [options]);

  const selectAll = useCallback((): void => {
    setOptions((lastOptions) => {
      const newOptions = lastOptions.map((opt) => {
        return {
          ...opt,
          selected: !opt.filtered && !opt.disabled ? !isAllSelected : opt.selected
        };
      });

      triggerOnChange(newOptions);

      return newOptions;
    });
  }, [setOptions, isAllSelected, triggerOnChange]);

  //  #endregion "SELECTION"

  return {
    type,
    sections,
    options,

    visibility,
    show,
    hide,

    first,
    prev,
    next,
    last,

    selectOption,
    hasSectionOptionsToInteract,
    selectSectionOptions,
    isAllSectionOptionsSelected,

    isAllDisabled,
    isAllSelected,
    selectAll,

    applyFilter,
    activeFilter,
    resetFilter
  } as InternalDropdownContextProps;
};
