/* eslint-disable no-shadow */
import classNames from 'classnames';
import React, {
  Children,
  cloneElement,
  ComponentPropsWithRef,
  createRef,
  forwardRef,
  ForwardRefRenderFunction,
  ReactElement,
  RefObject,
  useCallback,
  useEffect,
  useId
} from 'react';
import { Accordion, AccordionProps } from '../../atoms/accordion';
import { AccordionRef } from '../../atoms/accordion/accordion';

export type AccordionGroupType = 'single' | 'multiple';

export interface AccordionGroupProps extends ComponentPropsWithRef<'div'> {
  /**
   *  List of accordion props to generate as checkboxes.
   */
  items?: AccordionProps[];
  /**
   *  List of accordion components to use as children of the group.
   */
  children?: ReactElement<AccordionProps> | Array<ReactElement<AccordionProps>>;
  /**
   *  Option to open multiple accordions or just one at a time
   */
  type: AccordionGroupType;
}

export const InnerAccordionGroup: ForwardRefRenderFunction<HTMLDivElement, AccordionGroupProps> = (
  { items = [], className = '', children, type = 'single', ...props },
  ref
) => {
  /**
   * Building Accordion class name dynamically according parameters.
   */
  const AccordionGroupClassName = classNames('agora-accordion-group', 'mt-0 p-0 w-full', className);

  const generatedAccordionGroupId = useId();

  let childrenAccordions: Array<ReactElement<AccordionProps>>;
  if (children) {
    if (!Array.isArray(children)) {
      childrenAccordions = [children] as Array<ReactElement<AccordionProps>>;
    } else {
      childrenAccordions = children;
    }
  } else {
    childrenAccordions = [];
  }

  const itemsAccordions = items.map((item, index) => {
    const id = `accordion-group-${generatedAccordionGroupId}-accordion-${index}`;
    return (
      <Accordion {...item} key={id}>
        {item.children}
      </Accordion>
    );
  });

  const refsMap = new Map<string, RefObject<AccordionRef>>();

  const updateAllAccordions = useCallback(
    (prop: AccordionProps) => {
      if (type === 'multiple') {
        return;
      }

      refsMap.forEach((v, k) => {
        if (prop.id !== k) {
          v.current?.collapse();
        }
      });
    },
    [type]
  );

  const parsedAccordions = Children.map([...childrenAccordions, ...itemsAccordions], (child, index) => {
    const innerRef = createRef<AccordionRef>();
    const id = `accordion-group-${generatedAccordionGroupId}-accordion-${index}`;

    refsMap.set(id, innerRef);

    return cloneElement<AccordionProps>(child, {
      ...child.props,
      ref: innerRef,
      id,
      onExpanded: updateAllAccordions
    });
  });

  useEffect(() => {
    if (type !== 'multiple') {
      let firstExpandedKey = '';

      refsMap.forEach((r, k) => {
        if (r.current?.isExpanded && !firstExpandedKey) {
          firstExpandedKey = k;
        }
      });

      refsMap.forEach((r, k) => {
        if (k !== firstExpandedKey) {
          r.current?.collapse();
        }
      });
    }
  }, [type]);

  return (
    <div className={AccordionGroupClassName} {...props} ref={ref}>
      {parsedAccordions}
    </div>
  );
};

export const AccordionGroup = forwardRef<HTMLDivElement, AccordionGroupProps>(InnerAccordionGroup);
