import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import './pill.scss';

/**
 * The available variants of the pill. This variants will define the background and border color of the pill.
 */
export type PillVariant = 'primary' | 'secondary' | 'secondary-light' | 'neutral' | 'informative' | 'success' | 'warning' | 'danger';

/**
 * The available pill appearances. Defines the pill shape.
 */
export type PillAppearance = 'solid' | 'outline';

/**
 * The available pill size. Defines the pill size.
 */
export type PillSize = 'default' | 'large';

export interface PillProps extends ComponentPropsWithRef<'div'> {
  /**
   * The pill appearance. Define the pill Shape.
   */
  appearance?: PillAppearance;
  /**
   * The variant of the pill. This variants will define the background and border color of the given Pill
   */
  variant?: PillVariant;
  /**
   * Children content.
   */
  children?: ReactNode;
  /**
   * Pill or Circular pill style
   */
  circular?: BooleanProp;
  /**
   *  Size of pill
   */
  size?: PillSize;
}

export const InnerPill: ForwardRefRenderFunction<HTMLDivElement, PillProps> = (
  { appearance = 'solid', variant = 'primary', children, className = '', circular = false, size = 'default', ...props },
  ref
) => {
  /**
   * Building pill class name dynamically according parameters.
   */
  const pillClassName = classNames(
    {
      'inline-flex items-center justify-center': !props.hidden,
      'pill-circular': stringToBoolean(circular),
      'pill-large': size === 'large'
    },

    `pill pill-${appearance}-${variant}`,
    className
  );

  return (
    <div className={pillClassName} {...props} ref={ref}>
      <div className="children-wrapper">{children}</div>
    </div>
  );
};

export const Pill = forwardRef<HTMLDivElement, PillProps>(InnerPill);
