import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, useId } from 'react';
import './progress-bar.scss';

export interface ProgressBarProps extends ComponentPropsWithRef<'div'> {
  /**
   * The current value of the progress.
   */
  value?: number;
  /**
   * The maximum value of the progress.
   */
  max?: number;
  /**
   * The label text to display.
   */
  label?: string;
  /**
   * Whether to hide the label or not.
   */
  hideLabel?: boolean;
  /**
   * Whether to hide the percentage value or not.
   */
  hidePercentageValue?: boolean;
  /**
   * Whether the progress is current/active or not.
   */
  current?: boolean;
  /**
   * Whether the progress has error or not.
   */
  hasError?: boolean;
}

export const InnerProgressBar: ForwardRefRenderFunction<HTMLDivElement, ProgressBarProps> = (
  { value = 0, max = 0, className, label, hideLabel, hidePercentageValue, current, hasError, ...props },
  ref
) => {
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;

  const validatedValue = Math.max(0, Math.min(value, max));
  const progressPercentage = max !== 0 ? ((validatedValue / max) * 100).toFixed(0) : 0;

  const classes = classNames('agora-progress-bar', className, {
    'progress-error': hasError,
    'progress-complete': validatedValue > 0 && validatedValue === max,
    'progress-empty': validatedValue === 0,
    'progress-progress': current && validatedValue < max,
    'progress-hold': !current && validatedValue < max
  });

  const percentageClasses = classNames('progress-bar-percentage', {
    'sr-only': hidePercentageValue,
    'not-sr-only': !hidePercentageValue
  });

  return (
    <div className={classes} {...props} ref={ref}>
      {!hideLabel && <label htmlFor={propsClone.id}>{label}</label>}
      <progress
        id={propsClone.id}
        className="progress-bar"
        value={validatedValue}
        max={max}
        aria-valuetext={`${validatedValue}/${max}`}
        aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
      />
      <p className={percentageClasses} role="status">
        {progressPercentage}%
      </p>
    </div>
  );
};

export const ProgressBar = forwardRef<HTMLDivElement, ProgressBarProps>(InnerProgressBar);
