import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode, useId } from 'react';
import { HeadingLevel } from '../../../../models';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import './card-hero-helper.scss';

export interface HelpTopic {
  description: ReactNode;
  anchor: AnchorProps;
}

export interface CardHeroHelperProps extends Omit<ComponentPropsWithoutRef<'div'>, 'title'> {
  /**
   * header title.
   */
  headingLevel?: HeadingLevel;
  /**
   * The title of the card
   */
  title?: ReactNode;
  /**
   * The subtitle of the card
   */
  subtitle?: ReactNode;
  /**
   * The list of help topics of the card
   */
  helpTopics: HelpTopic[];
}

export const CardHeroHelper: FC<CardHeroHelperProps> = ({
  subtitle,
  headingLevel: Heading = 'h2',
  title,
  helpTopics = [],
  className,
  ...props
}) => {
  const generatedId = useId();

  const CardHeroHelperClasses = classNames('card-hero-helper', className);

  const headingPlacement = {
    title: !!title,
    subtitle: !title && !!subtitle
  };

  const buildText = (text: ReactNode, isHeading: boolean): ReactNode => {
    return isHeading ? <Heading>{text}</Heading> : text;
  };

  return (
    <div className={CardHeroHelperClasses} {...props}>
      {(subtitle || title) && (
        <div className="flex gap-8 flex-col">
          {subtitle && <div className="subtitle-container">{buildText(subtitle, headingPlacement.subtitle)}</div>}
          {title && <div className="title-container">{buildText(title, headingPlacement.title)}</div>}
        </div>
      )}

      {helpTopics.length > 0 && (
        <ul className="flex flex-col justify-start items-start gap-32">
          {helpTopics?.map((block, index) => {
            const key = `card-hero-helper-${generatedId}-description-block-${index}`;
            return (
              <li key={key} className="flex flex-col gap-16 justify-start items-start">
                <div>{block?.description}</div>
                <Anchor {...block?.anchor} />
              </li>
            );
          })}
        </ul>
      )}
    </div>
  );
};
