import classNames from 'classnames';
import React, {
  Children,
  cloneElement,
  FC,
  FocusEvent,
  FocusEventHandler,
  HtmlHTMLAttributes,
  isValidElement,
  MouseEvent,
  MouseEventHandler,
  ReactNode,
  RefObject,
  useCallback,
  useEffect,
  useId,
  useMemo,
  useRef,
  useState
} from 'react';
import { createPortal } from 'react-dom';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils/string-to-boolean';
import './tooltip.scss';

const TOOLTIP_CHANGE_EVENT_NAME = 'agora-ds-tooltip-change';

interface EnrichedChildren {
  ref: RefObject<any>;
  onMouseEnter: MouseEventHandler;
  onMouseLeave: MouseEventHandler;
  onFocus: FocusEventHandler;
  onBlur: FocusEventHandler;
  children?: ReactNode;
}

/**
 * The available variants of the Tooltip. This variants will define the background of the Tooltip.
 */
export type TooltipVariant = 'light' | 'dark' | 'info';
/**
 * The available positions of the Tooltip. This positions will define the position of the Tooltip.
 */
export type TooltipPosition = 'top' | 'bottom' | 'left' | 'right';
/**
 * The available alignments of the Tooltip. This alignments will define the alignment of the Tooltip.
 */
export type TooltipAlignment = 'start' | 'center' | 'end';

export interface TooltipProps extends Omit<HtmlHTMLAttributes<HTMLDivElement>, 'title'> {
  /**
   * The variant of the Tooltip.
   */
  variant?: TooltipVariant;
  /**
   * The position of the Tooltip.
   */
  position?: TooltipPosition;
  /**
   * The alignment of the Tooltip.
   */
  alignment?: TooltipAlignment;
  /**
   * The title of the Tooltip.
   */
  title?: ReactNode;
  /**
   * If the Tooltip has a title.
   */
  hasTitle?: BooleanProp;
  /**
   * The description of the Tooltip.
   */
  description: ReactNode;
}

export const Tooltip: FC<TooltipProps> = ({
  hasTitle,
  variant = 'dark',
  position = 'top',
  alignment = 'center',
  title,
  description,
  children,
  ...props
}) => {
  const generatedId = useId();

  const ref = useRef(null);

  const [top, setTop] = useState(0);
  const [left, setLeft] = useState(0);
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);

  const setDimensions = useCallback(() => {
    if (ref.current) {
      const rect = (ref.current as HTMLElement)?.getBoundingClientRect();

      if (rect) {
        setTop(rect.top + window.scrollY);
        setLeft(rect.left + window.scrollX);
        setWidth(rect.width);
        setHeight(rect.height);
      }
    }
  }, []);

  const propsClone = useMemo(() => ({ ...props }), [props]);

  propsClone.id = propsClone.id ?? generatedId;

  const TooltipContainerClassName = classNames('tooltip-container absolute z-tooltip', props.className);

  const TooltipClassName = classNames('tooltip', {
    [`tooltip--${variant}`]: variant,
    [`tooltip--${position}--${alignment}`]: position && alignment
  });

  const [visibility, setVisibility] = useState(false);

  const showTooltip = () => {
    setVisibility(true);
    const event = new CustomEvent<string | undefined>(TOOLTIP_CHANGE_EVENT_NAME, { detail: propsClone.id });
    document.dispatchEvent(event);
  };

  const hideTooltip = () => {
    setVisibility(false);
  };

  const handleEscapeKey = (event: KeyboardEvent) => {
    if (event.key === 'Escape') {
      setVisibility(false);
    }
  };

  useEffect(() => {
    document.addEventListener('keydown', handleEscapeKey);

    const handleTooltipChange = (evt: any) => {
      const customEventArgs = evt as CustomEvent<string | undefined>;
      if (customEventArgs.detail !== propsClone.id) {
        setVisibility(false);
      }
    };

    document.addEventListener(TOOLTIP_CHANGE_EVENT_NAME, handleTooltipChange);

    return () => {
      document.removeEventListener('keydown', handleEscapeKey);
      document.removeEventListener(TOOLTIP_CHANGE_EVENT_NAME, handleTooltipChange);
    };
  }, [propsClone.id]);

  const parsedChildren = Children.map(children, (child) => {
    if (!isValidElement<EnrichedChildren>(child)) {
      return child;
    }

    const onMouseEnter = (evt: MouseEvent<any>) => {
      setDimensions();

      showTooltip();

      child.props.onMouseEnter?.(evt);
    };

    const onMouseLeave = (evt: MouseEvent<any>) => {
      hideTooltip();

      child.props.onMouseLeave?.(evt);
    };

    const onFocus = (evt: FocusEvent<any>) => {
      setDimensions();

      showTooltip();

      child.props.onFocus?.(evt);
    };

    const onBlur = (evt: FocusEvent<any>) => {
      hideTooltip();

      child.props.onBlur?.(evt);
    };

    return cloneElement<EnrichedChildren>(child, { ref, onMouseEnter, onMouseLeave, onFocus, onBlur });
  });

  useEffect(() => {
    const cb = () => {
      setDimensions();
    };

    document.addEventListener('scroll', cb);

    return () => {
      document.removeEventListener('scroll', cb);
    };
  }, [setDimensions]);

  const topPositioning = useMemo(() => {
    if (alignment === 'start') {
      return {
        top: '-10px',
        left: '0px',
        transform: 'translateY(-100%)'
      };
    }

    if (alignment === 'center') {
      return {
        top: '-10px',
        left: `${width / 2}px`,
        transform: 'translateY(-100%) translateX(-50%)'
      };
    }

    if (alignment === 'end') {
      return {
        top: '-10px',
        left: `${width}px`,
        transform: 'translateY(-100%) translateX(-100%)'
      };
    }
  }, [alignment, width]);

  const bottomPositioning = useMemo(() => {
    if (alignment === 'start') {
      return {
        top: `${height + 10}px`,
        left: '0px',
        transform: 'none'
      };
    }

    if (alignment === 'center') {
      return {
        top: `${height + 10}px`,
        left: `${width / 2}px`,
        transform: 'translateX(-50%)'
      };
    }

    if (alignment === 'end') {
      return {
        top: `${height + 10}px`,
        left: `${width}px`,
        transform: 'translateX(-100%)'
      };
    }
  }, [alignment, height, width]);

  const leftPositioning = useMemo(() => {
    if (alignment === 'start') {
      return {
        top: '0px',
        left: '-10px',
        transform: 'translateX(-100%)'
      };
    }

    if (alignment === 'center') {
      return {
        top: `${height / 2}px`,
        left: '-10px',
        transform: 'translateX(-100%) translateY(-50%)'
      };
    }

    if (alignment === 'end') {
      return {
        top: `${height}px`,
        left: '-10px',
        transform: 'translateX(-100%) translateY(-100%)'
      };
    }
  }, [alignment, height]);

  const rightPositioning = useMemo(() => {
    if (alignment === 'start') {
      return {
        top: '0px',
        left: `${width + 10}px`,
        transform: 'none'
      };
    }

    if (alignment === 'center') {
      return {
        top: `${height / 2}px`,
        left: `${width + 10}px`,
        transform: 'translateY(-50%)'
      };
    }

    if (alignment === 'end') {
      return {
        top: `${height}px`,
        left: `${width + 10}px`,
        transform: 'translateY(-100%)'
      };
    }
  }, [alignment, height, width]);

  const tooltipPosition = useMemo(() => {
    switch (position) {
      case 'top':
        return topPositioning;
      case 'bottom':
        return bottomPositioning;
      case 'left':
        return leftPositioning;
      case 'right':
        return rightPositioning;
    }
  }, [position, topPositioning, bottomPositioning, leftPositioning, rightPositioning]);

  const TooltipContent = useMemo(() => {
    return (
      <div
        className={TooltipContainerClassName}
        style={{
          top: `${top}px`,
          left: `${left}px`
        }}
      >
        <div className="relative w-full h-full">
          <div
            {...propsClone}
            style={{ ...tooltipPosition }}
            id={propsClone.id}
            className={TooltipClassName}
            role="tooltip"
            aria-describedby={propsClone.id}
          >
            {stringToBoolean(hasTitle) && <div className="title">{title}</div>}
            <p className="description">{description}</p>
          </div>
        </div>
      </div>
    );
  }, [TooltipClassName, description, hasTitle, title, propsClone, tooltipPosition, top, left]);

  const memoPortal = useMemo(() => {
    if (visibility) {
      return createPortal(TooltipContent, document.body);
    }
    return null;
  }, [visibility, TooltipContent]);

  return (
    <>
      {parsedChildren}
      {memoPortal}
    </>
  );
};
