import React, { AnchorHTMLAttributes, FC, ReactNode } from 'react';
import { CardLinksFooterProps } from '../models/CardLinksFooterProps';
import { LinkableIconsProps } from '../models/LinkableIconsProps';
import { LinkableImagesProps } from '../models/LinkableImagesProps';
import { FooterSocialUsefulCopyright } from '../shared-components/footer-social-useful-copyright';
import { PublicFooterHighlightCardLinks } from './public-footer-highlight-card-links';

const PublicFooterMainPlusCardLinks: FC<{
  brandImage: LinkableImagesProps;
  badgeImage?: LinkableImagesProps;
  caption: string | ReactNode;
  partnersLogos: LinkableImagesProps[];
  socialsLink?: LinkableIconsProps[];
  usefulLinks?: AnchorHTMLAttributes<HTMLAnchorElement>[];
  copyright: string | ReactNode;
  cardLinks: CardLinksFooterProps;
}> = ({ brandImage, badgeImage, caption, partnersLogos, socialsLink, usefulLinks, copyright, cardLinks }) => {
  return (
    <div className="bg-[var(--color-primary-900)]">
      <div className="container mx-auto">
        <div className="grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-32">
          <div className="xs:col-span-4 md:col-span-3 xl:col-span-6 xs:pt-32 md:pt-64">
            <div className="mb-32">
              {brandImage.link && brandImage.image && (
                <a
                  {...brandImage.link}
                  className="inline-block border-2 border-transparent rounded-4 outline-none focus:rounded-none focus:outline focus:outline-2 focus:-outline-offset-2 focus:outline-[var(--color-focus)]"
                >
                  <img alt={brandImage.image.alt} {...brandImage.image} />
                </a>
              )}
              {!brandImage.link && brandImage.image && <img {...brandImage.image} alt={brandImage.image.alt} />}
            </div>
            <div className="mb-64">
              <span className="text-m-regular text-[var(--color-neutral-500)]">{caption}</span>
            </div>
            <div>
              {badgeImage?.link && badgeImage?.image && (
                <a
                  {...badgeImage.link}
                  className="inline-block border-2 border-transparent rounded-4 outline-none focus:rounded-none focus:outline focus:outline-2 focus:-outline-offset-2 focus:outline-[var(--color-focus)]"
                >
                  <img alt={badgeImage.image.alt} {...badgeImage.image} />
                </a>
              )}
              {badgeImage && !badgeImage.link && badgeImage.image && <img {...badgeImage.image} alt={badgeImage.image.alt} />}
            </div>
          </div>
          <div className="xs:col-span-4 md:col-span-1 xl:col-span-1 min-h-full min-w-full md:relative xl:relative">
            <hr className="border-1 border-white opacity-5 md:h-[calc(100%-2px)] xl:h-[calc(100%-2px)] max-md:w-full md:bottom-0 right-0 absolute" />
          </div>
          <div className="xs:col-span-4 md:col-span-4 xl:col-span-4 xs:pt-32 md:pt-64">
            <div className="xs:col-span-4 md:col-span-4 xl:col-span-4 xs:py-32 xs:pl-0 md:pt-0 md:pb-64 md:pl-0 xl:pl-32 xl:pb-64 xl:pt-0">
              <PublicFooterHighlightCardLinks {...cardLinks} />
            </div>
          </div>
        </div>

        <hr className="border-1 border-white opacity-5 w-full absolute left-0" />
        <div className="grid xs:justify-items-center md:justify-items-center xl:justify-items-start xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-32 py-32">
          <div className="xs:col-span-4 md:col-span-8 xl:col-span-12">
            <ul className="flex flex-wrap xs:gap-32 md:gap-[30px]">
              {partnersLogos
                .filter((_i, index) => index < 10)
                .map((partner, index) => {
                  const key = `logo-${index}`;

                  return (
                    <li key={key}>
                      {partner?.link && partner?.image && (
                        <a
                          {...partner.link}
                          className="inline-block outline-none border-2 border-transparent rounded-4 opacity-50 focus:rounded-none focus:outline focus:outline-2 focus:-outline-offset-2 focus:outline-[var(--color-focus)] focus:opacity-100 hover:opacity-100"
                        >
                          <img alt={partner.image.alt} {...partner.image} />
                        </a>
                      )}
                      {!partner?.link && partner?.image && <img {...partner?.image} alt={partner?.image.alt} />}
                    </li>
                  );
                })}
            </ul>
          </div>
        </div>
        <hr className="border-1 border-white opacity-5 w-full absolute left-0" />

        <FooterSocialUsefulCopyright socialsLink={socialsLink} usefulLinks={usefulLinks} copyright={copyright} />
      </div>
    </div>
  );
};

export { PublicFooterMainPlusCardLinks };
