import classNames from 'classnames';
import React, { FC, MouseEvent, ReactNode, RefObject } from 'react';
import { BooleanProp } from '../../../../../models';
import { stringToBoolean } from '../../../../../utils';
import { Button } from '../../../../atoms/button';
import { MenuItem, MenuItemRef } from '../../../../molecules/menu-item';
import { MenuProps, SubmenuItemProps } from '../../models';
import { closeOtherSubmenus } from '../../utils/close-other-menu';
import { handleClickMenuItem } from '../../utils/handle-click-menu-item';
import { HeaderMenuOptions } from '../header-menu-item-options/header-menu-item-options';

const HeaderBurgerSecondaryNavigation: FC<{
  item: MenuProps;
  backButtonLabelBurger: ReactNode;
  hasBackButtonBurger: BooleanProp;
  itemsSubmenuClone: SubmenuItemProps;
  handleBackButtonBurger: (evt: MouseEvent<HTMLButtonElement>) => void;
  submenuLevel2Height: number;
  menuItemId: string;
  menuItemRef: RefObject<MenuItemRef>;
  setBackdropVisible: (val: boolean) => void;
  submenuLevel3Height: number;
}> = ({
  item,
  backButtonLabelBurger,
  hasBackButtonBurger,
  itemsSubmenuClone,
  handleBackButtonBurger,
  submenuLevel2Height,
  menuItemId,
  menuItemRef,
  setBackdropVisible,
  submenuLevel3Height
}) => {
  return (
    <>
      {item.relatedNavMenu?.menuItemsHeaderNavigation && Object.keys(itemsSubmenuClone).length > 1 && (
        <div className="agora-header-burger-navigation-options-submenu-level-2 overflow-hidden hidden sticky w-full right-0 z-sticky shadow-bottom-high">
          {stringToBoolean(hasBackButtonBurger) && backButtonLabelBurger && (
            <Button
              variant="neutral"
              appearance="link"
              hasIcon
              leadingIcon="agora-line-arrow-left-anchor"
              leadingIconHover="agora-solid-arrow-left-anchor"
              className="xs:mx-32 md:mx-64 xl:mx-64 xs:mt-16 md:mt-32 xl:mt-32"
              onClick={(evt: MouseEvent<HTMLButtonElement>) => {
                handleBackButtonBurger?.(evt);
                if (evt.currentTarget.parentElement?.parentElement?.parentElement) {
                  evt.currentTarget.parentElement?.parentElement?.parentElement
                    ?.querySelectorAll('.agora-menu-item')
                    .forEach((element: Element) => {
                      element.classList.remove('!hidden');
                      evt.currentTarget.parentElement?.parentElement?.classList.remove('fade-right-left');
                      evt.currentTarget.parentElement?.classList.add('hidden');
                      evt.currentTarget.parentElement?.parentElement?.parentElement?.classList.add('fade-left-right');
                      evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.add('pt-32');
                    });
                }
              }}
            >
              {backButtonLabelBurger}
            </Button>
          )}

          <div className="overflow-y-auto overflow-x-hidden" style={{ maxHeight: `calc(100vh - ${submenuLevel2Height}px)` }}>
            <p className="text-m-semibold text-[var(--color-neutral-500)] xs:px-32 md:px-64 xl:px-64 pt-32 pb-16">
              {item.menuItem.children}
            </p>

            <ul role="menubar">
              {item.relatedNavMenu.menuItemsHeaderNavigation
                ?.filter((_i, idx) => idx < 5)
                .map((subitem: MenuProps, idx) => {
                  const subId = `${menuItemId}-submenu-${idx}`;
                  const containerHeaderMenuOptionsLevel2 = classNames(
                    'agora-header-burger-navigation-options-submenu-level-3 overflow-hidden hidden sticky w-full right-0 z-sticky shadow-bottom-high',
                    {
                      'bg-[var(--color-neutral-50)]': subitem.submenuItem?.highlightItems,
                      'bg-[var(--color-neutral-100)]': !subitem.submenuItem?.highlightItems
                    }
                  );

                  return (
                    <li key={subId} role="none">
                      {/* Menu item with same elements of secondary navigation */}
                      <MenuItem
                        {...subitem.menuItem}
                        ref={menuItemRef}
                        itemType="secondary"
                        vertically
                        onClick={(evt: MouseEvent<any>) => {
                          subitem.menuItem.onClick?.(evt);
                          if ((evt.currentTarget as Element).nextElementSibling) {
                            (evt.currentTarget as Element).parentElement?.parentElement
                              ?.querySelectorAll('.agora-menu-item')
                              .forEach((element: Element) => {
                                element.classList.add('!hidden');
                                element.parentElement?.parentElement?.previousElementSibling?.classList.add('hidden');
                                element.parentElement?.parentElement?.parentElement?.previousElementSibling?.classList.add('hidden');
                                element.parentElement?.parentElement?.previousElementSibling?.previousElementSibling?.classList.add(
                                  'hidden'
                                );

                                (evt.currentTarget as Element).nextElementSibling?.classList.add('fade-right-left');
                                (evt.currentTarget as Element).parentElement?.parentElement?.classList.remove('fade-left-right');
                                (evt.currentTarget as Element).parentElement?.parentElement?.parentElement?.classList.remove(
                                  'fade-left-right'
                                );
                              });
                          }

                          handleClickMenuItem(evt, setBackdropVisible);
                        }}
                      />
                      {subitem?.submenuItem?.submenuItems && (
                        <div className={containerHeaderMenuOptionsLevel2}>
                          {stringToBoolean(hasBackButtonBurger) && backButtonLabelBurger && (
                            <Button
                              variant="neutral"
                              appearance="link"
                              hasIcon
                              leadingIcon="agora-line-arrow-left-anchor"
                              leadingIconHover="agora-solid-arrow-left-anchor"
                              className="xs:mx-32 md:mx-64 xl:mx-64 xs:mt-16 md:mt-32 xl:mt-32"
                              onClick={(evt: MouseEvent<HTMLButtonElement>) => {
                                handleBackButtonBurger?.(evt);
                                if (evt.currentTarget.parentElement?.parentElement?.parentElement) {
                                  evt.currentTarget.parentElement.parentElement.parentElement
                                    .querySelectorAll('.agora-menu-item')
                                    .forEach((element: Element) => {
                                      element.classList.remove('!hidden');
                                      evt.currentTarget.parentElement?.parentElement?.classList.remove('fade-right-left');
                                    });

                                  (
                                    evt.currentTarget.parentElement?.parentElement?.parentElement?.previousSibling as HTMLElement
                                  ).classList.remove('hidden');
                                  (
                                    evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement
                                      ?.previousSibling as HTMLElement
                                  ).classList.remove('hidden');

                                  evt.currentTarget?.parentElement?.classList.add('hidden');
                                  (evt.currentTarget.parentElement?.previousSibling as HTMLElement).click();
                                  evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.remove(
                                    'fade-right-left'
                                  );
                                  evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.add(
                                    'fade-left-right'
                                  );
                                }
                              }}
                            >
                              {backButtonLabelBurger}
                            </Button>
                          )}

                          <div
                            className="overflow-y-auto overflow-x-hidden [&>*:nth-child(2)]:pt-16 xl:[&>*:nth-child(2)]:mx-64"
                            style={{ maxHeight: `calc(100vh - ${submenuLevel3Height}px)` }}
                          >
                            <p className="text-m-semibold text-[var(--color-neutral-500)] xs:px-32 md:px-64 xl:px-64 pt-32 pb-16">
                              {item.menuItem.children}
                            </p>
                            {/* if secondary items have direct options render on a HeaderMenuOptions component */}
                            <HeaderMenuOptions
                              submenu={subitem?.submenuItem}
                              closeOtherMenuItemOpened={closeOtherSubmenus}
                              setBackdropVisible={setBackdropVisible}
                            />
                            {subitem?.submenuItem?.highlightItems && (
                              <div
                                aria-label={subitem?.submenuItem?.highlightItems.submenuAriaLabel}
                                className="bg-[var(--color-neutral-100)] xl:[&>*:nth-child(1)]:mx-64"
                              >
                                <HeaderMenuOptions
                                  submenu={subitem?.submenuItem?.highlightItems}
                                  closeOtherMenuItemOpened={closeOtherSubmenus}
                                  setBackdropVisible={setBackdropVisible}
                                  className="flex flex-wrap gap-32 w-full"
                                />
                              </div>
                            )}
                          </div>
                        </div>
                      )}
                    </li>
                  );
                })}
            </ul>
          </div>
        </div>
      )}
    </>
  );
};

export { HeaderBurgerSecondaryNavigation };
