import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent as ReactKeyboardEvent,
  ReactNode,
  Ref,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp, FeedbackState, Months } from '../../../models';
import { WeekDays } from '../../../models/week-days.interface';
import { stringToBoolean } from '../../../utils';
import { Icon } from '../../atoms/icon';
import { DatePickerDropdown } from './date-picker-dropdown';
import { GroupedDate, GroupedDateRef } from './grouped-date/grouped-date';
import './input-date.scss';
import { parseInputDate } from './utils/parse-input-date';
import { Feedback } from '../../shared/feedback';

export interface InputDateRef {
  value?: Date;
}

export interface InputDateProps extends Omit<ComponentPropsWithRef<'input'>, 'ref' | 'onChange'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Sets the input in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below input.
   */
  feedbackText?: ReactNode;
  /**
   * Show or hide the leading icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Name of the icon leading icon to be displayed.
   */
  icon?: string;
  /**
   * The placeholder to apply in the input day part
   */
  dayInputPlaceholder?: string;
  /**
   * The placeholder to apply in the input month part
   */
  monthInputPlaceholder?: string;
  /**
   * The placeholder to apply in the input year part
   */
  yearInputPlaceholder?: string;
  /**
   * Accessible text to apply to the trailing calendar icon.
   */
  calendarIconAriaLabel: string;
  /**
   * Accessible text to apply to the date picker previous year button.
   */
  previousYearAriaLabel: string;
  /**
   * Accessible text to apply to the date picker previous month button.
   */
  previousMonthAriaLabel: string;
  /**
   * Accessible text to apply to the date picker next month button.
   */
  nextMonthAriaLabel: string;
  /**
   * Accessible text to apply to the date picker next year button.
   */
  nextYearAriaLabel: string;
  /**
   * Labels for each month.
   */
  monthsLabels?: Months;
  /**
   * Labels for each weekday.
   */
  weekdaysLabels?: WeekDays;
  /**
   * Accessible text to identify the current focused day.
   */
  focusedDayAriaLabel?: string;
  /**
   * Accessible text to identify the current selected day.
   */
  selectedDayAriaLabel: string;
  /**
   * Accessible text to identify the highlighted today day.
   */
  todayDayAriaLabel: string;
  /**
   * Label for the date picker today button.
   */
  todayLabel: string;
  /**
   * Accessible text for the date picker today button.
   */
  todayAriaLabel?: string;
  /**
   * Label for the date picker cancel button.
   */
  cancelLabel: string;
  /**
   * Accessible text for the date picker cancel button.
   */
  cancelAriaLabel?: string;
  /**
   * Label for the date picker ok button.
   */
  okLabel: string;
  /**
   * Accessible text for the date picker ok button.
   */
  okAriaLabel?: string;
  /**
   *   Event triggered whenever the selected value changes
   */
  onChange?: (date: Date) => void;
}

const InnerInputDate: ForwardRefRenderFunction<InputDateRef, InputDateProps> = (
  {
    className,
    label,
    hideLabel,
    hasError,
    hasFeedback,
    feedbackText,
    feedbackState = 'info',
    hasIcon,
    icon,
    calendarIconAriaLabel,
    previousYearAriaLabel,
    previousMonthAriaLabel,
    nextMonthAriaLabel,
    nextYearAriaLabel,
    monthsLabels,
    weekdaysLabels,
    focusedDayAriaLabel,
    selectedDayAriaLabel,
    todayDayAriaLabel,
    todayLabel,
    todayAriaLabel,
    cancelLabel,
    cancelAriaLabel,
    okLabel,
    okAriaLabel,
    value,
    defaultValue,
    onChange,
    ...props
  },
  ref: Ref<InputDateRef | null>
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const innerInputRef = useRef<GroupedDateRef>(null);

  const dropdownRef = useRef<HTMLDivElement>(null);

  const wrapperRef = useRef<HTMLDivElement>(null);

  useImperativeHandle(
    ref,
    () => {
      return {
        value: innerInputRef.current?.value
      };
    },
    [innerInputRef.current]
  );

  const parsedValue = value?.toString() ?? defaultValue?.toString();

  const [groupedDateCurrentValue, setGroupedDateCurrentValue] = useState<Date | undefined>(parseInputDate(parsedValue));
  const [overlayCurrentValue, setOverlayCurrentValue] = useState<Date | undefined>(parseInputDate(parsedValue));

  const [overlayVisible, setOverlayVisible] = useState(false);

  const inputClassBuilder = () => {
    return {
      wrapperClasses: classNames('agora-date-picker', className, {
        'input-disabled': propsClone.disabled,
        'input-read-only': propsClone.readOnly,
        'input-has-error': stringToBoolean(hasError)
      }),

      labelSectionClasses: classNames('input-label-wrapper', {
        'hidden': stringToBoolean(hideLabel),
        'flex items-end mb-8': !stringToBoolean(hideLabel) && label
      }),

      inputWrapperClasses: classNames('input-wrapper relative flex items-center'),

      inputClasses: classNames('agora-input-date', {
        'has-leading-icon': hasIcon && icon?.startsWith('agora-line'),
        'has-value': groupedDateCurrentValue && groupedDateCurrentValue?.toString().length > 0
      })
    };
  };

  const { wrapperClasses, labelSectionClasses, inputWrapperClasses, inputClasses } = inputClassBuilder();

  const showOverlay = () => {
    setOverlayVisible(true);
    dropdownRef.current?.focus();
  };

  const hideOverlay = () => {
    setOverlayVisible(false);
    innerInputRef.current?.focus();
  };

  const toggleOverlay = () => {
    if (overlayVisible) {
      hideOverlay();
    } else {
      showOverlay();
    }
  };

  const handleOnDismiss = () => {
    hideOverlay();
  };

  //  HANDLE INPUT DATE KEYDOWN FOR ESCAPE AND TO DISABLE DEFAULT OVERLAY
  const handleInputDateKeydown = (evt: ReactKeyboardEvent<HTMLInputElement>) => {
    if (evt.code === 'Escape') {
      handleOnDismiss();
    }

    if (evt.code === 'Space') {
      evt.preventDefault();

      showOverlay();
    }

    propsClone.onKeyDown?.(evt);
  };

  const handleDatePickerCancel = () => {
    handleOnDismiss();
  };

  //  HANDLE CLICK OUTSIDE DIALOG
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (!overlayVisible) {
        return;
      }

      if (!wrapperRef.current?.contains(event.target as HTMLDivElement)) {
        handleOnDismiss();
      }
    };

    document.addEventListener('click', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, [overlayVisible]);

  //  HANDLE ESCAPE
  useEffect(() => {
    const keydownHandler = (evt: KeyboardEvent) => {
      if (evt.key === 'Escape') {
        evt.preventDefault();
        handleOnDismiss();
      }
    };

    wrapperRef.current?.addEventListener('keydown', keydownHandler);

    return () => {
      wrapperRef.current?.removeEventListener('keydown', keydownHandler);
    };
  }, []);

  //  ONCHANGE STUFF
  //  IN ORDER TO TAME THE NUMBER OF ONCHANGE CALLS, WE PASS THE RESPONSIBILITY OF "CHANGING" TO THE GROUPED DATE.
  //  ALL CHANGES OCCURRED IN DATEPICKER OVERLAY WILL BE PASSED DOWN TO GROUPED INPUT DATE STATE, THEN A GROUPED DATE ONCHANGE OCCURS AND IT WILL TRIGGER A EXTERNAL ONCHANGE
  const onGroupedDateChangeHandler = (date: Date) => {
    setOverlayCurrentValue(date);
    handleOnDismiss();
    onChange?.(date);
  };

  const onOverlayChangeHandler = (date: Date) => {
    setGroupedDateCurrentValue(date);
    handleOnDismiss();
  };

  const dropdownDatePickerLabels = {
    previousYearAriaLabel,
    previousMonthAriaLabel,
    nextMonthAriaLabel,
    nextYearAriaLabel,
    monthsLabels,
    weekdaysLabels,
    focusedDayAriaLabel,
    selectedDayAriaLabel,
    todayDayAriaLabel,
    todayLabel,
    todayAriaLabel,
    cancelLabel,
    cancelAriaLabel,
    okLabel,
    okAriaLabel
  };

  return (
    <div hidden={!!propsClone.hidden} className={wrapperClasses} ref={wrapperRef}>
      {/* Label */}
      <div className={labelSectionClasses}>
        <label htmlFor={propsClone.id} className="text-[var(--color-primary-900)] text-m-medium">
          {label}
        </label>
      </div>

      {/* Input wrapper */}
      <div className={inputWrapperClasses}>
        <GroupedDate
          {...propsClone}
          ref={innerInputRef}
          readOnly={propsClone.readOnly}
          disabled={propsClone.disabled}
          className={inputClasses}
          value={groupedDateCurrentValue}
          aria-invalid={stringToBoolean(hasError)}
          aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
          handleInputDateKeydown={handleInputDateKeydown}
          onChange={onGroupedDateChangeHandler}
        />

        {/* Input Leading Icon  */}
        {hasIcon && icon && icon.startsWith('agora-line') && (
          <div className="input-leading-icon">
            <Icon name={icon} aria-hidden />
          </div>
        )}

        {/* Input Trailing Icon  */}
        <button
          type="button"
          disabled={!!propsClone.disabled || !!propsClone.readOnly}
          onClick={toggleOverlay}
          className="input-trailing-icon"
          aria-label={calendarIconAriaLabel}
        >
          <Icon name="agora-line-calendar" aria-hidden className="icon-calendar-default" />
          <Icon name="agora-solid-calendar" aria-hidden className="icon-calendar-hover" />
        </button>

        {overlayVisible && (
          <DatePickerDropdown
            labels={dropdownDatePickerLabels}
            date={overlayCurrentValue ?? new Date()}
            onChange={onOverlayChangeHandler}
            onCancel={handleDatePickerCancel}
          />
        )}
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputDate = forwardRef<InputDateRef, InputDateProps>(InnerInputDate);
