import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode, useMemo } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Button, ButtonAppearance, ButtonVariant } from '../button';
import './tag.scss';

/**
 * The available variants of the Tag. This variants will define the background and border color of the Tag.
 */
export type TagVariant = 'light' | 'dark' | 'info';

export interface TagProps extends ComponentPropsWithRef<'button'> {
  /**
   * The variant of the Tag. This variants will define the background and border color of the given Tag
   */
  variant?: TagVariant;
  /**
   * Alternative text for the close tag icon.
   */
  closeTagAltText?: string;
  /**
   * Defines if the button uses an icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Defines the name of the icon to use.
   */
  icon?: string;
  /**
   * Defines the name of the icon to be displayed on mouse hover. If none specified, it will the appropriate variant of the icon.
   */
  iconHover?: string;
  /**
   * Alternative text for the icon.
   */
  iconAltText?: string;
  /**
   * Children content.
   */
  children?: ReactNode;
}

export const InnerTag: ForwardRefRenderFunction<HTMLButtonElement, TagProps> = (
  { variant = 'light', closeTagAltText, icon = '', iconHover = '', iconAltText = '', children, className = '', ...props },
  ref
) => {
  /**
   * Building Tag class name dynamically according parameters.
   */
  const tagClassName = classNames(`btn-tag btn-tag-${variant}`, className);

  const buttonStyleMemo = useMemo<{ appearance: ButtonAppearance; variant: ButtonVariant }>(() => {
    switch (variant) {
      case 'dark':
        return {
          appearance: 'outline',
          variant: 'neutral',
          darkMode: true
        };
      case 'info':
        return {
          appearance: 'solid',
          variant: 'neutral'
        };

      case 'light':
      default:
        return {
          appearance: 'outline',
          variant: 'neutral'
        };
    }
  }, [variant]);

  return (
    <Button
      {...props}
      fullWidth={false}
      hasIcon
      trailingIcon="agora-line-x"
      trailingIconHover="agora-solid-x"
      trailingIconAltText={closeTagAltText}
      leadingIcon={stringToBoolean(props.hasIcon) ? icon : undefined}
      leadingIconHover={stringToBoolean(props.hasIcon) ? iconHover : undefined}
      leadingIconAltText={stringToBoolean(props.hasIcon) ? iconAltText : undefined}
      className={tagClassName}
      ref={ref}
      {...buttonStyleMemo}
    >
      {children}
    </Button>
  );
};

export const Tag = forwardRef<HTMLButtonElement, TagProps>(InnerTag);
