import { stringToBoolean } from '../../../../utils';

const menuClick = (el: Element) => {
  const menuItem = el.previousSibling as HTMLButtonElement;
  if (menuItem.ariaExpanded && stringToBoolean(menuItem.ariaExpanded)) {
    menuItem.click();
  }
};

const checkElmNotCurrentBurgerMenu = (el: Element, navigationBurger: HTMLCollectionOf<Element>) => {
  if (el !== navigationBurger[0]) {
    el.classList.add('hidden');
  }
  menuClick(el);
  if (
    el.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('agora-header-burger-navigation-options-menu') &&
    !el.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('hidden')
  ) {
    el.parentElement?.parentElement?.parentElement?.parentElement?.classList.add('hidden');
    navigationBurger[0].classList.remove('hidden');
    navigationBurger[0]?.querySelectorAll('.agora-menu-item').forEach((element: Element) => {
      element.classList.remove('!hidden');
    });
    return;
  }
  if (!el.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('hidden')) {
    el.parentElement?.parentElement?.parentElement?.classList.add('hidden');
    navigationBurger[0]?.querySelectorAll('.agora-menu-item').forEach((element: Element) => {
      element.classList.remove('!hidden');
    });
  }
};
const checkElmNotCurrentBurgerSubmenu = (el: Element) => {
  el.classList.remove('hidden');
  const menuItem = el.previousElementSibling as HTMLButtonElement;
  if (menuItem.ariaExpanded && !stringToBoolean(menuItem.ariaExpanded)) {
    menuItem.click();
  }
};
const checkElmNotCurrentBurgerSubmenuLevel2 = (el: Element, navigationBurger: HTMLCollectionOf<Element>) => {
  navigationBurger[0].classList.remove('hidden');
  el?.querySelectorAll('.agora-header-burger-navigation-options-submenu-level-2 .agora-menu-item').forEach((element: Element) => {
    element.classList.remove('!hidden');
  });
};

const checkElmNotCurrentNotCBurgerNotSubmenu = (
  el: Element,
  navigationBurger: HTMLCollectionOf<Element>,
  navigationBurgerLevel2: HTMLCollectionOf<Element>
) => {
  const menuItem = el.previousSibling as HTMLButtonElement;
  if (menuItem.classList.contains('agora-burger')) {
    navigationBurger[0].classList.add('hidden');
    navigationBurger[0]?.querySelectorAll('.agora-menu-item').forEach((element: Element) => {
      element.classList.remove('!hidden');
    });
    [...navigationBurgerLevel2].forEach((nbl2) => nbl2.classList.add('hidden'));
  } else {
    el.classList.add('hidden');
  }
  menuClick(el);
};

const checkElmNotCurrentNoChildren = (
  el: Element,
  navigationBurger: HTMLCollectionOf<Element>,
  currentSubmenu: HTMLDivElement | HTMLButtonElement
) => {
  currentSubmenu.classList.remove('hidden');
  menuClick(el);
  navigationBurger[0].classList.add('hidden');
  navigationBurger[0]?.querySelectorAll('.agora-menu-item').forEach((element: Element) => {
    element.classList.remove('!hidden');
  });
};

const checkSubmenuItemsVisible = (navigationBurgerSubmenu: HTMLCollectionOf<Element>, navigationBurger: HTMLCollectionOf<Element>) => {
  Array.from(navigationBurgerSubmenu).forEach((nbs) => {
    if (!nbs.classList.contains('hidden')) {
      nbs.classList.add('hidden');
      navigationBurger[0]?.querySelectorAll('.agora-menu-item').forEach((element: Element) => {
        element.classList.remove('!hidden');
        if (element.hasAttribute('aria-expanded')) {
          (element as HTMLElement).click();
        }
      });
    }
  });
};

const handleCloseBurgerMenu = (
  el: Element,
  currentSubmenu: HTMLDivElement | HTMLButtonElement,
  generalSubmenus: HTMLCollectionOf<Element>,
  navigationBurger: HTMLCollectionOf<Element>,
  navigationBurgerSubmenu: HTMLCollectionOf<Element>,
  navigationBurgerLevel2: HTMLCollectionOf<Element>,
  navigationBurgerSubmenuLevel3: HTMLCollectionOf<Element>
) => {
  if (!el.classList.contains('hidden') && el !== currentSubmenu && Array.from(generalSubmenus).find((gs) => gs === currentSubmenu)) {
    el.classList.add('hidden');
    menuClick(el);
    return;
  }
  // Find current is on burger submenu
  const bsSubmenu = Array.from(navigationBurgerSubmenu).find((bs) => bs === currentSubmenu);
  // If element is visible, not equal to current and current is burger menu
  if (el.classList.contains('hidden') === false && el !== currentSubmenu && [...navigationBurger].includes(currentSubmenu)) {
    checkElmNotCurrentBurgerMenu(el, navigationBurger);
  }
  // If element is visible, not equal to current and current is burger submenu
  else if (el.classList.contains('hidden') === false && el !== currentSubmenu && [...navigationBurgerSubmenu].includes(currentSubmenu)) {
    checkElmNotCurrentBurgerSubmenu(el);
  }
  // If element is visible, not equal to current and current is burger submenu level 2
  else if (el.classList.contains('hidden') === false && el !== currentSubmenu && [...navigationBurgerLevel2].includes(currentSubmenu)) {
    checkElmNotCurrentBurgerSubmenuLevel2(el, navigationBurger);
  }
  // If element is visible, not equal to current, current not equal to burger menu / burger submenu level 2 / burger submenu level 3 and current not on burger submenu
  else if (
    el.classList.contains('hidden') === false &&
    el !== currentSubmenu &&
    ![...navigationBurger].includes(currentSubmenu) &&
    ![...navigationBurgerLevel2].includes(currentSubmenu) &&
    ![...navigationBurgerSubmenuLevel3].includes(currentSubmenu) &&
    bsSubmenu === undefined
  ) {
    checkElmNotCurrentNotCBurgerNotSubmenu(el, navigationBurger, navigationBurgerLevel2);
  }
  // If element is visible, not equal to current and current not has child
  else if (el.classList.contains('hidden') === false && el !== currentSubmenu && !currentSubmenu.hasChildNodes()) {
    checkElmNotCurrentNoChildren(el, navigationBurger, currentSubmenu);
    return;
  }

  //else burger submenu has items visible and not enter other conditions
  else {
    checkSubmenuItemsVisible(navigationBurgerSubmenu, navigationBurger);
  }
};

export const closeOtherSubmenus = (currentSubmenu: HTMLDivElement | HTMLButtonElement) => {
  const generalSubmenus = document.getElementsByClassName('agora-header-general-options-menu');
  const navigationSubmenus = document.getElementsByClassName('agora-header-navigation-options-menu');
  const navBurger = document.getElementsByClassName('agora-header-burger-navigation');
  const navigationBurger = document.getElementsByClassName('agora-header-burger-navigation-options-menu');
  const navigationBurgerSubmenu = document.getElementsByClassName('agora-header-burger-navigation-options-submenu');
  const navigationBurgerLevel2 = document.getElementsByClassName('agora-header-burger-navigation-options-submenu-level-2');
  const navigationBurgerSubmenuLevel3 = document.getElementsByClassName('agora-header-burger-navigation-options-submenu-level-3');

  [...generalSubmenus, ...navigationSubmenus, ...navigationBurger, ...navigationBurgerLevel2, ...navigationBurgerSubmenuLevel3].forEach(
    (el: Element) => {
      // If not burger menu visible
      if (navBurger[0].hasAttribute('hidden') && el.classList.contains('hidden') === false && el !== currentSubmenu) {
        el.classList.add('hidden');
        menuClick(el);
      } else {
        handleCloseBurgerMenu(
          el,
          currentSubmenu,
          generalSubmenus,
          navigationBurger,
          navigationBurgerSubmenu,
          navigationBurgerLevel2,
          navigationBurgerSubmenuLevel3
        );
      }
    }
  );
};
