import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode, useId } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import './radio.scss';

export interface RadioButtonProps extends ComponentPropsWithRef<'input'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Sets the input in a error state.
   */
  hasError?: BooleanProp;
  /**
   * Child elements to render as custom label.
   */
  children?: ReactNode;
}

export const InnerRadio: ForwardRefRenderFunction<HTMLInputElement, RadioButtonProps> = (
  { label, hasError, hideLabel, required = true, children, ...props },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const inputWrapper = classNames('agora-radio-button', {
    disabled: props.disabled,
    error: stringToBoolean(hasError)
  });

  return (
    <div hidden={propsClone.hidden} className={inputWrapper}>
      <input
        {...propsClone}
        className="inner-input"
        type="radio"
        aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
        ref={ref}
        required={required}
        tabIndex={propsClone.disabled ? -1 : 0}
      />

      <div className="radio-wrapper">
        <span className="mark" />

        <label htmlFor={propsClone.id} className="label">
          {children ?? label}
        </label>
      </div>
    </div>
  );
};

export const RadioButton = forwardRef<HTMLInputElement, RadioButtonProps>(InnerRadio);
