import classNames from 'classnames';
import React, {
  FC,
  forwardRef,
  ForwardRefRenderFunction,
  HTMLProps,
  MouseEvent,
  MutableRefObject,
  KeyboardEvent as ReactKeyboardEvent,
  ReactNode,
  Ref,
  useEffect,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Avatar, AvatarProps } from '../../atoms/avatar';
import { Icon } from '../../atoms/icon';
import './menu-item.scss';
import { ButtonProps } from '../../atoms/button';
import { AnchorProps } from '../../atoms/anchor';

/**
 * Menu item type
 */
export type MenuItemType = 'primary' | 'search' | 'menu' | 'secondary' | 'user' | 'avatar' | 'close';

const IconWrapper: FC<{
  name: string;
  iconHover: string;
  iconAltText?: string;
  className?: string;
  hasChildren: boolean;
  darkMode: BooleanProp | undefined;
}> = ({ name = 'no-icon', iconHover = 'no-icon', iconAltText, className, hasChildren, darkMode }) => {
  const wrapperClassName = classNames('icon-wrapper', className);
  const lineClassName = classNames('line', {
    'fill-white': stringToBoolean(darkMode),
    'fill-[var(--color-neutral-900)]': !stringToBoolean(darkMode)
  });
  const solidClassName = classNames('solid', {
    'fill-white': stringToBoolean(darkMode),
    'fill-[var(--color-neutral-900)]': !stringToBoolean(darkMode)
  });

  return (
    <div className={wrapperClassName}>
      <Icon
        name={name.replace('solid', 'line')}
        className={lineClassName}
        dimensions="m"
        aria-hidden={!iconAltText}
        {...(!hasChildren ? { alt: iconAltText } : {})}
      />
      <Icon
        name={(iconHover ?? name).replace('line', 'solid')}
        className={solidClassName}
        dimensions="m"
        aria-hidden={!iconAltText}
        {...(!hasChildren ? { alt: iconAltText } : {})}
      />
    </div>
  );
};

type BaseProps = {
  /**
   * Remove default icons.
   */
  noIcons?: BooleanProp;

  /**
   * Type of menu item
   */
  itemType: MenuItemType;

  /**
   * Menu on dark background
   */
  darkMode?: BooleanProp;

  /**
   *  Menu item is a action button
   */
  actionItem?: BooleanProp;

  /**
   * Children content.
   */
  children?: ReactNode;

  /**
   *  Vertically , used with primary or secondary menu type, allow to change style of current option
   */
  vertically?: BooleanProp;
};

export type MenuItemProps = (BaseProps & HTMLProps<HTMLButtonElement | HTMLAnchorElement | HTMLDivElement>) | (BaseProps & AvatarProps);

const ContentMenuItem: FC<MenuItemProps> = ({ itemType, children, darkMode, noIcons = false, ...props }) => {
  const leadingClassName = classNames(`${children ? 'mr-8' : ''} ${itemType}`);
  const trailingClassName = classNames(`${children ? 'ml-8' : ''} ${itemType}`);
  const childrenClassName = classNames({
    'text-m-regular': itemType !== 'secondary' && itemType !== 'menu',
    'text-m-semibold': itemType === 'secondary' || itemType === 'menu',
    'text-white': stringToBoolean(darkMode),
    'text-[var(--color-neutral-900)]': !stringToBoolean(darkMode)
  });

  const changeIconOpen = () => {
    if (itemType === 'search' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-line-search';
    }
    if (itemType === 'menu' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-line-menu';
    }
    if (itemType === 'user' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-line-user';
    }
    if (itemType === 'user' && stringToBoolean(props['aria-expanded'])) {
      return 'agora-line-x';
    }
    return 'agora-line-x';
  };
  const changeIconOpenHover = () => {
    if (itemType === 'search' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-solid-search';
    }
    if (itemType === 'menu' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-solid-menu';
    }
    if (itemType === 'user' && !stringToBoolean(props['aria-expanded'])) {
      return 'agora-solid-user';
    }
    if (itemType === 'user' && stringToBoolean(props['aria-expanded'])) {
      return 'agora-solid-x';
    }
    return 'agora-solid-x';
  };

  return (
    <>
      {!stringToBoolean(noIcons) && (itemType === 'search' || itemType === 'menu' || itemType === 'user' || itemType === 'close') && (
        <IconWrapper
          className={leadingClassName}
          name={changeIconOpen()}
          iconHover={changeIconOpenHover()}
          hasChildren={!!children}
          darkMode={darkMode}
        />
      )}
      {children && itemType !== 'close' && <span className={childrenClassName}>{children}</span>}
      {!stringToBoolean(noIcons) && (itemType === 'primary' || itemType === 'secondary') && (
        <IconWrapper
          className={trailingClassName}
          name="agora-line-chevron-down"
          iconHover="agora-solid-chevron-down"
          hasChildren={!!children}
          darkMode={darkMode}
        />
      )}
    </>
  );
};

export interface MenuItemRef {
  current: HTMLButtonElement | HTMLAnchorElement | null;
  toggleMenuItem: () => void;
}

export type ExtendedInterfaceMenu = HTMLButtonElement | HTMLAnchorElement | AvatarProps | MenuItemRef | null;

export const InnerMenuItem: ForwardRefRenderFunction<ExtendedInterfaceMenu, MenuItemProps> = (
  { itemType = 'primary', darkMode = false, actionItem = false, children, className, noIcons = false, vertically = false, ...props },
  ref: Ref<ExtendedInterfaceMenu>
) => {
  const menuItemClassName = classNames(
    {
      'agora-menu-item text-m-regular text-white darkMode default': stringToBoolean(darkMode) && itemType === 'primary',
      'agora-menu-item text-m-regular text-white darkMode secondary ': stringToBoolean(darkMode) && itemType !== 'primary',
      'agora-menu-item text-m-regular text-neutral-900 secondary ': !stringToBoolean(darkMode) && itemType !== 'primary',
      'agora-menu-item text-m-regular text-neutral-900 default': !stringToBoolean(darkMode) && itemType === 'primary',
      'agora-menu-item text-m-regular justify-center': !children && itemType !== 'avatar',
      'agora-menu-item avatar': itemType === 'avatar',
      'vertically': stringToBoolean(vertically) && (itemType === 'primary' || itemType === 'secondary')
    },
    className
  );

  const [ariaExpanded, setAriaExpanded] = useState(stringToBoolean(props['aria-expanded']));

  const menuItemRef = useRef<HTMLButtonElement | HTMLAnchorElement | null>(null);

  const toggleExpanded = () => {
    const { current } = menuItemRef as MenuItemRef;
    if (current?.hasAttribute('aria-expanded')) {
      const previousAriaExpanded = !stringToBoolean(current?.getAttribute('aria-expanded') ?? false);
      current?.setAttribute('aria-expanded', previousAriaExpanded.toString());
      setAriaExpanded(previousAriaExpanded);
    }
  };

  const onMouseDownHandler = (evt: MouseEvent<any>) => {
    evt.preventDefault();
    evt.currentTarget?.classList.add('active');
    props.onMouseDown?.(evt);
  };

  const onMouseUpHandler = (evt: MouseEvent<any>) => {
    evt.preventDefault();
    evt.currentTarget?.classList.remove('active');
    props.onMouseUp?.(evt);
  };

  const onKeyDownHandler = (evt: ReactKeyboardEvent<any>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      evt.currentTarget?.classList.add('active');
    }

    props.onKeyDown?.(evt);
  };

  const onKeyUpHandler = (evt: ReactKeyboardEvent<any>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      evt.currentTarget?.classList.remove('active');
      evt.currentTarget?.setAttribute('data-custom-triggered-click', 'true');
      evt.currentTarget?.click();
    }
    props.onKeyUp?.(evt);
  };

  const onClickHandler = (evt: MouseEvent<any>) => {
    //  TRUE MOUSE CLICK
    if (evt.detail > 0) {
      props.onClick?.(evt);
      if (!stringToBoolean(noIcons)) {
        evt.preventDefault();
      }
      toggleExpanded();
      return;
    }

    toggleExpanded();

    //  CLICK FROM KEYBOARD

    //  NEED TO CHECK THIS ATTRIBUTE BECAUSE FIREFOX DOES NOT TRIGGER CLICK EVENTS WHENEVER THE ENTER/SPACE/NUMPADENTER KEY IS PRESSED.
    //  TO FIX THAT INCONSISTENCY, WE ADD A CUSTOM ATTRIBUTE TO SIGNAL THE ARTIFICIAL CLICK TRIGGER AND PREVENT THE CHROMIUM BASED BROWSERS OF DUPLICATE CLICK EVENTS.
    //  ON KEYUP THE CLICK EVENT IS TRIGGERED AND WILL BE ADDED THE CUSTOM ATTRIBUTE. THEN, IF THE BROWSER TRIGGERED ANOTHER CLICK, THE HANDLER WILL NOT FIND THE CUSTOM ATTRIBUTE AND WILL NOT TRIGGER A DUPLICATED EVENT.
    const shouldTriggerClick = menuItemRef.current?.getAttribute('data-custom-triggered-click') === 'true';
    if (shouldTriggerClick) {
      menuItemRef.current?.removeAttribute('data-custom-triggered-click');
    }

    if (!shouldTriggerClick) {
      evt.preventDefault();
      return;
    }

    props.onClick?.(evt);
  };
  useImperativeHandle(
    ref,
    () => {
      return {
        current: menuItemRef.current,
        toggleMenuItem: () => toggleExpanded()
      } as MenuItemRef;
    },
    [menuItemRef]
  );
  useEffect(() => {
    const keydownHandler = (evt: KeyboardEvent) => {
      if (evt.code === 'Escape') {
        setAriaExpanded(false);
      }
    };

    document.addEventListener('keydown', keydownHandler);
    return () => {
      document.removeEventListener('keydown', keydownHandler);
    };
  }, []);

  return (
    <>
      {actionItem && itemType !== 'avatar' && (
        <button
          {...(props as ButtonProps)}
          role={itemType === 'menu' ? 'button' : 'menuitem'}
          className={menuItemClassName}
          type="button"
          onClick={onClickHandler}
          onKeyDown={onKeyDownHandler}
          onKeyUp={onKeyUpHandler}
          onMouseDown={onMouseDownHandler}
          onMouseUp={onMouseUpHandler}
          ref={menuItemRef as MutableRefObject<HTMLButtonElement>}
        >
          <ContentMenuItem {...props} noIcons={noIcons} itemType={itemType} darkMode={darkMode} aria-expanded={ariaExpanded}>
            {children}
          </ContentMenuItem>
        </button>
      )}
      {!actionItem && itemType !== 'avatar' && (
        <a
          {...(props as AnchorProps)}
          role={itemType === 'menu' ? 'link' : 'menuitem'}
          tabIndex={0}
          className={menuItemClassName}
          ref={menuItemRef as MutableRefObject<HTMLAnchorElement>}
          onClick={onClickHandler}
          onKeyDown={onKeyDownHandler}
          onKeyUp={onKeyUpHandler}
          onMouseDown={onMouseDownHandler}
          onMouseUp={onMouseUpHandler}
        >
          <ContentMenuItem {...props} noIcons={noIcons} itemType={itemType} darkMode={darkMode} aria-expanded={ariaExpanded}>
            {children}
          </ContentMenuItem>
        </a>
      )}
      {itemType === 'avatar' && (
        <Avatar
          {...(props as AvatarProps)}
          darkMode={stringToBoolean(darkMode)}
          aria-expanded={ariaExpanded}
          aria-haspopup={props['aria-haspopup']}
          aria-current={props['aria-current']}
          aria-label={props['aria-label']}
          role="menuitem"
          className={menuItemClassName}
          onClick={onClickHandler}
          onKeyDown={onKeyDownHandler}
          onKeyUp={onKeyUpHandler}
          onMouseDown={onMouseDownHandler}
          onMouseUp={onMouseUpHandler}
        />
      )}
    </>
  );
};

export const MenuItem = forwardRef<ExtendedInterfaceMenu, MenuItemProps>(InnerMenuItem);
