import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction, ReactNode, useMemo } from 'react';
import { Button } from '../button';
import { Icon } from '../icon';
import { StatusCardType } from './status-card';

export interface InteractiveStatusCardProps extends ComponentPropsWithoutRef<'div'> {
  className?: string;
  interactive?: boolean;
  children?: ReactNode;
  type?: StatusCardType;
  onActivate?: () => void;
}

export const InnerInteractiveStatusCard: ForwardRefRenderFunction<HTMLDivElement, InteractiveStatusCardProps> = (
  { className, onActivate, interactive, children, 'aria-label': ariaLabel, ...props },
  ref
) => {
  const containerClasses = classNames(`status-card status-${props.type}`, className, {
    interactive
  });

  const onClickHandler = () => {
    onActivate?.();
  };

  const leadingIconName = useMemo(() => {
    switch (props.type) {
      case 'info':
        return 'agora-solid-info-mark';
      case 'success':
        return 'agora-solid-check-circle';
      case 'warning':
        return 'agora-solid-info-mark';
      case 'danger':
        return 'agora-solid-alert-triangle';

      default:
        return 'agora-solid-info-mark';
    }
  }, [props.type]);

  return (
    <div role="status" {...props} ref={ref}>
      <Button
        className={containerClasses}
        type="button"
        onClick={onClickHandler}
        fullWidth
        hasIcon
        aria-label={ariaLabel}
        trailingIcon="agora-line-arrow-right-circle"
        trailingIconHover="agora-solid-arrow-right-circle"
      >
        <Icon name={leadingIconName} aria-hidden />
        {children}
      </Button>
    </div>
  );
};

export const InteractiveStatusCard = forwardRef<HTMLDivElement, InteractiveStatusCardProps>(InnerInteractiveStatusCard);
