import classNames from 'classnames';
import React, { Children, cloneElement, FC, HtmlHTMLAttributes, isValidElement, ReactElement, useId, useMemo } from 'react';
import { ProgressBarProps } from '../../atoms/progress-bar/progress-bar';
import './progress-bar-group.scss';

/**
 * ProgressBarGroup component.
 */
export interface ProgressBarGroupProps extends HtmlHTMLAttributes<HTMLDivElement> {
  /**
   * The label text to display.
   */
  label?: string;
  /**
   * Whether to hide the label or not.
   */
  hideLabel?: boolean;
  /**
   * List of progress components to use as children of the group.
   */
  children?: ReactElement<ProgressBarProps> | Array<ReactElement<ProgressBarProps>>;
  /**
   * Whether to hide the percentage value or not.
   */
  hidePercentageValues?: boolean;
  /**
   * Whether to display the progress bars in a sequential order or not.
   */
  sequential?: boolean;
}

const ProgressBarGroup: FC<ProgressBarGroupProps> = ({
  label,
  hideLabel,
  hidePercentageValues,
  children,
  sequential,
  ...props
}): ReactElement => {
  const generatedId = useId();

  const propsClone = { ...props, id: props.id ?? generatedId };

  const progressBars = Children.toArray(children);

  const activeCurrent = useMemo(() => {
    let foundCurrent = false;
    let currentCount = 0;

    progressBars.forEach((p) => {
      if (isValidElement(p)) {
        if (p.props.current) {
          currentCount += 1;
          foundCurrent = true;
        }
      }
    });

    if (!foundCurrent) {
      return 0;
    }

    if (foundCurrent && currentCount === 1) {
      return progressBars.findIndex((p) => isValidElement(p) && p.props.current);
    }

    const firstNotCompletedIndex = progressBars.findIndex((p) => {
      if (isValidElement(p)) {
        const validatedValue = Math.max(0, Math.min(p.props.value, p.props.max));

        return validatedValue < p.props.max;
      }

      return false;
    });

    return firstNotCompletedIndex < 0 ? progressBars.length - 1 : firstNotCompletedIndex;
  }, [progressBars]);

  const sumMaxValues = useMemo(() => {
    let currentCount = 0;

    progressBars.forEach((p) => {
      if (isValidElement(p)) {
        currentCount += p.props.max;
      }
    });

    return currentCount;
  }, [progressBars]);

  const sumValues = useMemo(() => {
    let currentCount = 0;

    progressBars.forEach((p) => {
      if (isValidElement(p)) {
        currentCount += p.props.value;
      }
    });

    return currentCount;
  }, [progressBars]);

  // function to render progress bars
  const renderProgressBars = (child: ReactElement<ProgressBarProps>, index: number) => {
    const isCurrent = index === activeCurrent;
    let currentVal = child.props.value ?? 0;

    if (sequential) {
      currentVal = activeCurrent < index ? 0 : currentVal;
    }

    return cloneElement(child, {
      'key': child.props.id ?? index,
      'current': isCurrent,
      'value': currentVal,
      'aria-hidden': true
    });
  };

  // function to be used to calculate the progress value
  const progressValue = () => {
    let value = 0;
    Children.forEach(children, (child, index) => {
      if (isValidElement(child)) {
        if (child.props.value !== undefined && child.props.max !== undefined) {
          if (!sequential || index <= activeCurrent) {
            value += Math.min(child.props.value, child.props.max);
          }
        }
      }
    });
    return Math.round((value / sumMaxValues) * 100);
  };

  const percentageClasses = classNames('progress-bar-percentage', {
    'sr-only': hidePercentageValues,
    'not-sr-only': !hidePercentageValues
  });

  return (
    <div {...propsClone}>
      {!hideLabel && <label htmlFor={propsClone.id}>{label}</label>}
      <div
        className="progress-bar-group"
        role="progressbar"
        aria-valuenow={sumValues}
        aria-valuetext={`${sumValues}/${sumMaxValues}`}
        aria-valuemax={100}
      >
        {Children.map(children, (child, index) => (isValidElement(child) ? renderProgressBars(child, index) : null))}
      </div>
      {!hidePercentageValues && (
        <div className={percentageClasses} role="status">
          {progressValue()}%
        </div>
      )}
    </div>
  );
};

export { ProgressBarGroup };
