import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  FC,
  Fragment,
  MouseEvent as ReactMouseEvent,
  ReactNode,
  RefObject,
  useEffect,
  useMemo,
  useRef,
  useState
} from 'react';
import { useWindowSize } from '../../../../../hooks';
import { BooleanProp } from '../../../../../models';
import { stringToBoolean } from '../../../../../utils';
import { AnchorProps } from '../../../../atoms/anchor';
import { AvatarProps } from '../../../../atoms/avatar';
import { ButtonProps } from '../../../../atoms/button';
import { DrawerRef } from '../../../../atoms/drawer';
import { MenuItem, MenuItemRef } from '../../../../molecules/menu-item';
import { BackdropRef, DrawerContentProps, MenuItemGeneralOptionsProps, SubmenuItemProps } from '../../models';
import { closeOtherSubmenus } from '../../utils/close-other-menu';
import { handleClickMenuItem } from '../../utils/handle-click-menu-item';
import { HeaderMenuOptions } from '../header-menu-item-options/header-menu-item-options';
import './header-general-options.scss';

export interface HeaderGeneralOptionsProps extends ComponentPropsWithRef<'div'> {
  /**
   * Activate general options navigation darkMode
   */
  darkModeHeaderGeneralOptions?: BooleanProp;

  /**
   * Navigation aria-label of general options
   */
  ariaLabelHeaderGeneralOptions: string;

  /**
   * Has menu item language
   */
  hasLanguage: BooleanProp;

  /**
   * Has menu item search
   */
  hasSearch: BooleanProp;

  /**
   *  Has menu item user
   */
  hasUser: BooleanProp;

  /**
   * General options navigation menu items
   */
  menuItemsHeaderGeneralOptions: MenuItemGeneralOptionsProps;
}

const HeaderGeneralOptions: FC<
  HeaderGeneralOptionsProps &
    BackdropRef & { refDrawer: RefObject<DrawerRef> } & {
      setDrawerContent(val: DrawerContentProps | null | undefined): void;
    }
> = ({
  darkModeHeaderGeneralOptions,
  ariaLabelHeaderGeneralOptions,
  menuItemsHeaderGeneralOptions,
  hasLanguage,
  hasSearch,
  hasUser,
  setBackdropVisible,
  refDrawer,
  setDrawerContent,
  className
}) => {
  const menuItemLanguageRef = useRef<MenuItemRef>(null);
  const avatarMenuItemRef = useRef(null);

  // #region ClassNames
  const headerGeneralOptionsClassName = classNames(
    'container mx-auto agora-header-general-options',
    {
      darkMode: stringToBoolean(darkModeHeaderGeneralOptions)
    },
    className
  );
  const searchClassName = classNames({ 'ml-auto': !hasLanguage || !menuItemsHeaderGeneralOptions?.language?.submenuItem?.submenuItems });
  const userClassName = classNames({ 'ml-auto': !hasSearch && !menuItemsHeaderGeneralOptions?.language?.submenuItem?.submenuItems });

  // #endregion

  const [headerHeight, setHeaderHeight] = useState(0);
  const { width } = useWindowSize();
  const isMobile = width < 768;

  // #region Language Handler
  const [initLanguage, setInitLanguage] = useState<string | null>(null);
  const toggleLanguage = (evt: ReactMouseEvent<HTMLButtonElement>, selectedAriaLabel = '') => {
    // Update text on menu item
    (evt.currentTarget.parentNode?.parentNode?.parentNode?.parentNode?.parentNode?.previousSibling?.firstChild as HTMLElement).innerText =
      evt.currentTarget.innerText;
    // Reset attributes of previous language
    [...document.getElementsByClassName('language')].forEach((element) => {
      element.removeAttribute('data-aria-selected');
      element.removeAttribute('aria-label');
    });
    // Get ISO of selected language
    const dataIso = evt.currentTarget.getAttribute('data-iso');
    // Set attributes on new language selected
    document.querySelectorAll('.language').forEach((element) => {
      if (element.getAttribute('data-iso') === dataIso) {
        (element.parentNode?.parentNode?.parentNode?.parentNode?.parentNode?.previousSibling?.firstChild as HTMLElement).innerText =
          evt.currentTarget.innerText;
        element.setAttribute('data-aria-selected', 'true');
        element.setAttribute('aria-label', `${element.textContent} ${selectedAriaLabel}`);
      }
    });
  };

  useEffect(() => {
    if (menuItemsHeaderGeneralOptions.language?.submenuItem?.submenuItems) {
      const currentLang = (menuItemsHeaderGeneralOptions.language.submenuItem.submenuItems as []).find(
        (lang: ButtonProps | AnchorProps) => lang['data-aria-selected'] === 'true'
      );
      setInitLanguage((currentLang as unknown as ButtonProps | AnchorProps)?.children?.toString() ?? '-');
      const languageItems = Array.from(menuItemLanguageRef.current?.current?.nextElementSibling?.querySelectorAll('.language') ?? []);
      const currentLanguageItem = languageItems?.find((el) => el.hasAttribute('data-aria-selected'));
      currentLanguageItem?.setAttribute(
        'aria-label',
        `${currentLanguageItem.textContent} ${menuItemsHeaderGeneralOptions.language.submenuItem.selectedAriaLabel}`
      );
    }
  }, [
    menuItemsHeaderGeneralOptions?.language?.submenuItem?.selectedAriaLabel,
    menuItemsHeaderGeneralOptions?.language?.submenuItem?.submenuItems
  ]);
  // #endregion

  const userAvatarPropsCloneMenuItem = useMemo(() => {
    return { ...menuItemsHeaderGeneralOptions.user?.menuItem };
  }, [menuItemsHeaderGeneralOptions.user?.menuItem]);

  const userNotLoggedSubmenuItemPropsClone = useMemo(() => {
    return { ...menuItemsHeaderGeneralOptions.user?.submenuItem };
  }, [menuItemsHeaderGeneralOptions.user?.submenuItem]);

  useEffect(() => {
    const resizeGeneral = () => {
      setHeaderHeight(document.querySelector('.agora-header-general')?.clientHeight ?? 0);

      (userAvatarPropsCloneMenuItem as AvatarProps).tooltipPosition = 'bottom';
      (userAvatarPropsCloneMenuItem as AvatarProps).tooltipAlignment = 'end';
      userNotLoggedSubmenuItemPropsClone.submenuAlignment = 'end';
    };

    resizeGeneral();

    window.addEventListener('resize', resizeGeneral);
    return () => {
      window.removeEventListener('resize', resizeGeneral);
    };
  }, [userAvatarPropsCloneMenuItem, userNotLoggedSubmenuItemPropsClone]);

  const renderMenuItems = (menuItems: MenuItemGeneralOptionsProps) => {
    let component: ReactNode[] = [];
    Object.keys(menuItems).forEach((keys) => {
      const generatedKey = `menu-items-${keys}`;
      switch (keys) {
        case 'language':
          component = [
            ...component,
            <Fragment key={generatedKey}>
              {stringToBoolean(hasLanguage) && menuItemsHeaderGeneralOptions.language?.submenuItem?.submenuItems && (
                <li role="none" className="mr-auto">
                  <MenuItem
                    itemType="primary"
                    aria-haspopup
                    aria-expanded={false}
                    darkMode={darkModeHeaderGeneralOptions}
                    ref={menuItemLanguageRef}
                    onClick={(evt: ReactMouseEvent<any>) => {
                      handleClickMenuItem(evt, setBackdropVisible);
                    }}
                  >
                    {initLanguage}
                  </MenuItem>

                  <div
                    style={{ maxHeight: `calc(100vh - ${headerHeight}px)` }}
                    className="agora-header-general-options-menu overflow-auto hidden absolute w-full right-0 z-sticky bg-[var(--color-neutral-100)] shadow-bottom-high"
                  >
                    <HeaderMenuOptions
                      submenu={menuItemsHeaderGeneralOptions.language.submenuItem}
                      closeOtherMenuItemOpened={closeOtherSubmenus}
                      setBackdropVisible={setBackdropVisible}
                      onClick={(evt: ReactMouseEvent<HTMLButtonElement>) => {
                        toggleLanguage(evt, menuItemsHeaderGeneralOptions.language?.submenuItem?.selectedAriaLabel);
                      }}
                    />
                  </div>
                </li>
              )}
            </Fragment>
          ];
          break;
        case 'search':
          component = [
            ...component,
            <Fragment key={generatedKey}>
              {stringToBoolean(hasSearch) && menuItemsHeaderGeneralOptions.search?.submenuItem?.submenuSearchContent && (
                <li role="none" className={searchClassName} key={generatedKey}>
                  <MenuItem
                    itemType="search"
                    aria-haspopup
                    aria-expanded={false}
                    darkMode={darkModeHeaderGeneralOptions}
                    onClick={(evt: ReactMouseEvent<HTMLButtonElement>) => {
                      handleClickMenuItem(evt, setBackdropVisible);
                    }}
                  >
                    {!isMobile && menuItemsHeaderGeneralOptions.search.menuItem.children}
                  </MenuItem>
                  <div
                    style={{ maxHeight: `calc(100vh - ${headerHeight}px)` }}
                    className="agora-header-general-options-menu overflow-auto hidden absolute w-full right-0 z-sticky bg-[var(--color-neutral-100)] shadow-bottom-high"
                  >
                    <div
                      aria-label={menuItemsHeaderGeneralOptions.search.submenuItem.submenuAriaLabel}
                      className="container mx-auto xs:py-32 md:py-64 xl:py-64"
                    >
                      <HeaderMenuOptions
                        submenu={menuItemsHeaderGeneralOptions.search.submenuItem}
                        closeOtherMenuItemOpened={closeOtherSubmenus}
                        setBackdropVisible={setBackdropVisible}
                      />
                    </div>
                  </div>
                </li>
              )}
            </Fragment>
          ];
          break;
        case 'user':
          component = [
            ...component,
            <Fragment key={generatedKey}>
              {stringToBoolean(hasUser) && menuItemsHeaderGeneralOptions.user?.submenuItem?.submenuItems && (
                <li role="none" className={userClassName}>
                  <MenuItem
                    itemType={isMobile ? 'user' : 'primary'}
                    aria-haspopup
                    aria-expanded={false}
                    darkMode={darkModeHeaderGeneralOptions}
                    onClick={(evt: ReactMouseEvent<HTMLButtonElement>) => {
                      handleClickMenuItem(evt, setBackdropVisible);
                    }}
                  >
                    {!isMobile && menuItemsHeaderGeneralOptions.user.menuItem.children}
                  </MenuItem>

                  <div
                    style={{ maxHeight: `calc(100vh - ${headerHeight}px)` }}
                    className="agora-header-general-options-menu overflow-auto hidden absolute w-full right-0 z-sticky bg-[var(--color-neutral-100)] shadow-bottom-high"
                  >
                    <HeaderMenuOptions
                      submenu={userNotLoggedSubmenuItemPropsClone as SubmenuItemProps}
                      closeOtherMenuItemOpened={closeOtherSubmenus}
                      setBackdropVisible={setBackdropVisible}
                    />
                  </div>
                </li>
              )}
              {stringToBoolean(hasUser) && menuItemsHeaderGeneralOptions.user?.drawerContent && (
                <li role="none" className={userClassName}>
                  <MenuItem
                    {...userAvatarPropsCloneMenuItem}
                    className="my-8"
                    ref={avatarMenuItemRef}
                    actionItem
                    itemType={'avatar'}
                    aria-haspopup
                    aria-expanded={false}
                    darkMode={darkModeHeaderGeneralOptions}
                    onKeyUp={(evt: any) => {
                      if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
                        handleClickMenuItem(
                          evt,
                          setBackdropVisible,
                          menuItemsHeaderGeneralOptions.user?.drawerContent,
                          refDrawer,
                          setDrawerContent
                        );
                      }
                    }}
                    onClick={(evt: ReactMouseEvent<HTMLButtonElement>) => {
                      handleClickMenuItem(
                        evt,
                        setBackdropVisible,
                        menuItemsHeaderGeneralOptions.user?.drawerContent,
                        refDrawer,
                        setDrawerContent
                      );
                    }}
                  />
                </li>
              )}
            </Fragment>
          ];
          break;
        default:
          break;
      }
    });
    return component;
  };

  return (
    <div className={headerGeneralOptionsClassName}>
      <nav aria-label={ariaLabelHeaderGeneralOptions}>
        <ul role="menubar" className="flex xs:gap-16 md:gap-32 xl:gap-32">
          {renderMenuItems(menuItemsHeaderGeneralOptions)}
        </ul>
      </nav>
    </div>
  );
};

export { HeaderGeneralOptions };
