import React, { ComponentPropsWithRef, FC, MouseEvent, ReactNode, useCallback, useEffect, useRef, useState } from 'react';

import classNames from 'classnames';
import { useWindowSize } from '../../../hooks';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Anchor } from '../../atoms/anchor';
import { Button } from '../../atoms/button';
import { Drawer, DrawerRef } from '../../atoms/drawer';
import './header.scss';
import { DrawerContentProps, MenuProps } from './models';
import { HeaderBurgerNavigation } from './shared-components/header-burger-navigation/header-burger-navigation';
import { HeaderGeneralOptions, HeaderGeneralOptionsProps } from './shared-components/header-general-options/header-general-options';
import { HeaderNavigation, HeaderNavigationProps } from './shared-components/header-navigation/header-navigation';

export interface HeaderProps extends ComponentPropsWithRef<'div'> {
  /**
   * Used to help identify page, SEO and accessibility when in homepage.
   * Can be a set of keywords or simple the name of our application.
   * When not in homepage, h1 should be on main with the title of the page
   */
  headerSEO: string;

  /**
   * Current page is homepage
   */
  isHomepage: BooleanProp;

  /**
   * Header on darkMode
   */
  darkModeHeader: BooleanProp;

  /**
   * Show/hide general options navigation
   */
  hasHeaderGeneralOptions: BooleanProp;

  /**
   * Configuration headerGeneralOptions
   */
  headerGeneralOptions: HeaderGeneralOptionsProps;

  /**
   * Show/hide header primary navigation
   */
  hasHeaderPrimaryNavigation: BooleanProp;

  /**
   * Configuration headerPrimaryNavigation
   */
  headerPrimaryNavigation: HeaderNavigationProps;

  /**
   * Show/hide header secondary navigation
   */
  hasHeaderSecondaryNavigation: BooleanProp;

  /**
   * Configuration headerSecondaryNavigation
   */
  headerSecondaryNavigation: HeaderNavigationProps;

  /**
   * Menu burger label
   */
  menuLabel: ReactNode;

  /**
   * Has menu burger back button
   */
  hasBackButtonBurger?: BooleanProp;

  /**
   * Menu burger back button label
   */
  backButtonLabelBurger?: ReactNode;

  /**
   * Menu burger handle click
   */
  handleBackButtonBurger?: (evt: MouseEvent<HTMLButtonElement>) => void;
}

const Header: FC<HeaderProps> = ({
  headerSEO,
  isHomepage,
  darkModeHeader = stringToBoolean('false'),
  hasHeaderGeneralOptions = stringToBoolean('false'),
  hasHeaderPrimaryNavigation = stringToBoolean('false'),
  hasHeaderSecondaryNavigation = stringToBoolean('false'),
  headerGeneralOptions,
  headerPrimaryNavigation,
  headerSecondaryNavigation,
  menuLabel,
  hasBackButtonBurger,
  backButtonLabelBurger,
  handleBackButtonBurger
}) => {
  const { width } = useWindowSize();
  const isTablet = width >= 768 && width < 1200;
  const isMobile = width < 768;

  // #region Opened Menus
  const getOpenMenuNav = () => {
    const allSubmenus = [
      ...document.getElementsByClassName('agora-header-general-options-menu'),
      ...document.getElementsByClassName('agora-header-navigation-options-menu'),
      ...document.getElementsByClassName('agora-header-burger-navigation-options-menu'),
      ...document.getElementsByClassName('agora-drawer-authentication')
    ];

    const openSubmenu: Element | undefined = allSubmenus.find((menu) => !menu.classList.contains('hidden'));

    if (
      openSubmenu?.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('agora-header-general') ||
      openSubmenu?.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('agora-header-primary') ||
      openSubmenu?.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('agora-header-secondary')
    ) {
      return openSubmenu.parentElement.parentElement.parentElement.parentElement.parentElement;
    } else if (openSubmenu?.parentElement?.parentElement?.parentElement?.classList.contains('agora-header-burger-navigation')) {
      return openSubmenu.parentElement.parentElement.parentElement;
    } else if (openSubmenu?.classList.contains('drawer-open-right')) {
      return openSubmenu;
    }
  };

  const trapFocus = (evt: KeyboardEvent, focusableElements: NodeListOf<Element>) => {
    const focusableElementsVisible = Array.from(focusableElements).filter(
      (value) => !value?.parentElement?.parentElement?.parentElement?.classList.contains('hidden') || !value.classList.contains('!hidden')
    );

    if (focusableElementsVisible) {
      const firstElement = focusableElementsVisible[0] as HTMLElement;
      const lastElement = focusableElementsVisible[focusableElementsVisible.length - 1] as HTMLElement;

      if (evt.shiftKey && document.activeElement === firstElement) {
        evt.preventDefault();
        lastElement.focus();
      } else if (!evt.shiftKey && document.activeElement === lastElement) {
        evt.preventDefault();
        firstElement.focus();
      }
    }
  };

  const trapFocusBurger = (evt: KeyboardEvent, focusableElements: Element[]) => {
    const focusableElementsVisible = Array.from(focusableElements).filter(
      (value) =>
        !value?.parentElement?.parentElement?.parentElement?.parentElement?.classList.contains('hidden') &&
        !value.classList.contains('!hidden')
    );

    if (focusableElementsVisible) {
      const firstElement = focusableElementsVisible.find(
        (el) => !el.classList.contains('hidden') && !el.classList.contains('!hidden')
      ) as HTMLElement;
      const reverseFocusableElementsVisible = Array.from(focusableElementsVisible).reverse();
      const lastElement = reverseFocusableElementsVisible.find(
        (el) => !el.classList.contains('hidden') && !el.classList.contains('!hidden')
      ) as HTMLElement;

      if (evt.shiftKey && document.activeElement === firstElement) {
        evt.preventDefault();
        lastElement.focus();
      } else if (
        (!evt.shiftKey && document.activeElement === lastElement) ||
        !document.querySelector('.agora-header-burger-navigation')?.contains(document.activeElement)
      ) {
        evt.preventDefault();
        firstElement.focus();
      }
    }
  };

  // #endregion

  // #region "BACKDROP"

  const [backdropVisible, setBackdropVisible] = useState<boolean>(false);

  const backdropRef = useRef<HTMLDivElement | null>(null);

  const callSetBackdropVisible = (val: boolean) => {
    setBackdropVisible(val);
  };

  const handleClickOutside = () => {
    setBackdropVisible(false);

    const allSubmenus = [
      ...document.getElementsByClassName('agora-header-general-options-menu'),
      ...document.getElementsByClassName('agora-header-navigation-options-menu'),
      ...document.getElementsByClassName('agora-header-burger-navigation-options-menu')
    ];
    allSubmenus.forEach((el: Element) => {
      const parentAllSubmenus = el?.parentElement;
      if (el.classList.contains('hidden') === false || parentAllSubmenus?.classList.contains('hidden') === false) {
        if (parentAllSubmenus?.classList.contains('agora-wrapper-menu') === true) {
          parentAllSubmenus.classList.add('hidden');
        }
        el.classList.add('hidden');
        document.body.classList.remove('overflow-hidden');
        const menuItem =
          parentAllSubmenus?.classList.contains('agora-wrapper-menu') === true
            ? (parentAllSubmenus.previousSibling as HTMLButtonElement)
            : (el.previousSibling as HTMLButtonElement);
        if (menuItem?.ariaExpanded && stringToBoolean(menuItem?.ariaExpanded)) {
          menuItem.click();
        }
      }
      if (el.classList.contains('agora-header-burger-navigation-options-menu') === true) {
        el.classList.add('pt-32');
        el.querySelectorAll('.agora-menu-item').forEach((element) => {
          element.classList.remove('!hidden');
        });
        el.querySelectorAll('.agora-header-burger-navigation-options-submenu-level-2').forEach((element) => {
          element.classList.add('hidden');
        });
      }
    });
  };

  const navigationTab = useCallback((evt: KeyboardEvent, openedMenu: Element) => {
    const focusableElements: Element[] = Array.from(
      openedMenu.querySelectorAll('button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])')
    );
    const burgerMenu = document.querySelector('.agora-header-burger-navigation-options-menu');
    const burgerSubmenu = document.querySelector('.agora-header-burger-navigation-options-submenu');
    const burgerSubmenuLevel2 = document.querySelector('.agora-header-burger-navigation-options-submenu-level-2');
    const burgerSubmenuLevel3 = document.querySelector('.agora-header-burger-navigation-options-submenu-level-3');

    let focusableElementsClone: Element[] = [];

    focusableElements.forEach((e) => {
      if (
        ((burgerSubmenuLevel3 as Element)?.contains(e) && burgerSubmenuLevel3?.classList.contains('hidden')) ||
        ((burgerSubmenuLevel2 as Element)?.contains(e) && burgerSubmenuLevel2?.classList.contains('hidden')) ||
        ((burgerSubmenu as Element)?.contains(e) && burgerSubmenu?.classList.contains('hidden')) ||
        ((burgerMenu as Element)?.contains(e) && burgerMenu?.classList.contains('hidden'))
      ) {
        return;
      }

      focusableElementsClone = [...focusableElementsClone, e];
    });
    trapFocusBurger(evt, focusableElementsClone);
  }, []);

  useEffect(() => {
    const { current } = backdropRef;

    const keydownHandler = (evt: KeyboardEvent) => {
      if (evt.code === 'Escape') {
        handleClickOutside();
        document.body.classList.remove('overflow-hidden');
      }
      if (evt.key === 'Tab' && current) {
        const openedMenu = getOpenMenuNav();
        if (openedMenu) {
          if (openedMenu.classList.contains('agora-header-burger-navigation')) {
            navigationTab(evt, openedMenu);
          } else {
            const focusableElements = openedMenu.querySelectorAll(
              'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
            );
            trapFocus(evt, focusableElements);
          }
        }
      }
    };

    current?.addEventListener('click', handleClickOutside);
    document.addEventListener('keydown', keydownHandler);

    return () => {
      current?.removeEventListener('click', handleClickOutside);
      document.removeEventListener('keydown', keydownHandler);
    };
  }, [backdropVisible, navigationTab]);

  // #endregion "BACKDROP"

  // #region "DRAWER"
  const refDrawer = useRef<DrawerRef>(null);
  const [drawerContent, setDrawerContent] = useState<DrawerContentProps | null | undefined>(null);

  const drawerClassNames = classNames('agora-drawer-authentication', {
    darkMode: darkModeHeader
  });

  const callSetDrawerContent = (val: DrawerContentProps | null | undefined) => setDrawerContent(val);

  const [headerDrawerHeight, setHeaderDrawerHeight] = useState(0);
  const [footerDrawerHeight, setFooterDrawerHeight] = useState(0);

  const handleCloseDrawer = () => {
    refDrawer.current?.closeDrawer();
    document.body.classList.remove('overflow-hidden');
  };

  const handleFooterDrawer = (onClick: () => void) => {
    handleCloseDrawer();
    onClick();
  };

  useEffect(() => {
    const { current } = refDrawer;

    const headerResize = () => {
      if (refDrawer.current) {
        setHeaderDrawerHeight(
          (refDrawer.current.current?.current as HTMLElement).querySelector('nav')?.previousElementSibling?.clientHeight ?? 0
        );
        setFooterDrawerHeight(
          (refDrawer.current.current?.current as HTMLElement).querySelector('nav')?.nextElementSibling?.clientHeight ?? 0
        );
      }
    };

    const keydownHandler = (evt: KeyboardEvent) => {
      if (evt.code === 'Escape') {
        handleClickOutside();
        document.body.classList.remove('overflow-hidden');
      }
      if (evt.key === 'Tab' && current) {
        const openedMenu = getOpenMenuNav();
        if (openedMenu) {
          const focusableElements = openedMenu.querySelectorAll('button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])');
          trapFocus(evt, focusableElements);
        }
      }
    };

    headerResize();

    window.addEventListener('resize', headerResize);
    document.addEventListener('keydown', keydownHandler);

    return () => {
      window.removeEventListener('resize', headerResize);
      document.removeEventListener('keydown', keydownHandler);
    };
  }, [drawerContent]);

  // #endregion "DRAWER"

  // #region "GENERAL HEADER"
  const headerGeneralOptionsClone = { ...headerGeneralOptions };
  headerGeneralOptionsClone.darkModeHeaderGeneralOptions = darkModeHeader;

  const headerGeneralOptionsClassNames = classNames('agora-header-general z-sticky', {
    darkMode: stringToBoolean(darkModeHeader)
  });

  // #endregion "GENERAL HEADER"

  // #region "PRIMARY HEADER"

  const headerPrimaryNavigationClone = { ...headerPrimaryNavigation };
  headerPrimaryNavigationClone.darkModeHeaderNavigation = darkModeHeader;

  const headerPrimaryNavigationClassNames = classNames('agora-header-primary z-sticky', {
    darkMode: stringToBoolean(darkModeHeader)
  });

  // #endregion "PRIMARY HEADER"

  // #region "SECONDARY HEADER"

  const [headerSecondaryNav, setHeaderSecondaryNav] = useState<HeaderNavigationProps | undefined | null>(null);

  useEffect(() => {
    if (headerPrimaryNavigationClone.menuItemsHeaderNavigation) {
      const menu = headerPrimaryNavigationClone.menuItemsHeaderNavigation.filter((_i, index) => index < 5);
      const currentMenuIndex = menu.findIndex((item: MenuProps) => item.menuItem['aria-current'] === 'page');
      setHeaderSecondaryNav(
        menu[currentMenuIndex]?.relatedNavMenu ??
          ({
            ...menu[currentMenuIndex],
            relatedNavMenu: {
              headerNavigationLegalSentence: headerPrimaryNavigationClone.headerNavigationLegalSentence,
              menuItemsHeaderNavigation: []
            }
          } as unknown as HeaderNavigationProps)
      );
    }
  }, [headerPrimaryNavigationClone.headerNavigationLegalSentence, headerPrimaryNavigationClone.menuItemsHeaderNavigation]);

  const hasHeaderSecondaryNavigationClone = hasHeaderSecondaryNavigation;

  const headerSecondaryNavigationClassNames = classNames('agora-header-secondary z-sticky pt-16', {
    darkMode: stringToBoolean(darkModeHeader)
  });
  // #endregion "SECONDARY HEADER"

  // #region "SCROLL"

  const [scrolled, setScrolled] = useState<boolean>(false);

  useEffect(() => {
    const onScroll: EventListener = () => {
      const primaryNav = document.querySelector('.agora-header-primary');
      const secondaryNav = document.querySelector('.agora-header-secondary');
      const burgerNav = document.querySelector('.agora-header-burger-navigation');

      setScrolled((last) => !last);

      if (window.scrollY === 0 && !isMobile && !isTablet) {
        primaryNav?.removeAttribute('hidden');
        secondaryNav?.removeAttribute('hidden');
        primaryNav?.classList.add('fade-in-up');
        secondaryNav?.classList.add('fade-in-up');
        burgerNav?.setAttribute('hidden', 'true');
        burgerNav?.classList.remove('fade-in-up');
        setTimeout(() => {
          primaryNav?.classList.remove('fade-in-up');
          secondaryNav?.classList.remove('fade-in-up');
        }, 550);
      }

      if (scrolled) {
        return;
      }

      if (window.scrollY > 1 && !isMobile && !isTablet) {
        primaryNav?.setAttribute('hidden', 'true');
        secondaryNav?.setAttribute('hidden', 'true');
        primaryNav?.classList.remove('fade-in-up');
        secondaryNav?.classList.remove('fade-in-up');
        burgerNav?.removeAttribute('hidden');
        burgerNav?.classList.add('fade-in-up');
      }
    };

    window.addEventListener('scroll', onScroll);

    return () => window.removeEventListener('scroll', onScroll);
  }, [isMobile, isTablet, scrolled, setScrolled]);
  // #endregion "SCROLL"

  // #region "MENU BURGER"

  const headerBurgerNavigationClassNames = classNames('agora-header-burger-navigation', {
    darkMode: stringToBoolean(darkModeHeader)
  });

  useEffect(() => {
    const resizeMenuWithBurger = () => {
      const primaryNav = document.querySelector('.agora-header-primary');
      const secondaryNav = document.querySelector('.agora-header-secondary');
      const burgerNav = document.querySelector('.agora-header-burger-navigation');
      if (isMobile || isTablet) {
        handleClickOutside();
        primaryNav?.setAttribute('hidden', 'true');
        secondaryNav?.setAttribute('hidden', 'true');
        burgerNav?.removeAttribute('hidden');
      } else {
        window.scrollTo(0, 0);
        handleClickOutside();
        primaryNav?.removeAttribute('hidden');
        secondaryNav?.removeAttribute('hidden');
        burgerNav?.setAttribute('hidden', 'true');
      }
    };

    resizeMenuWithBurger();

    window.addEventListener('resize', resizeMenuWithBurger);
    return () => {
      window.removeEventListener('resize', resizeMenuWithBurger);
    };
  }, [isMobile, isTablet]);

  // #endregion "MENU BURGER"

  const menuItemWithFreeContent = headerPrimaryNavigationClone?.menuItemsHeaderNavigation?.find(
    (el) => el.relatedNavMenu?.freeMenuItemsHeaderNavigation !== undefined
  );

  const [heightGeneralNav, setHeightGeneralNav] = useState(0);
  const [heightPrimaryNav, setHeightPrimaryNav] = useState(0);
  const [heightSecondaryNav, setHeightSecondaryNav] = useState(0);

  useEffect(() => {
    if (document) {
      const generalNavElem = document.querySelector('.agora-header-general');
      if (generalNavElem) {
        setHeightGeneralNav(generalNavElem.clientHeight);
      }

      const primaryNavElem = document.querySelector('.agora-header-primary');
      if (primaryNavElem) {
        setHeightPrimaryNav(primaryNavElem.clientHeight);
      }

      const secondaryNavElem = document.querySelector('.agora-header-secondary');
      if (secondaryNavElem) {
        setHeightSecondaryNav(secondaryNavElem.clientHeight);
      }
    }
  }, []);

  // This only need, because Firefox not has in production mode the ":has" css attribute, but is planned on next versions (so we keep this and css lines)
  useEffect(() => document.querySelector('.agora-header')?.parentElement?.classList.add('sticky', 'top-0', 'z-sticky'), []);

  return (
    <>
      <div className="relative z-sticky agora-header">
        {stringToBoolean(isHomepage) && <h1 className="sr-only">{headerSEO}</h1>}
        {stringToBoolean(hasHeaderGeneralOptions) && headerGeneralOptionsClone?.menuItemsHeaderGeneralOptions && (
          <div className={headerGeneralOptionsClassNames}>
            <HeaderGeneralOptions
              current={backdropRef.current}
              backdropVisible={backdropVisible}
              setBackdropVisible={callSetBackdropVisible}
              refDrawer={refDrawer}
              setDrawerContent={callSetDrawerContent}
              {...headerGeneralOptionsClone}
            />
          </div>
        )}
        {stringToBoolean(hasHeaderPrimaryNavigation) && (
          <div className={headerPrimaryNavigationClassNames}>
            <HeaderNavigation
              current={backdropRef.current}
              backdropVisible={backdropVisible}
              setBackdropVisible={callSetBackdropVisible}
              {...headerPrimaryNavigationClone}
              headerNavigationType="primary"
              optionsMenuOverflow={{ maxHeight: `calc(100vh - ${heightGeneralNav + heightPrimaryNav}px)` }}
            />
          </div>
        )}
        {stringToBoolean(hasHeaderSecondaryNavigationClone) &&
          headerSecondaryNav &&
          (headerSecondaryNav?.menuItemsHeaderNavigation || headerSecondaryNavigation?.headerNavigationContent) && (
            <div className={headerSecondaryNavigationClassNames}>
              <HeaderNavigation
                {...headerSecondaryNav}
                darkModeHeaderNavigation={darkModeHeader}
                ariaLabelHeaderNavigation={headerSecondaryNavigation?.ariaLabelHeaderNavigation}
                headerNavigationContent={headerSecondaryNavigation?.headerNavigationContent}
                current={backdropRef.current}
                backdropVisible={backdropVisible}
                setBackdropVisible={callSetBackdropVisible}
                headerNavigationType="secondary"
                optionsMenuOverflow={{ maxHeight: `calc(100vh - ${heightGeneralNav + heightPrimaryNav + heightSecondaryNav}px)` }}
              />
            </div>
          )}
        {headerPrimaryNavigationClone.menuItemsHeaderNavigation && (
          <div className={headerBurgerNavigationClassNames} hidden>
            <HeaderBurgerNavigation
              menuLabel={menuLabel}
              headerSecondaryNavigation={headerSecondaryNavigation}
              headerPrimaryNavigation={headerPrimaryNavigationClone}
              darkModeHeader={darkModeHeader}
              current={backdropRef.current}
              backdropVisible={backdropVisible}
              setBackdropVisible={callSetBackdropVisible}
              hasHeaderGeneralOptions={hasHeaderGeneralOptions}
              headerGeneralOptions={headerGeneralOptions}
              hasHeaderPrimaryNavigation={hasHeaderPrimaryNavigation}
              hasHeaderSecondaryNavigation={hasHeaderSecondaryNavigationClone}
              hasBackButtonBurger={hasBackButtonBurger}
              backButtonLabelBurger={backButtonLabelBurger}
              handleBackButtonBurger={handleBackButtonBurger}
              headerSEO={headerSEO}
              isHomepage={isHomepage}
            />
            {menuItemWithFreeContent?.menuItem['aria-current'] === 'page' && (
              <div className="container mx-auto flex xs:justify-start md:justify-end py-16">
                {menuItemWithFreeContent.relatedNavMenu?.freeMenuItemsHeaderNavigation}
              </div>
            )}
          </div>
        )}
      </div>
      {backdropVisible && <div className="z-backdrop bg-[var(--color-backdrop)] absolute w-full h-screen" ref={backdropRef} />}

      <Drawer className={drawerClassNames} position="right" dismissOnEscape ref={refDrawer} onClickedOutside={handleClickOutside}>
        {drawerContent && (
          <>
            <div>
              <Button
                className="agora-drawer-header"
                variant="primary"
                appearance="solid"
                hasIcon
                fullWidth
                trailingIcon="agora-line-chevron-right"
                trailingIconHover="agora-solid-chevron-right"
                onClick={handleCloseDrawer}
              >
                {drawerContent.headerLabel}
              </Button>
            </div>
            <nav
              aria-label={drawerContent.menuDrawer['aria-label']}
              className="overflow-auto"
              style={{ maxHeight: `calc(100vh - ${headerDrawerHeight + footerDrawerHeight}px)` }}
            >
              <ul role="menubar">
                {drawerContent.menuDrawer.items.map((c, ind) => {
                  const keyDrw = `menu-drawer-${ind}`;
                  return (
                    <li role="none" key={keyDrw}>
                      <Anchor
                        className="agora-drawer-menu-item"
                        appearance="link"
                        variant="neutral"
                        hasIcon
                        fullWidth
                        leadingIcon={c.leadingIcon}
                        leadingIconHover={c.leadingIconHover}
                        href={c.href}
                        role="menuitem"
                      >
                        {c.children}
                      </Anchor>
                    </li>
                  );
                })}
              </ul>
            </nav>
            <div className="absolute bottom-0 w-full">
              <Button
                className="agora-drawer-footer"
                variant="neutral"
                appearance="link"
                hasIcon
                fullWidth
                leadingIcon="agora-line-log-out"
                leadingIconHover="agora-solid-log-out"
                onClick={() => handleFooterDrawer(drawerContent.footer.onClick)}
              >
                {drawerContent.footer.footerLabel}
              </Button>
            </div>
          </>
        )}
      </Drawer>
    </>
  );
};

export { Header };
