import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode, useRef } from 'react';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { Icon, IconProps } from '../../../atoms/icon';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-single.scss';

/**
 * Card Single Variants
 */
export type CardSingleVariants = 'default' | 'neutral' | 'white';

export interface CardSingleProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * The variant of the card
   */
  variant: CardSingleVariants;
  /**
   * The icon props
   */
  icon?: IconProps;
  /**
   * Title Tag Card.
   */
  headingLevel?: HeadingLevel;
  /**
   * The title of the card
   */
  title: string;
  /**
   * The description of the card
   */
  description?: ReactNode;
  /**
   * Anchor props.
   */
  mainAnchor?: AnchorProps;
  /**
   * Defines blockedLink prop.
   */
  blockedLink?: BooleanProp;
}

export const CardSingle: FC<CardSingleProps> = ({
  variant = 'default',
  icon,
  headingLevel: Heading = 'h3',
  title,
  description,
  mainAnchor,
  blockedLink,
  className,
  ...props
}) => {
  const cardSingleRef = useRef<HTMLDivElement | null>(null);
  const cardSingleAnchorRef = useRef<HTMLAnchorElement | null>(null);

  const headingPlacement = {
    title: !!title,
    description: !title && !!description,
    anchor: !title && !description && !!mainAnchor && !mainAnchor.hidden
  };

  const anchorPlacement = {
    anchor: false,
    title: false,
    description: false
  };

  let mainAnchorClone: AnchorProps = { ...mainAnchor };

  if (mainAnchor) {
    mainAnchorClone = {
      ...mainAnchor
    } as AnchorProps;

    if (mainAnchorClone.hasIcon && !mainAnchorClone.trailingIcon && !mainAnchorClone.leadingIcon) {
      mainAnchorClone.trailingIcon = 'agora-line-arrow-right-circle';
      mainAnchorClone.trailingIconHover = 'agora-solid-arrow-right-circle';
    }

    if (mainAnchor.hidden) {
      //  MUST DELETE IN ORDER TO SPREAD IT LATER
      delete mainAnchorClone.hidden;

      //  ONE OF THE TEXTS MUST BE FILLED
      anchorPlacement.title = !!title;
      anchorPlacement.description = !title && !!description;
    } else {
      anchorPlacement.anchor = true;
    }
  }

  useBlockedLink({
    parentCardRef: cardSingleRef,
    anchorRef: cardSingleAnchorRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  const CardSingleClasses = classNames('card-single', `card-${variant}`, className);

  const buildText = (text: ReactNode, anchorProps: AnchorProps, isAnchor: boolean, isHeading: boolean): ReactNode => {
    if (!isAnchor) {
      return isHeading ? <Heading>{text}</Heading> : text;
    }

    return (
      <>
        {isHeading && (
          <Heading>
            <Anchor {...anchorProps} appearance="link" ref={cardSingleAnchorRef}>
              {text}
            </Anchor>
          </Heading>
        )}
        {!isHeading && (
          <Anchor {...anchorProps} appearance="link" ref={cardSingleAnchorRef}>
            {text}
          </Anchor>
        )}
      </>
    );
  };
  const anchorContainerClassNames = classNames('anchor-container', { 'icon-only': mainAnchorClone?.iconOnly });

  return (
    <div {...props} ref={cardSingleRef} role="presentation" className={CardSingleClasses}>
      {icon && (
        <div className="icon-container">
          <Icon {...icon} className="card-single-icon" dimensions="m" />
        </div>
      )}

      <div className="info-container">
        {title && <div className="title-container">{buildText(title, mainAnchorClone, anchorPlacement.title, headingPlacement.title)}</div>}

        {description && (
          <div className="description-container">
            {buildText(description, mainAnchorClone, anchorPlacement.description, headingPlacement.description)}
          </div>
        )}
      </div>

      {anchorPlacement.anchor && (
        <div className={anchorContainerClassNames}>
          {buildText(mainAnchorClone.children, mainAnchorClone, anchorPlacement.anchor, headingPlacement.anchor)}
        </div>
      )}
    </div>
  );
};
