import classNames from 'classnames';
import React, { ChangeEvent, ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode, useId, useRef, useState } from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Feedback } from '../../shared/feedback';
import { Icon } from '../icon';
import './input-number.scss';

export interface InputNumberProps extends ComponentPropsWithRef<'input'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Sets the input in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below input.
   */
  feedbackText?: ReactNode;
  /**
   * Show or hide the leading icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Name of the icon leading icon to be displayed.
   */
  icon?: string;
  /**
   * Label text for increase button. Recommended for accessibility.
   */
  increaseButtonAltText?: string;
  /**
   *  Label text for decrease button. Recommended for accessibility.
   */
  decreaseButtonAltText?: string;
}

export const InnerInputNumber: ForwardRefRenderFunction<HTMLInputElement, InputNumberProps> = (
  {
    label,
    hideLabel,
    hasError,
    increaseButtonAltText = 'increase',
    decreaseButtonAltText = 'decrease',
    hasFeedback,
    feedbackState = 'info',
    feedbackText,
    hasIcon,
    icon,
    className,
    required = true,
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const { onChange, min, max } = propsClone;

  const inputWrapperRef = useRef<HTMLInputElement>(null);

  const [currentValue, setCurrentValue] = useState(propsClone.value);

  /**
   * Local onChange event handler. This functions enforces the max char limit.
   */
  const handleChangeEvent = (evt: ChangeEvent<HTMLInputElement>) => {
    const { value } = evt.currentTarget;

    setCurrentValue(value);

    onChange?.(evt);

    if (max === parseFloat(value?.toString() ?? '0') || min === parseFloat(value?.toString() ?? '0')) {
      const inputElem = inputWrapperRef.current?.querySelector('input');
      if (inputElem) {
        inputElem.focus();
      }
    }
  };

  const [isHovered, setIsHovered] = useState(false);

  /**
   * Builds main Classes for whole component
   */
  const inputClassBuilder = () => {
    return {
      // main wrapper class
      inputWrapperClasses: classNames(
        'agora-input-number-wrapper',

        {
          'flex flex-col': !propsClone.hidden,
          'input-disabled': !!propsClone.disabled,
          'input-has-error': stringToBoolean(hasError),
          'is-hovered': isHovered
        }
      ),
      // classes for above input sections [label]
      labelSectionClasses: classNames(
        'input-label-wrapper flex items-end',

        {
          'mb-8': label && !stringToBoolean(hideLabel),
          'justify-end': !label || stringToBoolean(hideLabel),
          'justify-between': label && !stringToBoolean(hideLabel)
        }
      ),
      // text input component classes
      inputClasses: classNames(
        'agora-input-number',
        {
          'has-leading-icon': hasIcon && icon?.indexOf('agora-line') === 0, // allowing only line family icon
          'has-value': currentValue && currentValue.toString().length > 0
        },
        className
      ),

      leadingIconWrapperClasses: classNames('input-leading-icon')
    };
  };

  const { inputWrapperClasses, labelSectionClasses, inputClasses, leadingIconWrapperClasses } = inputClassBuilder();

  /**
   * Execute stepUp locally and execute update value
   */
  const handleStepUp = () => {
    const inputElem = inputWrapperRef.current?.querySelector('input');

    if (inputElem) {
      inputElem.stepUp();
      inputElem.dispatchEvent(new Event('change', { bubbles: true }));
    }
  };

  /**
   * Execute stepDown locally and execute update value
   */
  const handleStepDown = () => {
    const inputElem = inputWrapperRef.current?.querySelector('input');

    if (inputElem) {
      inputElem.stepDown();
      inputElem.dispatchEvent(new Event('change', { bubbles: true }));
    }
  };

  /**
   * Sets the correct hoovering state of the component
   */
  const handleMouseHoovering = (isHover: boolean) => {
    setIsHovered(isHover);
  };

  return (
    <div hidden={!!propsClone.hidden} className={inputWrapperClasses}>
      {/* Label */}
      <div className={labelSectionClasses}>
        <label hidden={stringToBoolean(hideLabel)} htmlFor={propsClone.id} className="text-[var(--color-primary-900)] text-m-medium">
          {label}
        </label>
      </div>

      {/* Text input wrapper */}
      <div className="input-wrapper flex relative items-center" ref={inputWrapperRef}>
        <input
          {...propsClone}
          type="number"
          className={inputClasses}
          disabled={!!propsClone.disabled}
          aria-invalid={stringToBoolean(hasError)}
          aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
          ref={ref}
          required={required}
          onChange={handleChangeEvent}
        />

        {/* Input Leading Icon [only line family icon] */}
        {hasIcon && icon && icon.startsWith('agora-line') && (
          <div className={leadingIconWrapperClasses}>
            <Icon name={icon} aria-hidden />
          </div>
        )}

        {/* Steps controls */}
        <div
          className="input-number-controls"
          onFocus={() => null}
          onMouseOver={() => handleMouseHoovering(true)}
          onMouseLeave={() => handleMouseHoovering(false)}
        >
          <button
            disabled={min === parseFloat(currentValue?.toString() ?? '0') || !!propsClone.disabled || !!propsClone.readOnly}
            type="button"
            onClick={handleStepDown}
            aria-label={decreaseButtonAltText}
          >
            <Icon name="agora-line-minus-circle" aria-hidden className="icon-step icon-step-default" />
            <Icon name="agora-solid-minus-circle" aria-hidden className="icon-step icon-step-hover" />
          </button>
          <button
            disabled={max === parseFloat(currentValue?.toString() ?? '0') || !!propsClone.disabled || !!propsClone.readOnly}
            type="button"
            onClick={handleStepUp}
            aria-label={increaseButtonAltText}
          >
            <Icon name="agora-line-plus-circle" aria-hidden className="icon-step icon-step-default" />
            <Icon name="agora-solid-plus-circle" aria-hidden className="icon-step icon-step-hover" />
          </button>
        </div>
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputNumber = forwardRef<HTMLInputElement, InputNumberProps>(InnerInputNumber);
