import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode, useId } from 'react';
import { HeadingLevel } from '../../../../models';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import './card-hero-index.scss';

export interface CardHeroIndexProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * The title of the card
   */
  title?: ReactNode;
  /**
   * Title header.
   */
  headingLevel?: HeadingLevel;
  /**
   * The list of indexes
   */
  index: AnchorProps[];
}

export const CardHeroIndex: FC<CardHeroIndexProps> = ({ title, headingLevel: Heading = 'h3', index = [], className, ...props }) => {
  const generatedId = useId();

  const CardExpandableClasses = classNames('card-hero-index', className);

  return (
    <div className={CardExpandableClasses} {...props}>
      {title && <Heading className="card-hero-index-title">{title}</Heading>}

      {index.length > 0 && (
        <ul className="index-list">
          {index.map((cardIndex, idx) => {
            const key = `card-index-${generatedId}-index-item-${idx}`;
            return (
              <li key={key}>
                <Anchor {...cardIndex} appearance="link" variant="neutral">
                  {cardIndex.children}
                </Anchor>
              </li>
            );
          })}
        </ul>
      )}
    </div>
  );
};
