import classNames from 'classnames';
import React, { ChangeEvent, ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, useId, useState } from 'react';
import { BooleanProp } from '../../../models/boolean-type';
import { stringToBoolean } from '../../../utils';
import { Button } from '../button/button';
import { Icon } from '../icon/icon';
import './input-search-bar.scss';

export interface InputSearchBarProps extends ComponentPropsWithRef<'input'> {
  /**
   * Defines if the user specifies a leading icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Name of the icon leading icon to be displayed.
   */
  icon?: string;
  /**
   * Defines if the voice action button is visible.
   */
  hasVoiceActionButton?: BooleanProp;
  /**
   *  Alternative text for the voice action button
   */
  voiceActionAltText?: string;
  /**
   *  Alternative text for the search action button
   */
  searchActionAltText?: string;
  /**
   * Callback to execute whenever the user activates the microphone button.
   */
  onVoiceActivate?: () => void;
  /**
   * Callback to execute whenever the user activates the search button.
   */
  onSearchActivate?: (term: string) => void;
}

const InnerInputSearchBar: ForwardRefRenderFunction<HTMLInputElement, InputSearchBarProps> = (
  {
    className,
    hasIcon,
    icon,
    hasVoiceActionButton = true,
    voiceActionAltText = '',
    searchActionAltText = '',
    onVoiceActivate,
    onSearchActivate,
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const { onChange } = propsClone;

  const [currentTarget, setCurrentValue] = useState(propsClone.value);

  const handleChangeEvent = (evt: ChangeEvent<HTMLInputElement>) => {
    const { value } = evt.currentTarget;
    setCurrentValue(value);
    onChange?.(evt);
  };

  const inputWrapperClassNames = classNames(
    'agora-input-search-bar',
    className,
    { flex: !propsClone.hidden },
    ' flex-row items-center gap-8 bg-white p-8 ps-16 w-full'
  );

  const onVoiceButtonClick = () => {
    onVoiceActivate?.();
  };

  const onSearchButtonClick = () => {
    onSearchActivate?.(currentTarget?.toString() ?? '');
  };

  return (
    <div hidden={!!propsClone.hidden} className={inputWrapperClassNames}>
      {hasIcon && icon && icon.startsWith('agora-line') && <Icon name={icon} aria-hidden />}

      <input
        {...propsClone}
        className="flex-grow-[1] outline-none"
        ref={ref}
        type="text"
        disabled={!!propsClone.disabled}
        onChange={handleChangeEvent}
      />

      <div className="actions-container flex flex-row gap-16">
        {stringToBoolean(hasVoiceActionButton) && (
          <Button
            className="microphone-icon"
            appearance="link"
            iconOnly
            hasIcon
            leadingIcon="agora-line-mic-on"
            leadingIconHover="agora-solid-mic-on"
            aria-label={voiceActionAltText}
            onClick={onVoiceButtonClick}
          />
        )}
        <Button
          className="magnifier-icon"
          appearance="solid"
          iconOnly
          hasIcon
          leadingIcon="agora-line-search"
          leadingIconHover="agora-solid-search"
          aria-label={searchActionAltText}
          onClick={onSearchButtonClick}
        />
      </div>
    </div>
  );
};

export const InputSearchBar = forwardRef<HTMLInputElement, InputSearchBarProps>(InnerInputSearchBar);
