import React, { FC, HTMLAttributes, KeyboardEvent, ReactNode, useMemo } from 'react';
import { Icon } from '../../atoms/icon/icon';
import { SortOrder } from './models/sort-order.type';
import { SortType } from './models/sort-type.type';

const DESCENDING_ICON_NAMES: Record<SortType, string> = {
  string: 'agora-line-sort-alpha-up',
  date: 'agora-solid-sort-generic-up',
  numeric: 'agora-solid-sort-generic-up'
};

const ASCENDING_ICON_NAMES: Record<SortType, string> = {
  string: 'agora-solid-sort-alpha-down',
  date: 'agora-solid-sort-generic-down',
  numeric: 'agora-solid-sort-generic-down'
};

export interface TableHeaderCellProps extends HTMLAttributes<HTMLTableCellElement> {
  children: ReactNode;
  /**
   * The sort type applied.
   * */
  sortType?: SortType;
  /**
   * The sort order  applied.
   * */
  sortOrder?: SortOrder;
  /**
   * The default icon alt text.
   */
  defaultIconAlt?: string;
  /**
   * The ascending icon alt text.
   */
  ascendingIconAlt?: string;
  /**
   * The descending icon alt text.
   */
  descendingIconAlt?: string;
  /**
   * Callback to be called when the sort change
   */
  onSortChange?: (sortOrder: SortOrder) => void;
}

const TableHeaderCell: FC<TableHeaderCellProps> = ({
  sortType,
  sortOrder = 'none',
  onSortChange,
  defaultIconAlt,
  ascendingIconAlt,
  descendingIconAlt,
  children,
  ...props
}) => {
  const circularSortOrder = ['none', 'ascending', 'descending'] as SortOrder[];
  const currentSortOrderIndex = circularSortOrder.findIndex((order) => order === sortOrder);

  const sortIconName = useMemo(() => {
    if (!sortType) {
      return '';
    }

    if (sortOrder === 'ascending') {
      return ASCENDING_ICON_NAMES[sortType];
    }

    if (sortOrder === 'descending') {
      return DESCENDING_ICON_NAMES[sortType];
    }

    return 'agora-solid-sort-vertical';
  }, [sortType, sortOrder]);

  const sortIconAlt = useMemo(() => {
    if (!sortType) {
      return '';
    }

    if (sortOrder === 'ascending') {
      return ascendingIconAlt;
    }

    if (sortOrder === 'descending') {
      return descendingIconAlt;
    }

    return defaultIconAlt;
  }, [sortType, sortOrder]);

  const applySort = () => {
    if (!sortType) {
      return '';
    }

    let nextSortOrder = currentSortOrderIndex + 1;

    if (nextSortOrder >= circularSortOrder.length) {
      nextSortOrder = 0;
    }

    onSortChange?.(circularSortOrder[nextSortOrder]);
  };

  const handleKeyUpHeader = (evt: KeyboardEvent<HTMLButtonElement>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      applySort();
    }
  };

  return (
    <th {...props} className={props.className}>
      {sortType && (
        <button className="table-header-cell" onClick={applySort} onKeyUp={handleKeyUpHeader}>
          <div className="w-24">
            <Icon name={sortIconName} alt={sortIconAlt} />
          </div>
          {children}
        </button>
      )}

      {!sortType && <span className="table-header-cell">{children}</span>}
    </th>
  );
};

export { TableHeaderCell };
