import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode, useId } from 'react';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { Icon } from '../../../atoms/icon';
import { List } from '../../../atoms/list';
import './card-links.scss';
import { HeadingLevel } from '../../../../models/heading-level-type';

/**
 * The available variants of the card links
 */
export type CardLinkVariant = 'default' | 'dark';

export interface CardLinksProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * Heading level to apply in the card.
   */
  headingLevel?: HeadingLevel;
  /**
   * The category of the card
   */
  category?: ReactNode;
  /**
   * The title of the card
   */
  title: ReactNode;
  /**
   * The description of the card
   */
  description?: ReactNode;
  /**
   * The list of links of the card
   */
  listLinks: AnchorProps[];
  /**
   * The list of topics of the card
   */
  topics?: string[];
  /**
   * The variant of the card
   */
  variant?: CardLinkVariant;
}

export const CardLinks: FC<CardLinksProps> = ({
  headingLevel: Heading = 'h2',
  title,
  category,
  description,
  listLinks = [],
  topics,
  variant = 'default',
  className = '',
  ...props
}) => {
  const generatedId = useId();

  const CardLinksClasses = classNames('card-links', `card-${variant}`, className);

  const headingPlacement = {
    title: !!title,
    description: !title && !!description,
    category: !title && !description && !!category
  };

  const buildText = (text: ReactNode, isHeading: boolean): ReactNode => {
    return isHeading ? <Heading>{text}</Heading> : text;
  };

  return (
    <div className={CardLinksClasses} {...props}>
      <div className="body-container">
        {category && <div className="category">{buildText(category, headingPlacement.category)}</div>}

        {title && <div className="title">{buildText(title, headingPlacement.title)}</div>}

        {description && <div className="description">{buildText(description, headingPlacement.description)}</div>}
      </div>

      {listLinks.length > 0 && (
        <ul className="links">
          {listLinks.map((link, index) => {
            const anchorKey = `card-link-${generatedId}-link-${index}`;
            return (
              <li key={anchorKey} className="link-container">
                <Anchor
                  appearance="link"
                  href={link.href}
                  darkMode={variant === 'dark'}
                  variant={variant === 'default' ? 'primary' : 'neutral'}
                  hasIcon={link.hasIcon ?? true}
                  trailingIcon={link.trailingIcon ?? 'agora-line-arrow-right-circle'}
                  trailingIconHover={link.trailingIcon ?? 'agora-line-arrow-right-circle'}
                >
                  {link.children}
                </Anchor>
              </li>
            );
          })}
        </ul>
      )}

      {topics && topics.length > 0 && (
        <List className="topics">
          {topics.map((topic, index) => {
            const listItem = `card-link-${generatedId}-topic-${index}`;

            return (
              <li key={listItem} className="topic">
                {topic}
                {index !== topics.length - 1 && <Icon name="agora-solid-bullet" alt="bullet" />}
              </li>
            );
          })}
        </List>
      )}

      <hr />
    </div>
  );
};
