import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode } from 'react';
import './tab-body.scss';

export interface TabBodyProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Children content.
   */
  children?: ReactNode;
}

export const TabBody: FC<TabBodyProps> = ({ children, className = '', ...props }) => {
  const TabBodyClassName = classNames('tab-body', className);
  return (
    <div className={TabBodyClassName} {...props}>
      {children}
    </div>
  );
};
