import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC } from 'react';
import { Button } from '../button/button';
import { Icon } from '../icon';
import { useToastContext } from './toast-context';
import './toast.scss';

export type ToastType = 'success' | 'failure' | 'warning' | 'info';

export interface ToastProps extends Omit<ComponentPropsWithoutRef<'div'>, 'id'> {
  /**
   * The id of the toast
   */
  id: number;
  /**
   * Title of the toast
   */
  title: string;
  /**
   * Description of the toast
   */
  description: string;
  /**
   * Type variant of the toast
   */
  type: ToastType;
  /**
   * Alternative text to apply to the toast close button
   */
  closeLabel: string;
}

export const Toast: FC<ToastProps> = ({ className, id, title, description, type, closeLabel }: ToastProps) => {
  const { removeToast } = useToastContext();

  const getIconNameForType = (selectedType: ToastType): string => {
    switch (selectedType) {
      case 'success':
        return 'agora-solid-check-circle';
      case 'failure':
        return 'agora-solid-alert-triangle';
      case 'warning':
        return 'agora-solid-info-mark';
      default:
        return 'agora-solid-info-mark';
    }
  };

  const handleCloseClick = () => {
    removeToast({
      id,
      type,
      title,
      description,
      closeLabel
    } as ToastProps);
  };

  const toastContainerClassnames = classNames('toast', `toast-${type}`, className);

  return (
    <div className={toastContainerClassnames} role="alert" key={id}>
      <Icon className="toast-icon mt-[10px]" name={getIconNameForType(type)} dimensions="m" aria-hidden />

      <div className="toast-messages flex flex-col gap-8">
        <p className="toast-title mt-8">{title}</p>
        <p className="toast-description">{description}</p>
      </div>

      <Button
        className="toast-close-button"
        appearance="link"
        variant="neutral"
        onClick={handleCloseClick}
        aria-label={closeLabel}
        hasIcon
        iconOnly
        trailingIcon="agora-line-x"
        trailingIconHover="agora-solid-x"
      ></Button>
    </div>
  );
};
