import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ImgHTMLAttributes, ReactNode, useRef } from 'react';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { Icon } from '../../../atoms/icon';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-search-result.scss';

export interface CardSearchResultProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Card category label.
   */
  category?: ReactNode;
  /**
   * Card date label.
   */
  date?: string;
  /**
   * Card heading level.
   */
  headingLevel?: HeadingLevel;
  /**
   * Anchor props to render in heading.
   */
  mainAnchor: AnchorProps;
  /**
   * Card description label.
   */
  description?: ReactNode;
  /**
   * card topics.
   */
  topics?: Array<ReactNode>;
  /**
   * card image.
   */
  image?: ImgHTMLAttributes<HTMLImageElement>;
  /**
   * Makes the whole card clickable.
   */
  blockedLink?: BooleanProp;
}

export const CardSearchResult: FC<CardSearchResultProps> = ({
  category,
  date,
  headingLevel: Heading = 'h3',
  description,
  topics,
  image,
  mainAnchor,
  blockedLink = false,
  className,
  ...props
}) => {
  const cardSearchResultRef = useRef<HTMLDivElement | null>(null);

  const anchorRef = useRef<HTMLAnchorElement | null>(null);

  const CardSearchResultClass = classNames('card-search-result', className);

  const containerClass = classNames(
    'flex flex-col gap-8',
    { 'xl:col-span-3 md:col-span-2': image },
    { 'xl:col-span-full md:col-span-full': !image }
  );

  useBlockedLink({
    anchorRef,
    parentCardRef: cardSearchResultRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  return (
    <div ref={cardSearchResultRef} role="presentation" className={CardSearchResultClass} {...props}>
      {category && <div className="category">{category}</div>}

      <div className="content-body">
        <div className={containerClass}>
          {date && <div className="date-container">{date}</div>}

          <Heading className="title">
            <Anchor {...mainAnchor} ref={anchorRef} />
          </Heading>

          {description && <div className="description-container">{description}</div>}
        </div>

        {image && (
          <div className="container-image xl:col-span-1 md:col-span-2 w-full">
            <img src={image.src} className="card-image w-full" alt={image.alt} />
          </div>
        )}
      </div>

      {topics && topics.length > 0 && (
        <div className="topic-list">
          {topics.map((topic, index) => (
            <div key={`key-${Number(index)}`} className="topic-item">
              {index > 0 && <Icon aria-hidden className="fill-neutral-500" name="agora-solid-bullet" />}
              {topic}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};
