import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ImgHTMLAttributes, ReactElement } from 'react';
import { CardHeroHelperProps } from '../card-hero-helper';
import { CardHeroIndexProps } from '../card-hero-index';
import { CardHeroSitemapProps } from '../card-hero-sitemap';
import './card-full-image.scss';
export interface CardFullImageProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Children content.
   */
  children: ReactElement<CardHeroSitemapProps | CardHeroIndexProps | CardHeroHelperProps>;
  /**
   * The image to be displayed.
   */
  image: ImgHTMLAttributes<HTMLImageElement>;
}

export const CardFullImage: FC<CardFullImageProps> = ({ image, ...props }) => {
  const cardFullImageClassName = classNames('card-full-image', props.className);

  return (
    <div className={cardFullImageClassName} {...props}>
      <div className="card-background">
        <div className="card-image-container">
          <img className="card-image" src={image.src} alt={image.alt} aria-hidden={image['aria-hidden'] ?? true} />
        </div>
      </div>
      <div className="card-content">
        <div className="grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-x-32">
          <div className="xs:col-span-4 md:col-span-8 xl:col-span-5 xl:col-start-7">{props.children}</div>
        </div>
      </div>
    </div>
  );
};
