import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, useId } from 'react';
import './loader-dialog.scss';

export interface LoaderDialogProps extends Omit<ComponentPropsWithoutRef<'div'>, 'title'> {
  /**
   * The title of the loader.
   */
  title?: string;
  /**
   * The subtitle of the loader.
   */
  subtitle?: string;
}
export const LoaderDialog: FC<LoaderDialogProps> = ({ title, subtitle, ...props }) => {
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;
  const loaderClass = classNames('loader-dialog');

  return (
    <div
      id={propsClone.id}
      {...propsClone}
      className="bg-[var(--color-backdrop)] absolute z-dialog w-full h-full flex flex-col justify-center items-center"
    >
      <div className="flex flex-col gap-[64px] justify-center items-center w-full">
        {(title || subtitle) && (
          <div className="flex w-full flex-col gap-8 justify-center items-center text-center">
            {title && (
              <div className="xl:text-2xl-semibold md:text-2xl-semibold  xs:text-l-semibold text-white xl:line-clamp-1 md:line-clamp-1 xs:line-clamp-2">
                {title}
              </div>
            )}
            {subtitle && (
              <div className="xl:text-2xl-light md:text-2xl-light xs:text-m-light text-white xl:line-clamp-1 md:line-clamp-1 xs:line-clamp-2">
                {subtitle}
              </div>
            )}
          </div>
        )}
        <div className={loaderClass} role="status">
          <span className="!absolute !-m-px !h-px !w-px !overflow-hidden !whitespace-nowrap !border-0 !p-0 ![clip:rect(0,0,0,0)]"></span>
        </div>
      </div>
    </div>
  );
};
