import classNames from 'classnames';
import React, { cloneElement, ComponentPropsWithoutRef, FC, ReactElement, useId } from 'react';
import './step-list.scss';

export interface StepListProps extends ComponentPropsWithoutRef<'ol'> {
  /**
   * Children content.
   */
  children?: ReactElement<HTMLLIElement> | Array<ReactElement<HTMLLIElement>>;
  /**
   * Array the item.
   */
  items?: Array<ReactElement<HTMLLIElement>>;
}

export const StepList: FC<StepListProps> = ({ children, items = [], ...props }) => {
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;
  const StepLisClass = classNames(`step-list`);

  let stepListChildren: Array<ReactElement> = [];
  if (children) {
    if (!Array.isArray(children)) {
      stepListChildren = [children];
    } else {
      stepListChildren = children;
    }
  }

  const StepListItems = items?.map((data, index) => {
    const idLi = `${generatedId}-${index}`;
    const key = `li-${index}`;
    return cloneElement(data, { id: idLi, key });
  });

  const allItems = [...StepListItems, ...stepListChildren];
  return (
    <ol className={StepLisClass} id={propsClone.id} {...propsClone}>
      {allItems}
    </ol>
  );
};
