import classNames from 'classnames';
import React, {
  ChangeEvent,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  ReactNode,
  useCallback,
  useId,
  useState
} from 'react';
import { BooleanProp } from '../../../models/boolean-type';
import { Button } from '../button/button';
import { FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Feedback } from '../../shared/feedback';
import './input-email-bar.scss';

const EMAIL_REGEXP = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;

export interface InputEmailBarProps extends ComponentPropsWithRef<'input'> {
  /**
   * If true, the label is displayed above the input.
   */
  hasLabel?: BooleanProp;
  /**
   * Label to display above the input.
   */
  label?: string;
  /**
   * Callback to execute when the user clicks on the send button.
   */
  onSendHandler?: (email: string) => void;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Text informative displayed below input text.
   */
  feedbackText?: ReactNode;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Indicates the feedback state of feedback text. This param will change the left feedback icon and dye it.
   */
  hasError?: BooleanProp;
}

const InnerInputEmailBar: ForwardRefRenderFunction<HTMLInputElement, InputEmailBarProps> = (
  {
    className,
    hasLabel,
    label,
    hasFeedback,
    feedbackState = 'info',
    feedbackText,
    hasError,
    required = true,
    onSendHandler,
    pattern = EMAIL_REGEXP,
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const { onChange } = propsClone;

  const [email, setEmail] = useState<string>('');
  const [isValidEmail, setIsValidEmail] = useState<boolean>(false);

  const handleChangeEvent = useCallback(
    (evt: ChangeEvent<HTMLInputElement>) => {
      const { value } = evt.currentTarget;
      setEmail(value);
      setIsValidEmail(new RegExp(pattern).test(value));
      onChange?.(evt);
    },
    [onChange, pattern]
  );

  const onSendButtonClick = useCallback(() => {
    if (onSendHandler && new RegExp(pattern).test(email)) {
      onSendHandler(email);
    }
  }, [onSendHandler, pattern, email]);

  const inputWrapperClassNames = classNames('agora-input-email-bar', className);

  return (
    <div hidden={!!propsClone.hidden} className={inputWrapperClassNames}>
      {hasLabel && (
        <label className="agora-input-email-bar-label" htmlFor={propsClone.id}>
          {label}
        </label>
      )}
      <div className={`agora-input-email-bar-container ${propsClone.disabled ? 'disabled' : ''}`}>
        <input
          {...propsClone}
          className="flex-grow-[1] outline-none"
          ref={ref}
          type="email"
          disabled={!!propsClone.disabled}
          onChange={handleChangeEvent}
          required={required}
        />

        <div className="actions-container">
          <Button
            className="magnifier-icon"
            appearance="solid"
            iconOnly
            hasIcon
            leadingIcon="agora-line-plane"
            leadingIconHover="agora-solid-plane"
            aria-label={hasLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
            onClick={onSendButtonClick}
            disabled={!isValidEmail}
          />
        </div>
      </div>
      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputEmailBar = forwardRef<HTMLInputElement, InputEmailBarProps>(InnerInputEmailBar);
