import classNames from 'classnames';
import React, { FC, HTMLProps, ReactNode } from 'react';
import { BreadcrumbDesktop } from './desktop/breadcrumb-desktop';
import { BreadcrumbMobile } from './mobile/breadcrumb-mobile';

export interface BreadcrumbLink {
  /**
   * The Breadcrumb page link url.
   */
  url: string;
  /**
   * The Breadcrumb page link label.
   */
  label: string;
}

export interface BreadcrumbProps extends HTMLProps<HTMLDivElement> {
  /**
   * The links that make up the breadcrumb in hierarchical order.
   */
  items?: BreadcrumbLink[];
  /**
   * The flag to set Dark mode.
   */
  darkMode?: boolean;
  /**
   *  Section Title. Only visible on mobile resolutions.
   */
  sectionTitle?: ReactNode;
}

export const Breadcrumb: FC<BreadcrumbProps> = ({ items = [], ...props }) => {
  const baseClasses = classNames('agora-breadcrumb', props.className, {
    'bg-[var(--color-primary-900)] dark-mode': props.darkMode
  });

  const desktopBreadcrumbClassnames = classNames(baseClasses, 'xs:hidden xl:flex');
  const mobileBreadcrumbClassnames = classNames(baseClasses, 'xs:flex xl:hidden');

  return (
    <>
      <BreadcrumbDesktop {...props} items={items} className={desktopBreadcrumbClassnames} />
      <BreadcrumbMobile {...props} items={items} className={mobileBreadcrumbClassnames} />
    </>
  );
};
