import classNames from 'classnames';
import React, {
  forwardRef,
  ForwardRefRenderFunction,
  HTMLAttributes,
  ReactNode,
  Ref,
  useCallback,
  useEffect,
  useId,
  useImperativeHandle,
  useMemo,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../../models/boolean-type';
import { useInternalDropdownContext } from './internal-dropdown-context';
import { InternalDropdownFilterSection, InternalDropdownFilterSectionRef } from './internal-dropdown-filter-section';
import { InternalNoResults } from './internal-dropdown-no-results';
import { InternalDropdownOptionProp } from './internal-dropdown-option';
import { InternalDropdownSection } from './internal-dropdown-section';
import { InternalDropdownSelectAllSection } from './internal-dropdown-select-all-section';
import './internal-dropdown.scss';

export interface InternalDropdownRef {
  listboxElem: HTMLDivElement | null;
  resetFilter: () => void;
}

export interface InternalDropdownProps extends Omit<HTMLAttributes<HTMLDivElement>, 'onChange'> {
  optionsVisible?: number;

  onChange?: (options: InternalDropdownOptionProp[]) => void;

  searchable?: BooleanProp;

  searchAriaLabel?: string;

  searchPlaceholder?: string;

  noSearchResults?: ReactNode;

  searchDebounceTimeout?: number;

  onInputSearchChange?: (value: string) => void;

  selectAllLabel?: string;
}

const Component: ForwardRefRenderFunction<InternalDropdownRef | null, InternalDropdownProps> = (
  { selectAllLabel, searchable, optionsVisible = 6, noSearchResults = '', ...props }: InternalDropdownProps,
  ref: Ref<InternalDropdownRef | null>
) => {
  const context = useInternalDropdownContext();
  const visibility = context.visibility;
  const activeFilter = context.activeFilter;
  const sections = context.sections;
  const options = context.options;
  const type = context.type;

  const filterRef = useRef<InternalDropdownFilterSectionRef>(null);
  const resetFilter = useCallback(() => {
    if (searchable && filterRef.current) {
      filterRef.current.resetFilter();
    }
  }, [searchable]);

  const innerRef = useRef<HTMLDivElement>(null);
  useImperativeHandle(
    ref,
    () => {
      return {
        listboxElem: innerRef.current,
        resetFilter
      } as InternalDropdownRef;
    },
    [resetFilter]
  );

  const [maxHeightStyleProp, setMaxHeightStyleProp] = useState('');

  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const getFilterSectionHeight = useCallback(() => {
    if (innerRef.current && searchable) {
      const queryResults = innerRef.current.querySelectorAll('.agora-dropdown-filter-section-item');

      const elem = (queryResults[0] as HTMLDivElement) ?? null;

      if (elem) {
        const val = elem.getAttribute('data-filter-section-height');
        return val ? Number(val) : 0;
      }
    }

    return 0;
  }, [searchable]);

  const getSelectAllSectionHeight = useCallback(() => {
    if (innerRef.current && type === 'checkbox') {
      const queryResults = innerRef.current.querySelectorAll('.agora-dropdown-select-all-section-item');

      const elem = (queryResults[0] as HTMLDivElement) ?? null;

      if (elem) {
        const val = elem.getAttribute('data-select-all-section-height');
        return val ? Number(val) : 0;
      }
    }

    return 0;
  }, [type]);

  const getVisibleSectionNamesHeight = useCallback((visibleSections: string[]) => {
    let heightCount = 0;

    if (innerRef.current) {
      const queryResults = innerRef.current.querySelectorAll('.agora-dropdown-section .section-name');

      queryResults.forEach((elem) => {
        const sectionName = elem.getAttribute('data-section-option-id');

        if (sectionName && visibleSections.includes(sectionName)) {
          const val = elem.getAttribute('data-section-name-height');
          heightCount = heightCount + (val ? Number(val) : 0);
        }
      });
    }

    return heightCount;
  }, []);

  const getVisibleOptionsHeight = useCallback(
    (visibleOptions: InternalDropdownOptionProp[]) => {
      let heightCount = 0;

      if (innerRef.current) {
        const visibleOptionsKeys = visibleOptions.map((opt) => {
          return `${opt.name}-${type}-${opt.value}`;
        });

        const queryResults = innerRef.current.querySelectorAll('.agora-dropdown-section .section-option');

        queryResults.forEach((elem) => {
          const optionId = elem.getAttribute('data-section-option-id');

          if (optionId && visibleOptionsKeys.includes(optionId)) {
            const val = elem.getAttribute('data-section-option-height');
            heightCount = heightCount + (val ? Number(val) : 0);
          }
        });
      }

      return heightCount;
    },
    [type]
  );

  const getListboxBorderWidths = useCallback(() => {
    if (!innerRef.current) {
      return 0;
    }

    const computedStyle = getComputedStyle(innerRef.current);

    if (computedStyle) {
      const borderTopWidth = parseFloat(computedStyle.borderTopWidth);
      const borderBottomWidth = parseFloat(computedStyle.borderBottomWidth);
      return borderTopWidth + borderBottomWidth;
    }

    return 0;
  }, [innerRef]);

  useEffect(() => {
    if (visibility) {
      const visibleOptions = options.filter((opt) => !opt.filtered);

      if (visibleOptions.length > optionsVisible) {
        const containedOptions = visibleOptions.slice(0, optionsVisible);
        const visibleSections = [...new Set(containedOptions.map((opt) => opt.name ?? '').filter((opt) => !!opt))];

        let maxHeight = 0;
        //  GET FILTER SECTION HEIGHT
        maxHeight = maxHeight + getFilterSectionHeight();
        //  GET SELECT ALL SECTION HEIGHT
        maxHeight = maxHeight + getSelectAllSectionHeight();
        //  GET VISIBLE SECTIONS NAMES HEIGHT
        maxHeight = maxHeight + getVisibleSectionNamesHeight(visibleSections);
        //  GET NON FILTERED OPTIONS HEIGHT
        maxHeight = maxHeight + getVisibleOptionsHeight(containedOptions);

        if (maxHeight > 0) {
          //  GET LISTBOX BORDERS ONLY IF THERE ARE SOME OPTIONS VISIBLE
          maxHeight = maxHeight + getListboxBorderWidths();
          setMaxHeightStyleProp(`${maxHeight}px`);
        } else {
          setMaxHeightStyleProp('none');
        }
      } else {
        setMaxHeightStyleProp('none');
      }
    }
  }, [
    visibility,
    type,
    activeFilter,
    options,
    optionsVisible,
    getFilterSectionHeight,
    getSelectAllSectionHeight,
    getVisibleSectionNamesHeight,
    getVisibleOptionsHeight,
    getListboxBorderWidths
  ]);

  const dropdownClasses = classNames('agora-dropdown', propsClone.className, { visible: visibility });

  const hasOptions = useMemo(() => {
    return options.length > 0;
  }, [options]);

  const hasSectionsWithResults = useMemo(() => {
    return options.some((opt) => !opt.filtered);
  }, [options]);

  const allSectionResultsDisabled = useMemo(() => {
    const visibleResults = options.filter((opt) => !opt.filtered);
    return hasSectionsWithResults && visibleResults.findIndex((opt) => !opt.disabled) === -1;
  }, [hasSectionsWithResults, options]);

  const sectionWithResults = useMemo(() => {
    return options.filter((opt) => !opt.filtered).map((opt) => opt.name);
  }, [options]);

  const hasMoreThanOneSection = sections.length > 1;

  return (
    <div
      className={dropdownClasses}
      ref={innerRef}
      aria-label={hasMoreThanOneSection ? props['aria-label'] : ''}
      role={hasMoreThanOneSection ? 'list' : 'none'}
      style={{
        maxHeight: maxHeightStyleProp === '' ? '0px' : maxHeightStyleProp,
        overflowY: maxHeightStyleProp === '' ? 'hidden' : 'auto'
      }}
    >
      {searchable && (
        <InternalDropdownFilterSection ref={filterRef} aria-label={propsClone.searchAriaLabel} placeholder={propsClone.searchPlaceholder} />
      )}

      {type === 'checkbox' && hasSectionsWithResults && !allSectionResultsDisabled && (
        <InternalDropdownSelectAllSection label={selectAllLabel} />
      )}

      {hasSectionsWithResults &&
        sections
          .filter((s) => sectionWithResults.indexOf(s.value) >= 0)
          .map((section, index) => {
            const key = `agora-dropdown-option-${propsClone.id}-${index}`;
            return <InternalDropdownSection key={key} {...section} />;
          })}

      {(!hasOptions || !hasSectionsWithResults) && <InternalNoResults label={noSearchResults} />}
    </div>
  );
};

export const InternalDropdown = forwardRef<InternalDropdownRef | null, InternalDropdownProps>(Component);
