import React, { FC } from 'react';
import { Button } from '../../../atoms/button';
import { Icon } from '../../../atoms/icon';
import { useDatePickerStateContext } from '../contexts/use-date-picker-state-context';

export const DialogHeader: FC = () => {
  const ctx = useDatePickerStateContext();

  return (
    <div className="header w-full">
      <h2 className="flex justify-center py-8 w-full text-black font-bold text-m-light normal-case" aria-live="polite">
        {`${ctx.focusedMonth} ${ctx.focusedYear}`}
      </h2>

      <div className="flex flex-row justify-between">
        <div className="flex gap-8 justify-center">
          <Button appearance="outline" onClick={ctx.moveToPreviousYear} aria-label={ctx.getLabels().previousYearAriaLabel}>
            <Icon name="agora-line-chevrons-left" dimensions="s" aria-hidden />
          </Button>

          <Button appearance="outline" onClick={ctx.moveToPreviousMonth} aria-label={ctx.getLabels().previousMonthAriaLabel}>
            <Icon name="agora-line-chevron-left" dimensions="s" aria-hidden />
          </Button>
        </div>

        <div className="flex gap-8 justify-center">
          <Button appearance="outline" onClick={ctx.moveToNextMonth} aria-label={ctx.getLabels().nextMonthAriaLabel}>
            <Icon name="agora-line-chevron-right" dimensions="s" aria-hidden />
          </Button>

          <Button appearance="outline" onClick={ctx.moveToNextYear} aria-label={ctx.getLabels().nextYearAriaLabel}>
            <Icon name="agora-line-chevrons-right" dimensions="s" aria-hidden />
          </Button>
        </div>
      </div>
    </div>
  );
};
