// TimelineEvent.tsx
import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, MouseEvent as ReactMouseEvent, ReactNode, useRef } from 'react';
import { BooleanProp } from '../../../../../models';
import { stringToBoolean } from '../../../../../utils';
import './timeline-event.scss';
export interface TimelineEventProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Hide the label
   */
  hideLabel?: BooleanProp;
  /**
   * Label for the event
   */
  label?: string;
  /**
   * Additional class names
   */
  className?: string;
  /**
   * Is the event active
   */
  active?: boolean;
  /**
   * Has an icon
   */
  hasIcon?: BooleanProp;
  /**
   * Icon name
   */
  icon?: string;
  /**
   * Icon alt text
   */
  altIcon?: string;
  /**
   * Determines if the timeline event is interactive or not
   */
  interactive?: BooleanProp;
  /**
   * Children
   */
  children: ReactNode;
}

export const TimelineEvent: FC<TimelineEventProps> = ({ label, hideLabel, active, className, interactive, ...props }) => {
  const eventRef = useRef<HTMLDivElement>(null);

  const handleClick = (evt: ReactMouseEvent<HTMLDivElement>) => {
    props.onClick?.(evt);
  };

  const handleKeyUp = (evt: React.KeyboardEvent<HTMLDivElement>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      if (eventRef.current) {
        const newEvt = new MouseEvent('click', {
          detail: 1
        });
        eventRef.current?.dispatchEvent(newEvt);
      }
    }
  };

  const eventWithoutChildren = { ...props };
  delete eventWithoutChildren.children;

  const timelineEventClassNames = classNames('timeline-step', className, { active: active });
  const timelineLabelClassNames = classNames('timeline-label', { 'invisible h-0 overflow-hidden': stringToBoolean(hideLabel) });

  return (
    <div
      ref={eventRef}
      className={timelineEventClassNames}
      tabIndex={interactive ? 0 : -1}
      role="button"
      aria-current={active}
      onClickCapture={handleClick}
      onKeyUp={handleKeyUp}
      aria-label={stringToBoolean(hideLabel) ? eventWithoutChildren['aria-label'] ?? label : eventWithoutChildren['aria-label']}
    >
      <div className={timelineLabelClassNames}>{label}</div>

      <div className="timeline-event-container">
        <div className="timeline-event-left-indicator" />
        <div className="timeline-event-dot">
          <div className="timeline-event" />
        </div>
        <div className="timeline-event-right-indicator" />
      </div>
    </div>
  );
};
