import React, { FC } from 'react';
import { TimelineEventProps } from '../timeline-event/timeline-event';
import { Icon } from '../../../../atoms/icon';
import './timeline-event-vertical.scss';

export const TimelineEventVertical: FC<TimelineEventProps> = ({ ...props }) => {
  return (
    <div className="timeline-item">
      <div className="timeline-event">
        {!props.hasIcon ? (
          <div className="timeline-dot" />
        ) : (
          <div className="timeline-icon">
            <Icon name={props.icon} alt={props.altIcon} />
          </div>
        )}
        <div className="timeline-line" />
      </div>
      <div className="timeline-content">{props.children}</div>
    </div>
  );
};
