import classNames from 'classnames';
import React, { Children, cloneElement, FC, HtmlHTMLAttributes, isValidElement, ReactElement, useId, useMemo, useState } from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { ButtonProps, ButtonVariant } from '../../atoms/button';
import './button-group.scss';

export interface ButtonGroupProps extends Omit<HtmlHTMLAttributes<HTMLDivElement>, 'onChange'> {
  /**
   * The flag to set Dark mode.
   */
  darkMode?: BooleanProp;
  /**
   *  button variant.
   */
  variant?: ButtonVariant;
  /**
   * Defines if the anchor should be fluid
   */
  fullWidth?: BooleanProp;
  /**
   * Group name
   */
  name?: string;
  /**
   *  List of button components to use as children of the group.
   */
  children?: ReactElement<ButtonProps> | Array<ReactElement<ButtonProps>>;
  /**
   *  Event triggered whenever any of the buttons changes is own state.
   */
  onChange?: (selectedButtons: Array<ReactElement<ButtonProps>>) => void;
  /**
   * Defines if the group allows multiple selection.
   */
  multiple?: BooleanProp;
}
const ButtonGroup: FC<ButtonGroupProps> = ({ variant = 'primary', multiple, fullWidth = false, onChange, children, ...props }) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const [selectedButtonIndexes, setSelectedButtonIndexes] = useState<number[]>([]);

  const childrenButtons: Array<ReactElement<ButtonProps>> = useMemo(() => {
    const ret: Array<ReactElement<ButtonProps>> = [];

    if (Array.isArray(children)) {
      children.forEach((child, index) => {
        if (isValidElement<ButtonProps>(child)) {
          if (child.props.selected) {
            setSelectedButtonIndexes((last) => {
              if (multiple) {
                const rawIndexes = [...last, index];
                return [...new Set(rawIndexes)];
              }

              return [index];
            });
          }

          ret.push(child);
        }
      });
    } else if (isValidElement<ButtonProps>(children)) {
      setSelectedButtonIndexes([0]);
      ret.push(children);
    }

    return ret;
  }, [children, setSelectedButtonIndexes, multiple]);

  const handleButtonClick = (index: number) => {
    const newSelectedButtonIndexes = [...selectedButtonIndexes];
    if (stringToBoolean(multiple)) {
      const isSelected = selectedButtonIndexes.includes(index);
      if (isSelected) {
        const buttonIndex = newSelectedButtonIndexes.indexOf(index);
        newSelectedButtonIndexes.splice(buttonIndex, 1);
      } else {
        newSelectedButtonIndexes.push(index);
      }
    } else {
      const isSelected = selectedButtonIndexes.length > 0 && selectedButtonIndexes[0] === index;
      if (isSelected) {
        newSelectedButtonIndexes.splice(0, 1);
      } else {
        newSelectedButtonIndexes.splice(0, newSelectedButtonIndexes.length, index);
      }
    }

    setSelectedButtonIndexes(newSelectedButtonIndexes);

    if (onChange) {
      const selectedButtons = childrenButtons.filter((_, indx) => newSelectedButtonIndexes.includes(indx));
      onChange(selectedButtons);
    }
  };

  const classes = classNames(
    'button-group',
    propsClone.className,
    { full: stringToBoolean(fullWidth) },
    { darkMode: stringToBoolean(propsClone.darkMode) }
  );

  return (
    <div className="agora-button-group">
      {/* button group */}
      <div {...propsClone} className={classes} role="group" aria-label={propsClone.name} id={propsClone.id}>
        {Children.map(childrenButtons, (child, index) => {
          const isSelected = selectedButtonIndexes.includes(index);

          return cloneElement<ButtonProps>(child, {
            ...child.props,
            id: child.props.id ?? `${propsClone.id}-button-${index}`,
            variant: isSelected ? variant : 'neutral',
            appearance: 'solid',
            onClick: (evt) => {
              handleButtonClick(index);
              child.props.onClick?.(evt);
            },
            selected: isSelected,
            darkMode: propsClone.darkMode
          } as ButtonProps);
        })}
      </div>
    </div>
  );
};

export { ButtonGroup };
