import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction } from 'react';

export interface SearchPaginationNumberOverlayProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Dots value.
   */
  value?: string;
}

export const InnerSearchPaginationNumberOverlayProps: ForwardRefRenderFunction<HTMLDivElement, SearchPaginationNumberOverlayProps> = (
  { className, value, ...props },
  ref
) => {
  const classes = classNames('nav-number-overlay', className);

  return (
    <div {...props} ref={ref} className={classes}>
      {value}
    </div>
  );
};

export const SearchPaginationNumberOverlay = forwardRef<HTMLDivElement, SearchPaginationNumberOverlayProps>(
  InnerSearchPaginationNumberOverlayProps
);
