import { useEffect, useRef, useState } from 'react';

// eslint-disable-next-line @typescript-eslint/ban-types
const debounce = (func: Function, timeout = 300) => {
  let timer: any;

  return (...args: any) => {
    if (timer) {
      clearTimeout(timer);
      timer = undefined;
    }

    timer = setTimeout(() => {
      func(args);
    }, timeout);
  };
};

export { debounce };

export const useDebounceHook = (func: any, timeout = 300) => {
  const timer = useRef<any>(null);
  const [args, setArgs] = useState<any>(null);

  useEffect(() => {
    if (timer.current) {
      clearTimeout(timer.current);
    }

    timer.current = setTimeout(() => {
      func(args);
    }, timeout);

    return () => {
      if (timer.current) {
        clearTimeout(timer.current);
      }
    };
  }, [func, timeout, args]);

  return {
    setArgs
  };
};
