import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  FC,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  MouseEvent,
  ReactNode,
  Ref,
  useImperativeHandle,
  useRef
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Icon } from '../icon';
import './anchor.scss';

/**
 * The available variants of the anchors. This variants will define the background and border color of the anchors.
 */
export type AnchorVariant = 'primary' | 'informative' | 'neutral' | 'success' | 'warning' | 'danger';

/**
 * The available anchor appearances. Defines the anchor shape.
 */
export type AnchorAppearance = 'link' | 'text';

const IconWrapper: FC<{
  name: string;
  iconHover: string;
  iconAltText?: string;
  className?: string;
}> = ({ name = 'no-icon', iconHover = 'no-icon', iconAltText, className }) => {
  const wrapperClassName = classNames('icon-wrapper', className);

  return (
    <div className={wrapperClassName}>
      <Icon className="line" name={name.replace('solid', 'line')} dimensions="m" alt={iconAltText} aria-hidden={!iconAltText} />
      <Icon
        className="solid"
        name={(iconHover ?? name).replace('line', 'solid')}
        dimensions="m"
        alt={iconAltText}
        aria-hidden={!iconAltText}
      />
    </div>
  );
};

export interface AnchorProps extends ComponentPropsWithRef<'a'> {
  /**
   * The flag to set Dark mode.
   */
  darkMode?: BooleanProp;
  /**
   * The anchor appearance. Define the anchor Shape.
   */
  appearance?: AnchorAppearance;
  /**
   * The variant of the anchor. This variants will define the background and border color of the given Anchor
   */
  variant?: AnchorVariant;
  /**
   * Defines if the anchor should be fluid
   */
  fullWidth?: BooleanProp;
  /**
   * Defines if the anchor uses an icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Defines if children content it is to be rendered.
   */
  iconOnly?: BooleanProp;
  /**
   * Defines the name of the leading icon to use.
   */
  leadingIcon?: string;
  /**
   * Defines the name of the leading icon to be displayed on mouse hover. If none specified, it will the appropriate variant of the icon.
   */
  leadingIconHover?: string;
  /**
   * Alternative text for the leading icon.
   */
  leadingIconAltText?: string;
  /**
   * Defines the name of the trailing icon to use.
   */
  trailingIcon?: string;
  /**
   * Defines the name of the trailing icon to be displayed on mouse hover. If none specified, it will the appropriate variant of the icon.
   */
  trailingIconHover?: string;
  /**
   * Alternative text for the trailing icon.
   */
  trailingIconAltText?: string;

  /**
   * Children content.
   */
  children?: ReactNode;

  /**
   * Defines if anchor is inline text or not. An inline element does not start on a new line and takes up as much width as necessary. See https://www.w3schools.com/html/html_blocks.asp.
   */
  inline?: BooleanProp;

  /**
   * Data attributes
   */
  [dataAttribute: `data-${string}`]: string;
}

export const InnerAnchor: ForwardRefRenderFunction<HTMLAnchorElement, AnchorProps> = (
  {
    darkMode = false,
    appearance = 'text',
    variant = 'primary',
    fullWidth = false,
    hasIcon = false,
    iconOnly = false,
    leadingIcon,
    leadingIconHover,
    leadingIconAltText,
    trailingIcon,
    trailingIconHover,
    trailingIconAltText,
    children,
    className = '',
    inline = false,
    role = 'link',
    ...props
  },
  ref: Ref<HTMLAnchorElement | null>
) => {
  const innerRef = useRef<HTMLAnchorElement>(null);

  useImperativeHandle(ref, () => innerRef.current);
  /**
   * Building anchor class name dynamically according parameters.
   */
  const anchorClassName = classNames(
    `agora-anchor agora-anchor-${appearance}-${variant}`,
    { 'agora-anchor-full': fullWidth },

    { 'inline-flex items-center': !props.hidden },
    { 'justify-center min-h-[44px] min-w-[44px] py-8': !stringToBoolean(inline) },
    { 'justify-start': stringToBoolean(inline) },

    { 'agora-anchor-with-icon': stringToBoolean(hasIcon) || stringToBoolean(iconOnly) },
    { 'is-icon-only': stringToBoolean(iconOnly) },

    { 'dark-mode': stringToBoolean(darkMode) },

    className
  );

  const onKeyDownHandler = (evt: KeyboardEvent<HTMLAnchorElement>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      innerRef.current?.classList.add('active');
    }
    props.onKeyDown?.(evt);
  };

  const onKeyUpHandler = (evt: KeyboardEvent<HTMLAnchorElement>) => {
    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      innerRef.current?.classList.remove('active');
      innerRef.current?.setAttribute('data-custom-triggered-click', 'true');
      innerRef.current?.click();
    }

    props.onKeyUp?.(evt);
  };

  // Need this to let focus goes on story where no action click defined
  const onMouseDownHandler = (evt: MouseEvent<HTMLAnchorElement>) => {
    evt.preventDefault();
    innerRef.current?.classList.add('active');
    props.onMouseDown?.(evt);
  };

  // Need this to let focus goes on story where no action click defined
  const onMouseUpHandler = (evt: MouseEvent<HTMLAnchorElement>) => {
    evt.preventDefault();
    innerRef.current?.classList.remove('active');
    props.onMouseUp?.(evt);
  };

  const onClickHandler = (evt: MouseEvent<HTMLAnchorElement>) => {
    //  TRUE MOUSE CLICK
    if (evt.detail > 0) {
      props.onClick?.(evt);
      return;
    }

    //  CLICK FROM KEYBOARD

    //  NEED TO CHECK THIS ATTRIBUTE BECAUSE FIREFOX DOES NOT TRIGGER CLICK EVENTS WHENEVER THE ENTER/SPACE/NUMPADENTER KEY IS PRESSED.
    //  TO FIX THAT INCONSISTENCY, WE ADD A CUSTOM ATTRIBUTE TO SIGNAL THE ARTIFICIAL CLICK TRIGGER AND PREVENT THE CHROMIUM BASED BROWSERS OF DUPLICATE CLICK EVENTS.
    //  ON KEYUP THE CLICK EVENT IS TRIGGERED AND WILL BE ADDED THE CUSTOM ATTRIBUTE. THEN, IF THE BROWSER TRIGGERED ANOTHER CLICK, THE HANDLER WILL NOT FIND THE CUSTOM ATTRIBUTE AND WILL NOT TRIGGER A DUPLICATED EVENT.
    const shouldTriggerClick = innerRef.current?.getAttribute('data-custom-triggered-click') === 'true';
    innerRef.current?.removeAttribute('data-custom-triggered-click');

    if (!shouldTriggerClick) {
      evt.preventDefault();
      return;
    }

    props.onClick?.(evt);
  };

  return (
    <a
      className={anchorClassName}
      {...props}
      tabIndex={props.tabIndex ?? 0}
      role={role}
      ref={innerRef}
      onClick={onClickHandler}
      onKeyDown={onKeyDownHandler}
      onKeyUp={onKeyUpHandler}
      onMouseDown={onMouseDownHandler}
      onMouseUp={onMouseUpHandler}
    >
      {((hasIcon && iconOnly && trailingIcon === 'no-trailing') ||
        (hasIcon && iconOnly && leadingIcon === undefined && trailingIcon === undefined) ||
        (hasIcon && iconOnly && leadingIcon && leadingIcon === 'no-leading' && trailingIcon === 'no-trailing') ||
        (hasIcon && leadingIcon && leadingIcon !== 'no-leading')) && (
        <IconWrapper
          className="leading"
          name={leadingIcon && leadingIcon !== 'no-leading' ? leadingIcon : 'no-icon'}
          iconHover={leadingIconHover ?? 'no-icon'}
          iconAltText={leadingIconAltText}
        />
      )}
      {!iconOnly && children && <span className="children-wrapper">{children}</span>}
      {((hasIcon && iconOnly && leadingIcon === 'no-leading' && trailingIcon !== 'no-trailing') ||
        (hasIcon && trailingIcon && trailingIcon !== 'no-trailing')) && (
        <IconWrapper
          className="trailing"
          name={trailingIcon ?? 'no-icon'}
          iconHover={trailingIconHover ?? 'no-icon'}
          iconAltText={trailingIconAltText}
        />
      )}
    </a>
  );
};

export const Anchor = forwardRef<HTMLAnchorElement, AnchorProps>(InnerAnchor);
