import React, { createContext, Dispatch, FC, ReactNode, SetStateAction } from 'react';
import { Months } from '../../../../models';
import { WeekDays } from '../../../../models/week-days.interface';

export type DatePickerStateContextType = {
  focusedDate: Date;
  setFocusedDate: Dispatch<SetStateAction<Date>>;

  selectedDate: Date;
  setSelectedDate: Dispatch<SetStateAction<Date>>;

  cancel: () => void;

  labels: {
    previousYearAriaLabel: string;
    previousMonthAriaLabel: string;
    nextMonthAriaLabel: string;
    nextYearAriaLabel: string;
    monthsLabels?: Months;
    weekdaysLabels?: WeekDays;
    focusedDayAriaLabel?: string;
    selectedDayAriaLabel: string;
    todayDayAriaLabel: string;
    todayLabel: string;
    todayAriaLabel?: string;
    cancelLabel: string;
    cancelAriaLabel?: string;
    okLabel: string;
    okAriaLabel?: string;
  };
};

export const DatePickerStateContext = createContext<DatePickerStateContextType | undefined>(undefined);

export interface DatePickerStateProviderProps {
  children: ReactNode;
  value: DatePickerStateContextType;
}

export const DatePickerStateProvider: FC<DatePickerStateProviderProps> = ({ value, children }) => {
  return <DatePickerStateContext.Provider value={value}>{children}</DatePickerStateContext.Provider>;
};
