import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode, useMemo, useRef } from 'react';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { Pill, PillVariant } from '../../../atoms/pill';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-topic.scss';

/**
 * Card Topic style
 */
export type CardTopicVariant = 'default' | 'white' | 'informative' | 'success' | 'warning' | 'danger';
/**
 * Card Topic Statuses
 */
export type CardTopicStatus = 'success' | 'warning' | 'danger';

export interface CardTopicProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * Heading level to apply in the card.
   */
  headingLevel?: HeadingLevel;
  /**
   * Defines variant prop.
   */
  variant: CardTopicVariant;
  /**
   * The title of the card
   */
  title?: ReactNode;
  /**
   * Defines the status description.
   */
  description?: ReactNode;
  /**
   * Defines the pill text.
   */
  pillLabel?: ReactNode;
  /**
   * Defines the status text.
   */
  statusLabel?: ReactNode;
  /**
   * Defines the status of the card topic.
   */
  status?: CardTopicStatus;
  /**
   * Anchor props.
   */
  mainAnchor?: AnchorProps;
  /**
   * Defines blockedLink prop.
   */
  blockedLink?: BooleanProp;
}

export const CardTopic: FC<CardTopicProps> = ({
  headingLevel: Heading = 'h3',
  variant = 'default',
  title,
  description,
  pillLabel,
  statusLabel,
  status = 'success',
  mainAnchor,
  blockedLink = false,
  className,
  ...props
}) => {
  const variantMemo = useMemo(() => {
    if (variant === 'default' || variant === 'white') {
      return 'primary';
    }

    return variant;
  }, [variant]);

  const cardTopicRef = useRef<HTMLDivElement | null>(null);
  const cardTopicAnchorRef = useRef<HTMLAnchorElement | null>(null);

  const headingPlacement = {
    title: !!title,
    description: !title && !!description,
    pill: !title && !description && !!pillLabel,
    anchor: !title && !description && !pillLabel && !!mainAnchor && !mainAnchor.hidden
  };

  const anchorPlacement = {
    anchor: false,
    title: false,
    description: false,
    pill: false
  };

  let mainAnchorClone: AnchorProps = { ...mainAnchor };

  if (mainAnchor) {
    mainAnchorClone = {
      ...mainAnchor
    } as AnchorProps;

    if (mainAnchorClone.hasIcon && !mainAnchorClone.trailingIcon && !mainAnchorClone.leadingIcon) {
      mainAnchorClone.trailingIcon = 'agora-line-arrow-right-circle';
      mainAnchorClone.trailingIconHover = 'agora-solid-arrow-right-circle';
    }

    if (mainAnchor.hidden) {
      //  MUST DELETE IN ORDER TO SPREAD IT LATER
      delete mainAnchorClone.hidden;

      //  ONE OF THE TEXTS MUST BE FILLED
      anchorPlacement.title = !!title;
      anchorPlacement.description = !title && !!description;
      anchorPlacement.pill = !title && !description && !!pillLabel;
    } else {
      anchorPlacement.anchor = true;
    }
  }

  const buildText = (text: ReactNode, anchorProps: AnchorProps, isAnchor: boolean, isHeading: boolean): ReactNode => {
    if (!isAnchor) {
      return isHeading ? <Heading>{text}</Heading> : text;
    }

    return (
      <>
        {isHeading && (
          <Heading>
            <Anchor {...anchorProps} appearance="link" variant={variantMemo} ref={cardTopicAnchorRef}>
              {text}
            </Anchor>
          </Heading>
        )}
        {!isHeading && (
          <Anchor {...anchorProps} appearance="link" variant={variantMemo} ref={cardTopicAnchorRef}>
            {text}
          </Anchor>
        )}
      </>
    );
  };

  useBlockedLink({
    parentCardRef: cardTopicRef,
    anchorRef: cardTopicAnchorRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  const cardTopicClasseNames = classNames('card-topic', `card-${variant}`, { 'has-anchor-label': !mainAnchor?.iconOnly }, className);
  const cardStatusClasseNames = classNames('card-topic-status', `card-topic-status-${status}`);
  const anchorContainerClassNames = classNames('card-topic-anchor', { 'icon-only': mainAnchorClone?.iconOnly });

  return (
    <div {...props} role="presentation" className={cardTopicClasseNames} ref={cardTopicRef}>
      <div className="content-info">
        {pillLabel && (
          <div className="card-topic-pill">
            <Pill appearance="solid" size="default" variant={variantMemo as PillVariant}>
              {buildText(pillLabel, mainAnchorClone, anchorPlacement.pill, headingPlacement.pill)}{' '}
            </Pill>
          </div>
        )}

        {title && (
          <div className="card-topic-title">{buildText(title, mainAnchorClone, anchorPlacement.title, headingPlacement.title)}</div>
        )}

        {statusLabel && (
          <div className="flex items-center gap-8">
            <div className={cardStatusClasseNames} />
            <div>{statusLabel}</div>
          </div>
        )}

        {description && (
          <div className="card-topic-description">
            {buildText(description, mainAnchorClone, anchorPlacement.description, headingPlacement.description)}
          </div>
        )}
      </div>

      {anchorPlacement.anchor && (
        <div className={anchorContainerClassNames}>
          {buildText(mainAnchorClone.children, mainAnchorClone, anchorPlacement.anchor, headingPlacement.anchor)}
        </div>
      )}
    </div>
  );
};
