import classNames from 'classnames';

import React, {
  ChangeEvent,
  ComponentPropsWithRef,
  DragEvent,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  ChangeEvent as ReactChangeEvent,
  Ref,
  useCallback,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { Button } from '../button';
import { Icon } from '../icon';
import './input-file.scss';
import { createSyntheticChangeEvent } from './utils/create-synthetic-change-event';

export interface InputFileProps extends ComponentPropsWithRef<'input'> {
  /**
   * The max count of uploaded files
   */
  maxCount?: number;
  /**
   * The maxSize of the file in bytes
   */
  maxSize?: number;
  /**
   * The extensionsBlackList of the uploader
   */
  extensionsBlackList: string[];
  /**
   * The addedLabel of the uploader
   */
  addedLabel: string;
  /**
   * The announceRemovedFileLabel of the uploader
   */
  announceRemovedFileLabel: string;
  /**
   * The removeFileLabel of the uploader
   */
  removeFileLabel: string;
  /**
   * The fileListLabel of the uploader
   */
  fileListLabel: string;
  /**
   * The fileDropZoneAriaLabel of the uploader
   */
  fileDropZoneAriaLabel: string;
  /**
   * The dropYourFilesHereLabel of the uploader
   */
  dropYourFilesHereLabel: string;
  /**
   * The allowedFilesAriaLabel of the uploader
   */
  allowedFilesAriaLabel: string;
  /**
   * The uploadFilesLabel of the uploader
   */
  uploadFilesLabel: string;
  /**
   * The id of the uploader
   */
  id?: string;
  /**
   * The fileLabel of the uploader
   */
  fileLabel: string;
}

const InnerInputFile: ForwardRefRenderFunction<HTMLInputElement, InputFileProps> = (
  {
    maxSize = Number.MAX_SAFE_INTEGER,
    maxCount = 0,
    extensionsBlackList,
    addedLabel,
    fileListLabel,
    removeFileLabel,
    announceRemovedFileLabel,
    fileDropZoneAriaLabel,
    dropYourFilesHereLabel,
    allowedFilesAriaLabel,
    uploadFilesLabel,
    id: propId,
    fileLabel,
    onChange,
    ...rest
  },
  ref: Ref<HTMLInputElement | null>
) => {
  const generatedId = useId();
  const id = propId ?? generatedId;
  const formattedExtensions = extensionsBlackList.map((extension) => ' .' + extension).join(' / ');
  const [isDraggingOver, setIsDraggingOver] = useState(false);
  const [files, setFiles] = useState<File[]>([]);
  const [lastRemoved, setLastRemoved] = useState<File | null>(null);
  const [announceMessage, setAnnounceMessage] = useState('');
  const dropRef = useRef<HTMLDivElement>(null);

  const fileInputRef = useRef<HTMLInputElement>(null);
  useImperativeHandle(ref, () => fileInputRef.current);

  const fileDropContentClassName = classNames('file-drop-content', {
    'border-dashed': isDraggingOver
  });

  const handleButtonClick = () => {
    const fileInput = fileInputRef.current;
    if (fileInput) {
      fileInput.click();
    }
  };

  const isFileExtensionAllowed = (fileName: string): boolean => {
    const fileExtension = fileName.split('.').pop()?.toLowerCase() ?? '';
    const isAllowed = extensionsBlackList.includes(fileExtension);

    return isAllowed;
  };

  const generateFileListAnnouncement = useCallback(() => {
    const fileList = files.map((file) => file.name).join(', ');
    return `${fileListLabel}: ${fileList}`;
  }, [files, fileListLabel]);

  const addFiles = (filesToAdd: File[]) => {
    const validFiles = filesToAdd.filter((file) => file.size <= maxSize && isFileExtensionAllowed(file.name));

    if (validFiles.length > 0) {
      setFiles((prevFiles) => {
        if (!maxCount || maxCount == 0) {
          return [...prevFiles, ...validFiles];
        }

        if (maxCount >= prevFiles.length + validFiles.length) {
          return [...prevFiles, ...validFiles].slice(0, maxCount);
        }

        //  MAX COUNT EXCEEDED. DO NOTHING
        return prevFiles;
      });
    }
  };

  const handleFileChange = (event: ReactChangeEvent<HTMLInputElement>) => {
    setLastRemoved(null);
    const selectedFiles = Array.from(event.target.files ?? []);
    addFiles(selectedFiles);
  };

  const handleDragOver = (event: DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();

    setIsDraggingOver(true);
    const modifiedEvent = { ...event };
    modifiedEvent.dataTransfer.dropEffect = 'copy';
    if (!dropRef.current) {
      return;
    }

    dropRef.current.classList.add('dragging-over');
  };

  const handleDragLeave = () => {
    setIsDraggingOver(false);
    if (!dropRef.current) {
      return;
    }
    dropRef.current.classList.remove('dragging-over');
  };

  const handleDrop = (event: DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.stopPropagation();

    if (!dropRef.current) {
      return;
    }

    dropRef.current.classList.remove('dragging-over');

    const droppedFiles = Array.from(event.dataTransfer.files ?? []);

    addFiles(droppedFiles);
  };

  const handleRemoveFile = (fileToRemove: File) => {
    setLastRemoved(fileToRemove);
    setFiles((prevFiles) => prevFiles.filter((file) => file !== fileToRemove));
  };

  function handleKeyDown(e: KeyboardEvent) {
    if (e.key === 'Enter') {
      handleButtonClick();
    }
  }

  useEffect(() => {
    const isFileAdded = lastRemoved === null;
    const actionMessage = isFileAdded ? `${addedLabel}` : `${announceRemovedFileLabel}`;
    const actionFile = isFileAdded ? files[files.length - 1] : lastRemoved;
    const fileNamesMessage = `${fileLabel} ${actionMessage}: ${actionFile?.name}`;
    const updatedFileListAnnouncement = generateFileListAnnouncement();
    const fullMessage = `${fileNamesMessage}. ${updatedFileListAnnouncement}`;

    setAnnounceMessage(fullMessage);

    const handlePaste = (e: Event) => {
      const { clipboardData } = e as ClipboardEvent;
      if (clipboardData && clipboardData.files.length > 0) {
        const clipboardFiles = Array.from(clipboardData.files);
        addFiles(clipboardFiles);
      }
    };

    window.addEventListener('paste', handlePaste);

    return () => {
      window.removeEventListener('paste', handlePaste);
    };
  }, [files, lastRemoved, addedLabel, generateFileListAnnouncement, announceRemovedFileLabel, fileLabel]);

  useEffect(() => {
    if (fileInputRef.current) {
      const list = new DataTransfer();

      files.forEach((f) => {
        list.items.add(f);
      });

      fileInputRef.current.files = list.files;

      const syntheticEvent = createSyntheticChangeEvent(fileInputRef.current);
      onChange?.(syntheticEvent as ChangeEvent<HTMLInputElement>);
    }
  }, [files]);

  return (
    <div className="file-container">
      <div ref={dropRef} className="file-drop-container" onDragOver={handleDragOver} onDragLeave={handleDragLeave} onDrop={handleDrop}>
        <div
          className={fileDropContentClassName}
          aria-label={fileDropZoneAriaLabel}
          tabIndex={0}
          role="button"
          onClick={handleButtonClick}
          onKeyDown={handleKeyDown}
        >
          <div className="file-drop-info-section">
            <Icon className="icon-element" name="agora-line-download" dimensions="m" aria-hidden />
            <p className="file-drop-info-title">{dropYourFilesHereLabel}</p>
            <p className="file-drop-info-subtitle">
              {allowedFilesAriaLabel} {formattedExtensions}
            </p>
            <input ref={fileInputRef} {...rest} id={id} type="file" className="hidden" multiple onChange={handleFileChange} />
            <div className="upload-btn flex gap-16 items-center justify-center min-h-[60px] min-w-[60px] bg-[var(--color-neutral-200)] p-16 rounded-4">
              <span>{uploadFilesLabel}</span>
              <span>
                <Icon className="icon-element fill-[var(--color-neutral-900)]" name="agora-solid-upload" dimensions="m" aria-hidden />
              </span>
            </div>
            <p className="file-drop-info-subtitle-mobile">
              {allowedFilesAriaLabel} {formattedExtensions}
            </p>
          </div>
        </div>
      </div>
      <div id={`announce-region-${id}`} aria-live="assertive" aria-atomic="true" className="sr-only">
        {announceMessage}
      </div>
      {files.length > 0 && (
        <div className="file-list">
          <ul>
            {files.map((file, index) => {
              const fileIndex = `${id}-${file.name}-${index}`;
              return (
                <div key={fileIndex}>
                  <div className="file-item">
                    <div className="flex w-full justify-between">
                      <p className="break-all mt-[16px] mb-[8px]">{file.name}</p>
                      <Button
                        iconOnly
                        leadingIcon="agora-line-x-circle"
                        leadingIconHover="agora-line-x-circle"
                        hasIcon
                        appearance="link"
                        variant="neutral"
                        className="close-btn"
                        onClick={() => handleRemoveFile(file)}
                        aria-label={`${removeFileLabel} ${file.name}`}
                      />
                    </div>
                  </div>
                  {index !== files.length - 1 && <div className="file-divider" />}
                </div>
              );
            })}
          </ul>
        </div>
      )}
    </div>
  );
};

export const InputFile = forwardRef<HTMLInputElement, InputFileProps>(InnerInputFile);
