import React, { FC } from 'react';
import { Anchor } from '../../../atoms/anchor';
import { CardFooterProps } from '../models';

const PublicFooterHighlightCardLinks: FC<CardFooterProps> = (cardLinks) => {
  return (
    <>
      {cardLinks?.title && <h2 className="xs:text-m-bold md:text-l-bold text-white">{cardLinks.title}</h2>}
      {cardLinks?.links && (
        <ul className="mt-32">
          {cardLinks.links
            .filter((_i, index) => index < 5)
            .map((link, index) => {
              const key = `link-${index}`;
              return (
                <li key={key} className="py-8">
                  <Anchor {...link} darkMode appearance="link" variant="neutral" />
                </li>
              );
            })}
        </ul>
      )}
    </>
  );
};

export { PublicFooterHighlightCardLinks };
