import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  MouseEvent,
  ReactNode,
  Ref,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Icon } from '../icon';
import './checkbox.scss';

export interface CheckboxProps extends ComponentPropsWithRef<'input'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Sets the input in a error state.
   */
  hasError?: BooleanProp;
  /**
   * Child elements to render as custom label.
   */
  children?: ReactNode;
}

export const InnerCheckbox: ForwardRefRenderFunction<HTMLInputElement, CheckboxProps> = (
  { label, hasError, hideLabel, required = true, children, ...props },
  ref: Ref<HTMLInputElement | null>
) => {
  const inputRef = useRef<HTMLInputElement>(null);
  useImperativeHandle(ref, () => inputRef.current);

  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;

  const [currentChecked, setCurrentChecked] = useState(stringToBoolean(propsClone.checked ?? propsClone.defaultChecked));

  const handleCustomCheckboxKeyUp = (evt: KeyboardEvent<HTMLDivElement>) => {
    if (propsClone.disabled || propsClone.readOnly) {
      return;
    }

    if (evt.code === 'Space' || evt.code === 'Enter' || evt.code === 'NumpadEnter') {
      evt.preventDefault();
      evt.stopPropagation();
      inputRef.current?.click();
    }
  };

  const handleCustomCheckboxClick = (evt: MouseEvent<HTMLInputElement>) => {
    if (propsClone.disabled || propsClone.readOnly) {
      return;
    }

    if (evt.detail > 0) {
      evt.preventDefault();
      evt.stopPropagation();
      inputRef.current?.click();
    }
  };

  const handleChange = () => {
    setCurrentChecked((last) => !last);
  };

  const inputWrapper = classNames('agora-checkbox', 'inline-flex items-start min-h-[44px]', {
    disabled: props.disabled,
    error: stringToBoolean(hasError),
    checked: currentChecked
  });

  useEffect(() => {
    const checked = propsClone.checked ?? propsClone.defaultChecked;
    if (checked !== undefined) {
      setCurrentChecked(checked);
    }
  }, [propsClone.checked, propsClone.defaultChecked]);

  return (
    <div
      {...propsClone}
      className={inputWrapper}
      id={`agora-checkbox-custom-${propsClone.id}`}
      role="checkbox"
      tabIndex={props.disabled ? -1 : 0}
      aria-checked={currentChecked}
      aria-disabled={!!props.disabled}
      aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
      aria-required={required}
      aria-invalid={stringToBoolean(hasError)}
      onKeyDownCapture={handleCustomCheckboxKeyUp}
      onClickCapture={handleCustomCheckboxClick}
    >
      <input className="hidden" {...propsClone} type="checkbox" ref={inputRef} onChange={handleChange} />

      <span className="mark">{currentChecked && <Icon name="agora-line-check" aria-hidden />}</span>

      {!hideLabel && (
        <label className="label" htmlFor={propsClone.id}>
          <span>{children ?? label}</span>
        </label>
      )}
    </div>
  );
};

export const Checkbox = forwardRef<HTMLInputElement, CheckboxProps>(InnerCheckbox);
