import classNames from 'classnames';
import React, { forwardRef, ForwardRefRenderFunction, HTMLAttributes, MouseEvent, Ref } from 'react';
import './skip-navigation.scss';

export const InnerSkipNavigation: ForwardRefRenderFunction<HTMLAnchorElement, HTMLAttributes<HTMLAnchorElement>> = (
  { children, ...props },
  ref?: Ref<HTMLAnchorElement>
) => {
  const navigationClassName = classNames('skip-navigation', props.className);

  const handleClick = (evt: MouseEvent<HTMLAnchorElement>) => {
    const mainContent = document.getElementById('main');
    if (mainContent) {
      mainContent.focus();
    }
    props.onClick?.(evt);
  };

  return (
    <a {...props} ref={ref} className={navigationClassName} href="#main" onClick={handleClick}>
      {children}
    </a>
  );
};

export const SkipNavigation = forwardRef<HTMLAnchorElement, HTMLAttributes<HTMLAnchorElement>>(InnerSkipNavigation);
