import classNames from 'classnames';
import React, { ComponentPropsWithRef, FC, ReactNode } from 'react';
import { Button, ButtonProps } from '../../../atoms/button';
import { Icon, IconProps } from '../../../atoms/icon';
import './card-emphasize.scss';

/**
 * The available variants of the card emphasize
 */
export type CardEmphasizeAlignment = 'left' | 'center' | 'right';

export interface CardEmphasizeProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * The icon props
   */
  icon?: IconProps;
  /**
   * The title of the card
   */
  title?: ReactNode;
  /**
   * The description of the card
   */
  description?: ReactNode;
  /**
   * The button props
   */
  mainButton?: ButtonProps;
  /**
   * The variant of the card
   */
  alignment: CardEmphasizeAlignment;
}

export const CardEmphasize: FC<CardEmphasizeProps> = ({
  icon,
  title,
  description,
  mainButton,
  alignment = 'left',
  className,
  ...props
}) => {
  const cardEmphasizeClasseNames = classNames('card-emphasize', `card-${alignment}`, className);

  const iconProps = {
    ...icon,
    className: classNames('card-emphasize-icon', icon?.className)
  };

  const buttonProps = {
    ...mainButton,
    className: classNames('card-emphasize-button', mainButton?.className)
  } as ButtonProps;

  return (
    <div {...props} role="presentation" className={cardEmphasizeClasseNames}>
      {icon && <Icon {...iconProps} />}
      {title && <div className="card-emphasize-title">{title}</div>}
      {description && <div className="card-emphasize-description">{description}</div>}
      {mainButton && <Button {...buttonProps} />}
    </div>
  );
};
