import { MouseEvent as ReactMouseEvent, RefObject } from 'react';
import { closeOtherSubmenus } from './close-other-menu';
import { DrawerRef } from '../../../atoms/drawer';
import { DrawerContentProps } from '../models';

export const handleClickMenuItem = (
  evt: ReactMouseEvent<HTMLButtonElement>,
  setBackdropVisible: (val: boolean) => void,
  val?: DrawerContentProps | null | undefined,
  refDrawer?: RefObject<DrawerRef>,
  setDrawerContent?: (val: DrawerContentProps | null | undefined) => void
) => {
  const current = evt.currentTarget as HTMLButtonElement;
  const submenu = current.nextElementSibling as HTMLDivElement;
  const avatarMenu = current.classList.contains('avatar');

  if (submenu) {
    closeOtherSubmenus(submenu);
    if (submenu.classList.contains('hidden')) {
      setBackdropVisible(true);
      submenu.classList.remove('hidden');
      document.body.classList.add('overflow-hidden');
    } else {
      setBackdropVisible(false);
      submenu.classList.add('hidden');
      document.body.classList.remove('overflow-hidden');
    }
    return;
  }

  document.body.classList.remove('overflow-hidden');
  closeOtherSubmenus(submenu);
  setBackdropVisible(false);

  if (avatarMenu) {
    if (refDrawer?.current && setDrawerContent) {
      setDrawerContent(val);
      refDrawer.current.openDrawer();
      document.body.classList.add('overflow-hidden');
    }
  }
};
