import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction, ReactNode } from 'react';
import { FeedbackState } from '../../../models';
import { Icon } from '../../atoms/icon';
import './feedback.scss';
import classNames from 'classnames';

export interface FeedbackProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;

  /**
   * Feedback text displayed below input.
   */
  feedbackText?: ReactNode;
}

export const InnerFeedback: ForwardRefRenderFunction<HTMLDivElement, FeedbackProps> = (
  { className, feedbackState = 'info', feedbackText, ...props },
  ref
) => {
  const feedbackWrapperClasses = classNames('feedback', className);
  const feedbackIconWrapperClasses = classNames('feedback-icon-wrapper', `feedback-icon-wrapper-${feedbackState}`);

  /**
   * Dictionary of possible icons feedback.
   */
  const feedbackStateIconsMap = {
    info: 'agora-solid-info-mark',
    danger: 'agora-solid-alert-triangle',
    success: 'agora-solid-check-circle',
    warning: 'agora-solid-alert-circle'
  };

  return (
    <div {...props} className={feedbackWrapperClasses} ref={ref}>
      <span className={feedbackIconWrapperClasses}>
        <Icon name={feedbackStateIconsMap[feedbackState]} dimensions="s" aria-hidden />
      </span>
      <p className="feedback-text">{feedbackText}</p>
    </div>
  );
};

export const Feedback = forwardRef<HTMLDivElement, FeedbackProps>(InnerFeedback);
