import { createContext, useContext } from 'react';
import { DialogElementRef } from '../../atoms/dialog';
import { LoaderDialogProps } from './loader-dialog';

export interface LoaderDialogContextProps {
  current: DialogElementRef | null;
  visibility?: boolean;
  showLoader: (props: LoaderDialogProps) => void;
  hideLoader: () => void;
}

const LoaderDialogContext = createContext<LoaderDialogContextProps>({} as LoaderDialogContextProps);

const useLoaderDialogContext = (): LoaderDialogContextProps => {
  const context = useContext(LoaderDialogContext);
  return context;
};

export { LoaderDialogContext, useLoaderDialogContext };
