import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode } from 'react';
import './internal-dropdown-no-results.scss';

export interface InternalNoResultsProps extends ComponentPropsWithoutRef<'div'> {
  label: ReactNode;
}

export const InternalNoResults: FC<InternalNoResultsProps> = ({ className, label, ...props }) => {
  const classes = classNames('agora-dropdown-no-results', className);

  return (
    <div {...props} className={classes}>
      <div className="label">{label}</div>
    </div>
  );
};
