import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactElement, ReactNode } from 'react';
import { TableLayout } from './models/table-layout.type';
import { TableBodyProps } from './table-body';
import { TableCaptionProps } from './table-caption';
import { TableFooterProps } from './table-footer';
import { TableHeaderProps } from './table-header';
import { TablePagination, TablePaginationProps } from './table-pagination';
import './table.scss';

export interface TableProps extends ComponentPropsWithRef<'table'> {
  /**
   * Define the title of table
   */
  caption?: ReactNode;
  /**
   * Table children content
   */
  children?:
    | ReactElement<TableCaptionProps>
    | ReactElement<TableHeaderProps>
    | ReactElement<TableBodyProps>
    | ReactElement<TableFooterProps>
    | Array<
        ReactElement<TableCaptionProps> | ReactElement<TableHeaderProps> | ReactElement<TableBodyProps> | ReactElement<TableFooterProps>
      >;
  /**
   * Define layout for mobile breakpoint
   */
  mobileLayout?: TableLayout;
  /**
   * Define layout for tablet breakpoint
   */
  tabletLayout?: TableLayout;
  /**
   * Define layout for desktop breakpoint
   */
  desktopLayout?: TableLayout;
  /**
   * Configurations used for pagination
   */
  paginationProps?: TablePaginationProps;
}

const InternalTable: ForwardRefRenderFunction<HTMLTableElement, TableProps> = (
  { caption, className = '', mobileLayout = 'list', tabletLayout = 'table', desktopLayout = 'table', children, paginationProps, ...props },
  ref
) => {
  const tableClasseNames = classNames(
    'agora-table-container',
    {
      'layout-table-mobile': mobileLayout === 'table',
      'layout-list-mobile': mobileLayout === 'list',
      'layout-table-tablet': tabletLayout === 'table',
      'layout-list-tablet': tabletLayout === 'list',
      'layout-table-desktop': desktopLayout === 'table',
      'layout-list-desktop': desktopLayout === 'list'
    },
    className
  );

  return (
    <div className="agora-table">
      <table {...props} className={tableClasseNames} ref={ref}>
        {caption && <caption>{caption}</caption>}
        {children}
      </table>
      {paginationProps && <TablePagination {...paginationProps} />}
    </div>
  );
};

const Table = forwardRef<HTMLTableElement, TableProps>(InternalTable);

export { Table };
