import { createContext, ReactNode, useContext } from 'react';
import { DialogElementRef } from '../../atoms/dialog';
import { PopupDialogProps } from './popup-dialog';

export interface PopupContextProps {
  current: DialogElementRef | null;
  visibility?: boolean;
  show: (children: ReactNode, popupProps: PopupDialogProps) => void;
  hide: () => void;
}

const PopupContext = createContext<PopupContextProps>({} as PopupContextProps);

const usePopupContext = (): PopupContextProps => {
  return useContext(PopupContext);
};

export { PopupContext, usePopupContext };
