import React, { FC, ReactNode, useMemo } from 'react';
import { Toast } from './toast';
import { ToastContext } from './toast-context';
import './toast.scss';
import { useToast } from './use-toast';
import { useWindowSize } from '../../../hooks';

export type ToastPositions = 'top-left' | 'top-right' | 'bottom-left' | 'bottom-right';

export interface ToastProviderProps {
  children?: ReactNode;
  position?: ToastPositions;
}
const ToastProvider: FC<ToastProviderProps> = ({ children, position = 'bottom-right' }: ToastProviderProps) => {
  const { toasts, showToast, removeToast } = useToast();
  const { width } = useWindowSize();

  const contextValue = useMemo(
    () => ({
      showToast,
      removeToast
    }),
    [showToast, removeToast]
  );

  const memoToast = useMemo(() => {
    const VISIBLE_TOAST_NUMBER_MOBILE = 3;
    const VISIBLE_TOAST_NUMBER_DESKTOP = 5;
    const isMobile = width <= 768;
    const visibleToastsCount = isMobile ? VISIBLE_TOAST_NUMBER_MOBILE : VISIBLE_TOAST_NUMBER_DESKTOP;

    if (position.includes('bottom')) {
      return [...toasts].slice(-1 * visibleToastsCount);
    }

    return [...toasts].reverse().slice(0, visibleToastsCount);
  }, [position, toasts, width]);

  return (
    <ToastContext.Provider value={contextValue}>
      {children}
      {memoToast.length > 0 && (
        <div className={`toast-list toast-list--${position}`} aria-live="assertive">
          {memoToast.map((toast) => {
            return <Toast key={toast.id} {...toast} />;
          })}
        </div>
      )}
    </ToastContext.Provider>
  );
};

export { ToastProvider };
