import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, ComponentType, FC, HTMLProps, LazyExoticComponent, Suspense, SVGProps } from 'react';
import { SVGIconProps } from '../../../models/svg-icon.interface';
import { isBundledScribbles, scribblesList, ScribblesName } from './assets';
import './scribbles.scss';

const scribblesCache: Record<ScribblesName, LazyExoticComponent<ComponentType<SVGIconProps>>> = {};

const loadScribbles = (name: ScribblesName): LazyExoticComponent<ComponentType<SVGIconProps>> => {
  const cachedScribbles = scribblesCache[name];

  if (cachedScribbles) {
    return cachedScribbles;
  }

  const lazyScribble = scribblesList[name];

  scribblesCache[name] = lazyScribble;

  return lazyScribble;
};

type SuperScribblesProps = SVGProps<SVGSVGElement> & HTMLProps<HTMLImageElement>;

export interface ScribblesProps extends SuperScribblesProps {
  /**
   * Scribbles name to be used.
   */
  name: ScribblesName;
  /**
   * Dark mode
   */
  darkMode?: boolean;
  /**
   * Callback to run when scribble is loaded.
   */
  onScribblesLoad?: () => void;

  /**
   * Callback to run when scribble fails to load.
   */
  onScribblesError?: () => void;
}

export const Scribbles: FC<ScribblesProps> = ({ name, darkMode, onScribblesLoad, onScribblesError, ...props }) => {
  const classes = classNames('agora-scribbles', props.className, name, {
    'dark-mode': darkMode
  });

  const onLoad = () => {
    onScribblesLoad?.();
  };

  const onError = () => {
    onScribblesError?.();
  };

  //  CHECK IF HAS NAME AND IF EXISTS ON BUNDLE
  const isScribbles = name !== '' && isBundledScribbles(name);
  if (!isScribbles) {
    onError();
    return;
  }

  //  LOAD IT
  const LazyScribble = loadScribbles(name);

  const fullProps = {
    ...props,
    className: classes,
    title: props.title ?? props.alt
  } as ComponentPropsWithoutRef<'svg'>;

  onLoad();

  return (
    LazyScribble && (
      <Suspense>
        <LazyScribble {...fullProps} />
      </Suspense>
    )
  );
};
