import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  MouseEvent,
  ReactNode,
  useId,
  useImperativeHandle,
  useMemo,
  useRef,
  useState
} from 'react';
import { BooleanProp, HeadingLevel } from '../../../models';
import { stringToBoolean } from '../../../utils/string-to-boolean';
import { Icon } from '../icon';
import './accordion.scss';

export interface AccordionRef {
  id: string;
  current: HTMLDivElement;
  expand: () => void;
  collapse: () => void;
  isExpanded: boolean;
}

export interface AccordionProps extends Omit<ComponentPropsWithRef<'div'>, 'title'> {
  /**
   * Accordion Header Title
   */
  title?: ReactNode;
  /**
   * header Accordion.
   */
  headingLevel?: HeadingLevel;
  /**
   * Children content.
   */
  children?: ReactNode;
  /**
   * Defines if the button uses an icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Defines the name of the icon to use.
   */
  icon?: string;
  /**
   * Alternative text for the icon.
   */
  iconAltText?: string;
  /**
   * Alternative area-hidden for the icon.
   */
  iconAriaHidden?: BooleanProp;
  /**
   * Event function called whenever the accordion expands.
   */
  onExpanded?: (prop: AccordionProps) => void | undefined;
  /**
   * Event function called whenever the accordion collapses.
   */
  onCollapsed?: (prop: AccordionProps) => void | undefined;
  /**
   * Initial value for the expanded value.
   */
  expanded?: BooleanProp;
}
/**
 * Return icon default animation class according icon name and default position.
 */

export const InnerAccordion: ForwardRefRenderFunction<HTMLDivElement | AccordionRef, AccordionProps> = (
  {
    children,
    hasIcon,
    icon,
    iconAltText,
    iconAriaHidden = false,
    title,
    headingLevel: Component = 'h3',
    className = '',
    expanded = false,
    onExpanded,
    onCollapsed,
    ...props
  },
  ref
) => {
  const isIconAriaHidden = stringToBoolean(iconAriaHidden);

  const innerRef = useRef<HTMLDivElement>(null);

  const generatedId = useId();

  const propsClone = useMemo(
    () => ({
      ...props,
      id: props.id ?? generatedId
    }),
    [props, generatedId]
  );

  const [isExpanded, setIsExpanded] = useState(expanded);

  useImperativeHandle(
    ref,
    () => {
      return {
        id: propsClone.id,
        current: innerRef.current,
        isExpanded,
        expand: () => {
          setIsExpanded(true);
          onExpanded?.(propsClone as AccordionProps);
        },
        collapse: () => {
          setIsExpanded(false);
          onCollapsed?.(propsClone as AccordionProps);
        }
      } as AccordionRef;
    },
    [propsClone, innerRef, isExpanded, onExpanded, onCollapsed]
  );

  /**
   * Building Accordion class name dynamically according parameters.
   */
  const AccordionClassName = classNames(`accordion`, className, { 'accordion-open': isExpanded });

  const AccordionBodyClassName = classNames('accordion-body', { 'accordion-body-block': isExpanded });
  const AccordionIconClassName = classNames('icon icon-chevron', { 'icon-chevron-flip': isExpanded });

  const toggleExpanded = () => {
    if (isExpanded) {
      onCollapsed?.(propsClone as AccordionProps);
    } else {
      onExpanded?.(propsClone as AccordionProps);
    }
    setIsExpanded((last) => !last);
  };

  const onMouseDownHandler = (evt: MouseEvent<HTMLButtonElement>) => {
    evt.preventDefault();
  };

  const onMouseUpHandler = (evt: MouseEvent<HTMLButtonElement>) => {
    evt.preventDefault();
  };

  const onMouseClickHandler = () => {
    toggleExpanded();
  };

  return (
    <div className={AccordionClassName} {...propsClone} ref={innerRef}>
      <Component className="flex justify-start items-center w-full">
        <button
          type="button"
          onMouseDown={onMouseDownHandler}
          onMouseUp={onMouseUpHandler}
          onClick={onMouseClickHandler}
          className="accordion-header"
          aria-expanded={isExpanded}
          id={`agora-accordion-${propsClone.id}-control`}
          aria-controls={`agora-accordion-${propsClone.id}-region`}
        >
          {hasIcon && <Icon name={icon} alt={iconAltText} className="icon" dimensions="m" aria-hidden={isIconAriaHidden} />}
          {title && <span className="title">{title}</span>}

          <Icon name="agora-line-chevron-down" aria-hidden className={AccordionIconClassName} />
        </button>
      </Component>

      <div
        className={AccordionBodyClassName}
        id={`agora-accordion-${propsClone.id}-region`}
        role="region"
        aria-labelledby={`agora-accordion-${propsClone.id}-control`}
      >
        {children}
      </div>
    </div>
  );
};

export const Accordion = forwardRef<HTMLDivElement | AccordionRef, AccordionProps>(InnerAccordion);
