import classNames from 'classnames';
import React, { FC, MouseEvent, ReactNode, useId, useRef } from 'react';
import { BooleanProp } from '../../../../../models';
import { MenuItem, MenuItemRef } from '../../../../molecules/menu-item';
import { MenuProps, SubmenuItemProps } from '../../models';
import { closeOtherSubmenus } from '../../utils/close-other-menu';
import { handleClickMenuItem } from '../../utils/handle-click-menu-item';
import { HeaderMenuOptions } from '../header-menu-item-options/header-menu-item-options';
import { HeaderNavigationProps } from '../header-navigation/header-navigation';
import { HeaderBurgerSecondaryNavigation } from './header-burger-secondary-navigation';

const HeaderBurgerPrimaryNavigation: FC<{
  headerPrimaryNavigationClone: HeaderNavigationProps;
  headerHeight: number;
  backButtonLabelBurger: ReactNode;
  hasBackButtonBurger: BooleanProp;
  setBackdropVisible: (val: boolean) => void;
  submenuHeight: number;
  handleBackButtonBurger: (evt: MouseEvent<HTMLButtonElement>) => void;
  submenuLevel2Height: number;
  submenuLevel3Height: number;
}> = ({
  headerPrimaryNavigationClone,
  headerHeight,
  backButtonLabelBurger,
  hasBackButtonBurger,
  setBackdropVisible,
  submenuHeight,
  handleBackButtonBurger,
  submenuLevel2Height,
  submenuLevel3Height
}) => {
  const menuItemId = useId();
  const menuItemRef = useRef<MenuItemRef>(null);

  const headerBurgerNavigationContainerClassNames = classNames(
    'agora-header-burger-navigation-options-menu overflow-y-auto overflow-x-hidden hidden absolute w-full right-0 z-sticky shadow-bottom-high pt-32'
  );

  return (
    <>
      {headerPrimaryNavigationClone.menuItemsHeaderNavigation && (
        <div style={{ maxHeight: `calc(100vh - ${headerHeight}px)` }} className={headerBurgerNavigationContainerClassNames}>
          <nav aria-label={headerPrimaryNavigationClone.ariaLabelHeaderNavigation} className="overflow-hidden">
            <ul role="menubar">
              {headerPrimaryNavigationClone.menuItemsHeaderNavigation
                ?.filter((_i, index) => index < 5)
                .map((item: MenuProps, index) => {
                  const id = `${menuItemId}-menu-${index}`;
                  const containerHeaderMenuOptions = classNames(
                    'agora-header-burger-navigation-options-submenu overflow-y-auto overflow-x-hidden hidden sticky w-full right-0 z-sticky shadow-bottom-high [&>*:first-child]:px-0 xl:[&>*:first-child]:max-w-full xl:[&>*:first-child]:mx-64',
                    {
                      'bg-[var(--color-neutral-50)]': item.submenuItem?.highlightItems,
                      'bg-[var(--color-neutral-100)]': !item.submenuItem?.highlightItems
                    }
                  );

                  const itemsSubmenuClone = {
                    ...item.submenuItem,
                    ...item.relatedNavMenu?.menuItemsHeaderNavigation?.map((sub) => sub.menuItem)
                  };

                  itemsSubmenuClone.hasSubmenuItemsSectionTitle = true;
                  itemsSubmenuClone.submenuItemsSectionTitle = item.menuItem.children;
                  itemsSubmenuClone.backButtonLabel = backButtonLabelBurger;
                  itemsSubmenuClone.hasBackButton = hasBackButtonBurger;

                  itemsSubmenuClone.handleBackButton = (evt: MouseEvent<HTMLButtonElement>) => {
                    item.submenuItem?.handleBackButton?.(evt);
                    if (evt.currentTarget.parentElement?.parentElement) {
                      evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement
                        ?.querySelectorAll('.agora-menu-item')
                        .forEach((element: Element) => {
                          element.classList.remove('!hidden');
                          evt.currentTarget.parentElement?.parentElement?.classList.remove('fade-right-left');
                          evt.currentTarget.parentElement?.parentElement?.classList.add('hidden');
                          evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement?.classList.add('fade-left-right');
                          evt.currentTarget.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.parentElement?.classList.add(
                            'pt-32'
                          );
                        });
                      (evt.currentTarget.parentElement?.parentElement?.previousSibling as HTMLElement).click();
                    }
                  };

                  return (
                    <li key={id} role="none" className="overflow-hidden">
                      {/* Menu item with same elements of primary navigation */}
                      <MenuItem
                        {...item.menuItem}
                        ref={menuItemRef}
                        noIcons={!!item.relatedNavMenu?.menuItemsHeaderNavigation === true ? false : item.menuItem.noIcons}
                        itemType="primary"
                        vertically
                        onClick={(evt: MouseEvent<any>) => {
                          item.menuItem.onClick?.(evt);
                          if ((evt.currentTarget as Element).nextElementSibling) {
                            (evt.currentTarget as Element).parentElement?.parentElement
                              ?.querySelectorAll('.agora-menu-item')
                              .forEach((element: Element) => {
                                element.classList.add('!hidden');
                                (evt.currentTarget as Element).nextElementSibling?.classList.add('fade-right-left');
                                (evt.currentTarget as Element).parentElement?.parentElement?.classList.remove('fade-left-right');
                              });
                            const childCurrent = (evt.currentTarget as Element).nextSibling
                              ?.childNodes as unknown as HTMLCollectionOf<HTMLElement>;
                            Array.from(childCurrent).forEach((element) => {
                              element.classList.remove('hidden');
                            });
                            (evt.currentTarget as Element).parentElement?.parentElement?.parentElement?.parentElement?.classList.remove(
                              'pt-32'
                            );
                          }

                          handleClickMenuItem(evt, setBackdropVisible);
                        }}
                      />
                      {itemsSubmenuClone.submenuItems && Object.keys(itemsSubmenuClone).length > 1 && (
                        <div className={containerHeaderMenuOptions}>
                          {/* if primary items have direct options render on a HeaderMenuOptions component */}
                          <HeaderMenuOptions
                            submenu={itemsSubmenuClone as SubmenuItemProps}
                            closeOtherMenuItemOpened={closeOtherSubmenus}
                            setBackdropVisible={setBackdropVisible}
                            className="overflow-y-auto overflow-x-hidden xs:[&>*]:px-32 md:[&>*]:px-64 xl:[&>*]:px-0"
                            style={{ maxHeight: `calc(100vh - ${submenuHeight}px)` }}
                          />
                          {itemsSubmenuClone?.highlightItems && (
                            <div
                              aria-label={itemsSubmenuClone?.highlightItems.submenuAriaLabel}
                              className="bg-[var(--color-neutral-100)] [&>*:first-child]:px-0 xl:[&>*:first-child]:max-w-full xl:[&>*:first-child]:mx-64"
                            >
                              <HeaderMenuOptions
                                submenu={itemsSubmenuClone?.highlightItems}
                                closeOtherMenuItemOpened={closeOtherSubmenus}
                                setBackdropVisible={setBackdropVisible}
                                className="flex flex-wrap gap-32"
                              />
                            </div>
                          )}
                        </div>
                      )}

                      {/* if primary navigation has secondary item for secondary navigation */}
                      <HeaderBurgerSecondaryNavigation
                        item={item}
                        backButtonLabelBurger={backButtonLabelBurger}
                        hasBackButtonBurger={hasBackButtonBurger}
                        itemsSubmenuClone={itemsSubmenuClone as SubmenuItemProps}
                        handleBackButtonBurger={handleBackButtonBurger}
                        submenuLevel2Height={submenuLevel2Height}
                        menuItemId={menuItemId}
                        menuItemRef={menuItemRef}
                        setBackdropVisible={setBackdropVisible}
                        submenuLevel3Height={submenuLevel3Height}
                      />
                    </li>
                  );
                })}
            </ul>
          </nav>
        </div>
      )}
    </>
  );
};

export { HeaderBurgerPrimaryNavigation };
