import classNames from 'classnames';
import React, { ChangeEvent, ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode, useId, useRef, useState } from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Feedback } from '../../shared/feedback';
import { Icon } from '../icon';
import './input-password.scss';

export interface InputPasswordProps extends ComponentPropsWithRef<'input'> {
  /**
   * Input descriptive label.
   */
  label?: string;
  /**
   * Decides if display/hide label.
   */
  hideLabel?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Text informative displayed below input text.
   */
  feedbackText?: ReactNode;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Highlight the input indicating some error
   */
  hasIcon?: BooleanProp;
  /**
   * Name of the icon leading icon to be displayed.
   */
  icon?: string;
  /**
   * Indicates the feedback state of feedback text. This param will change the left feedback icon and dye it.
   */
  hasError?: BooleanProp;
  /**
   * Alternative text for toggle password/plain text icon button
   */
  togglePasswordAltIconText?: string;
}

const InnerInputPassword: ForwardRefRenderFunction<HTMLInputElement, InputPasswordProps> = (
  {
    label,
    hideLabel,
    hasError,
    hasFeedback,
    feedbackState = 'info',
    feedbackText,
    hasIcon,
    icon,
    className,
    required = true,
    togglePasswordAltIconText = 'show/hide',
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const { onChange } = propsClone;

  const inputWrapperRef = useRef<HTMLInputElement>(null);

  const [showPassword, setShowPassword] = useState(false);

  const [currentValue, setCurrentValue] = useState(propsClone.value);

  /**
   * Show/Hide password
   */
  const handleTogglePassword = () => {
    if (!propsClone.disabled && !propsClone.readOnly) {
      setShowPassword(!showPassword);

      if (inputWrapperRef?.current) {
        inputWrapperRef.current.querySelector('input')?.focus();
      }
    }
  };

  /**
   * Local onChange event handler.
   */
  const handleChangeEvent = (evt: ChangeEvent<HTMLInputElement>) => {
    const { value } = evt.currentTarget;
    setCurrentValue(value);
    onChange?.(evt);
  };

  /**
   * Builds main Classes for whole component
   */
  const inputClassBuilder = () => {
    return {
      // main wrapper class
      inputWrapperClasses: classNames(
        'agora-input-password-wrapper',

        {
          'flex flex-col': !propsClone.hidden,
          'input-disabled': !!propsClone.disabled,
          'input-has-error': stringToBoolean(hasError)
        }
      ),
      // classes for above input sections [label and hint icon]
      labelSectionClasses: classNames(
        'input-label-wrapper flex items-end',

        {
          'mb-8': label && !stringToBoolean(hideLabel),
          'justify-end': !label || stringToBoolean(hideLabel),
          'justify-between': label && !stringToBoolean(hideLabel)
        }
      ),
      // text input component classes
      inputClasses: classNames(
        'agora-input-password',
        {
          'has-leading-icon': hasIcon && icon?.indexOf('agora-line') === 0, // allowing only line family icon
          'has-value': currentValue && currentValue?.toString().length > 0
        },
        className
      ),
      leadingIconWrapperClasses: classNames('input-leading-icon')
    };
  };

  const { inputWrapperClasses, labelSectionClasses, inputClasses, leadingIconWrapperClasses } = inputClassBuilder();

  return (
    <div hidden={!!propsClone.hidden} className={inputWrapperClasses}>
      {/* Label */}
      <div className={labelSectionClasses}>
        <label hidden={stringToBoolean(hideLabel)} htmlFor={propsClone.id} className="text-[var(--color-primary-900)] text-m-medium">
          {label}
        </label>
      </div>

      {/* Text input wrapper */}
      <div className="input-wrapper flex relative items-center" ref={inputWrapperRef}>
        <input
          {...propsClone}
          type={showPassword ? 'text' : 'password'}
          className={inputClasses}
          disabled={!!propsClone.disabled}
          readOnly={!!propsClone.readOnly}
          aria-invalid={stringToBoolean(hasError)}
          aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
          ref={ref}
          required={required}
          onChange={handleChangeEvent}
        />

        {/* Input Leading Icon [only line family icon] */}
        {hasIcon && icon && icon.startsWith('agora-line') && (
          <div className={leadingIconWrapperClasses}>
            <Icon name={icon} aria-hidden />
          </div>
        )}

        {/* right trailing icon */}
        <button
          type="button"
          className="input-btn-toggle bg-transparent"
          onClick={handleTogglePassword}
          aria-label={togglePasswordAltIconText}
          disabled={propsClone.disabled ?? propsClone.readOnly}
        >
          <Icon name={`agora-line-${showPassword ? 'eye-off' : 'eye'}`} alt={togglePasswordAltIconText} />
        </button>
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputPassword = forwardRef<HTMLInputElement, InputPasswordProps>(InnerInputPassword);
