import classNames from 'classnames';
import React, { ChangeEvent, Children, cloneElement, FC, HtmlHTMLAttributes, ReactElement, ReactNode, useId } from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Checkbox, CheckboxProps } from '../../atoms/checkbox/checkbox';
import { Feedback } from '../../shared/feedback';

export interface CheckboxGroupProps extends Omit<HtmlHTMLAttributes<HTMLDivElement>, 'onChange'> {
  /**
   *  Input label text.
   */
  label?: string;
  /**
   * Decides if display/hide label.
   */
  hideLabel?: BooleanProp;
  /**
   *  List of checkboxes props to generate as checkboxes.
   */
  items?: CheckboxProps[];
  /**
   * Sets the checkbox in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below checkbox-group.
   */
  feedbackText?: ReactNode;
  /**
   *  List of checkbox components to use as children of the group.
   */
  children?: ReactElement<CheckboxProps> | Array<ReactElement<ReactElement<CheckboxProps>>>;
  /**
   *  Event triggered whenever any of the checkboxes changes is own state.
   */
  onChange?: (evt: ChangeEvent<HTMLInputElement>) => void;
}
const CheckboxGroup: FC<CheckboxGroupProps> = ({
  className,
  label,
  hideLabel,
  hasError,
  hasFeedback,
  feedbackText,
  feedbackState = 'info',
  onChange,
  items = [],
  children,
  ...props
}) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  let childrenCheckboxes: Array<ReactElement<ReactElement<CheckboxProps>>>;

  if (children) {
    if (!Array.isArray(children)) {
      childrenCheckboxes = [children] as Array<ReactElement<ReactElement<CheckboxProps>>>;
    } else {
      childrenCheckboxes = children;
    }
  } else {
    childrenCheckboxes = [];
  }

  const itemsCheckboxes = items.map((item, index) => {
    const key = `cb-group-${propsClone.id}-${index}`;
    return <Checkbox key={key} {...item} value={item.value ?? undefined} />;
  });

  const allCheckboxes = [...itemsCheckboxes, ...childrenCheckboxes];

  const innerOnChange = (nativeEvent: ChangeEvent<HTMLInputElement>) => {
    onChange?.(nativeEvent);
  };

  const containerClasses = classNames('agora-checkbox-group', className);

  return (
    <div className={containerClasses}>
      <span
        className="label text-[var(--color-primary-900)] text-m-medium mb-8 block"
        hidden={stringToBoolean(hideLabel)}
        id={`agora-checkbox-group-${propsClone.id}-label`}
      >
        {label}
      </span>
      <div
        {...propsClone}
        className="group"
        role="group"
        aria-label={stringToBoolean(hideLabel) ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
        aria-labelledby={`agora-checkbox-group-${propsClone.id}-label`}
      >
        {Children.map(allCheckboxes, (child) => {
          return cloneElement<CheckboxProps>(child, {
            ...child.props,
            onChange: innerOnChange
          } as CheckboxProps);
        })}
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export { CheckboxGroup };
