import React, { ComponentPropsWithoutRef, FC, ImgHTMLAttributes, ReactNode, useRef } from 'react';
import classNames from 'classnames';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { Pill, PillVariant } from '../../../atoms/pill';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-article.scss';

export interface CardArticleProps extends Omit<ComponentPropsWithoutRef<'div'>, 'title'> {
  /**
   * Children content.
   */
  children?: ReactNode;
  /**
   * variant.
   */
  variant?: 'default' | 'indented';
  /**
   * Image Card.
   */
  image?: ImgHTMLAttributes<HTMLImageElement>;
  /**
   * label Pill.
   */
  pillLabel?: ReactNode;
  /**
   * variant Pill.
   */
  pillVariant?: PillVariant;
  /**
   * Heading level to apply in the card.
   */
  headingLevel?: HeadingLevel;
  /**
   * Title card.
   */
  title?: ReactNode;
  /**
   * Subtitle card.
   */
  subtitle?: ReactNode;
  /**
   * Anchor props.
   */
  mainAnchor?: AnchorProps;
  /**
   * Makes the whole card clickable.
   */
  blockedLink?: BooleanProp;
}

export const CardArticle: FC<CardArticleProps> = ({
  pillLabel,
  pillVariant = 'primary',
  mainAnchor,
  blockedLink = false,
  children,
  image,
  headingLevel: Heading = 'h3',
  title,
  subtitle,
  variant = 'default',
  className,
  ...props
}) => {
  const cardArticleRef = useRef<HTMLDivElement | null>(null);
  const cardArticleAnchorRef = useRef<HTMLAnchorElement | null>(null);

  const headingPlacement = {
    title: !!title,
    subtitle: !title && !!subtitle,
    children: !title && !subtitle && !!children,
    pill: !title && !subtitle && !children && !!pillLabel,
    anchor: !title && !subtitle && !children && !pillLabel && !!mainAnchor && !mainAnchor.hidden
  };

  const anchorPlacement = {
    anchor: false,
    title: false,
    subtitle: false,
    children: false,
    pill: false
  };

  let mainAnchorClone: AnchorProps = { ...mainAnchor };

  if (mainAnchor) {
    mainAnchorClone = {
      ...mainAnchor
    } as AnchorProps;

    if (mainAnchorClone.hasIcon && !mainAnchorClone.trailingIcon && !mainAnchorClone.leadingIcon) {
      mainAnchorClone.trailingIcon = 'agora-line-arrow-right-circle';
      mainAnchorClone.trailingIconHover = 'agora-solid-arrow-right-circle';
    }

    if (mainAnchor.hidden) {
      //  MUST DELETE IN ORDER TO SPREAD IT LATER
      delete mainAnchorClone.hidden;

      //  ONE OF THE TEXTS MUST BE FILLED
      anchorPlacement.title = !!title;
      anchorPlacement.subtitle = !title && !!subtitle;
      anchorPlacement.children = !title && !subtitle && !!children;
      anchorPlacement.pill = !title && !subtitle && !children && !!pillLabel;
    } else {
      anchorPlacement.anchor = true;
    }
  }

  const buildText = (text: ReactNode, anchorProps: AnchorProps, isAnchor: boolean, isHeading: boolean): ReactNode => {
    if (!isAnchor) {
      return isHeading ? <Heading>{text}</Heading> : text;
    }

    return (
      <>
        {isHeading && (
          <Heading>
            <Anchor {...anchorProps} appearance="link" ref={cardArticleAnchorRef}>
              {text}
            </Anchor>
          </Heading>
        )}
        {!isHeading && (
          <Anchor {...anchorProps} appearance="link" ref={cardArticleAnchorRef}>
            {text}
          </Anchor>
        )}
      </>
    );
  };

  useBlockedLink({
    parentCardRef: cardArticleRef,
    anchorRef: cardArticleAnchorRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  const cardClassNames = classNames(
    'agora-card-article',
    { indented: variant === 'indented' },
    { default: variant === 'default' },
    className,
    { 'has-image': !!image }
  );

  const anchorContainerClassNames = classNames('anchor-container', { 'icon-only': mainAnchorClone?.iconOnly });

  return (
    <div {...props} role="presentation" ref={cardArticleRef} className={cardClassNames}>
      {image && (
        <div className="container-image">
          <img src={image.src} className="card-image w-full" alt={image.alt} />
        </div>
      )}

      <div className="container-body">
        {pillLabel && (
          <div className="pill-container">
            <Pill size="default" appearance="solid" variant={pillVariant}>
              {buildText(pillLabel, mainAnchorClone, anchorPlacement.pill, headingPlacement.pill)}
            </Pill>
          </div>
        )}

        {subtitle && (
          <div className="subtitle-container">
            {buildText(subtitle, mainAnchorClone, anchorPlacement.subtitle, headingPlacement.subtitle)}
          </div>
        )}

        {title && <div className="title-container">{buildText(title, mainAnchorClone, anchorPlacement.title, headingPlacement.title)}</div>}

        {children && (
          <div className="children-container">
            {buildText(children, mainAnchorClone, anchorPlacement.children, headingPlacement.children)}
          </div>
        )}

        {anchorPlacement.anchor && (
          <div className={anchorContainerClassNames}>
            {buildText(mainAnchorClone.children, mainAnchorClone, anchorPlacement.anchor, headingPlacement.anchor)}
          </div>
        )}
      </div>
    </div>
  );
};
