import classNames from 'classnames';
import React, {
  ChangeEvent,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState
} from 'react';

const MIN_YEAR = 1000;

export interface GroupedDateRef {
  value?: Date;
  focus: () => void;
}

export interface GroupedDateProps {
  value?: Date;
  onChange?: (date: Date) => void;
  handleInputDateKeydown: (event: KeyboardEvent<HTMLInputElement>) => void;
  dayInputPlaceholder?: string;
  monthInputPlaceholder?: string;
  yearInputPlaceholder?: string;
  className?: string;
  readOnly?: boolean;
  disabled?: boolean;
}

const isValidDate = (year: number, month: number, day: number): boolean => {
  if (year < MIN_YEAR) {
    return false;
  }

  const date = new Date(year, month - 1, day);
  return date.getFullYear() === year && date.getMonth() === month - 1 && date.getDate() === day;
};

const isLeapYear = (year: number): boolean => {
  return year % 4 === 0 && (year % 100 !== 0 || year % 400 === 0);
};

export const InnerGroupedDate: ForwardRefRenderFunction<GroupedDateRef, GroupedDateProps> = (
  {
    value,
    onChange,
    handleInputDateKeydown,
    dayInputPlaceholder = '',
    monthInputPlaceholder = '',
    yearInputPlaceholder = '',
    className = '',
    readOnly = false,
    disabled = false
  },
  ref
) => {
  const [day, setDay] = useState('');
  const [month, setMonth] = useState('');
  const [year, setYear] = useState('');

  const dayRef = useRef<HTMLInputElement>(null);
  const monthRef = useRef<HTMLInputElement>(null);
  const yearRef = useRef<HTMLInputElement>(null);

  const validateDate = useCallback(() => {
    if (day && month && year) {
      const isValid = isValidDate(Number(year), Number(month), Number(day));
      if (isValid && onChange) {
        onChange(new Date(Number(year), Number(month) - 1, Number(day)));
      }
    }
  }, [day, month, year]);

  // Function to validate and adjust the day value for February based on the year and month
  const validateFebruaryDay = (newDay: string, newMonth: string, newYear: string) => {
    const newDayNumber = Number(newDay);
    const newMonthNumber = Number(newMonth);
    const newYearNumber = Number(newYear);

    if (newMonthNumber === 2) {
      if (isLeapYear(newYearNumber)) {
        if (newDayNumber > 29) {
          return '29';
        }
      } else if (newDayNumber > 28) {
        return '28';
      }
    }

    return newDay;
  };

  useEffect(() => {
    if (value) {
      setDay(value.getDate().toString());
      setMonth((value.getMonth() + 1).toString());
      setYear(value.getFullYear().toString());
    }
  }, [value]);

  useEffect(() => {
    validateDate();
  }, [validateDate]);

  // day input
  const handleDayChange = (event: ChangeEvent<HTMLInputElement>) => {
    const newDay = parseInt(event.target.value, 10).toString();

    if (newDay === '' || (parseInt(newDay, 10) >= 1 && parseInt(newDay, 10) <= 31)) {
      setDay(newDay);
    }
  };

  // month input
  const handleMonthChange = (event: ChangeEvent<HTMLInputElement>) => {
    const newMonth = parseInt(event.target.value, 10).toString();

    if (newMonth === '' || (parseInt(newMonth, 10) >= 1 && parseInt(newMonth, 10) <= 12)) {
      setMonth(newMonth);
    }
  };

  // year input
  const handleYearChange = (event: ChangeEvent<HTMLInputElement>) => {
    const newYear = parseInt(event.target.value, 10).toString();

    if (newYear === '' || (parseInt(newYear, 10) >= 0 && parseInt(newYear, 10) <= 9999)) {
      setYear(newYear);
      setDay(validateFebruaryDay(day, month, newYear));
    }
  };

  // clear inputs on backspace
  const handleInputsClear = (event: KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Backspace') {
      const target = event.target as HTMLInputElement;

      if (target === dayRef.current) {
        setDay('');
      } else if (target === monthRef.current) {
        setMonth('');
      } else if (target === yearRef.current) {
        setYear('');
      }
    }
  };

  const handleTargetIsDay = (target: HTMLInputElement, key: string, dayValue: number) => {
    if (target === dayRef.current) {
      if (key === 'ArrowUp') {
        dayValue = dayValue >= 31 ? 1 : dayValue + 1;
      } else if (key === 'ArrowDown') {
        dayValue = dayValue <= 1 ? 31 : dayValue - 1;
      }
      setDay(dayValue.toString().padStart(2, '0'));
    }
  };

  const handleTargetIsMonth = (target: HTMLInputElement, key: string, monthValue: number) => {
    if (target === monthRef.current) {
      if (key === 'ArrowUp') {
        monthValue = monthValue >= 12 ? 1 : monthValue + 1;
      } else if (key === 'ArrowDown') {
        monthValue = monthValue <= 1 ? 12 : monthValue - 1;
      }

      if (monthValue >= 1 && monthValue <= 12) {
        setMonth(monthValue.toString().padStart(2, '0'));
      }
    }
  };

  const handleTargetIsYear = (target: HTMLInputElement, key: string, yearValue: number) => {
    if (target === yearRef.current) {
      if (yearValue === 0) {
        // Set the initial value to the current year
        yearValue = new Date().getFullYear();
      } else {
        yearValue = key === 'ArrowUp' ? yearValue + 1 : yearValue - 1;
      }

      if (yearValue >= MIN_YEAR && yearValue <= 9999) {
        setYear(yearValue.toString());
      }
    }
  };

  // Function to increment or decrement date value using arrow keys
  const handleInputKeyUp = (event: KeyboardEvent<HTMLInputElement>) => {
    if (disabled) {
      event.preventDefault();
      return;
    }

    if (readOnly && event.key !== 'Tab') {
      event.preventDefault();
      return;
    }

    const target = event.target as HTMLInputElement;

    if (event.key === 'ArrowUp' || event.key === 'ArrowDown') {
      const v = target.value ? parseInt(target.value, 10) : 0;
      handleTargetIsDay(target, event.key, v);
      handleTargetIsMonth(target, event.key, v);
      handleTargetIsYear(target, event.key, v);
    }
  };

  // combine handleInputDateKeydown and handleInputKeyDown to one function
  const handleCombinedInputKeyDown = (event: KeyboardEvent<HTMLInputElement>) => {
    if (disabled) {
      event.preventDefault();
      return;
    }

    if (readOnly && event.key !== 'Tab') {
      event.preventDefault();
      return;
    }

    handleInputsClear(event);
    handleInputDateKeydown?.(event);
  };

  // format day, month and year

  const formattedDay = useMemo(() => {
    let newDay = '';
    if (day !== '') {
      if (parseInt(day, 10) < 10 && day.length === 1) {
        newDay = `0${day}`;
      } else {
        newDay = day;
      }
    }

    return newDay;
  }, [day]);

  const formattedMonth = useMemo(() => {
    let newMonth = '';
    if (month !== '') {
      if (parseInt(month, 10) < 10 && month.length === 1) {
        newMonth = `0${month}`;
      } else {
        newMonth = month;
      }
    }

    return newMonth;
  }, [month]);

  const formattedYear = useMemo(() => {
    return year !== '' ? year : '';
  }, [year]);

  const focus = useCallback(() => {
    dayRef.current?.focus();
  }, []);

  useImperativeHandle(
    ref,
    () => {
      return {
        value: new Date(Number(year), Number(month) - 1, Number(day)),
        focus
      };
    },
    [day, month, year, focus]
  );

  const groupedDateWrapperClasses = classNames('agora-input-date', className);

  return (
    <div className={groupedDateWrapperClasses}>
      <div className="grouped-date">
        <input
          ref={dayRef}
          type="text"
          placeholder={dayInputPlaceholder}
          onKeyDown={handleCombinedInputKeyDown}
          onKeyUp={handleInputKeyUp}
          value={formattedDay}
          onChange={handleDayChange}
          className="grouped-date__day"
          min={1}
          max={31}
          disabled={disabled}
        />
        <span className="grouped-date__separator">/</span>
        <input
          ref={monthRef}
          type="text"
          placeholder={monthInputPlaceholder}
          onKeyDown={handleCombinedInputKeyDown}
          onKeyUp={handleInputKeyUp}
          value={formattedMonth}
          onChange={handleMonthChange}
          className="grouped-date__month"
          min={1}
          max={12}
          disabled={disabled}
        />
        <span className="grouped-date__separator">/</span>
        <input
          ref={yearRef}
          type="text"
          placeholder={yearInputPlaceholder}
          onKeyDown={handleCombinedInputKeyDown}
          onKeyUp={handleInputKeyUp}
          value={formattedYear}
          onChange={handleYearChange}
          className="grouped-date__year"
          disabled={disabled}
        />
      </div>
    </div>
  );
};

export const GroupedDate = forwardRef<GroupedDateRef, GroupedDateProps>(InnerGroupedDate);
