import classNames from 'classnames';
import React, {
  ChangeEvent,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent as ReactKeyboardEvent,
  ReactNode,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Icon } from '../../atoms/icon';
import { Feedback } from '../../shared/feedback';

import FlagsList, { IFlagProps } from './resources/config';
export const PhoneCountryCodes = FlagsList.map((l) => l.code);
export type PhoneCountryCode = (typeof PhoneCountryCodes)[number];

export const PhoneCountryISOs = FlagsList.map((l) => l.iso);
export type PhoneCountryISO = (typeof PhoneCountryISOs)[number];

import { Dropdown, DropdownOption, DropdownOptionProp, DropdownRef, DropdownSection } from '../../molecules/dropdown';
import './input-phone.scss';

export interface InputPhoneRef {
  input: HTMLInputElement;
  iso: PhoneCountryISO;
  code: PhoneCountryCode;
}

export interface InputPhoneProps extends Omit<ComponentPropsWithRef<'input'>, 'onChange'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Sets the input in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below input.
   */
  feedbackText?: ReactNode;
  /**
   *  Define de initial country selected using the country ISO. Will override defaultCode prop.
   */
  defaultISO?: PhoneCountryISO;
  /**
   *  Define de initial country selected using the country phone code (without +). Will be overridden by defaultISO prop.
   */
  defaultCode?: PhoneCountryCode;
  /**
   * Alternative text that labels the list of country flag options of the dropdown list.
   */
  dropdownAriaLabel?: string;
  /**
   * Enable the search bar to filter items.
   */
  searchable?: BooleanProp;
  /**
   * Placeholder text for input search bar.
   */
  searchInputPlaceholder?: string;
  /**
   * Accessible text for the input search bar.
   */
  searchInputAriaLabel?: string;
  /**
   *  Text to show when filtering presents no results.
   */
  searchNoResultsText?: string;
  /**
   *  Number of visible items to display in the DropdownList. Remaining items will be accessible via scroll bar.
   */
  visibleCount?: number;
  /**
   *  Callback to run whenever the input value or country dropdown changes.
   */
  onChange: (args: InputPhoneRef) => void;
}

const InnerInputPhone: ForwardRefRenderFunction<InputPhoneRef | HTMLInputElement, InputPhoneProps> = (
  {
    label = 'Input Label',
    defaultISO,
    defaultCode,
    hideLabel,
    hasError,
    hasFeedback,
    feedbackText,
    feedbackState = 'info',
    className = '',
    searchInputAriaLabel,
    searchInputPlaceholder,
    searchNoResultsText,
    visibleCount,
    required = true,
    searchable,
    dropdownAriaLabel,
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const wrapperRef = useRef<HTMLDivElement>(null);

  const inputRef = useRef<HTMLInputElement>(null);

  const buttonRef = useRef<HTMLButtonElement>(null);

  const dropdownRef = useRef<DropdownRef | null>(null);

  const [isDropdownVisible, setIsDropdownVisible] = useState(false);

  const [hasValue, setHasValue] = useState<boolean>(propsClone.value ? propsClone.value.toString().length > 0 : false);

  let initial: IFlagProps = FlagsList[0];

  const searchForProp = defaultISO ?? defaultCode;

  if (searchForProp) {
    initial =
      FlagsList.find((country) => {
        return country.code === searchForProp || country.iso.toLocaleLowerCase() === searchForProp.toLocaleLowerCase();
      }) ?? initial;
  }

  const [currentCountry, setCurrentCountry] = useState<IFlagProps>(initial);

  //  Necessary to override the default initial value and currentCountry in case of initial code or iso values changed
  useEffect(() => {
    setCurrentCountry(initial);
  }, [initial]);

  useImperativeHandle(
    ref,
    () => {
      return {
        input: inputRef.current as HTMLInputElement,
        iso: currentCountry.iso,
        code: currentCountry.code
      } as InputPhoneRef;
    },
    [currentCountry]
  );

  const onHide = () => {
    setIsDropdownVisible(false);
    if (wrapperRef.current?.contains(document.activeElement)) {
      inputRef.current?.focus();
    }
  };

  const onShow = () => {
    setIsDropdownVisible(true);
  };

  const handleDropDownControlKeyUp = (evt: ReactKeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (dropdownRef.current) {
      if (code === 'Escape') {
        dropdownRef.current.hide();
      }

      if (dropdownRef.current.visibility) {
        if (code === 'ArrowDown') {
          dropdownRef.current.first();
        }

        if (code === 'ArrowUp') {
          dropdownRef.current.last();
        }

        if (code === 'Tab') {
          dropdownRef.current.first();
        }
      }
    }
  };

  const handleDropDownControlKeyDownCapture = (evt: ReactKeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (dropdownRef.current?.visibility) {
      if (code === 'Tab' || code === 'ArrowDown' || code === 'ArrowUp') {
        //  PREVENT SCROLL AND TAB INDEX CHANGE FOCUS
        evt.preventDefault();
        evt.stopPropagation();
      }
    }
  };

  const onDropdownChange = (options: DropdownOptionProp[]) => {
    const selectedCountry = FlagsList.find((f) => f.iso === options[0].value);

    if (!selectedCountry) {
      return;
    }

    setCurrentCountry({
      country: selectedCountry.country,
      code: selectedCountry.code,
      iso: selectedCountry.iso,
      flag: selectedCountry.flag
    } as IFlagProps);

    propsClone.onChange?.({
      input: inputRef.current as HTMLInputElement,
      iso: selectedCountry.iso,
      code: selectedCountry.code
    } as InputPhoneRef);
  };

  const handleInputChange = (evt: ChangeEvent<HTMLInputElement>) => {
    const { value } = evt.currentTarget;

    setHasValue(value.length > 0);

    propsClone.onChange?.({
      input: inputRef.current as HTMLInputElement,
      iso: currentCountry.iso,
      code: currentCountry.code
    } as InputPhoneRef);
  };

  const handleDropDownControlSelectClicked = () => {
    if (propsClone.disabled || propsClone.readOnly || !dropdownRef.current) {
      return;
    }

    if (!dropdownRef.current.visibility) {
      dropdownRef.current.show();
    } else {
      dropdownRef.current.hide();
    }
  };

  useEffect(() => {
    const handleClickOutside = (evt: MouseEvent) => {
      if (dropdownRef.current?.visibility && !wrapperRef.current?.contains(evt.target as HTMLElement)) {
        dropdownRef.current.hide();
      }
      if (!wrapperRef.current?.contains(evt.target as HTMLElement)) {
        (document.activeElement as HTMLElement).blur();
      }
    };

    document?.addEventListener('click', handleClickOutside);

    return () => {
      document?.removeEventListener('click', handleClickOutside);
    };
  }, []);

  //  REMOVE ARIA DESCENDANT ON HIDE OVERLAY
  useEffect(() => {
    if (!isDropdownVisible && buttonRef.current) {
      buttonRef.current.setAttribute('aria-activedescendant', '');
    }
  }, [isDropdownVisible]);

  //  PREVENT NON NUMERIC INPUTS
  useEffect(() => {
    const keypressHandler = (evt: KeyboardEvent) => {
      if (/\d/.test(evt.key)) {
        return true;
      }

      evt.preventDefault();
      evt.stopPropagation();

      return false;
    };

    const elem: HTMLInputElement | null = inputRef.current;

    if (elem) {
      elem.addEventListener('keypress', keypressHandler);
    }

    return () => {
      if (elem) {
        elem.removeEventListener('keypress', keypressHandler);
      }
    };
  }, []);

  const wrapperClassNames = classNames('agora-input-phone', className);

  const inputWrapperClassNames = classNames('input-wrapper', {
    'has-error': stringToBoolean(hasError),
    'has-value': hasValue,
    'disabled': propsClone.disabled,
    'readonly': propsClone.readOnly
  });

  return (
    <div hidden={!!propsClone.hidden} className={wrapperClassNames} ref={wrapperRef}>
      {/* Label */}
      {label && !stringToBoolean(hideLabel) && (
        <label htmlFor={propsClone.id} className="agora-input-phone-label">
          {label}
        </label>
      )}

      {/* Input wrapper */}
      <div className={inputWrapperClassNames}>
        <button
          type="button"
          ref={buttonRef}
          className="country-dropdown-control"
          tabIndex={propsClone.disabled || propsClone.readOnly ? -1 : 0}
          onClick={handleDropDownControlSelectClicked}
          onKeyUp={handleDropDownControlKeyUp}
          onKeyDownCapture={handleDropDownControlKeyDownCapture}
          role="combobox"
          aria-controls={`${propsClone.id}-dropdown-container`}
          aria-expanded={isDropdownVisible}
          aria-label={hideLabel ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
          aria-haspopup="listbox"
          aria-required={required}
          disabled={propsClone.disabled}
        >
          <img className="shadow-center-lower" src={`${currentCountry.flag}`} alt={currentCountry.country} width={26} />
          <Icon name="agora-solid-arrow-down" aria-hidden className="ml-8" />
        </button>

        <span className="country-code text-[var(--color-neutral-300)]" aria-disabled>
          +({currentCountry.code ?? FlagsList[0].code})
        </span>

        <input
          type="tel"
          id={propsClone.id}
          className="agora-input-text"
          {...propsClone}
          disabled={propsClone.disabled}
          readOnly={propsClone.readOnly}
          aria-invalid={stringToBoolean(hasError)}
          aria-disabled={stringToBoolean(propsClone.disabled)}
          aria-required={required}
          ref={inputRef}
          onChange={handleInputChange}
        />

        <div className="absolute -bottom-8 left-0 w-full min-h-[8px]">
          <Dropdown
            className="min-w-[224px]"
            ref={dropdownRef}
            onHide={onHide}
            onShow={onShow}
            onChange={onDropdownChange}
            type="text"
            searchable={searchable}
            searchAriaLabel={searchInputAriaLabel}
            searchPlaceholder={searchInputPlaceholder}
            noSearchResults={searchNoResultsText}
            optionsVisible={visibleCount}
          >
            <DropdownSection
              name={'countries'}
              value={`input-phone-${propsClone.id}-countries-dropdown-section`}
              hideSectionName
              aria-label={dropdownAriaLabel}
            >
              {FlagsList.map((country, index) => {
                const optionIndex = `input-phone-${propsClone.id}-countries-dropdown-section-${index}`;
                return (
                  <DropdownOption
                    key={optionIndex}
                    value={country.iso}
                    selected={country.iso.toLocaleLowerCase() === currentCountry.iso.toLocaleLowerCase()}
                    hasIcon
                    icon={country.flag}
                  >{`+(${country.code}) ${country.country}`}</DropdownOption>
                );
              })}
            </DropdownSection>
          </Dropdown>
        </div>
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputPhone = forwardRef<InputPhoneRef | HTMLInputElement, InputPhoneProps>(InnerInputPhone);
