import classNames from 'classnames';
import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, HTMLProps } from 'react';
import './glossary-letters.scss';

export type LetterStatus = 'default' | 'disabled' | 'selected';

export interface GlossaryLetter extends HTMLProps<HTMLAnchorElement> {
  /**
   * Letter to be rendered
   */
  letter: string;
  /**
   *  Glossary letter status
   */
  status: LetterStatus;
  /**
   * Data attributes
   */
  [dataAttribute: `data-${string}`]: string;
}

export interface GlossaryLettersProps extends ComponentPropsWithRef<'nav'> {
  /**
   * Letters Array
   */
  letters?: GlossaryLetter[];
}

export const InnerGlossaryLetters: ForwardRefRenderFunction<HTMLDivElement, GlossaryLettersProps> = ({ letters, ...props }, ref) => {
  const NavElementClass = classNames('navElement');
  const GlossaryContainerClass = classNames('glossary-container');

  return (
    <nav {...props} className={NavElementClass} ref={ref}>
      <div className={GlossaryContainerClass}>
        {letters?.map(({ letter, status, ...anchorProps }) => {
          const isDisabled = status === 'disabled';
          const isSelected = status === 'selected';
          const anchorClassName = classNames('anchor', { 'anchor-disabled': isDisabled }, { 'anchor-selected': isSelected });
          const tabIndex = isDisabled ? -1 : 0;
          const href = `#${letter}`;

          return (
            <a key={`${letter}-${href}`} {...anchorProps} className={anchorClassName} tabIndex={tabIndex} href={href}>
              {letter}
            </a>
          );
        })}
      </div>
    </nav>
  );
};

export const GlossaryLetters = forwardRef<HTMLDivElement, GlossaryLettersProps>(InnerGlossaryLetters);
