import React, {
  ChangeEvent,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  MouseEvent,
  RefObject,
  useEffect,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { useDebounceHook } from '../../../../utils';
import { Icon } from '../../../atoms/icon';
import { useInternalDropdownContext } from './internal-dropdown-context';
import './internal-dropdown-filter-section.scss';

export interface InternalDropdownFilterSectionRef extends RefObject<HTMLInputElement> {
  filterElem: HTMLLIElement | null;
  resetFilter: () => void;
}

export interface InternalDropdownFilterSectionProps extends ComponentPropsWithRef<'input'> {
  debounceTimeout?: number;
}

const InnerInternalDropdownFilterSection: ForwardRefRenderFunction<
  InternalDropdownFilterSectionRef | null,
  InternalDropdownFilterSectionProps
> = ({ debounceTimeout = 300, ...props }, ref) => {
  const dropdownContext = useInternalDropdownContext();
  const visibility = dropdownContext.visibility;
  const applyFilter = dropdownContext.applyFilter;
  const hide = dropdownContext.hide;
  const next = dropdownContext.next;
  const prev = dropdownContext.prev;
  const last = dropdownContext.last;

  const [currentFilter, setCurrentFilter] = useState('');

  const debouncedApplyFilter = useDebounceHook(applyFilter, debounceTimeout);

  const onInputChange = (evt: ChangeEvent<HTMLInputElement>) => {
    setCurrentFilter(evt.target.value ?? '');
  };

  useEffect(() => {
    debouncedApplyFilter.setArgs(currentFilter);
  }, [currentFilter, debouncedApplyFilter]);

  const resetFilter = () => {
    setCurrentFilter('');
  };

  const inputRef = useRef<HTMLInputElement>(null);
  const innerRef = useRef<HTMLLIElement>(null);
  useImperativeHandle(
    ref,
    () => {
      return {
        current: innerRef.current,
        resetFilter
      } as InternalDropdownFilterSectionRef;
    },
    [innerRef]
  );

  const onKeyDownCaptureHandler = (evt: KeyboardEvent<HTMLInputElement>) => {
    const code = evt.code;

    if (code === 'Tab') {
      evt.preventDefault();
      evt.stopPropagation();
    }
  };

  const onKeyUpHandler = (evt: KeyboardEvent<HTMLInputElement>) => {
    const code = evt.code;
    const shiftKey = evt.shiftKey;

    switch (code) {
      case 'Tab':
        if (shiftKey) {
          prev();
        } else {
          next();
        }
        break;
      case 'Escape':
        hide();
        break;
      case 'ArrowUp':
        last();
        break;
      case 'Enter':
      case 'NumpadEnter':
      case 'ArrowDown':
        next();
        break;
    }
  };

  const onMouseDownCaptureHandler = (evt: MouseEvent<SVGElement | HTMLImageElement>) => {
    evt.preventDefault();
    inputRef.current?.focus();
  };

  useEffect(() => {
    if (innerRef.current && visibility) {
      const { height } = innerRef.current.getBoundingClientRect();
      innerRef.current.setAttribute('data-filter-section-height', `${height}`);
    }
  }, [visibility]);

  return (
    <ul className="agora-dropdown-filter-section" role="none">
      <li className="agora-dropdown-filter-section-item" ref={innerRef} role="none">
        <input
          ref={inputRef}
          {...props}
          className={'input-filter'}
          type="text"
          value={currentFilter}
          onChange={onInputChange}
          onKeyDownCapture={onKeyDownCaptureHandler}
          onKeyUp={onKeyUpHandler}
        />
        <Icon className="input-icon" name="agora-line-search" aria-hidden onMouseDownCapture={onMouseDownCaptureHandler} />
      </li>
    </ul>
  );
};

export const InternalDropdownFilterSection = forwardRef<InternalDropdownFilterSectionRef | null, InternalDropdownFilterSectionProps>(
  InnerInternalDropdownFilterSection
);
