import classNames from 'classnames';
import React, { cloneElement, ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction, useEffect, useMemo, useState } from 'react';
import { useWindowSize } from '../../../hooks';
import { stringToBoolean } from '../../../utils';
import { Button } from '../../atoms/button';
import { SearchPage, SearchPageProps } from './search-page';
import { SearchPaginationNumberOverlay } from './search-pagination-number-overlay';
import './search-pagination.scss';

export interface SearchPaginationProps extends Omit<ComponentPropsWithoutRef<'nav'>, 'onChange'> {
  /**
   * Define the total number of pages.
   */
  totalPages?: number;

  /**
   * The flag to hide previous page button.
   */
  hidePreviousPageButton?: boolean;

  /**
   * The flag to hide next page button.
   */
  hideNextPageButton?: boolean;

  /**
   * Define the number of visible page buttons at the beginning and end.
   */
  boundaryCount?: number;

  /**
   * Define the number of visible page buttons before and after the current page.
   */
  siblingCount?: number;

  /**
   * Search Pagination navigation alternative text.
   */
  label: string;

  /**
   * Next page button alternative text.
   */
  nextPageAriaLabel: string;

  /**
   * Previous page button alternative text.
   */
  previousPageAriaLabel: string;

  /**
   *  Event triggered whenever any of the search pages changes is current state.
   */
  onChange?: (page: number) => void;
}

export const InnerSearchPagination: ForwardRefRenderFunction<HTMLDivElement, SearchPaginationProps> = (
  {
    totalPages = 0,
    className,
    hidePreviousPageButton,
    hideNextPageButton,
    boundaryCount = 0,
    siblingCount = 0,
    label,
    nextPageAriaLabel,
    previousPageAriaLabel,
    onChange,
    ...props
  },
  ref
) => {
  const { width } = useWindowSize();
  const [activePage, setActivePage] = useState(0);
  const navArrowsClasses = classNames('nav-arrows');

  // Mobile screen limitations
  if (width <= 360) {
    boundaryCount = 1;
    siblingCount = 0;
  }

  useEffect(() => {
    onChange?.(activePage);
  }, [activePage]);

  const prev = () => {
    if (activePage > 0) {
      setActivePage((prevPage) => prevPage - 1);
    }
  };

  const next = () => {
    if (activePage < totalPages - 1) {
      setActivePage((prevPage) => prevPage + 1);
    }
  };

  const pages = Array.from({ length: totalPages }, (_, index) => {
    return <SearchPage key={index} pageNumber={index + 1} />;
  });

  const showAllPagesContent = useMemo(() => {
    return pages?.map((page, page_index) => {
      return cloneElement<SearchPageProps>(page, {
        ...page.props,
        onClick: () => setActivePage(page_index),
        current: stringToBoolean(activePage === page_index)
      });
    });
  }, [className, totalPages, boundaryCount, siblingCount, activePage]);

  const paginationContent = useMemo(() => {
    // Calculate left and right sibling index and make sure they are within range index 0 and totalPages
    const leftSiblingIndex = Math.max(activePage - siblingCount, 0);
    const rightSiblingIndex = Math.min(activePage + siblingCount, totalPages);

    // Get visible pagination numbers
    const leftRange = pages.slice(0, boundaryCount);
    const middleRange = pages.slice(leftSiblingIndex, rightSiblingIndex + 1);
    const rightRange = pages.slice(totalPages - boundaryCount, totalPages);
    const visiblePages = [...new Set([...leftRange, ...middleRange, ...rightRange])];

    // Preview pagination dots if necessary
    const shouldShowLeftDots = leftSiblingIndex > boundaryCount;
    const shouldShowRightDots = rightSiblingIndex < totalPages - (boundaryCount + 1);

    return pages?.map((page, page_index) => {
      if (visiblePages.some((item) => page == item)) {
        return cloneElement<SearchPageProps>(page, {
          ...page.props,
          onClick: () => setActivePage(page_index),
          current: stringToBoolean(activePage === page_index)
        });
      }

      if (shouldShowLeftDots && page_index == boundaryCount) {
        return <SearchPaginationNumberOverlay key="left-dots" value="..." />;
      }

      if (shouldShowRightDots && page_index == totalPages - boundaryCount - 1) {
        return <SearchPaginationNumberOverlay key="right-dots" value="..." />;
      }
    });
  }, [className, totalPages, boundaryCount, siblingCount, activePage]);

  return (
    <nav {...props} aria-label={label} className="search-pagination" ref={ref}>
      <Button
        onClick={prev}
        appearance="link"
        hasIcon="true"
        iconOnly="true"
        leadingIcon="agora-solid-chevron-left"
        leadingIconHover="agora-solid-chevron-left"
        disabled={activePage === 0}
        className={`${navArrowsClasses} ${hidePreviousPageButton ? 'hide-arrow' : 'show-arrow'}`}
        aria-label={previousPageAriaLabel}
      ></Button>
      <div className="nav-pages">{boundaryCount === 0 && siblingCount === 0 ? showAllPagesContent : paginationContent}</div>
      <Button
        onClick={next}
        appearance="link"
        hasIcon="true"
        iconOnly="true"
        trailingIcon="agora-solid-chevron-right"
        trailingIconHover="agora-solid-chevron-right"
        disabled={activePage === totalPages - 1}
        className={`${navArrowsClasses} ${hideNextPageButton ? 'hide-arrow' : 'show-arrow'}`}
        aria-label={nextPageAriaLabel}
      ></Button>
    </nav>
  );
};

export const SearchPagination = forwardRef<HTMLDivElement, SearchPaginationProps>(InnerSearchPagination);
