import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode, useRef } from 'react';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-frame.scss';

export interface CardFrameProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Children content.
   */
  children: ReactNode;
  /**
   * header Accordion.
   */
  headingLevel: HeadingLevel;
  /**
   * label header.
   */
  label: ReactNode;
  /**
   * Anchor props.
   */
  mainAnchor?: AnchorProps;
  /**
   * Makes the whole card clickable.
   */
  blockedLink?: BooleanProp;
}

export const CardFrame: FC<CardFrameProps> = ({
  className,
  label,
  headingLevel: Heading = 'h3',
  mainAnchor,
  blockedLink = false,
  children,
  ...props
}) => {
  const cardFrameRef = useRef<HTMLDivElement | null>(null);
  const cardFrameAnchorRef = useRef<HTMLAnchorElement | null>(null);

  const cardFrameClassNames = classNames('card-frame', className);

  const headingPlacement = {
    label: !!label,
    children: !label && !!children,
    anchor: !label && !children && !!mainAnchor && !mainAnchor.hidden
  };

  const anchorPlacement = {
    anchor: false,
    label: false,
    children: false
  };

  let mainAnchorClone: AnchorProps = { ...mainAnchor };

  if (mainAnchor) {
    mainAnchorClone = {
      ...mainAnchor
    } as AnchorProps;

    if (mainAnchorClone.hasIcon && !mainAnchorClone.trailingIcon && !mainAnchorClone.leadingIcon) {
      mainAnchorClone.leadingIcon = 'agora-line-arrow-right-circle';
      mainAnchorClone.leadingIconHover = 'agora-solid-arrow-right-circle';
    }

    if (mainAnchor.hidden) {
      //  MUST DELETE IN ORDER TO SPREAD IT LATER
      delete mainAnchorClone.hidden;

      //  ONE OF THE TEXTS MUST BE FILLED
      anchorPlacement.label = !!label;
      anchorPlacement.children = !label && !!children;
    } else {
      anchorPlacement.anchor = true;
    }
  }

  const buildText = (text: ReactNode, anchorProps: AnchorProps, isAnchor: boolean, isHeading: boolean): ReactNode => {
    if (!isAnchor) {
      return isHeading ? <Heading>{text}</Heading> : text;
    }

    return (
      <>
        {isHeading && (
          <Heading>
            <Anchor {...anchorProps} appearance="link" ref={cardFrameAnchorRef}>
              {text}
            </Anchor>
          </Heading>
        )}
        {!isHeading && (
          <Anchor {...anchorProps} appearance="link" ref={cardFrameAnchorRef}>
            {text}
          </Anchor>
        )}
      </>
    );
  };

  useBlockedLink({
    parentCardRef: cardFrameRef,
    anchorRef: cardFrameAnchorRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  const cardFrameContainerClass = classNames('container-body', { 'anchor-icon-only': mainAnchor?.iconOnly }, className);

  return (
    <div {...props} ref={cardFrameRef} role="presentation" className={cardFrameClassNames}>
      <div className={cardFrameContainerClass}>
        {label && <div className="label-container">{buildText(label, mainAnchorClone, anchorPlacement.label, headingPlacement.label)}</div>}

        {children && (
          <div className="children-container">
            {buildText(children, mainAnchorClone, anchorPlacement.children, headingPlacement.children)}
          </div>
        )}

        {anchorPlacement.anchor && (
          <div className="anchor-container">
            {buildText(mainAnchorClone.children, mainAnchorClone, anchorPlacement.anchor, headingPlacement.anchor)}
          </div>
        )}
      </div>
    </div>
  );
};
