import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction, ReactNode } from 'react';
import { StatusCardType } from './status-card';

export interface NonInteractiveStatusCardProps extends ComponentPropsWithoutRef<'div'> {
  className?: string;
  children?: ReactNode;
  type?: StatusCardType;
}

export const InnerNonInteractiveStatusCard: ForwardRefRenderFunction<HTMLDivElement, NonInteractiveStatusCardProps> = (
  { className, 'aria-label': ariaLabel, children, ...props },
  ref
) => {
  const containerClasses = classNames(`status-card status-${props.type} non-interactive`, className);

  return (
    <div {...props} role="status" className={containerClasses} ref={ref}>
      <span className="sr-only">{ariaLabel}</span>
      <div aria-hidden={!!ariaLabel}>{children}</div>
    </div>
  );
};

export const NonInteractiveStatusCard = forwardRef<HTMLDivElement, NonInteractiveStatusCardProps>(InnerNonInteractiveStatusCard);
