import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode } from 'react';
import './tab-header.scss';

export interface TabHeaderProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Children content.
   */
  children?: ReactNode;
}

export const TabHeader: FC<TabHeaderProps> = ({ children, className = '', ...props }) => {
  const TabHeaderClassName = classNames('tab-header', className);
  return (
    <div {...props} className={TabHeaderClassName}>
      {children}
    </div>
  );
};
