import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  Ref,
  useCallback,
  useEffect,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import './dialog.scss';

export interface DialogElementRef {
  /** Reference to the dialog container HTMLDivElement */
  current?: HTMLDivElement;
  /** Flag that tells if the dialog is visible or not */
  visible?: boolean;
  /** Show the dialog  */
  show: () => void;
  /** Hide the dialog  */
  hide: () => void;
}

export interface DialogProps extends ComponentPropsWithRef<'div'> {
  /**
   * Initial visibility state of the dialog
   * */
  visible?: BooleanProp;
  /**
   * Renders the dialog content as full screen without a backdrop
   */
  fullScreen?: BooleanProp;
  /**
   * If not a fullscreen dialog, allows the user to dismiss the dialog by clicking in the backdrop
   */
  dismissOnBackdropClick?: BooleanProp;
  /**
   * Allows the dialog to be closed by pressing Escape key.
   */
  dismissOnEscape?: BooleanProp;
}

export const InnerDialog: ForwardRefRenderFunction<DialogElementRef, DialogProps> = (
  {
    visible = false,
    fullScreen = false,
    dismissOnBackdropClick = true,
    dismissOnEscape = true,
    children,
    className,
    ...props
  }: DialogProps,
  ref: Ref<DialogElementRef>
) => {
  const innerDialogRef = useRef<HTMLDivElement | null>(null);
  const [isVisible, setIsVisible] = useState(stringToBoolean(visible));

  const show = useCallback((): void => {
    setIsVisible(true);
  }, [setIsVisible]);

  const hide = useCallback((): void => {
    setIsVisible(false);
  }, []);

  useImperativeHandle(
    ref,
    () => {
      return {
        current: innerDialogRef.current,
        visible: isVisible,
        show,
        hide
      } as DialogElementRef;
    },
    [innerDialogRef, hide, show, isVisible]
  );

  const handleKeyDown = useCallback(
    (event: KeyboardEvent) => {
      if (event.key === 'Escape' && stringToBoolean(dismissOnEscape)) {
        event.preventDefault();
        hide();

        return;
      }

      if (event.key === 'Tab' && innerDialogRef.current) {
        const focusableElements = innerDialogRef.current.querySelectorAll(
          'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
        );

        const firstElement = focusableElements[0] as HTMLElement;
        const lastElement = focusableElements[focusableElements.length - 1] as HTMLElement;

        if (event.shiftKey && document.activeElement === firstElement) {
          event.preventDefault();
          lastElement.focus();
        } else if (!event.shiftKey && document.activeElement === lastElement) {
          event.preventDefault();
          firstElement.focus();
        }
      }
    },
    [dismissOnEscape, hide]
  );

  const handleClickOutside = useCallback(
    (event: MouseEvent) => {
      if ((event.target as HTMLElement).classList.contains('agora-dialog-backdrop')) {
        hide();
      }
    },
    [hide]
  );

  useEffect(() => {
    document?.addEventListener('keydown', handleKeyDown);

    if (!stringToBoolean(fullScreen) && stringToBoolean(dismissOnBackdropClick)) {
      document?.addEventListener('click', handleClickOutside);
    }

    return () => {
      document?.removeEventListener('keydown', handleKeyDown);

      if (!stringToBoolean(fullScreen) && stringToBoolean(dismissOnBackdropClick)) {
        document?.removeEventListener('click', handleClickOutside);
      }
    };
  }, [dismissOnEscape, fullScreen, dismissOnBackdropClick, handleClickOutside, handleKeyDown]);

  useEffect(() => {
    if (isVisible) {
      document.body.classList.add('overflow-hidden');

      if (innerDialogRef.current) {
        const focusableElements = innerDialogRef.current.querySelectorAll(
          'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
        );
        const firstElement = focusableElements[0] as HTMLElement;

        if (firstElement) {
          firstElement.focus();
        }
      }
    } else {
      document.body.classList.remove('overflow-hidden');
    }
  }, [isVisible]);

  const containerClassNames = classNames(
    'agora-dialog',
    {
      closed: !isVisible
    },
    className
  );

  return (
    <div ref={innerDialogRef} className={containerClassNames} aria-modal="true" role="dialog" {...props}>
      {!fullScreen && <div className="agora-dialog-backdrop" />}
      {children}
    </div>
  );
};

export const Dialog = forwardRef<DialogElementRef, DialogProps>(InnerDialog);
