import { SyntheticEvent } from 'react';

export const createSyntheticChangeEvent = (target: HTMLInputElement): SyntheticEvent<HTMLInputElement, Event> => {
  const event = new Event('change', { bubbles: true });
  Object.defineProperty(event, 'target', { writable: false, value: target });

  let isDefaultPrevented = false;
  let isPropagationStopped = false;

  const preventDefault = () => {
    isDefaultPrevented = true;
    event.preventDefault();
  };

  const stopPropagation = () => {
    isPropagationStopped = true;
    event.stopPropagation();
  };

  return {
    nativeEvent: event,
    currentTarget: event.currentTarget as EventTarget & HTMLInputElement,
    target: event.target as EventTarget & HTMLInputElement,
    bubbles: event.bubbles,
    cancelable: event.cancelable,
    defaultPrevented: event.defaultPrevented,
    eventPhase: event.eventPhase,
    isTrusted: event.isTrusted,
    preventDefault,
    isDefaultPrevented: () => isDefaultPrevented,
    stopPropagation,
    isPropagationStopped: () => isPropagationStopped,
    persist: () => {},
    timeStamp: event.timeStamp,
    type: event.type
  };
};
