import classNames from 'classnames';
import React, { ComponentPropsWithRef, CSSProperties, FC, ReactNode, useId, useRef } from 'react';
import { BooleanProp } from '../../../../../models';
import { stringToBoolean } from '../../../../../utils';
import { MenuItem, MenuItemRef } from '../../../../molecules/menu-item';
import { closeOtherSubmenus } from '../../utils/close-other-menu';
import { handleClickMenuItem } from '../../utils/handle-click-menu-item';
import { HeaderMenuOptions } from '../header-menu-item-options/header-menu-item-options';
import './header-navigation.scss';
import { BackdropRef, HeaderNavigationType, MenuProps } from '../../models';

export interface HeaderNavigationProps extends ComponentPropsWithRef<'div'> {
  /**
   * Activate navigation darkMode
   */
  darkModeHeaderNavigation?: BooleanProp;

  /**
   * Navigation aria-label
   */
  ariaLabelHeaderNavigation: string;

  /**
   * Content can be a custom element of logo default position.
   */
  headerNavigationContent?: ReactNode;

  /**
   * Legal sentence
   */
  headerNavigationLegalSentence: string;

  /**
   * Navigation menu items
   */
  menuItemsHeaderNavigation: MenuProps[];

  /**
   * Free Navigation menu item
   */
  freeMenuItemsHeaderNavigation?: ReactNode;

  /**
   * / Header navigation type
   */
  headerNavigationType: HeaderNavigationType;
}

const HeaderNavigation: FC<HeaderNavigationProps & BackdropRef & { optionsMenuOverflow?: CSSProperties }> = ({
  darkModeHeaderNavigation,
  ariaLabelHeaderNavigation,
  headerNavigationContent,
  menuItemsHeaderNavigation,
  freeMenuItemsHeaderNavigation,
  headerNavigationType = 'primary',
  headerNavigationLegalSentence = '',
  className,
  setBackdropVisible,
  optionsMenuOverflow
}) => {
  const menuItemId = useId();
  const menuItemRef = useRef<MenuItemRef>(null);

  // #region ClassNames
  const headerNavigationClassName = classNames(
    'container mx-auto agora-header-navigation-options grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-32 min-h-[60px]',
    {
      darkMode: stringToBoolean(darkModeHeaderNavigation)
    },
    className
  );

  const legalSentenceClassName = classNames('text-m-regular', {
    'text-[var(--color-neutral-700)]': !darkModeHeaderNavigation,
    'text-[var(--color-white)]': darkModeHeaderNavigation,
    'sr-only': headerNavigationContent
  });

  // #endregion

  return (
    <div className={headerNavigationClassName}>
      <div className="xs:col-span-4 md:col-span-8 xl:col-span-5 flex items-center">
        {headerNavigationType === 'primary' && <p className={legalSentenceClassName}>{headerNavigationLegalSentence}</p>}
        {headerNavigationContent}
      </div>
      {menuItemsHeaderNavigation && (
        <nav aria-label={ariaLabelHeaderNavigation} className="xs:col-span-4 md:col-span-8 xl:col-span-7">
          <ul role="menubar" className="flex justify-end gap-32">
            {menuItemsHeaderNavigation
              ?.filter((_i, index) => index < 5)
              .map((item: MenuProps, index) => {
                const id = `${menuItemId}-menu-${index}`;
                const containerHeaderMenuOptions = classNames(
                  'agora-header-navigation-options-menu overflow-auto hidden absolute w-full right-0 z-sticky shadow-bottom-high',
                  {
                    'bg-[var(--color-neutral-50)]': item.submenuItem?.highlightItems,
                    'bg-[var(--color-neutral-100)]': !item.submenuItem?.highlightItems
                  }
                );
                return (
                  <li key={id} role="none">
                    <MenuItem
                      {...item.menuItem}
                      ref={menuItemRef}
                      itemType={headerNavigationType}
                      darkMode={darkModeHeaderNavigation}
                      onClick={(evt: any) => {
                        item.menuItem.onClick?.(evt);
                        handleClickMenuItem(evt, setBackdropVisible);
                      }}
                    />

                    {item.submenuItem && (
                      <div style={optionsMenuOverflow} className={containerHeaderMenuOptions}>
                        <HeaderMenuOptions
                          submenu={item.submenuItem}
                          closeOtherMenuItemOpened={closeOtherSubmenus}
                          setBackdropVisible={setBackdropVisible}
                        />
                        {item.submenuItem?.highlightItems && (
                          <div aria-label={item.submenuItem?.highlightItems.submenuAriaLabel} className="bg-[var(--color-neutral-100)]">
                            <HeaderMenuOptions
                              submenu={item.submenuItem?.highlightItems}
                              closeOtherMenuItemOpened={closeOtherSubmenus}
                              setBackdropVisible={setBackdropVisible}
                              className="flex flex-wrap gap-32"
                            />
                          </div>
                        )}
                      </div>
                    )}
                  </li>
                );
              })}
          </ul>
        </nav>
      )}
      {freeMenuItemsHeaderNavigation && (
        <div className="xs:col-span-4 md:col-span-8 xl:col-span-7 flex justify-end gap-32 pb-16 pt-[12px]">
          {freeMenuItemsHeaderNavigation}
        </div>
      )}
    </div>
  );
};

export { HeaderNavigation };
