import { getDateString } from '../../../../utils/get-date-string';

export const parseInputDate = (val?: string | null): Date | undefined => {
  if (!val) {
    return undefined;
  }

  if (/^(\d+-)(\d+-)(\*|\d+)$/.test(val)) {
    const parts = val.split('-');
    if (parts) {
      const day = +parts[0];
      const month = +parts[1];
      const year = +parts[2];
      const parsedDate = new Date(year, month - 1, day);

      return getDateString(parsedDate) === val ? parsedDate : undefined;
    }
  }

  return undefined;
};
