import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent as ReactKeyboardEvent,
  useEffect,
  useMemo,
  useRef,
  useState
} from 'react';
import { Button } from '../../atoms/button';
import { Dropdown, DropdownOption, DropdownOptionProp, DropdownRef, DropdownSection } from '../../molecules/dropdown';
import './table-pagination.scss';

export interface TablePaginationProps extends ComponentPropsWithRef<'div'> {
  /**
   * Defines the label for the items per page
   */
  itemsPerPageLabel: string;
  /**
   * Defines the label for the items
   */
  itemsLabel: string;
  /**
   * Defines the page size
   */
  itemsPerPage: number;
  /**
   * Total items of the table
   */
  totalItems: number;
  /**
   * Array of values to be displayed in the dropdown
   */
  availablePageSizes: number[];
  /**
   * Current page of the table
   */
  currentPage: number;
  /**
   * Aria label for the button
   */
  buttonDropdownAriaLabel: string;
  /**
   * Aria label for the dropdown list
   */
  dropdownListAriaLabel: string;
  /**
   * Aria label for the previous button
   */
  prevButtonAriaLabel?: string;
  /**
   * Aria label for the next button
   */
  nextButtonAriaLabel?: string;
  /**
   * Callback to be called when the page changes
   */
  onPageChange?: (newPage: number) => void;
  /**
   * Callback to be called when the dropdown changes
   */
  onPageSizeChange?: (newPageSize: number) => void;
}

const InnerTablePagination: ForwardRefRenderFunction<HTMLDivElement, TablePaginationProps> = (
  {
    itemsPerPageLabel,
    itemsLabel,
    itemsPerPage,
    totalItems,
    availablePageSizes,
    currentPage,
    buttonDropdownAriaLabel,
    dropdownListAriaLabel,
    prevButtonAriaLabel,
    nextButtonAriaLabel,
    ...props
  },
  ref
) => {
  const buttonRef = useRef<HTMLButtonElement>(null);
  const dropdownRef = useRef<DropdownRef>(null);
  const dropdownWrapperRef = useRef<HTMLDivElement>(null);

  const [isDropdownVisible, setIsDropdownVisible] = useState(false);

  const onDropdownButtonClickHandler = () => {
    if (dropdownRef.current) {
      if (dropdownRef.current.visibility) {
        dropdownRef.current.hide();
      } else {
        dropdownRef.current.show();
      }
    }
  };

  const onShowDropdownHandler = () => {
    setIsDropdownVisible(true);
  };

  const onHideDropDownHandler = () => {
    setIsDropdownVisible(false);
  };

  const onDropdownButtonKeyUp = (evt: ReactKeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (dropdownRef.current?.visibility) {
      if (code === 'ArrowDown') {
        dropdownRef.current.first();
      }

      if (code === 'ArrowUp') {
        dropdownRef.current.last();
      }

      if (code === 'Tab') {
        dropdownRef.current.first();
      }
    }
  };

  const onDropdownButtonKeyDownCapture = (evt: ReactKeyboardEvent<HTMLButtonElement>) => {
    const code = evt.code;

    if (dropdownRef.current?.visibility && code === 'Tab') {
      //  PREVENT TAB INDEX CHANGE FOCUS
      evt.preventDefault();
      evt.stopPropagation();
    }
  };

  const onDropdownChangeHandler = (options: DropdownOptionProp[]) => {
    if (options.length > 0) {
      const option = options[0];
      props.onPageSizeChange?.(+option.value);
    }
  };

  useEffect(() => {
    const handleClickOutside = (evt: MouseEvent) => {
      if (dropdownRef.current?.visibility && !dropdownWrapperRef.current?.contains(evt.target as HTMLElement)) {
        dropdownRef.current.hide();
      }
    };

    document?.addEventListener('click', handleClickOutside);

    return () => {
      document?.removeEventListener('click', handleClickOutside);
    };
  }, []);

  const firstItem = currentPage * itemsPerPage + 1;
  const lastItem = Math.min(currentPage * itemsPerPage + itemsPerPage, totalItems);
  const totalPages = Math.ceil(totalItems / itemsPerPage);

  const handlePrevPage = () => {
    props.onPageChange?.(Math.max(0, currentPage - 1));
  };

  const handleNextPage = () => {
    props.onPageChange?.(Math.min(currentPage + 1, totalPages - 1));
  };

  //  Utility to change button icon
  const iconDefinition = useMemo(() => {
    if (!isDropdownVisible) {
      return {
        default: 'agora-solid-chevron-down',
        hover: 'agora-line-chevron-down'
      };
    }

    return {
      default: 'agora-solid-chevron-up',
      hover: 'agora-line-chevron-up'
    };
  }, [isDropdownVisible]);

  return (
    <div className="agora-table-pagination" ref={ref}>
      <div className="section-items" ref={dropdownWrapperRef}>
        <Button
          ref={buttonRef}
          appearance="link"
          hasIcon
          trailingIcon={iconDefinition.default}
          trailingIconHover={iconDefinition.hover}
          aria-label={buttonDropdownAriaLabel}
          onKeyDownCapture={onDropdownButtonKeyDownCapture}
          onKeyUp={onDropdownButtonKeyUp}
          onClick={onDropdownButtonClickHandler}
        >
          {itemsPerPageLabel} {itemsPerPage}
        </Button>

        <div className="absolute -bottom-8 left-0 w-full min-h-[8px]">
          <Dropdown
            ref={dropdownRef}
            type="text"
            onHide={onHideDropDownHandler}
            onShow={onShowDropdownHandler}
            onChange={onDropdownChangeHandler}
          >
            <DropdownSection hideSectionName name="page-size-dropdown" value="page-size-dropdown" aria-label={dropdownListAriaLabel}>
              {availablePageSizes.map((ps) => {
                return (
                  <DropdownOption key={ps} value={String(ps)}>
                    {String(ps)}
                  </DropdownOption>
                );
              })}
            </DropdownSection>
          </Dropdown>
        </div>
      </div>
      <div className="results">
        <p>
          {firstItem} - {lastItem} / {totalItems} {itemsLabel}
        </p>
      </div>
      <div className="action-buttons">
        <div className="prev_box">
          <Button
            variant="neutral"
            darkMode
            className="prev"
            hasIcon
            iconOnly
            trailingIcon="agora-solid-chevron-left"
            trailingIconHover="agora-line-chevron-left"
            onClick={handlePrevPage}
            disabled={currentPage === 0}
            aria-label={prevButtonAriaLabel}
          />
        </div>
        <div className="next_box">
          <Button
            variant="neutral"
            darkMode
            className="next"
            hasIcon
            iconOnly
            trailingIcon="agora-solid-chevron-right"
            trailingIconHover="agora-line-chevron-right"
            onClick={handleNextPage}
            disabled={currentPage === totalPages - 1}
            aria-label={nextButtonAriaLabel}
          />
        </div>
      </div>
    </div>
  );
};

const TablePagination = forwardRef<HTMLDivElement, TablePaginationProps>(InnerTablePagination);

export { TablePagination };
