import classNames from 'classnames';
import React, { ComponentPropsWithoutRef, FC, ReactNode, useRef } from 'react';
import { BooleanProp, HeadingLevel } from '../../../../models';
import { stringToBoolean } from '../../../../utils/string-to-boolean';
import { Anchor, AnchorProps } from '../../../atoms/anchor';
import { useBlockedLink } from '../shared/use-blocked-link';
import './card-collection.scss';

export interface CardCollectionProps extends Omit<ComponentPropsWithoutRef<'div'>, 'title'> {
  /**
   * header Text.
   */
  headingLevel: HeadingLevel;
  /**
   * Defines the subtitle
   */
  subtitle?: ReactNode;
  /**
   * Defines the title
   */
  title?: ReactNode;
  /**
   * Anchor props.
   */
  mainAnchor?: AnchorProps;
  /**
   * Defines blockedLink prop.
   */
  blockedLink?: BooleanProp;
}

export const CardCollection: FC<CardCollectionProps> = ({
  headingLevel: Heading = 'h3',
  subtitle,
  title,
  mainAnchor,
  blockedLink = false,
  className,
  ...props
}) => {
  const cardCollectionRef = useRef<HTMLDivElement | null>(null);

  const cardCollectionAnchorRef = useRef<HTMLAnchorElement | null>(null);

  const CardCollectionClass = classNames('card-collection', className);

  const headingPlacement = {
    title: !!title,
    subtitle: !title && !!subtitle,
    anchor: !title && !subtitle && !!mainAnchor && !mainAnchor.hidden
  };

  const anchorPlacement = {
    anchor: false,
    title: false,
    subtitle: false
  };

  let mainAnchorClone: AnchorProps = { ...mainAnchor };

  if (mainAnchor) {
    mainAnchorClone = {
      ...mainAnchor
    } as AnchorProps;

    if (mainAnchorClone.hasIcon && !mainAnchorClone.trailingIcon && !mainAnchorClone.leadingIcon) {
      mainAnchorClone.trailingIcon = 'agora-line-arrow-right-circle';
      mainAnchorClone.trailingIconHover = 'agora-solid-arrow-right-circle';
    }

    if (mainAnchor.hidden) {
      //  MUST DELETE IN ORDER TO SPREAD IT LATER
      delete mainAnchorClone.hidden;

      //  ONE OF THE TEXTS MUST BE FILLED
      anchorPlacement.title = !!title;
      anchorPlacement.subtitle = !title && !!subtitle;
    } else {
      anchorPlacement.anchor = true;
    }
  }

  const buildText = (text: ReactNode, anchorProps: AnchorProps, isAnchor: boolean, isHeading: boolean): ReactNode => {
    if (!isAnchor) {
      return isHeading ? <Heading>{text}</Heading> : text;
    }

    return (
      <>
        {isHeading && (
          <Heading>
            <Anchor {...anchorProps} variant="neutral" darkMode appearance="link" ref={cardCollectionAnchorRef}>
              {text}
            </Anchor>
          </Heading>
        )}
        {!isHeading && (
          <Anchor {...anchorProps} variant="neutral" darkMode appearance="link" ref={cardCollectionAnchorRef}>
            {text}
          </Anchor>
        )}
      </>
    );
  };

  useBlockedLink({
    parentCardRef: cardCollectionRef,
    anchorRef: cardCollectionAnchorRef,
    blockedLink: stringToBoolean(blockedLink)
  });

  const anchorContainerClassNames = classNames('anchor-container', { 'icon-only': mainAnchorClone?.iconOnly });

  return (
    <div {...props} role="presentation" ref={cardCollectionRef} className={CardCollectionClass}>
      <div className="card-info">
        {subtitle && (
          <div className="subtitle-container">
            {buildText(subtitle, mainAnchorClone, anchorPlacement.subtitle, headingPlacement.subtitle)}
          </div>
        )}

        {title && <div className="title-container">{buildText(title, mainAnchorClone, anchorPlacement.title, headingPlacement.title)}</div>}

        {anchorPlacement.anchor && (
          <div className={anchorContainerClassNames}>
            {buildText(mainAnchorClone.children, mainAnchorClone, anchorPlacement.anchor, headingPlacement.anchor)}
          </div>
        )}
      </div>
      <div className="custom-svg-container -z-10 absolute top-0 left-0 min-w-full min-h-full w-full h-full flex justify-center items-center bg-[var(--color-primary-900)] xs:py-0 md:py-64 xl:py-128">
        <svg
          className="custom-svg"
          width="100%"
          height="100%"
          viewBox="0 0 384 378"
          aria-hidden
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
        >
          <g opacity="0.04" stroke="white" strokeWidth="24" strokeMiterlimit="9.56677" strokeLinecap="round">
            <path d="M-436 189.113C-237.528 189.113 -202.994 271.214 -118.581 252.744C-42.7446 236.151 30.3181 89.0689 81.1009 93.8569C133.588 98.8049 4.82279 267.863 60.1717 290.185C105.121 308.314 250.971 -0.840043 299.676 13.483C357.976 30.624 189.186 355.513 249.579 364.748C308.959 373.827 348.241 135.047 419.085 122.245C483.27 110.647 497 189.113 742 189.113" />
          </g>
        </svg>
      </div>
    </div>
  );
};
