import classNames from 'classnames';
import React, { ChangeEvent, Children, cloneElement, FC, HtmlHTMLAttributes, ReactElement, ReactNode, useId } from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { RadioButton } from '../../atoms/radio';
import { RadioButtonProps } from '../../atoms/radio/radio';
import { Feedback } from '../../shared/feedback';

export interface RadioButtonGroupProps extends Omit<HtmlHTMLAttributes<HTMLDivElement>, 'onChange'> {
  /**
   *  Input label text.
   */
  label?: string;
  /**
   * Decides if display/hide label.
   */
  hideLabel?: BooleanProp;
  /**
   * Group name
   */
  name: string;
  /**
   * Sets the radio button in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below radio-button.
   */
  feedbackText?: ReactNode;
  /**
   *  List of radiobutton props to generate as radio buttons.
   */
  items?: RadioButtonProps[];
  /**
   *  List of radio button components to use as children of the group.
   */
  children?: ReactElement<RadioButtonProps> | Array<ReactElement<ReactElement<RadioButtonProps>>>;
  /**
   *  Event triggered whenever any of the radio buttons changes is own state.
   */
  onChange?: (evt: ChangeEvent<HTMLInputElement>) => void;
}
const RadioButtonGroup: FC<RadioButtonGroupProps> = ({
  className,
  label,
  hideLabel,
  hasError,
  hasFeedback,
  feedbackState = 'info',
  feedbackText,
  onChange,
  items = [],
  children,
  ...props
}) => {
  const generatedId = useId();
  const generatedName = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;
  propsClone.name = propsClone.name ?? generatedName;

  let childrenRadioButtons: Array<ReactElement<ReactElement<RadioButtonProps>>>;

  if (children) {
    if (!Array.isArray(children)) {
      childrenRadioButtons = [children] as ReactElement<ReactElement<RadioButtonProps>>[];
    } else {
      childrenRadioButtons = children;
    }
  } else {
    childrenRadioButtons = [];
  }

  const itemsRadioButtons = items.map((item, index) => {
    const key = `rb-group-${propsClone.id}-${index}`;
    return <RadioButton key={key} {...item} />;
  });

  const allRadioButtons = [...itemsRadioButtons, ...childrenRadioButtons];

  const innerOnChange = (nativeEvent: ChangeEvent<HTMLInputElement>) => {
    onChange?.(nativeEvent);
  };

  const containerClasses = classNames('agora-radiobutton-group', className);

  return (
    <div className={containerClasses}>
      <span
        className="label text-[var(--color-primary-900)] text-m-medium mb-8 block"
        hidden={stringToBoolean(hideLabel)}
        id={`agora-radiobutton-group-${propsClone.id}-label`}
      >
        {label}
      </span>

      <div
        {...propsClone}
        className="group"
        role="radiogroup"
        aria-label={stringToBoolean(hideLabel) ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
        aria-labelledby={`agora-radiobutton-group-${propsClone.id}-label`}
      >
        {Children.map(allRadioButtons, (child) => {
          return cloneElement<RadioButtonProps>(child, {
            ...child.props,
            name: propsClone.name,
            onChange: innerOnChange
          } as RadioButtonProps);
        })}
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export { RadioButtonGroup };
