import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode } from 'react';

export interface NonInteractiveAvatarProps extends ComponentPropsWithRef<'div'> {
  alt: string;
  className?: string;
  children?: ReactNode;
}

export const InnerNonInteractiveAvatar: ForwardRefRenderFunction<HTMLDivElement, NonInteractiveAvatarProps> = (
  { className, children, alt, ...props },
  ref
) => {
  return (
    <div ref={ref} {...props} role="button" aria-label={alt} className={className}>
      {children}
    </div>
  );
};

export const NonInteractiveAvatar = forwardRef<HTMLDivElement, NonInteractiveAvatarProps>(InnerNonInteractiveAvatar);
