import classNames from 'classnames';
import React, {
  Children,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  isValidElement,
  KeyboardEvent,
  ReactElement,
  ReactNode,
  useEffect,
  useId,
  useImperativeHandle,
  useMemo,
  useRef,
  useState
} from 'react';
import { BooleanProp, FeedbackState } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { Icon } from '../../atoms/icon';
import { Feedback } from '../../shared/feedback';

import { Dropdown, DropdownOptionProp, DropdownRef, DropdownSectionProps, DropdownType } from '../../molecules/dropdown';
import './input-select.scss';

export interface InputSelectRef {
  selectedOptions: DropdownOptionProp[];
}

const parseChildrenProps = (children: ReactElement<DropdownSectionProps> | ReactElement<DropdownSectionProps>[]) => {
  let allItemsEnabled = 0;
  const newSelectedData = [] as DropdownOptionProp[];

  Children.forEach(children, (sectionChild) => {
    if (isValidElement<DropdownSectionProps>(sectionChild)) {
      Children.forEach(sectionChild.props.children, (optionChild) => {
        if (isValidElement<DropdownSectionProps>(optionChild)) {
          allItemsEnabled = optionChild.props.disabled ? allItemsEnabled : allItemsEnabled + 1;

          if (optionChild.props.selected) {
            newSelectedData.push(optionChild.props);
          }
        }
      });
    }
  });

  return {
    allItemsEnabled,
    newSelectedData
  };
};

export interface InputSelectProps extends Omit<ComponentPropsWithRef<'select'>, 'options' | 'onChange'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Input placeholder.
   */
  placeholder?: string;
  /**
   * Sets the input in a error state. This also affects the feedback state.
   */
  hasError?: BooleanProp;
  /**
   * Show or hide the feedback status text.
   */
  hasFeedback?: BooleanProp;
  /**
   * Defines the state of the feedback text. This param will change the feedback icon and dye it.
   */
  feedbackState?: FeedbackState;
  /**
   * Feedback text displayed below input.
   */
  feedbackText?: ReactNode;
  /**
   * Show or hide the leading icon.
   */
  hasIcon?: BooleanProp;
  /**
   * Name of the icon leading icon to be displayed.
   */
  icon?: string;
  /**
   * Alternative text for the dropdown that will be rendered.
   */
  dropdownAriaLabel?: string;
  /**
   *  Number of visible items to display in the DropdownList. Remaining items will be accessible via scroll bar.
   */
  visibleCount?: number;
  /**
   *   Event triggered whenever selected value or values changes.
   */
  onChange?: (options: DropdownOptionProp[]) => void;
  /**
   *  Select All item text to be rendered.
   */
  selectAllLabel?: string;
  /**
   *  Enable the search bar to filter items.
   */
  searchable?: BooleanProp;
  /**
   *  Placeholder text for input search bar.
   */
  searchInputPlaceholder?: string;
  /**
   *  Accessible text for the input search bar.
   */
  searchInputAriaLabel?: string;
  /**
   *  Event triggered whenever the input search value changes.
   */
  onSearchInputChange?: (value: string) => void;
  /**
   *  Text to show when filtering presents no results.
   */
  searchNoResultsText?: string;
  /**
   * Text placed as Select all options item
   */
  allSelectedLabel?: string;
  /**
   * Initial template structure for multiple items selections text output
   */
  pluralSelectedPlaceholder?: string;
  /**
   * Type of options to render
   */
  type: DropdownType;
  /**
   * Section list of dropdown options to render.
   */
  children: ReactElement<DropdownSectionProps> | ReactElement<DropdownSectionProps>[];
}

const InnerInputSelect: ForwardRefRenderFunction<InputSelectRef | HTMLSelectElement, InputSelectProps> = (
  {
    label,
    hideLabel,
    placeholder = '',
    pluralSelectedPlaceholder = '',
    visibleCount = 4,
    hasIcon,
    icon,
    hasFeedback,
    feedbackText,
    feedbackState = 'info',
    hasError,
    required = true,
    onChange,
    searchable,
    selectAllLabel,
    allSelectedLabel = '',
    searchInputPlaceholder,
    searchInputAriaLabel,
    searchNoResultsText,
    children,
    onSearchInputChange,
    type,
    dropdownAriaLabel,
    ...props
  },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const [isDropDownVisible, setIsDropDownVisible] = useState(false);

  const controlWrapperRef = useRef<HTMLDivElement>(null);

  const controlRef = useRef<HTMLDivElement>(null);

  const dropdownRef = useRef<DropdownRef>(null);

  //  INITIALIZE SELECTED VALUES
  const [selectedData, setSelectedData] = useState<DropdownOptionProp[]>([]);

  useImperativeHandle(
    ref,
    () => {
      return {
        selectedOptions: selectedData
      } as InputSelectRef;
    },
    [selectedData]
  );

  const mainWrapperClasses = classNames('agora-input-select', {
    'disabled': stringToBoolean(propsClone.disabled),
    'has-value': selectedData.length > 0,
    'has-error': stringToBoolean(hasError)
  });

  const controlClasses = classNames('agora-input-select-control', {
    'is-overlay-open': isDropDownVisible,
    'has-leading-icon': hasIcon && icon?.indexOf('agora-line') === 0 // allowing only line family icon
  });

  //  NEED TO REEVALUATE ALL CHILDREN TO ENSURE THAT SELECTED VALUES AND DISABLED VALUES ARE SYNC
  const stringOutputMemo: string = useMemo(() => {
    const { allItemsEnabled, newSelectedData } = parseChildrenProps(children);

    if (newSelectedData.length === allItemsEnabled) {
      return allSelectedLabel;
    }

    if (newSelectedData.length === 0) {
      return placeholder;
    }

    if (newSelectedData.length === 1) {
      return newSelectedData[0].children;
    }

    return `${newSelectedData.length} ${pluralSelectedPlaceholder}`;
  }, [children, placeholder, pluralSelectedPlaceholder, allSelectedLabel]);

  const focusInput = () => {
    if (!propsClone.disabled) {
      controlRef.current?.focus();
    }
  };

  const onHide = () => {
    setIsDropDownVisible(false);
  };

  const onShow = () => {
    setIsDropDownVisible(true);
  };

  const onDropDownChange = (values: DropdownOptionProp[]) => {
    // select local values
    setSelectedData(values);

    // trigger external data selection callback
    onChange?.(values);
  };

  const onDropDownClickHandler = () => {
    if (dropdownRef.current) {
      if (dropdownRef.current.visibility) {
        dropdownRef.current.hide();
      } else {
        dropdownRef.current.show();
      }
    }
  };

  const onDropDownKeyUpHandler = (evt: KeyboardEvent<HTMLDivElement>) => {
    const code = evt.code;

    if (dropdownRef.current) {
      if (code === 'Escape') {
        dropdownRef.current.hide();
      }

      if (dropdownRef.current.visibility) {
        if (code === 'ArrowDown') {
          dropdownRef.current.first();
        }

        if (code === 'ArrowUp') {
          dropdownRef.current.last();
        }

        if (code === 'Tab') {
          dropdownRef.current.first();
        }
      }
    }
  };

  const handleDropDownControlKeyDownCapture = (evt: KeyboardEvent<HTMLDivElement>) => {
    const code = evt.code;

    if (!dropdownRef.current) {
      return;
    }

    if (dropdownRef.current.visibility) {
      if (code === 'Tab' || code === 'ArrowDown' || code === 'ArrowUp') {
        //  PREVENT SCROLL AND TAB INDEX CHANGE FOCUS
        evt.preventDefault();
        evt.stopPropagation();
      }
    }

    if (evt.code === 'Enter' || evt.code === 'NumpadEnter' || evt.code === 'Space') {
      if (dropdownRef.current.visibility) {
        dropdownRef.current.hide();
      } else {
        dropdownRef.current.show();
      }
    }
  };

  useEffect(() => {
    if (!isDropDownVisible) {
      controlRef.current?.setAttribute('aria-activedescendant', '');
    }
  }, [isDropDownVisible]);

  useEffect(() => {
    const handleClickOutside = (evt: MouseEvent) => {
      if (dropdownRef.current?.visibility && !controlWrapperRef.current?.contains(evt.target as HTMLElement)) {
        dropdownRef.current.hide();
      }
    };

    document?.addEventListener('click', handleClickOutside);

    return () => {
      document?.removeEventListener('click', handleClickOutside);
    };
  }, []);

  return (
    <div className={mainWrapperClasses} hidden={!!propsClone.hidden}>
      {!stringToBoolean(hideLabel) && (
        <label
          id={`agora-input-select-${propsClone.id}-control-label`}
          htmlFor={`agora-input-select-${propsClone.id}-control`}
          className="agora-input-select-label"
          onClickCapture={focusInput}
        >
          {label}
        </label>
      )}

      <div className="agora-input-select-control-wrapper" ref={controlWrapperRef}>
        <div
          id={`agora-input-select-${propsClone.id}-control`}
          ref={controlRef}
          role="combobox"
          aria-invalid={stringToBoolean(hasError)}
          aria-disabled={stringToBoolean(propsClone.disabled)}
          aria-expanded={isDropDownVisible}
          aria-controls={propsClone.id}
          aria-haspopup="listbox"
          aria-required={required}
          aria-label={stringToBoolean(hideLabel) ? propsClone['aria-label'] ?? label : propsClone['aria-label']}
          aria-labelledby={label && !stringToBoolean(hideLabel) ? `agora-input-select-${propsClone.id}-control-label` : ''}
          className={controlClasses}
          tabIndex={propsClone.disabled ? -1 : 0}
          onClick={onDropDownClickHandler}
          onKeyUp={onDropDownKeyUpHandler}
          onKeyDownCapture={handleDropDownControlKeyDownCapture}
        >
          {hasIcon && icon && icon.startsWith('agora-line') && (
            <div className="input-icon input-leading-icon">
              <Icon name={icon} aria-hidden />
            </div>
          )}

          <div className="content">{stringOutputMemo}</div>

          <div className="indicator">
            <Icon name="agora-solid-arrow-down" aria-hidden />
          </div>
        </div>

        <div className="absolute -bottom-8 left-0 w-full min-h-[8px]">
          <Dropdown
            aria-label={dropdownAriaLabel}
            type={type}
            ref={dropdownRef}
            id={propsClone.id}
            selectAllLabel={selectAllLabel}
            optionsVisible={visibleCount}
            searchable={searchable}
            searchAriaLabel={searchInputAriaLabel}
            searchPlaceholder={searchInputPlaceholder}
            noSearchResults={searchNoResultsText}
            onSearchChange={onSearchInputChange}
            onChange={onDropDownChange}
            onHide={onHide}
            onShow={onShow}
          >
            {children}
          </Dropdown>
        </div>
      </div>

      {/* Feedback */}
      {stringToBoolean(hasFeedback) && feedbackText && (
        <Feedback feedbackState={!stringToBoolean(hasError) ? feedbackState : 'danger'} feedbackText={feedbackText} />
      )}
    </div>
  );
};

export const InputSelect = forwardRef<InputSelectRef | HTMLSelectElement, InputSelectProps>(InnerInputSelect);
