import React, { FC } from 'react';
import { Button } from '../../../atoms/button';
import { useDatePickerStateContext } from '../contexts/use-date-picker-state-context';

export const DialogFooter: FC = () => {
  const ctx = useDatePickerStateContext();

  const onOkPressed = () => {
    ctx.setSelectedDate(ctx.focusedDate);
  };

  const labels = ctx.getLabels();

  const additionalTodayProps: { 'aria-label'?: string } = {
    'aria-label': `${labels.todayLabel}, ${labels.todayAriaLabel}`
  };
  if (!labels.todayAriaLabel) {
    delete additionalTodayProps['aria-label'];
  }

  const additionalCancelProps: { 'aria-label'?: string } = {
    'aria-label': `${labels.cancelLabel}, ${labels.cancelAriaLabel}`
  };
  if (!labels.cancelAriaLabel) {
    delete additionalCancelProps['aria-label'];
  }

  const additionalOkProps: { 'aria-label'?: string } = {
    'aria-label': `${labels.okLabel}, ${labels.okAriaLabel}`
  };
  if (!labels.okAriaLabel) {
    delete additionalOkProps['aria-label'];
  }

  return (
    <div className="flex justify-between items-center">
      <div className="my-16">
        <Button appearance="link" onClick={ctx.focusToday} {...additionalTodayProps}>
          {labels.todayLabel}
        </Button>
      </div>
      <div className="my-16 flex items-end gap-8">
        <Button appearance="outline" onClick={ctx.cancel} {...additionalCancelProps}>
          {labels.cancelLabel}
        </Button>
        <Button appearance="solid" onClick={onOkPressed} {...additionalOkProps}>
          {labels.okLabel}
        </Button>
      </div>
    </div>
  );
};
