import classNames from 'classnames';
import React, { FC, KeyboardEvent as ReactKeyboardEvent, useEffect, useId, useRef, useState } from 'react';
import { Anchor } from '../../anchor';
import { Button } from '../../button';
import { BreadcrumbProps } from '../breadcrumb';
import './breadcrumb-mobile.scss';
import { stringToBoolean } from '../../../../utils';

export const BreadcrumbMobile: FC<BreadcrumbProps> = ({ sectionTitle, items = [], darkMode, ...props }) => {
  const generatedId = useId();
  const [isExpanded, setIsExpanded] = useState(false);
  const buttonRef = useRef<HTMLButtonElement>(null);
  const listRef = useRef<HTMLOListElement>(null);
  const BreadcrumbClassName = classNames(props.className, 'agora-breadcrumb-mobile');

  const ariaLabel = props['aria-label'] ?? 'Breadcrumb';

  /**
   * Function used to close the breadcrumb menu.
   */
  const closeMenuClickHandler = (evt: MouseEvent) => {
    if (listRef?.current && evt.target && !listRef.current.contains(evt.target as HTMLElement)) {
      setIsExpanded(false);
    }
  };

  const handleTabKey = (evt: ReactKeyboardEvent<HTMLAnchorElement>) => {
    const linkList = listRef.current?.querySelectorAll('a');
    const target = evt.target as HTMLAnchorElement;
    const liParentNode = target.parentNode as HTMLLIElement;

    if (evt.shiftKey) {
      const firstLiChild = linkList?.[0].parentNode as HTMLLIElement;
      if (firstLiChild === liParentNode) {
        evt.preventDefault();
        if (linkList) {
          linkList[linkList.length - 1]?.focus();
        }
      }
    } else {
      const lastLiWithChildAnchor = linkList?.[linkList.length - 1].parentNode as HTMLLIElement;
      if (lastLiWithChildAnchor === liParentNode) {
        evt.preventDefault();
        if (linkList) {
          linkList[0]?.focus();
        }
      }
    }
  };

  /**
   * Function used to set Keyboard Keydown Behavior.
   */
  const setKeyboardKeydownBehavior = (evt: ReactKeyboardEvent<HTMLAnchorElement>) => {
    if (evt.key === 'Escape' || evt.key === 'Esc') {
      setIsExpanded(false);
      buttonRef.current?.focus();
    }

    if (evt.key === 'Tab') {
      handleTabKey(evt);
    }

    if (evt.key === 'Enter') {
      setIsExpanded(false);
    }
  };

  /**
   * Used to setup the Event Listeners (mouse and keyboard).
   */
  useEffect(() => {
    if (isExpanded) {
      document.addEventListener('mousedown', closeMenuClickHandler);

      /**
       * Focus the first link when opening the menu.
       */
      const firstLink = listRef.current?.querySelectorAll('a');
      if (firstLink) {
        firstLink[0]?.focus();
      }
    }

    return () => {
      document.removeEventListener('mousedown', closeMenuClickHandler);
    };
  }, [isExpanded]);

  /**
   * Function used to Expand the breadcrumb Menu.
   */
  const expandMenu = () => {
    setIsExpanded((expanded) => {
      return !expanded;
    });
  };

  return (
    <nav aria-label={ariaLabel} className={BreadcrumbClassName}>
      <Button
        type="button"
        ref={buttonRef}
        aria-expanded={isExpanded}
        aria-controls={`agora-mobile-breadcrumb-menu-${generatedId}`}
        onClick={expandMenu}
        hasIcon
        leadingIcon="agora-line-chevron-left"
        leadingIconHover="agora-solid-chevron-left"
        appearance="link"
        variant="neutral"
        darkMode={stringToBoolean(darkMode)}
      >
        {items.at(-1)?.label}
      </Button>

      <ol id={`agora-mobile-breadcrumb-menu-${generatedId}`} className={`${!isExpanded ? 'hidden' : 'flex'}`} ref={listRef}>
        {sectionTitle && (
          <li>
            <p className="[&>*]:text-s-semibold  text-s-semibold [&>*]:text-[var(--color-neutral-500)] text-[var(--color-neutral-500)]">
              {sectionTitle}
            </p>
          </li>
        )}
        {items.map((link, index) => {
          const id = `agora-mobile-breadcrumb-link-${generatedId}${index}`;

          if (index === items.length - 1 || !link.url) {
            return (
              <li id={id} key={id}>
                <span aria-current="page" className="inline-flex items-center min-h-[44px]">
                  {link.label}
                </span>
              </li>
            );
          }

          return (
            <li id={id} key={id}>
              <Anchor variant="neutral" appearance="link" href={link.url} onKeyDown={setKeyboardKeydownBehavior}>
                {link.label}
              </Anchor>
            </li>
          );
        })}
      </ol>
    </nav>
  );
};
