import React, { ComponentPropsWithRef, forwardRef, ForwardRefRenderFunction, ReactNode } from 'react';
import { Button } from '../button';

export interface InteractiveAvatarProps extends ComponentPropsWithRef<'div'> {
  alt: string;
  className?: string;
  onActivate?: () => void;
  children?: ReactNode;
}

export const InnerInteractiveAvatar: ForwardRefRenderFunction<HTMLDivElement, InteractiveAvatarProps> = (
  {
    className,
    onActivate,
    children,
    alt,
    'aria-haspopup': ariaHasPopup,
    'aria-expanded': ariaExpanded,
    'aria-current': ariaCurrent,
    'aria-label': ariaLabel,
    role,
    ...props
  },
  ref
) => {
  const onClickHandler = () => {
    onActivate?.();
  };

  return (
    <div ref={ref} {...props} className={className}>
      <Button
        {...(ariaHasPopup !== undefined ? { 'aria-haspopup': ariaHasPopup } : {})}
        {...(ariaExpanded !== undefined ? { 'aria-expanded': ariaExpanded } : {})}
        {...(ariaCurrent !== undefined ? { 'aria-current': ariaCurrent } : {})}
        {...(role !== undefined ? { role: role.toString() } : {})}
        aria-label={alt ?? ariaLabel}
        type="button"
        className={className}
        onClick={onClickHandler}
      >
        {children}
      </Button>
    </div>
  );
};

export const InteractiveAvatar = forwardRef<HTMLDivElement, InteractiveAvatarProps>(InnerInteractiveAvatar);
