import React, { AnchorHTMLAttributes, FC, ReactNode } from 'react';

import { Anchor } from '../../../atoms/anchor';
import { Icon } from '../../../atoms/icon';
import { LinkableIconsProps } from '../models/LinkableIconsProps';

const FooterSocialUsefulCopyright: FC<{
  socialsLink?: LinkableIconsProps[];
  usefulLinks?: AnchorHTMLAttributes<HTMLAnchorElement>[];
  copyright: string | ReactNode;
}> = ({ socialsLink, usefulLinks, copyright }) => {
  return (
    <div className="grid items-center xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 md:gap-x-32">
      <div className="xs:col-span-4 md:col-start-2 md:col-span-6 xl:col-span-4 xs:py-16 md:py-32 xl:py-64">
        {socialsLink && (
          <ul className="flex md:flex-wrap xs:justify-center md:justify-start gap-x-32">
            {socialsLink
              .filter((_i, index) => index < 10)
              .map((social, index) => {
                const key = `social-${index}`;
                return (
                  <li key={key}>
                    {social?.link && social?.icon && (
                      <a
                        {...social.link}
                        className="group ease-in-out overflow-hidden relative flex flex-wrap justify-center content-center outline-none border-2 border-transparent rounded-4 focus:rounded-none focus:outline focus:outline-2 focus:-outline-offset-2 focus:outline-[var(--color-focus)] h-56 w-56 text-white"
                      >
                        <Icon
                          aria-hidden
                          name={social.icon}
                          dimensions="m"
                          className="absolute ease-in-out translate-x-0 translate-y-2/4 opacity-100 group-hover:opacity-0 group-focus:opacity-0 group-focus:translate-x-0 group-focus:-translate-y-0 group-hover:translate-x-0 group-hover:translate-y-0 fill-white"
                        />
                        <Icon
                          aria-hidden
                          name={social.iconHover}
                          dimensions="m"
                          className="absolute ease-in-out translate-x-0 translate-y-24 opacity-0  group-hover:opacity-100 group-focus:opacity-100 group-hover:translate-x-0 group-hover:translate-y-2/4 group-focus:translate-x-0 group-focus:translate-y-2/4 fill-white"
                        />
                      </a>
                    )}
                    {social && !social.link && social.icon && <Icon name={social.icon} alt={social.iconAlt} />}
                  </li>
                );
              })}
          </ul>
        )}
      </div>
      <div className="xs:col-span-4 md:col-span-8 xl:col-span-1 min-h-full min-w-full xl:relative">
        {socialsLink && socialsLink.length > 0 && (copyright || (usefulLinks && usefulLinks.length > 0)) && (
          <hr className="border-1 border-white opacity-5 xl:h-[calc(100%-2px)] max-xl:w-full xl:bottom-0 xs:left-0 md:left-0 xl:left-1/2 absolute" />
        )}
      </div>
      <div className="xs:col-span-4 md:col-span-8 xl:col-span-7 xs:py-32 md:py-64">
        <div className="xs:col-span-4 md:col-span-8 xl:col-span-7">
          {usefulLinks && (
            <ul className="flex flex-wrap xs:flex-col md:flex-row xl:flex-row md:justify-center xl:justify-end xs:gap-x-32 md:gap-[30px]">
              {usefulLinks
                .filter((_i, index) => index < 8)
                .map((link, index) => {
                  const key = `useful-${index}`;
                  return (
                    <li key={key} className="py-8">
                      <Anchor {...link} darkMode appearance="link" variant="neutral" />
                    </li>
                  );
                })}
            </ul>
          )}
        </div>
        <div className="xs:col-span-4 md:col-span-8 xl:col-span-7 mt-32">
          <span className="text-s-regular text-[var(--color-neutral-500)] flex md:justify-center xl:justify-end">{copyright}</span>
        </div>
      </div>
    </div>
  );
};

export { FooterSocialUsefulCopyright };
