import React, { AnchorHTMLAttributes, FC, ReactNode } from 'react';
import { CardFooterProps } from '../models/CardFooterProps';
import { CardLinksFooterProps } from '../models/CardLinksFooterProps';
import { ContactsFooterProps } from '../models/ContactsFooterProps';
import { LinkableIconsProps } from '../models/LinkableIconsProps';
import { LinkableImagesProps } from '../models/LinkableImagesProps';
import { MainFooter } from '../shared-components/footer-main';
import { PublicFooterHighlight } from './public-footer-highlight';
import { PublicFooterMainPlusCardLinks } from './public-footer-main-plus-card-links';
import './public-footer.scss';

export interface PublicFooterProps {
  /**
   *  Informative card. Links limited to first 3, remaining array is discarded.
   */
  card?: CardFooterProps;

  /**
   *  Contacts limited to first 2, remaining array is discarded.
   */
  contacts?: ContactsFooterProps[];

  /*
   * Informative list of links. Links limited to first 5, remaining array is discarded.
   */
  cardLinks?: CardLinksFooterProps;

  /**
   *  Image on left side with/without link.
   */
  brandImage: LinkableImagesProps;

  /**
   * Image on right side with/without link.
   */
  badgeImage?: LinkableImagesProps;

  /**
   * Small descriptive text
   */
  caption: string | ReactNode;

  /**
   *  List of images with or without link. Links limited to first 10, remaining array is discarded.
   */
  partnersLogos: LinkableImagesProps[];

  /**
   *  List of icons with/without link. Links limited to first 10, remaining array is discarded.
   */
  socialsLink?: LinkableIconsProps[];

  /**
   * List of links. Links limited to first 8, remaining array is discarded.
   */
  usefulLinks?: AnchorHTMLAttributes<HTMLAnchorElement>[];

  /*
   * Copyright text
   */
  copyright: string | ReactNode;
}

const PublicFooter: FC<PublicFooterProps> = ({
  card,
  contacts,
  cardLinks,
  brandImage,
  badgeImage,
  caption,
  partnersLogos,
  socialsLink,
  usefulLinks,
  copyright
}) => {
  return (
    <div className="agora-footer">
      {(card || contacts || !cardLinks) && (
        <>
          <PublicFooterHighlight card={card} cardLinks={cardLinks} contacts={contacts} />
          {(card || cardLinks || contacts) && (
            <hr className="border-1 xs:border-white md:border-transparent xl:border-transparent opacity-5 w-full absolute left-0" />
          )}
          <MainFooter
            brandImage={brandImage}
            badgeImage={badgeImage}
            caption={caption}
            partnersLogos={partnersLogos}
            socialsLink={socialsLink}
            usefulLinks={usefulLinks}
            copyright={copyright}
          />
        </>
      )}
      {!card && cardLinks && (
        <PublicFooterMainPlusCardLinks
          cardLinks={cardLinks}
          brandImage={brandImage}
          badgeImage={badgeImage}
          caption={caption}
          partnersLogos={partnersLogos}
          socialsLink={socialsLink}
          usefulLinks={usefulLinks}
          copyright={copyright}
        />
      )}
    </div>
  );
};

export { PublicFooter };
