import React, { ComponentPropsWithoutRef, forwardRef, ForwardRefRenderFunction, ReactNode, useMemo } from 'react';
import { Icon } from '../icon';
import { BooleanProp } from '../../../models';
import { stringToBoolean } from '../../../utils';
import { InteractiveStatusCard } from './interactive-status-card';
import { NonInteractiveStatusCard } from './non-interactive-status-card';
import './status-card.scss';

export type StatusCardType = 'info' | 'success' | 'warning' | 'danger';

export interface StatusCardProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * Status card description content
   */
  description: ReactNode;

  /**
   * The available Status card types. Defines the appearance of the cards.
   */
  type: StatusCardType;

  /**
   * The flag to make the status card interactive. When it is interactive,
   * card can be focused by clicking on it.
   */
  interactive?: BooleanProp;

  /**
   * Callback to run whenever the status card is interactive.
   */
  onActivate?: () => void;
}

export const InnerStatusCard: ForwardRefRenderFunction<HTMLDivElement, StatusCardProps> = (
  { className, description, type = 'info', interactive, onActivate, ...props },
  ref
) => {
  const memoContent = useMemo(() => {
    const handleIcon = (iconType: StatusCardType) => {
      switch (iconType) {
        case 'info':
          return 'agora-solid-info-mark';
        case 'success':
          return 'agora-solid-check-circle';
        case 'warning':
          return 'agora-solid-info-mark';
        case 'danger':
          return 'agora-solid-alert-triangle';
        default:
          return undefined;
      }
    };

    return (
      <>
        {!stringToBoolean(interactive) && <Icon className="status-icon" name={handleIcon(type)} aria-hidden />}
        <span className="status-description">{description}</span>
      </>
    );
  }, [className, description, type, interactive]);

  if (stringToBoolean(interactive)) {
    return (
      <InteractiveStatusCard
        {...props}
        className={className}
        type={type}
        ref={ref}
        onActivate={onActivate}
        interactive={stringToBoolean(interactive)}
      >
        {memoContent}
      </InteractiveStatusCard>
    );
  }

  return (
    <NonInteractiveStatusCard {...props} className={className} type={type} ref={ref}>
      {memoContent}
    </NonInteractiveStatusCard>
  );
};

export const StatusCard = forwardRef<HTMLDivElement, StatusCardProps>(InnerStatusCard);
