import classNames from 'classnames';
import React, { cloneElement, ComponentPropsWithoutRef, CSSProperties, FC, ReactElement, useId } from 'react';
import './list.scss';
import { Icon } from '../icon';

export type ListType = 'unordered' | 'ordered';

export type ListStyle = 'none' | 'bullet' | 'number' | 'icon';

export interface ListProps extends ComponentPropsWithoutRef<'ul'> {
  /**
   * Type list.
   */
  type?: ListType;
  /**
   * Style of the list depending on the chosen type.
   */
  listStyle?: ListStyle;
  /**
   * Choose the starting number of an ordered number list.
   */
  startNumber?: number;
  /**
   * Choose the icon for an unordered list.
   */
  icon?: string;
  /**
   * Children content.
   */
  children?: ReactElement<HTMLLIElement> | Array<ReactElement<HTMLLIElement>>;
  /**
   * Array the item.
   */
  items?: Array<ReactElement<HTMLLIElement>>;
}

export const List: FC<ListProps> = ({ type = 'unordered', listStyle = 'none', startNumber = 1, children, icon, items = [], ...props }) => {
  const ComponentType = type !== 'ordered' ? 'ul' : 'ol';
  const generatedId = useId();
  const propsClone = { ...props };
  propsClone.id = propsClone.id ?? generatedId;

  const classes = classNames(`list list-${type} list-${listStyle}`, propsClone.className);

  let listChildren: Array<ReactElement> = [];

  if (children) {
    if (!Array.isArray(children)) {
      [children].forEach((child) => listChildren.push(child as unknown as ReactElement));
    } else {
      listChildren = children;
    }
  }

  const listItems = items?.map((data, index) => {
    const idLi = `${generatedId}-${index}`;
    const key = `li-${index}`;
    return cloneElement(data, { id: idLi, key });
  });

  const allItems = [...listItems, ...listChildren];

  return (
    <ComponentType style={{ '--startNumber': startNumber } as CSSProperties} start={startNumber} className={classes} id={propsClone.id}>
      {allItems.map((item, index) => {
        const key = `li-${index}`;
        return (
          <li key={key}>
            {listStyle === 'icon' && type === 'unordered' && <Icon name={icon} aria-hidden />}
            <div className="list-items">{item.props.children}</div>
          </li>
        );
      })}
    </ComponentType>
  );
};
