import classNames from 'classnames';
import React, {
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  ReactNode,
  Ref,
  useEffect,
  useId,
  useImperativeHandle,
  useRef,
  useState
} from 'react';
import './drawer.scss';
import { BooleanProp } from '../../../models';
import { Dialog, DialogElementRef } from '../dialog';

export type DrawerPosition = 'left' | 'right' | 'top' | 'bottom';

export interface DrawerRef {
  current: DialogElementRef | null;
  openDrawer: () => void;
  closeDrawer: () => void;
}

export interface DrawerProps extends ComponentPropsWithRef<'dialog'> {
  /**
   * Children content.
   */
  children?: ReactNode;
  /**
   * Dismiss on escape.
   */
  dismissOnEscape?: BooleanProp;
  /**
   * Dismiss on back drop click.
   */
  dismissOnBackdropClick?: BooleanProp;
  /**
   * Position the drawer.
   */
  position?: DrawerPosition;

  /**
   * Callback to be called when click outside
   * */
  onClickedOutside?: () => void;
}
/**
 * Return icon default animation class drawer icon name and default position.
 */
export const InnerDrawer: ForwardRefRenderFunction<DialogElementRef | DrawerRef, DrawerProps> = (
  { children, dismissOnEscape = true, dismissOnBackdropClick = true, position = 'left', onClickedOutside, className, ...props },
  ref: Ref<DialogElementRef | DrawerRef>
) => {
  const [open, setOpen] = useState(false);
  const generatedId = useId();
  const drawerRef = useRef<DialogElementRef | null>(null);
  const propsClone = { ...props };
  const DrawerClassName = classNames(
    `drawer`,
    { 'drawer-open-top': open && position === 'top' },
    { 'drawer-open-bottom': open && position === 'bottom' },
    { 'drawer-open-left': open && position === 'left' },
    { 'drawer-open-right': open && position === 'right' },
    { 'drawer-close-top': !open && position === 'top' },
    { 'drawer-close-bottom': !open && position === 'bottom' },
    { 'drawer-close-left': !open && position === 'left' },
    { 'drawer-close-right': !open && position === 'right' },
    { 'drawer-horizontal': position === 'left' || position === 'right' },
    { 'drawer-vertical': position === 'top' || position === 'bottom' }
  );

  propsClone.id = propsClone.id ?? generatedId;

  const handleClose = () => {
    const fn = () => {
      setOpen(false);
      drawerRef.current?.hide();
      drawerRef.current?.current?.classList?.remove(`drawer-close-${position}`);
      drawerRef.current?.current?.removeEventListener('animationend', fn);
    };
    drawerRef.current?.current?.addEventListener('animationend', fn);
    drawerRef.current?.current?.classList.add(`drawer-close-${position}`);
  };

  const handleOpen = () => {
    setOpen(true);
    drawerRef.current?.show();
  };

  const handleKeyDown = (event: KeyboardEvent) => {
    if (event.key === 'Escape' && dismissOnEscape) {
      event.preventDefault();
      handleClose();
    }
  };

  const handleClickOutside = (event: MouseEvent) => {
    if (dismissOnBackdropClick && event.target === drawerRef?.current?.current) {
      const dialogElem = drawerRef.current.current;
      const rect = dialogElem?.getBoundingClientRect();
      if (rect) {
        const isInDialog =
          rect.top <= event.clientY &&
          event.clientY <= rect.top + rect.height &&
          rect.left <= event.clientX &&
          event.clientX <= rect.left + rect.width;
        if (!isInDialog) {
          handleClose();
          onClickedOutside?.();
        }
      }
    }
  };

  useEffect(() => {
    document?.addEventListener('keydown', handleKeyDown);
    document?.addEventListener('click', handleClickOutside);

    return () => {
      document?.removeEventListener('keydown', handleKeyDown);
      document?.removeEventListener('click', handleClickOutside);
    };
  }, [dismissOnEscape, dismissOnBackdropClick]);

  useImperativeHandle(
    ref,
    () => {
      return {
        current: drawerRef.current,
        openDrawer: () => handleOpen(),
        closeDrawer: () => handleClose()
      } as DrawerRef;
    },
    [drawerRef]
  );

  return (
    <Dialog ref={drawerRef} id={propsClone.id} className={className}>
      <div className={DrawerClassName}>{children}</div>
    </Dialog>
  );
};

export const Drawer = forwardRef<DialogElementRef | DrawerRef, DrawerProps>(InnerDrawer);
