import classNames from 'classnames';
import React, { Children, cloneElement, ComponentPropsWithoutRef, FC, isValidElement, ReactElement, useId, useState } from 'react';
import { AgoraIllustrateBookmark } from '../../../../assets/agora-illustrate-bookmark';
import { HeadingLevel } from '../../../../models';
import { AccordionProps } from '../../../atoms/accordion';
import './card-expandable.scss';

export interface CardExpandableProps extends ComponentPropsWithoutRef<'div'> {
  /**
   * header Text.
   */
  headingLevel: HeadingLevel;
  /**
   * Card title.
   */
  title?: string;
  /**
   * Card icon.
   */
  showBookmarkIcon?: boolean;
  /**
   * Card icon aria-label.
   */
  bookmarkIconAlt: string;
  /**
   * Children content.
   */
  children: ReactElement<AccordionProps>;
}

export const CardExpandable: FC<CardExpandableProps> = ({
  headingLevel: Component = 'h3',
  title,
  showBookmarkIcon,
  bookmarkIconAlt = '',
  children,
  className = '',
  ...props
}) => {
  const generatedAccordionId = useId();
  const [isExpanded, setIsExpanded] = useState(false);

  const CardExpandableClasses = classNames('card-expandable', className, { 'is-opened': isExpanded });

  const onExpandedHandler = () => {
    setIsExpanded(true);
  };

  const onCollapsedHandler = () => {
    setIsExpanded(false);
  };

  const mappedChildren: ReactElement<AccordionProps>[] = [];

  Children.forEach(children, (child, index) => {
    const id = `accordion-group-${generatedAccordionId}-accordion-${index}`;

    if (!isValidElement(child)) {
      return;
    }
    const clonedElement = cloneElement<AccordionProps>(child, {
      ...child.props,
      key: id,
      onExpanded: () => {
        child.props.onExpanded?.(child.props);
        onExpandedHandler();
      },
      onCollapsed: () => {
        child.props.onCollapsed?.(child.props);
        onCollapsedHandler();
      }
    });

    mappedChildren.push(clonedElement);
  });

  return (
    <div className={CardExpandableClasses} {...props}>
      {showBookmarkIcon && <AgoraIllustrateBookmark className="card-expandable-icon" title={bookmarkIconAlt} />}

      {title && (
        <div className="card-expandable-header">
          <Component className="header-title" aria-label={title}>
            {title}
          </Component>
        </div>
      )}
      {mappedChildren}
    </div>
  );
};
