import React, { FC, ReactNode, useCallback, useMemo, useRef, useState } from 'react';
import { BooleanProp } from '../../../models/boolean-type';
import { stringToBoolean } from '../../../utils';
import { Dialog, DialogElementRef } from '../../atoms/dialog/dialog';
import { PopupContext, PopupContextProps } from './popup-context';
import { PopupDialog, PopupDialogProps } from './popup-dialog';

export interface PopupProviderProps {
  /**
   * If not a fullscreen dialog, allows the user to dismiss the dialog by clicking in the backdrop
   */
  dismissOnBackdropClick?: BooleanProp;
  /**
   * Allows the dialog to be closed by pressing Escape key.
   */
  dismissOnEscape?: BooleanProp;
  /**
   * Children content.
   */
  children: ReactNode;
}

const PopupProvider: FC<PopupProviderProps> = ({ dismissOnBackdropClick = true, dismissOnEscape = true, children }) => {
  const dialogRef = useRef<DialogElementRef>(null);

  const [dialogChildren, setDialogChildren] = useState<ReactNode>();
  const [dialogProps, setDialogProps] = useState<PopupDialogProps>();

  const show = useCallback(
    (currentDialogChildren: ReactNode, currentDialogProps: PopupDialogProps) => {
      setDialogChildren(currentDialogChildren);
      setDialogProps(currentDialogProps);
      dialogRef.current?.show();
    },
    [setDialogChildren, setDialogProps]
  );

  const hide = useCallback((): void => {
    dialogRef.current?.hide();
  }, []);

  const contextValue = useMemo(
    () =>
      ({
        current: dialogRef.current,
        visibility: !!dialogRef.current?.visible,
        show,
        hide
      }) as PopupContextProps,
    [show, hide]
  );

  return (
    <PopupContext.Provider value={contextValue}>
      <Dialog
        ref={dialogRef}
        fullScreen={false}
        dismissOnBackdropClick={stringToBoolean(dismissOnBackdropClick)}
        dismissOnEscape={stringToBoolean(dismissOnEscape)}
        aria-label={dialogProps?.['aria-label']}
      >
        {!!dialogProps && <PopupDialog {...dialogProps}>{dialogChildren}</PopupDialog>}
      </Dialog>
      {children}
    </PopupContext.Provider>
  );
};

export { PopupProvider };
