import React, {
  Children,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  isValidElement,
  ReactElement,
  ReactNode,
  Ref,
  useEffect,
  useImperativeHandle,
  useRef
} from 'react';
import { BooleanProp } from '../../../models';

import { DropdownOptionProp } from './dropdown-option';
import { DropdownSectionProps } from './dropdown-section';
import { DropdownType } from './dropdown-type';
import { InternalDropdown, InternalDropdownRef } from './internals/internal-dropdown';
import { InternalDropdownContext } from './internals/internal-dropdown-context';
import { useDropdownState } from './internals/use-dropdown-state';

export interface DropdownRef {
  visibility: boolean;
  show: () => void;
  hide: () => void;
  first: () => void;
  last: () => void;
}

export interface DropdownProps extends Omit<ComponentPropsWithRef<'div'>, 'onChange'> {
  /**
   *  Number of visible options to display in the Dropdown. Remaining items will be accessible via scroll bar.
   */
  optionsVisible?: number;
  /**
   *  Define the type of sections that will be used. They can be of type "text" or "checkbox".
   */
  type?: DropdownType;
  /**
   *  Callback to run whenever the dropdown becomes visible.
   */
  onShow?: () => void;
  /**
   *  Callback to run whenever the dropdown becomes hidden.
   */
  onHide?: () => void;
  /**
   *  List of section to render.
   */
  children?: ReactElement<DropdownSectionProps> | ReactElement<DropdownSectionProps>[];
  /**
   *  Callback to run whenever the select state of an option changes.
   */
  onChange?: (options: DropdownOptionProp[]) => void;
  /**
   *  Enable the search bar to filter options.
   */
  searchable?: BooleanProp;
  /**
   *  Accessible text for the input search bar.
   */
  searchAriaLabel?: string;
  /**
   *  Placeholder text for input search bar.
   */
  searchPlaceholder?: string;
  /**
   *  Text to show when filtering return no results.
   */
  noSearchResults?: ReactNode;
  /**
   *  Delay to apply before starting filtering results.
   */
  searchDebounceTimeout?: number;
  /**
   *  Callback to run whenever the filter runs. Called using the filter term and the filtered result options.
   */
  onSearchChange?: (value: string, options: DropdownOptionProp[]) => void;
  /**
   *  Text to display in the "select all options across all sections" option.
   */
  selectAllLabel?: string;
}

const Component: ForwardRefRenderFunction<DropdownRef | null, DropdownProps> = (
  { type = 'text', onChange, onSearchChange, onShow, onHide, children, ...props },
  ref: Ref<DropdownRef | null>
) => {
  const innerRef = useRef<InternalDropdownRef>(null);

  const dropdownState = useDropdownState(innerRef, type, onChange, onSearchChange, onShow, onHide, children);

  //  CALL ON CHANGE TO INITIALIZE COMPONENTS THAT USE DROPDOWN WITH INITIAL DATA
  const firstCall = useRef(false);
  useEffect(() => {
    if (!firstCall.current) {
      firstCall.current = true;

      const options: DropdownOptionProp[] = [];

      Children.forEach(children, (sectionChild) => {
        if (isValidElement<DropdownSectionProps>(sectionChild)) {
          Children.forEach(sectionChild.props.children, (optionChild) => {
            if (isValidElement<DropdownOptionProp>(optionChild) && optionChild.props.selected) {
              options.push(optionChild.props);
            }
          });
        }
      });

      onChange?.(options);
    }
  }, []);

  useImperativeHandle(
    ref,
    () => {
      return {
        visibility: dropdownState.visibility,
        show: dropdownState.show,
        hide: dropdownState.hide,
        first: dropdownState.first,
        last: dropdownState.last
      } as DropdownRef;
    },
    [dropdownState.hide, dropdownState.show, dropdownState.first, dropdownState.last, dropdownState.visibility]
  );

  return (
    <InternalDropdownContext.Provider value={dropdownState}>
      <InternalDropdown {...props} ref={innerRef}>
        {children}
      </InternalDropdown>
    </InternalDropdownContext.Provider>
  );
};

export const Dropdown = forwardRef<DropdownRef | null, DropdownProps>(Component);
