import classNames from 'classnames';
import React, { FC } from 'react';
import { CardFooterProps } from '../models/CardFooterProps';
import { CardLinksFooterProps } from '../models/CardLinksFooterProps';
import { ContactsFooterProps } from '../models/ContactsFooterProps';
import { PublicFooterHighlightCard } from './public-footer-highlight-card';
import { PublicFooterHighlightCardLinks } from './public-footer-highlight-card-links';

const PublicFooterHighlight: FC<{ card?: CardFooterProps; cardLinks?: CardLinksFooterProps; contacts?: ContactsFooterProps[] }> = ({
  card,
  cardLinks,
  contacts
}) => {
  const footerHighlightClassName = classNames('footer-highlight', {
    'footer-highlight-only-card': card && contacts && !cardLinks,
    'footer-highlight-only-help': card && !contacts && !cardLinks,
    'footer-highlight-only-contacts': !card && contacts && !cardLinks
  });

  const cardFooterClassName = classNames('grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 gap-x-32 xl:grid-rows-1', {
    'xl:grid-flow-col': card && cardLinks,
    'xl:grid-flow-row': card && !cardLinks
  });
  return (
    <div className={footerHighlightClassName}>
      <div className="container mx-auto">
        <div className={cardFooterClassName}>
          <PublicFooterHighlightCard card={card} contacts={contacts} hasCardLinks={!!cardLinks} />
          {cardLinks && (
            <div className="grid gap-x-32 xs:grid-cols-4 md:grid-cols-4 xl-grid-cols-4 xs:col-span-4 md:col-span-4 xl:col-span-4 xl:row-span-2 bg-[var(--color-primary-900)] relative">
              <div
                aria-hidden
                className="md:hidden xl:hidden absolute w-[calc(100%+64px)] -left-[32px] h-full bg-[var(--color-primary-900)] z-0"
              />
              <div className="xs:col-span-4 md:col-span-4 xl:col-span-4 xs:py-32 xs:pl-0 md:py-64 md:pl-64 xl:py-64 xl:pl-64 z-[1]">
                <PublicFooterHighlightCardLinks {...cardLinks} />
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export { PublicFooterHighlight };
