import React, { FC, MouseEvent, useEffect, useState } from 'react';
import { MenuItem } from '../../../../molecules/menu-item';
import { HeaderProps } from '../../header';
import { BackdropRef } from '../../models';
import { handleClickMenuItem } from '../../utils/handle-click-menu-item';
import { HeaderBurgerPrimaryNavigation } from './header-burger-primary-navigation';
import { stringToBoolean } from '../../../../../utils';

const HeaderBurgerNavigation: FC<HeaderProps & BackdropRef> = ({
  headerSecondaryNavigation,
  headerPrimaryNavigation: headerPrimaryNavigationClone,
  darkModeHeader,
  setBackdropVisible,
  menuLabel,
  hasBackButtonBurger = stringToBoolean('true'),
  backButtonLabelBurger,
  handleBackButtonBurger
}) => {
  // #region "MENU BURGER"

  const [headerHeight, setHeaderHeight] = useState(0);
  const [submenuHeight, setSubmenuHeight] = useState(0);
  const [submenuLevel2Height, setSubmenuLevel2Height] = useState(0);
  const [submenuLevel3Height, setSubmenuLevel3Height] = useState(0);

  // #region Resize and functions
  const checkResize = (
    burgerNav: Element | null,
    heightGeneralNav: number,
    optionsSubmenu: Element | null,
    backButtonSubmenu: Element | undefined,
    backButtonSubmenuLevel2: Element | undefined,
    backButtonSubmenuLevel3: Element | undefined
  ) => {
    const heightBurgerNav = burgerNav?.clientHeight ?? 0;
    setHeaderHeight(heightGeneralNav + heightBurgerNav);

    const optNextSiblingPaddingTop = optionsSubmenu?.children[0] ? parseInt(getComputedStyle(optionsSubmenu?.children[0]).paddingTop) : 0;

    const optNextSiblingPaddingBottom = optionsSubmenu?.children[0]
      ? parseInt(getComputedStyle(optionsSubmenu?.children[0]).paddingBottom)
      : 0;

    const backButtonSubmenuMarginBottom = backButtonSubmenu ? parseInt(getComputedStyle(backButtonSubmenu).marginBottom) : 0;
    const backButtonSubmenuLevel2MarginTop = backButtonSubmenuLevel2 ? parseInt(getComputedStyle(backButtonSubmenuLevel2).marginTop) : 0;
    const backButtonSubmenuLevel3MarginTop = backButtonSubmenuLevel3 ? parseInt(getComputedStyle(backButtonSubmenuLevel3).marginTop) : 0;

    setSubmenuHeight(
      heightGeneralNav +
        heightBurgerNav +
        optNextSiblingPaddingTop +
        optNextSiblingPaddingBottom +
        backButtonSubmenuMarginBottom +
        (backButtonSubmenu as Element)?.getBoundingClientRect().height
    );
    setSubmenuLevel2Height(
      heightGeneralNav +
        heightBurgerNav +
        backButtonSubmenuLevel2MarginTop +
        (backButtonSubmenuLevel2 as Element)?.getBoundingClientRect().height
    );
    setSubmenuLevel3Height(
      heightGeneralNav +
        heightBurgerNav +
        backButtonSubmenuLevel3MarginTop +
        (backButtonSubmenuLevel3 as Element)?.getBoundingClientRect().height
    );
  };

  const checkMutationPrimary = (
    burgerNav: Element | null,
    heightGeneralNav: number,
    optionsSubmenu: Element | null,
    backButtonSubmenu: Element | undefined
  ) => {
    const heightBurgerNav = burgerNav?.clientHeight ?? 0;
    const optNextSiblingPaddingTop = optionsSubmenu?.children[0] ? parseInt(getComputedStyle(optionsSubmenu?.children[0]).paddingTop) : 0;

    const optNextSiblingPaddingBottom = optionsSubmenu?.children[0]
      ? parseInt(getComputedStyle(optionsSubmenu?.children[0]).paddingBottom)
      : 0;

    const backButtonSubmenuMarginBottom = backButtonSubmenu ? parseInt(getComputedStyle(backButtonSubmenu).marginBottom) : 0;

    setSubmenuHeight(
      heightGeneralNav +
        heightBurgerNav +
        optNextSiblingPaddingTop +
        optNextSiblingPaddingBottom +
        backButtonSubmenuMarginBottom +
        (backButtonSubmenu as Element)?.getBoundingClientRect().height
    );
  };

  const checkMutationSubmenuLevel2 = (
    burgerNav: Element | null,
    heightGeneralNav: number,
    backButtonSubmenuLevel2: Element | undefined
  ) => {
    const heightBurgerNav = burgerNav?.clientHeight ?? 0;
    const backButtonSubmenuLevel2MarginTop = backButtonSubmenuLevel2 ? parseInt(getComputedStyle(backButtonSubmenuLevel2).marginTop) : 0;
    setSubmenuLevel2Height(
      heightGeneralNav +
        heightBurgerNav +
        backButtonSubmenuLevel2MarginTop +
        (backButtonSubmenuLevel2 as Element)?.getBoundingClientRect().height
    );
  };

  const checkMutationSubmenuLevel3 = (
    mutation: MutationRecord,
    burgerNav: Element | null,
    heightGeneralNav: number,
    backButtonSubmenuLevel2: Element | undefined,
    backButtonSubmenuLevel3: Element | undefined
  ) => {
    const heightBurgerNav = burgerNav?.clientHeight ?? 0;
    const backButtonSubmenuLevel3MarginTop = backButtonSubmenuLevel3 ? parseInt(getComputedStyle(backButtonSubmenuLevel3).marginTop) : 0;

    const oldValueArray = mutation.oldValue?.split(' ') ?? [];
    // Needed to know if is a back or a go
    if (oldValueArray.find((word) => word === 'hidden')) {
      setSubmenuLevel2Height(heightGeneralNav + heightBurgerNav + (backButtonSubmenuLevel2 as Element)?.getBoundingClientRect().height);
    } else {
      setSubmenuLevel2Height(
        heightGeneralNav +
          heightBurgerNav +
          backButtonSubmenuLevel3MarginTop +
          (backButtonSubmenuLevel2 as Element)?.getBoundingClientRect().height
      );
    }

    setSubmenuLevel3Height(
      heightGeneralNav +
        heightBurgerNav +
        backButtonSubmenuLevel3MarginTop +
        (backButtonSubmenuLevel3 as Element)?.getBoundingClientRect().height
    );
  };

  const observers = (
    observer: MutationObserver,
    burgerNav: Element | null,
    optionsSubmenu: Element | null,
    optionsSubmenuLevel2: Element | null,
    optionsSubmenuLevel3: Element | null
  ) => {
    if (burgerNav) {
      observer.observe(burgerNav as Node, { attributes: true });
    }

    if (optionsSubmenu) {
      observer.observe(optionsSubmenu as Node, { attributes: true });
    }

    if (optionsSubmenuLevel2) {
      observer.observe(optionsSubmenuLevel2 as Node, { attributes: true, attributeOldValue: true });
    }

    if (optionsSubmenuLevel3) {
      observer.observe(optionsSubmenuLevel3 as Node, { attributes: true, attributeOldValue: true });
    }
  };

  useEffect(() => {
    const heightGeneralNav = document.querySelector('.agora-header-general')?.clientHeight ?? 0;
    const burgerNav = document.querySelector('.agora-header-burger-navigation');

    const optionsSubmenu = document.querySelector('.agora-header-burger-navigation-options-submenu');
    const optionsSubmenuLevel2 = document.querySelector('.agora-header-burger-navigation-options-submenu-level-2');
    const optionsSubmenuLevel3 = document.querySelector('.agora-header-burger-navigation-options-submenu-level-3');
    const backButtonSubmenu = Array.from(optionsSubmenu?.children[0].children ?? []).find((bt) => bt.classList.contains('agora-btn'));
    const backButtonSubmenuLevel2 = Array.from(optionsSubmenuLevel2?.children ?? []).find((bt) => bt.classList.contains('agora-btn'));
    const backButtonSubmenuLevel3 = Array.from(optionsSubmenuLevel3?.children ?? []).find((bt) => bt.classList.contains('agora-btn'));

    checkResize(burgerNav, heightGeneralNav, optionsSubmenu, backButtonSubmenu, backButtonSubmenuLevel2, backButtonSubmenuLevel3);

    // #region Check if hidden attribute remove from burger navigation and get client height
    const mutationCallback = (mutationsList: MutationRecord[]) => {
      for (const mutation of mutationsList) {
        switch (mutation.target) {
          case optionsSubmenu:
            checkMutationPrimary(burgerNav, heightGeneralNav, optionsSubmenu, backButtonSubmenu);
            return;
          case optionsSubmenuLevel2:
            checkMutationSubmenuLevel2(burgerNav, heightGeneralNav, backButtonSubmenuLevel2);
            return;
          case optionsSubmenuLevel3:
            checkMutationSubmenuLevel3(mutation, burgerNav, heightGeneralNav, backButtonSubmenuLevel2, backButtonSubmenuLevel3);
            return;
          default:
            break;
        }
        if (mutation.type !== 'attributes' || mutation.attributeName !== 'hidden') {
          return;
        }
        setHeaderHeight(heightGeneralNav + (mutation.target as Element).clientHeight);
      }
    };
    const observer = new MutationObserver(mutationCallback);
    observers(observer, burgerNav, optionsSubmenu, optionsSubmenuLevel2, optionsSubmenuLevel3);
    // #endregion

    window.addEventListener('resize', () =>
      checkResize(burgerNav, heightGeneralNav, optionsSubmenu, backButtonSubmenu, backButtonSubmenuLevel2, backButtonSubmenuLevel3)
    );
    return () => {
      window.removeEventListener('resize', () =>
        checkResize(burgerNav, heightGeneralNav, optionsSubmenu, backButtonSubmenu, backButtonSubmenuLevel2, backButtonSubmenuLevel3)
      );
      observer.disconnect();
    };
  }, []);
  // #endregion
  // #endregion "MENU BURGER"
  return (
    <div className="container mx-auto xs:flex md:grid xs:grid-cols-4 md:grid-cols-8 xl:grid-cols-12 xs:gap-0 md:gap-32 xs:justify-between md:justify-normal min-h-[60px]">
      <div className="agora-header-burger-navigation-content xs:col-span-3 md:col-span-4 xl:col-span-5 flex items-center">
        {headerSecondaryNavigation?.headerNavigationContent ?? headerPrimaryNavigationClone.headerNavigationContent}
      </div>
      <div className="xs:col-span-1 md:col-span-4 xl:col-span-7 justify-self-end">
        {/* Menu burger */}
        {headerPrimaryNavigationClone?.menuItemsHeaderNavigation && (
          <>
            <MenuItem
              className="agora-burger xs:[&>span]:hidden md:[&>span]:block xs:justify-end md:justify-start"
              itemType="menu"
              aria-haspopup="menu"
              aria-expanded="false"
              darkMode={darkModeHeader}
              onClick={(evt: MouseEvent<HTMLButtonElement>) => {
                handleClickMenuItem(evt, setBackdropVisible);
              }}
            >
              {menuLabel}
            </MenuItem>
            <HeaderBurgerPrimaryNavigation
              headerPrimaryNavigationClone={headerPrimaryNavigationClone}
              headerHeight={headerHeight}
              backButtonLabelBurger={backButtonLabelBurger}
              hasBackButtonBurger={hasBackButtonBurger}
              setBackdropVisible={setBackdropVisible}
              submenuHeight={submenuHeight}
              handleBackButtonBurger={() => handleBackButtonBurger}
              submenuLevel2Height={submenuLevel2Height}
              submenuLevel3Height={submenuLevel3Height}
            />
          </>
        )}
      </div>
    </div>
  );
};

export { HeaderBurgerNavigation };
