import { RefObject, useEffect } from 'react';

export interface IUseBlockedLink {
  parentCardRef: RefObject<HTMLDivElement | null>;
  anchorRef: RefObject<HTMLAnchorElement | null>;
  blockedLink?: boolean;
}

const useBlockedLink = ({ parentCardRef, anchorRef, blockedLink = false }: IUseBlockedLink) => {
  const activateLink = () => {
    if (anchorRef.current) {
      const event = new MouseEvent('click', {
        detail: 1
      });

      anchorRef.current?.dispatchEvent(event);
    }
  };

  const onClickCaptureHandler = (evt: MouseEvent) => {
    if (!blockedLink) {
      return;
    }

    const target = evt.target as HTMLElement;
    if (target.tagName !== 'A' && !target.matches('a *')) {
      //  Is blocked link
      //  Is not an anchor nor a child of an anchor
      //  Trigger main link

      evt.stopPropagation();
      evt.preventDefault();
      activateLink();
    }
  };

  const onMouseEnterHandler = () => {
    if (blockedLink) {
      const anchorElement = anchorRef?.current as HTMLAnchorElement;
      if (anchorElement) {
        anchorElement.classList.add('hover');
      }
    }
  };

  const onMouseLeaveHandler = () => {
    if (blockedLink) {
      const anchorElement = anchorRef?.current as HTMLAnchorElement;
      if (anchorElement) {
        anchorElement.classList.remove('hover');
      }
    }
  };

  useEffect(() => {
    const anchorElement = anchorRef?.current as HTMLAnchorElement;
    const parentCardElem = parentCardRef?.current as HTMLDivElement;

    if (parentCardElem && anchorElement) {
      if (blockedLink) {
        parentCardElem.classList.add('blocked-link');
        parentCardElem.addEventListener('click', onClickCaptureHandler, true);
        parentCardElem.addEventListener('mouseenter', onMouseEnterHandler);
        parentCardElem.addEventListener('mouseover', onMouseEnterHandler);
        parentCardElem.addEventListener('mouseleave', onMouseLeaveHandler);
      } else {
        parentCardElem.classList.remove('blocked-link');
        parentCardElem.removeEventListener('click', onClickCaptureHandler, true);
        parentCardElem.removeEventListener('mouseenter', onMouseEnterHandler);
        parentCardElem.removeEventListener('mouseleave', onMouseLeaveHandler);
      }
    }

    return () => {
      if (parentCardElem) {
        parentCardElem.classList.remove('blocked-link');
        parentCardElem.removeEventListener('click', onClickCaptureHandler, true);
        parentCardElem.removeEventListener('mouseenter', onMouseEnterHandler);
        parentCardElem.addEventListener('mouseover', onMouseEnterHandler);
        parentCardElem.removeEventListener('mouseleave', onMouseLeaveHandler);
      }
    };
  }, [parentCardRef, anchorRef, blockedLink]);
};

export { useBlockedLink };
