import classNames from 'classnames';
import React, {
  ChangeEvent,
  ComponentPropsWithRef,
  forwardRef,
  ForwardRefRenderFunction,
  KeyboardEvent,
  MouseEvent,
  useId,
  useRef,
  useState
} from 'react';
import { BooleanProp } from '../../../models';
import { Icon } from '../icon';
import './switch.scss';

export type SwitchPosition = 'start' | 'center' | 'space-between' | 'space-around' | 'space-evenly';

export interface SwitchProps extends ComponentPropsWithRef<'input'> {
  /**
   * Input label text.
   */
  label?: string;
  /**
   * Show or hide input label.
   */
  hideLabel?: BooleanProp;
  /**
   * Whether the switch is reversed.
   */
  reverse?: boolean;
  /**
   * The position of the switch.
   */
  position?: SwitchPosition;
  /**
   * Defines if the Switch should be fluid
   */
  fullWidth?: boolean;
}

export const InnerSwitch: ForwardRefRenderFunction<HTMLInputElement, SwitchProps> = (
  { className, label, hideLabel, disabled, reverse, position, fullWidth, onChange, ...props },
  ref
) => {
  const generatedId = useId();

  const propsClone = { ...props };

  propsClone.id = propsClone.id ?? generatedId;

  const [isOn, setIsOn] = useState(propsClone.checked ?? propsClone.defaultChecked);

  const wrapperRef = useRef<HTMLDivElement>(null);

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    if (disabled) {
      return;
    }
    setIsOn((last) => !last);
    onChange?.(event);
  };

  const handleCustomSwitchKeydown = (evt: KeyboardEvent<HTMLDivElement>) => {
    if (wrapperRef !== null && (evt.code === 'Space' || evt.code === 'Enter' || evt.code === 'NumpadEnter')) {
      evt.preventDefault();
      wrapperRef.current?.querySelector('input')?.click();
    }
  };

  const handleCustomSwitchClick = (evt: MouseEvent<HTMLDivElement>) => {
    if (wrapperRef !== null) {
      evt.preventDefault();
      wrapperRef.current?.querySelector('input')?.click();
    }
  };

  const switchWrapperClasses = classNames('switch-wrapper', className, {
    'switch-side': reverse,
    [`switch-${position}`]: position,
    'switch-full-width': fullWidth,
    'switch-disabled': disabled,
    'switch-focus': hideLabel,
    'switch-label-focus': !hideLabel
  });

  const markerClasses = classNames('toggle-ball', {
    'icon-disabled': disabled,
    'icon-off': !isOn && !disabled
  });

  const switchLabelClasses = classNames('switch-label', {
    'label-disabled': disabled
  });

  const ariaLabel = props['aria-label'] ?? label;

  return (
    <div ref={wrapperRef} className={switchWrapperClasses}>
      <input {...propsClone} type="checkbox" tabIndex={-1} disabled={disabled} onChange={handleChange} ref={ref} id={propsClone.id} />
      <div
        className={`toggle-switch ${isOn ? 'on' : 'off'}`}
        role="switch"
        aria-checked={isOn ? 'true' : 'false'}
        tabIndex={!disabled ? 0 : -1}
        aria-label={ariaLabel}
        onClick={handleCustomSwitchClick}
        onKeyDown={handleCustomSwitchKeydown}
      >
        <span className={markerClasses}>
          {isOn ? (
            <Icon name="agora-solid-check" aria-hidden className="on" dimensions="xs" />
          ) : (
            <Icon name="agora-solid-x" aria-hidden className="off" dimensions="xs" />
          )}
        </span>
      </div>
      {!hideLabel && label && (
        <label htmlFor={propsClone.id} className={switchLabelClasses}>
          {label}
        </label>
      )}
    </div>
  );
};

export const Switch = forwardRef<HTMLInputElement, SwitchProps>(InnerSwitch);
